<!-- 未缴费班级 -->
<template>
  <div>
    <div class="ivu-table ivu-table-default ivu-table-border relative">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <CheckboxGroup v-model="checkAllGroup" style="font-size: 12px">
          <table
            class="ivu-left-shadow"
            cellspacing="0"
            cellpadding="0"
            border="0"
            :style="{minWidth:tableWidth}"
            style="font-size: 12px!important;"
          >
            <thead>
              <tr class="ivu-table-row">
                <th
                  v-for="(item,index) in tableColumns"
                  :key="index"
                  class="ivu-table-column-center"
                  :width="item.width"
                >
                  <template v-if="item.isCheckBox">
                    <!--<Checkbox v-model="checkAll" @click.native="checkAllHandler" style="margin-left: 6px;">&nbsp;</Checkbox>-->
                    <label class="tal-ipt-checkbox" style="margin-left: 0">
                      <input
                        type="checkbox"
                        class="ivu-checkbox-inner"
                        style="margin-left: -9px;"
                        @click="checkAllHandler"
                        :checked="checkAll"
                      />
                      <span></span>
                    </label>
                  </template>
                  <template v-else>
                    <div class="ivu-table-cell">{{item.title}}</div>
                  </template>
                </th>
              </tr>
            </thead>
            <tbody class="ivu-table-tbody">
              <template v-for="(item, index1) in tableData">
                <template v-if="item.stagesList && item.stagesList.length">
                  <!-- 资源管理-任务管理 查看 task-highlight -->
                  <tr
                    class="ivu-table-row"
                    v-for="(item2,index2) in item.stagesList"
                    :key="index1 + '' +index2"
                    :class="{'task-highlight': item.isTaskRelate === 1 && isTaskMode}"
                  >
                    <template v-if="index2 === 0">
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <Button
                            size="small"
                            type="primary"
                            ghost
                            @click="copy(item.registId)"
                            style="margin-right: 5px"
                          >复制</Button>
                          <span>{{item.registId}}</span>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <a @click="showClassDetail(item.classId)">{{item.className}}</a>
                          <div
                            style="text-align: right;display: inline-block;"
                            v-if="item.businessType === 1"
                          >
                            <Tag color="green">全国班</Tag>
                          </div>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{getClassType(item.classType)}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.subjectNames}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <!--{{item.teacherNames}}-->
                          <template v-for="(tea, index) in item.teacherNames.split(',')">
                            <a
                              @click="showTeacherDetail(studentDetail.bindingCode, item.teacherIds.split(',')[index])"
                              :key="tea"
                            >{{ tea }}</a>
                            <template v-if="index != item.teacherNames.split(',').length - 1">
                              ,
                              <br :key="index" />
                            </template>
                          </template>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.tutorRealName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <Poptip
                            v-if="item.classtimeNames && item.classtimeNames.length > 15"
                            trigger="hover"
                            width="300"
                            word-wrap
                            :content="item.classtimeNames"
                            transfer
                          >{{getNameLength(item.classtimeNames)}}</Poptip>
                          <template v-else>{{item.classtimeNames}}</template>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.serviceCenterName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.classroomName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.passedCount + '/' + item.classCount}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.price}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell" v-if="item.businessType!=1">
                          <Poptip
                            v-if="item.classChangeName && item.classChangeName.length > 15"
                            trigger="hover"
                            width="300"
                            word-wrap
                            :content="item.classChangeName"
                            transfer
                          >{{getNameLength(item.classChangeName)}}</Poptip>
                          <template v-else>{{item.classChangeName}}</template>
                        </div>
                        <div class="ivu-table-cell" v-if="item.businessType==1">——</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.createTime}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.creatorName}}</div>
                      </td>
                    </template>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.stagesName}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.startCurriculumTime}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.endCurriculumTime}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div
                        class="ivu-table-cell"
                        v-if="item2.startCurriculumNum && item2.endCurriculumNum"
                      >{{item2.startCurriculumNum + '-' + item2.endCurriculumNum}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <Poptip
                          v-if="item.remark && item.remark.length > 15"
                          trigger="hover"
                          width="300"
                          word-wrap
                          :content="item.remark"
                          transfer
                        >{{getNameLength(item.remark)}}</Poptip>
                        <template v-else>{{item.remark}}</template>
                      </div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{getPayStatus(item2.status,item2.payStatus)}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.payTime || '--'}}</div>
                    </td>
                  </tr>
                </template>
              </template>
            </tbody>
          </table>
        </CheckboxGroup>
        <div v-show="!tableData.length">
          <p style="text-align: center;line-height: 50px;width: 100%">
            <span>暂无数据</span>
          </p>
        </div>
      </div>
    </div>

    <TeacherDetail
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :stuId="studentDetail.studentId"
      :stuSchoolCode="studentDetail.cityCode"
      :creatorEmpNo="teacherDetail.empNo"
    ></TeacherDetail>
    <ClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></ClassDetail>
  </div>
</template>
<script>
import TeacherMixin from '../../mixin/teacherDetail'
import ClassMixin from '../../mixin/classDetail'
import {
  notPayClassStagesList
} from '../../api/register'
import { copyText } from '../../utils/utils'
import TeacherDetail from '../tal-teacher-details'
import ClassDetail from '../tal-class-details'
export default {
  name: 'TalNotPaymentModal',
  mixins: [TeacherMixin, ClassMixin],
  components: {
    TeacherDetail,
    ClassDetail
  },
  props: {
    formData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    // 资源管理-任务管理 查看
    isTaskMode: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      tableLoading: false,
      tableData: [],
      cancelOrderErrorMap: {},
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      tableColumns: Object.freeze(
        [
          {
            title: '报名ID'
            // width: 150
          }, {
            title: '班级名称'
            // width: 200
          }, {
            title: '授课类型'
            // width: 100
          }, {
            title: '学科'
            // width: 100
          }, {
            title: '主讲教师'
            // width: 100
          }, {
            title: '辅导教师'
            // width: 100
          }, {
            title: '上课时间'
            // width: 140
          }, {
            title: '服务中心'
            // width: 140
          }, {
            title: '教室'
            // width: 100
          }, {
            title: '上课进度'
            // width: 100
          }, {
            title: '总金额'
            // width: 120
          }, {
            title: '变动记录'
            // width: 150
          }, {
            title: '报名时间'
            // width: 100
          }, {
            title: '操作人'
            // width: 100
          }, {
            title: '分期名称'
            // width: 100
          }, {
            title: '开课日期'
            // width: 100
          }, {
            title: '结课日期'
            // width: 100
          }, {
            title: '分期课次'
            // width: 100
          }, {
            title: '备注'
            // width: 150
          }, {
            title: '状态'
            // width: 100
          }, {
            title: '缴费时间'
            // width: 100
          }]),
      checkAll: false,
      checkAllGroup: [],
      allItem: [],
      creatorEmpNo: ''
    }
  },
  mounted () {
    this.getTableData()
  },
  methods: {
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        this.checkAllGroup = this.allItem
      }
    },
    // 变动记录
    getNameLength (name, len = 15) {
      if (name && name.length > len) {
        return name.substring(0, len) + '...'
      } else {
        return name
      }
    },
    getTableData () {
      this.checkAll = false
      this.allItem = []
      this.tableLoading = true
      this.checkAllGroup = []
      this.tableLoading = true
      let req = {
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId,
        subjectId: this.formData.subject ? this.formData.subject : undefined
      }
      req.year = this.formData.year ? this.formData.year : undefined
      req.termId = this.formData.term ? this.formData.term : undefined
      req.classType = this.formData.classType ? this.formData.classType : undefined
      this.cancelOrderErrorMap = {}
      notPayClassStagesList(req).then((res) => {
        this.tableData = []
        this.checkAllGroup = [] // 清空选中项
        this.checkAll = false // 清空选中项
        if (res && res.length) {
          this.tableData = res
          this.tableData.forEach((e) => {
            if (e.businessType === 0) {
              this.allItem.push(e.registId)
            }
            if (e.stagesList && e.stagesList.length) {
              e.rowspan = e.stagesList.length
            }
            this.cancelOrderErrorMap[e.registId] = e.name
            if (e.startDate) {
              e.startDate = e.startDate.substring(0, 10)
            }
            if (e.endDate) {
              e.endDate = e.endDate.substring(0, 10)
            }
          })
        }
      }).catch((err) => {
        this.tableData = []
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    // 缴费状态
    getPayStatus (status, payStatus) {
      let val = ''
      if (status === 2) {
        val = '已结课'
      } else if (payStatus === 0) {
        val = '不可以缴费'
      } else if (payStatus === 1) {
        val = '可以缴费'
      }
      return val
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    copy (text) {
      copyText(text)
    }
  },
  computed: {
    tableWidth: function () {
      let width = 0
      this.tableColumns.forEach((e) => {
        width += e.width
      })
      if (width) {
        return width + 'px'
      } else {
        return '100%'
      }
    }
  }
}
</script>
<style lang="less">
.ivu-message-notice-content-text {
  vertical-align: middle !important;
}

.task-highlight {
  > td {
    background-color: #fcf5e8 !important;
  }
}

.task-lowlight {
  > td {
    background-color: #fff !important;
  }
}
</style>
