<template>
  <!--新增学员信息-->
  <div style="position: relative">
    <Spin fix v-if="initLoading"></Spin>
    <Form ref="form" :model="formData" :label-width="140">
      <Row>
        <Col span="10">
          <FormItem label="姓名" required>
            <Input v-model.trim="formData.realName" style="width: 150px;" />
          </FormItem>
        </Col>
        <Col span="14">
          <FormItem label="出生日期">
            <DatePicker
              :value="formData.birthday"
              :editable="false"
              type="date"
              show-week-numbers
              placeholder="出生日期"
              @on-change="birthdayChange"
              style="width: 200px"
            ></DatePicker>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="10">
          <FormItem label="性别" required>
            <RadioGroup v-model.trim="formData.sex">
              <Radio :label="1">男</Radio>
              <Radio :label="0">女</Radio>
            </RadioGroup>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <FormItem label="所在城市" required>
          <Select
            :disabled="!isAdd"
            v-model.trim="formData.provinceId"
            @on-change="provinceChange"
            style="width:120px"
            placeholder="请选择省份"
            filterable
          >
            <Option
              v-for="item in provinceList2"
              :value="item.id.toString()"
              :key="item.id"
            >{{item.name}}</Option>
          </Select>
          <Select
            :disabled="!isAdd"
            v-model.trim="cityInfo"
            @on-change="cityChange"
            style="width:120px;margin-left: 20px"
            filterable
          >
            <Option
              v-for="item in cityList"
              :value="item.id + '-' + item.areaCode"
              :key="item.id"
            >{{item.name}}</Option>
          </Select>
        </FormItem>
      </Row>
      <Row>
        <FormItem label="年部" required>
          <Select
            v-model.trim="formData.gradeTypeId"
            @on-change="gradeTypeChange"
            style="width:120px"
          >
            <Option
              v-for="(item,index) in gradesTypeList"
              :value="item.schoolId"
              :key="index"
            >{{item.name}}</Option>
          </Select>
          <Select v-model.trim="formData.gradeId" @on-change="schoolChange('gradeChange')" style="width:120px;margin-left: 20px">
            <Option
              v-for="(item,index) in gradesList"
              :value="parseInt(item.schoolId)"
              :key="index"
            >{{item.name}}</Option>
          </Select>
        </FormItem>
      </Row>
      <Row v-if="gtName">
        <FormItem :label="gtName">
          <Col span="7">
            <Cascader
              placeholder="请选择省市区"
              v-model.trim="schoolArea"
              @on-change="changeArea"
              filterable
              :data="loadProvinceList"
              :load-data="getSurplusCityData"
            ></Cascader>
          </Col>
          <Select v-model.trim="schoolId" @on-change="schoolChange" style="width:120px;margin-left: 20px" filterable>
            <Option
              v-for="(item,index) in schoolList"
              :value="item.schoolId"
              :key="index"
            >{{item.name}}</Option>
          </Select>
          <Select v-if="formData.gradeTypeId === 1" v-model.trim="classOrder" style="width:120px;margin-left: 20px" filterable>
            <Option
              v-for="(item,index) in classList"
              :value="item.classOrder"
              :key="index"
            >{{item.className}}</Option>
          </Select>
        </FormItem>
      </Row>
      <Row>
        <FormItem
          v-for="(item, index) in formData.attentionReqList"
          :key="index"
          :required="index === 0"
          :label="'第' + (index+1) + '联系人手机号'">
          <Row>
            <Col span="6">
              <Input type="text" v-model.trim="item.tel" />
            </Col>
            <Col span="3" offset="1">
              <Select v-model.trim="item.typeId" style="width:80px">
                <Option
                  v-for="item2 in contactList"
                  :value="item2.value"
                  :key="item2.value"
                >{{ item2.label }}</Option>
              </Select>
            </Col>
            <Col span="4" v-show="(item.typeId === 8)">
              <Input type="text" v-model.trim="item.otherName" />
            </Col>
            <Col span="4" style="margin-left: 15px" v-show="(index !== 0)">
              <Button @click="contactRemove(index)">删除第{{index+1}}位联系人</Button>
            </Col>
            <Col span="4" style="margin-left: 15px" v-show="(index === 0 && isAdd)">
              <div v-if="!showBtn">
                <Button
                  type="default"
                  @click="sendMessageHandlerT(formData.attentionReqList[0].tel, 'regCode')"
                  v-if="!sendBtnT"
                  class="wd80"
                >发送验证码</Button>
                <Button type="default" :disabled="true" v-else class="wd80">{{msgNumT}}秒</Button>
              </div>
              <div v-if="showBtn">
                <Button
                  type="default"
                  @click="sendMessageHandler(formData.attentionReqList[0].tel, 'regCode')"
                  v-if="!sendBtn"
                  class="wd80"
                >发送验证码</Button>
                <Button type="default" :disabled="true" v-else class="wd80">{{msgNum}}秒</Button>
              </div>
            </Col>
          </Row>
        </FormItem>
        <FormItem required label="验证码" v-if="isAdd">
          <Input v-model.trim="formData.code" style="width: 200px;" />
        </FormItem>
        <FormItem>
          <Row>
            <Col span="12">
              <Button type="dashed" @click="contactAdd" icon="md-add">增加联系人</Button>
            </Col>
          </Row>
        </FormItem>
      </Row>
      <Row>
        <FormItem label="电子凭证接收邮箱">
          <Input v-model.trim="formData.accessEmail" style="width: 200px;" />
        </FormItem>
      </Row>
      <h3
        style="margin-top: 10px"
        v-show="!formData.recipientReqList || formData.recipientReqList.length === 0"
      >邮寄地址列表</h3>
      <Row
        style="padding-top: 20px"
        v-for="(item,index) in formData.recipientReqList.filter(obj => obj.delete !== 1)"
        :key="item.tag"
      >
        <h3 style="font-size: 14px">{{'邮寄地址' + (index+1)}}</h3>
        <Row>
          <Col span="10">
            <FormItem label="省市区" prop="address">
              <!--<Cascader-->
              <!--@on-visible-change="addressIndex = index"-->
              <!--@on-change="treeDataChange"-->
              <!--:value='addressMap[index]'-->
              <!--:data="cityTreeData">-->
              <!--</Cascader>-->
              <Cascader
                @on-visible-change="addressIndex = index"
                @on-change="treeDataChange"
                v-model.trim="addressMap[index]"
                filterable
                :data="loadProvinceList"
                :load-data="getSurplusCityData"
              ></Cascader>
            </FormItem>
          </Col>
          <Col span="10">
            <FormItem label="详细地址" prop="address">
              <Input v-model.trim="item.address" />
            </FormItem>
          </Col>
          <Col span="4">
            <Radio
              v-model.trim="item.isDefault"
              :true-value="1"
              :false-value="0"
              @on-change="defaultedAddressChange(index)"
              style="margin-left: 20px;margin-top: 7.5px;"
            >设为默认地址</Radio>
          </Col>
        </Row>
        <Row>
          <Col span="10">
            <FormItem label="收货人" prop="consignee">
              <Input v-model.trim="item.name" />
            </FormItem>
          </Col>
          <Col span="10">
            <FormItem label="联系电话" prop="tPhone">
              <Input v-model.trim="item.tel" />
            </FormItem>
          </Col>
          <Col span="4">
            <Button @click="defaultedAddressDel(index)" style="margin-left: 20px">删除</Button>
          </Col>
        </Row>
      </Row>
      <Row>
        <Col span="6">
          <Button
            type="dashed"
            @click="defaultedAddressAdd"
            icon="md-add"
            style="margin-left: 140px;margin-top: 20px"
          >新增地址</Button>
        </Col>
      </Row>
    </Form>
    <div class="ivu-modal-footer" style="margin-top: 10px">
      <Button type="primary" @click="save" :loading="isLoading">保存</Button>
      <Button @click="$emit('close')">关闭</Button>
    </div>

    <Modal
      v-model="hasLoggedOut"
      title="再次注册原因"
      :closable="false"
      :mask-closable="false"
    >
      <p style="marginBottom: 8px"> <Icon type="ios-alert-outline" /> 此号码进行过注册后注销操作，最近一次的历史注销时间为 {{loggedOutTime}} ,请在下方填写再次注册原因 </p>
      <Input v-model="registerReason" required type="textarea" placeholder="请输入" />
      <div slot="footer">
        <Button :disabled="!registerReason" @click="handleClick(loggedOutTime)">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
// import {
//   getDistrict,
// } from '../../api/customer'
import {
  getProvinceListByType,
  getCityList,
  isOpenCity,
  queryGradeByGradeType,
  getGradeType,
  getSchoolList
} from '../../api/bd'
import {
  addStudent,
  editStudent,
  updateUserSchool,
  getUserSchoolList,
  editReadingClass,
  getReadingClass
} from '../../api/student'
import verificationCodeInAddStu from '../../mixin/verificationCodeInAddStu'
import verificationCode from '../../mixin/verificationCode'
import { querySchoolClassList } from '../../api/scDic'
export default {
  name: 'TalStudentAdd',
  mixins: [verificationCodeInAddStu, verificationCode],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    isAdd: {
      type: Boolean,
      default: true
    },
    formData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    provinceList: {
      type: Array,
      default: function () {
        return []
      }
    },
    requireSchoolCity: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  computed: {
    gtName () {
      // this.schoolArea[0] = this.formData.provinceId
      // this.schoolArea[1] = this.cityInfo.split('-')[0]
      switch (this.formData.gradeTypeId) {
        case 5:
          return '就读幼儿园'
        case 1:
          return '就读小学'
        case 2:
          return '就读初中'
        case 3:
          return '就读高中'
        default:
          this.schoolArea = [] // eslint-disable-line
          return false
      }
    }
  },
  data () {
    return {
      registerReason: '', // 再次注册原因
      showBtn: false, // 展示新发验证码逻辑
      deleteAddress: [],
      initLoading: false,
      ruleInline: {
        realname: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        cityCode: [
          { required: true, message: '请选择所在城市', trigger: 'blur' }
        ],
        sex: [
          { required: true, type: 'number', message: '请选择性别', trigger: 'blur' }
        ],
        gradeTypeId: [
          { required: true, type: 'string', message: '请选择年部', trigger: 'blur' }
        ],
        gradeId: [
          { required: true, type: 'string', message: '请选择年级', trigger: 'blur' }
        ]
      },
      contactList: [
        { value: 1, label: '妈妈' },
        { value: 2, label: '爸爸' },
        { value: 3, label: '奶奶' },
        { value: 4, label: '爷爷' },
        { value: 5, label: '外婆' },
        { value: 6, label: '外公' },
        { value: 7, label: '本人' },
        { value: 8, label: '其他' }
      ],
      contactMap: {
        1: '妈妈',
        2: '爸爸',
        3: '奶奶',
        4: '爷爷',
        5: '外婆',
        6: '外公',
        7: '本人',
        8: '其他'
      },
      addressMap: [],
      // 所在城市省份列表
      provinceList2: [],
      // 所在城市城市列表
      cityList: [],
      gradesList: [],
      gradeTypeMap: {},
      gradesTypeList: [],
      addressIndex: 0,
      isLoading: false,
      cityInfo: '',
      cityTreeData: [],
      loadProvinceList: [],
      loadCityList: [],
      isOpenCity: false, // 是否开放城市
      schoolArea: [],
      schoolList: [],
      schoolId: '',
      classOrder: '',
      classList: '',
      uid: ''
    }
  },
  methods: {
    // 保存逻辑
    async save () {
      if (this.isLoading) {
        return
      }
      if (this.formData.realname === '') { this.$Message.warning('请填写姓名'); return }
      if (this.formData.areaCode === '') { this.$Message.warning('请选择所在城市'); return }
      if (this.formData.gradeTypeId === '') { this.$Message.warning('请选择年部'); return }
      if (this.formData.gradeId === '') { this.$Message.warning('请选择年级'); return }
      if (this.requireSchoolCity.includes(this.cityInfo.split('-')[1]) && !this.schoolId) { this.$Message.warning('请选择就读学校'); return }
      if (this.formData.accessEmail) {
        const reg = /^[a-zA-Z0-9_.-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
        if (!reg.test(this.formData.accessEmail)) {
          this.$Message.warning({
            content: '请输入正确的邮箱地址',
            duration: 3
          })
          return
        }
      }
      // 联系人逻辑
      let contacts = this.formData.attentionReqList
      if (!contacts || contacts.length === 0) { this.$Message.warning('缺少联系人信息'); return }
      let hasObj = {}
      for (let i = 0; i < contacts.length; i++) {
        // 香港分校不校验手机号码格式，由接口校验
        if (this.formData.provinceId !== '810000' && !(/(^\d{11}$)|(^\d{3}([a-zA-Z]{4})|(\*{4})\d{4}$)/.test(contacts[i].tel))) {
          this.$Message.warning({ content: '错误的电话号码：' + contacts[i].tel, duration: 5 })
          return false
        }
        if (hasObj[contacts[i].tel]) {
          this.$Message.warning({ content: '重复的电话号码：' + contacts[i].tel, duration: 5 })
          return false
        }
        if (i === 0 && (!contacts[i].tel || !contacts[i].typeId)) {
          this.$Message.warning('请选择第一联系人身份')
          return false
        }
        hasObj[contacts[i].tel] = true
        if (!contacts[i].typeId) {
          this.$Message.warning({ content: '第' + (i + 1) + '联系人缺少身份', duration: 5 })
          return false
        }
        if (contacts[i].typeId === 8) {
          contacts[i].typeName = contacts[i].otherName ? contacts[i].otherName : '其他'
        } else {
          contacts[i].typeName = contacts[i].typeId ? this.contactMap[contacts[i].typeId] : ''
        }
      }
      // 邮寄地址逻辑
      if (this.formData.recipientReqList.length) {
        for (let i = 0; i < this.formData.recipientReqList.length; i++) {
          let item = this.formData.recipientReqList[i]
          if (item.address === '' || item.name === '' || item.tel === '' || item.provinceId === '' || item.cityId === '' || item.districtId === '') {
            this.$Message.warning({ content: '请完善邮寄地址', duration: 5 })
            return
          }
          if (this.formData.provinceId !== '810000' && !(/(^\d{11}$)|(^\d{3}([a-zA-Z]{4})|(\*{4})\d{4}$)/.test(item.tel))) {
            this.$Message.warning({ content: '错误的收件人联系电话：' + item.tel, duration: 5 })
            return
          }
        }
      }
      this.formData.recipientReqList = this.formData.recipientReqList.concat(this.deleteAddress)
      if (this.isAdd) {
        // 新增学员校验二维码
        if (!this.formData.code) {
          this.$Message.warning('请输入验证码')
          return
        }
        try {
          const uid = await addStudent(this.formData)
          if (this.schoolId) {
            await updateUserSchool({
              cityCode: this.cityInfo.split('-')[1],
              uid,
              schoolId: this.schoolId.split('-')[1],
              gradeType: this.formData.gradeTypeId
            })
          }
          if (this.formData.gradeTypeId === 1 && this.classOrder) {
            await editReadingClass({
              cityCode: this.cityInfo.split('-')[1],
              uid,
              schoolId: this.schoolId.split('-')[1],
              schoolGrade: this.formData.gradeId,
              classOrder: this.classOrder
            })
          }
          this.$Message.success({ content: '新增成功', duration: 5 })
          this.$emit('success')
          this.isLoading = false
          this.modalValue = false
        } catch (err) {
          this.$Message.warning({ content: '新增失败:' + err.message, duration: 5 })
          this.isLoading = false
          this.formData.recipientReqList.map(obj => {
            return obj.delete !== 1
          })
        }
      } else {
        try {
          await editStudent(this.formData)
          if (this.schoolId) {
            await updateUserSchool({
              cityCode: this.formData.cityCode,
              uid: this.formData.uid,
              schoolId: this.schoolId.split('-')[1],
              gradeType: this.formData.gradeTypeId
            })
          }
          if (this.formData.gradeTypeId === 1 && this.classOrder) {
            await editReadingClass({
              cityCode: this.formData.cityCode,
              uid: this.formData.uid,
              schoolId: this.schoolId.split('-')[1],
              schoolGrade: this.formData.gradeId,
              classOrder: this.classOrder
            })
          }
          this.$Message.success({ content: '修改成功', duration: 5 })
          this.uid = this.formData.uid
          this.$emit('success')
          this.isLoading = false
          this.modalValue = false
        } catch (err) {
          this.$Message.warning({ content: '修改失败:' + err.message, duration: 5 })
          this.isLoading = false
        }
      }
    },
    birthdayChange (val) {
      this.formData.birthday = val
    },

    // 年部改变  清空就读小学、年级
    gradeTypeChange (val) {
      this.formData.gradeId = ''
      this.gradesList = []
      if (!val) return
      this.getGrades()
      this.changeArea()
      if (!this.isAdd) {
        this.getUserSchool()
      }
    },
    provinceChange (val) {
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.areaCode = ''
      this.gradesList = []
      this.gradesTypeList = []
      this.cityList = []
      if (val) {
        this.schoolArea[0] = val
        for (let i = 0; i < this.provinceList2.length; i++) {
          if (val === this.provinceList2[i].id.toString()) {
            this.cityList = this.provinceList2[i].cityDtoList
            break
          }
        }
      }
    },
    // 所在城市改变   清空年部、年级
    cityChange (val) {
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.gradesList = []
      this.gradesTypeList = []
      if (val) {
        let id = val.split('-')[0]
        let areaCode = val.split('-')[1]
        this.formData.cityId = id
        this.formData.areaCode = areaCode
        this.schoolArea[1] = id
        isOpenCity({ areaCode: areaCode, schoolCode: areaCode }).then((res) => {
          this.isOpenCity = res
          this.getGradeTypeHandler()
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      }
    },
    // 默认地址改变
    defaultedAddressChange (index) {
      this.formData.recipientReqList.forEach((e, i) => {
        if (index === i) {
          e.isDefault = 1
        } else {
          e.isDefault = 0
        }
      })
    },
    // 默认地址新增
    defaultedAddressAdd () {
      let obj = {
        address: '', // 详细地址
        name: '', // 收件人
        tel: '', // 联系电话
        isDefault: 0, // 是否默认地址
        provinceId: '', // 省Id
        cityId: '', // 城市Id
        districtId: '', // 区/县Id
        deleted: 0, // 是否删除
        delete: 0,
        tag: Math.random()
      }
      let recipientReqList = this.formData.recipientReqList
      if (!recipientReqList) {
        recipientReqList = []
      }
      for (let i = 0; i < recipientReqList.length; i++) {
        if (recipientReqList[i].deleted) {
          this.$set(recipientReqList[i], 'address', '')
          this.$set(recipientReqList[i], 'name', '')
          this.$set(recipientReqList[i], 'tel', '')
          this.$set(recipientReqList[i], 'isDefault', '')
          this.$set(recipientReqList[i], 'provinceId', '')
          this.$set(recipientReqList[i], 'cityId', '')
          this.$set(recipientReqList[i], 'districtId', '')
          this.$set(recipientReqList[i], 'deleted', 0)
          return
        }
      }
      if (this.formData.recipientReqList.length >= 5) {
        this.$Message.warning('最多添加五条邮寄地址')
        return false
      }
      this.formData.recipientReqList.push(obj)
    },
    // 地址删除
    defaultedAddressDel (index) {
      let item = this.formData.recipientReqList.splice(index, 1)
      item[0].deleted = 1
      item[0].delete = 1
      if (item[0].id) {
        this.deleteAddress.push({
          id: item[0].id,
          deleted: 1,
          delete: 1
        })
      }
      this.addressMap.splice(index, 1)
      // let item = this.formData.recipientReqList[index]
      // console.log(item)
      // if (item.id) {
      //   item.deleted = 1
      //   this.addressMap[index] = []
      // } else {
      //   this.formData.recipientReqList.splice(index, 1)
      //   console.log(this.formData.recipientReqList)
      // }
    },
    // 邮寄地址改变
    treeDataChange (value) {
      this.formData.recipientReqList[this.addressIndex].provinceId = value[0]
      this.formData.recipientReqList[this.addressIndex].cityId = value[1]
      this.formData.recipientReqList[this.addressIndex].districtId = value[2]
      this.addressMap[this.addressIndex] = [value[0], value[1], value[2]]
    },
    // 联系人添加
    contactAdd () {
      if (!this.formData.attentionReqList) {
        this.formData.attentionReqList = []
      }
      if (this.formData.attentionReqList.length >= 10) {
        this.$Message.warning('最多添加10个联系人')
        return false
      } else {
        this.formData.attentionReqList.push({
          'tel': '', // 联系人手机号
          'typeId': '', // 联系人类型Id
          'typeName': '', // 联系人类型名称
          'otherName': ''
        })
      }
    },
    // 联系人删除
    contactRemove (index) {
      this.formData.attentionReqList.splice(index, 1)
    },
    // 省市区级联
    loadCityTreeData (item, callback) {
      item.loading = true
      if (item.type === 'province') {
        getCityList({ provinceId: item.id }).then(res => {
          res.forEach((e) => {
            e.value = e.schoolId
            e.label = e.name
            e.children = []
            e.loading = false
            e.type = 'city'
          })
          item.children = res
          item.loading = false
          callback()
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      } else if (item.type === 'city') {
        getDistrict({ cityId: item.id }).then(res => {
          res.forEach((e) => {
            e.value = e.schoolId
            e.label = e.name
          })
          item.children = res
          item.loading = false
          callback()
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      } else {
        item.loading = false
        callback()
      }
    },
    // 获取年级
    getGrades () {
      let req = {
        gradeTypeId: this.formData.gradeTypeId,
        cityCode: this.isOpenCity ? this.formData.areaCode : '1111'
      }
      queryGradeByGradeType(req).then(res => {
        this.gradesList = res
      }).catch((err) => {
        this.$Message.warning({ content: '获取年级失败:' + err.message, duration: 5 })
      })
    },
    // 获取年部
    getGradeTypeHandler () {
      let _code = this.isOpenCity ? this.formData.areaCode : '1111'
      getGradeType({ cityCode: _code, schoolCode: _code }).then(res => {
        res.forEach((e) => {
          e.schoolId = parseInt(e.schoolId)
          this.gradeTypeMap[e.schoolId] = e.name
        })
        this.gradesTypeList = res.filter(r => r.name !== '非受限年部')
      }).catch((err) => {
        this.$Message.warning({ content: '获取年部失败:' + err.message, duration: 5 })
      })
    },
    resetParams () {
      this.addressMap[0] = []
      this.addressMap[1] = []
      this.addressMap[2] = []
      this.addressMap[3] = []
      this.addressMap[4] = []
      this.gradesList = []
      this.gradesTypeList = []
      this.provinceList2 = []
      this.cityList = []
      this.cityInfo = ''
    },
    getProvinceHandler () {
      if (!this.isAdd) {
        this.initLoading = true
        // 不传默认查询省份信息；传"c":查询省市；"d":查询省市区
        getProvinceListByType({ type: 'c' }).then((res) => {
          this.provinceList2 = res
          for (let i = 0; i < this.provinceList2.length; i++) {
            if (this.formData.provinceId === this.provinceList2[i].id.toString()) {
              this.cityList = this.provinceList2[i].cityDtoList
              break
            }
          }
        }).catch((err) => {
          this.$Message.warning({ content: '获取所在城市失败:' + err.message, duration: 5 })
        })
        this.cityInfo = this.formData.cityId + '-' + this.formData.areaCode
        this.$set(this.schoolArea, 0, this.formData.provinceId)
        this.$set(this.schoolArea, 1, this.formData.cityId)
        // 有城市的话查年部和年级
        if (this.formData.cityCode) {
          let _cityCode = this.formData.cityCode
          isOpenCity({ areaCode: _cityCode, schoolCode: _cityCode }).then((res) => {
            this.isOpenCity = res
            this.getGradeTypeHandler()
            if (this.formData.gradeTypeId || this.formData.gradeTypeId === 0) {
              if (this.formData.gradeId) {
                this.getGrades()
              }
            }
          }).catch(err => {
            this.$Message.warning({ content: err.message, duration: 5 })
          })
        }
        // 如果有邮寄地址，运行回显逻辑
        if (this.formData.recipientReqList && this.formData.recipientReqList.length > 0) {
          this.formData.recipientReqList.forEach((e, index) => {
            this.addressMap[index] = [e.provinceId, e.cityId, e.districtId]
          })
        }
        this.initLoading = false
      } else {
        getProvinceListByType({ type: 'c' }).then((res) => {
          this.provinceList2 = res
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      }
    },
    getProvinceWithTownHandler () {
      getProvinceListByType({ type: 'd' }).then((res) => {
        this.provinceWithTownData = res
        if (!this.isAdd) {
          this.getUserSchool()
          this.getReadingClass()
        }
        res.forEach((p, idx) => {
          let _p = {}
          let _city = []
          p.cityDtoList.forEach(city => {
            let _district = [
              {
                value: '',
                label: '全部'
              }
            ]
            city.districtList.forEach(district => {
              _district.push({
                value: district.schoolId,
                label: district.name
              })
            })
            _city.push({
              value: city.schoolId,
              label: city.name,
              children: _district,
              loading: false
            })
          })
          _p = {
            type: 'p',
            idx: idx,
            value: p.schoolId,
            label: p.name,
            children: _city,
            loading: false
          }
          this.loadProvinceList.push({
            type: 'p',
            idx: idx,
            value: p.schoolId,
            label: p.name,
            children: [],
            loading: false
          })
          this.cityTreeData.push(_p)
        })
      }).catch((err) => {
        this.$Message.warning({ content: '获取所在城市失败:' + err.message, duration: 5 })
      })
    },
    getSurplusCityData (item, callback) {
      item.loading = true
      if (item.type === 'p') {
        item.children = this.cityTreeData[item.idx].children
        this.loadCityList = this.cityTreeData[item.idx]
      }
      item.loading = false
      callback()
    },
    changeArea (val, schoolId) {
      this.schoolList = []
      this.schoolId = ''
      this.$nextTick(() => {
        // if (this.schoolArea[2]) {
        getSchoolList({
          cityCode: this.provinceWithTownData.filter(p => p.schoolId === this.schoolArea[0])[0].cityDtoList.filter(c => c.schoolId === this.schoolArea[1])[0].areaCode,
          provinceId: this.schoolArea[0],
          cityId: this.schoolArea[1],
          districtId: this.schoolArea[2],
          gradeTypeId: this.formData.gradeTypeId
        }).then(data => {
          this.schoolList = data.map(d => {
            d.schoolId = d.districtId + '-' + d.schoolId
            return d
          })
          if (typeof schoolId === 'string' && schoolId.split('-').length === 2) {
            this.schoolId = schoolId
            this.schoolChange()
          }
        }).catch(err => {
          this.$Message.error({
            content: '查询学校列表失败：' + err.message,
            duration: 5
          })
        })
        // }
      })
    },
    getReadingClass () {
      getReadingClass({
        cityCode: this.formData.cityCode,
        uid: this.formData.uid
      }).then(data => {
        if (data) {
          this.classOrder = String(data.classOrder)
        }
      }).catch(err => {
        this.$Message.error({
          content: '查询就读班级失败：' + err.message,
          duration: 5
        })
      })
    },
    schoolChange (type) {
      if (type === 'gradeChange') {
        this.classOrder = ''
      }
      if (this.formData.gradeTypeId !== 1) return // 只有小学支持选择班
      if (this.formData.cityCode && this.formData.gradeId && this.schoolId) {
        querySchoolClassList({
          cityCode: this.formData.cityCode,
          schoolId: this.schoolId.split('-')[1],
          gradeId: this.formData.gradeId
        }).then(data => {
          this.classList = data
        }).catch(err => {
          this.$Message.error({
            content: '查询班级列表失败：' + err.message,
            duration: 5
          })
        })
      }
    },
    getUserSchool () {
      getUserSchoolList({
        cityCode: this.formData.cityCode,
        uid: this.formData.uid,
        gradeType: this.formData.gradeTypeId
      }).then(data => {
        if (data && data.length) {
          this.$set(this.schoolArea, 0, data[0].provinceId)
          this.$set(this.schoolArea, 1, data[0].cityId)
          this.$set(this.schoolArea, 2, data[0].districtId)
          this.$nextTick(() => {
            this.changeArea('', data[0].districtId + '-' + data[0].schoolId)
          })
        }
      }).catch(err => {
        this.$Message.error({
          content: '获取学员就读学校失败：' + err.message,
          duration: 5
        })
      })
    },
    // 注销再注册弹窗-确认按钮处理函数
    handleClick (loggedOutTime) {
      // 注销再注册备注原因
      this.formData.lastLogout = !!loggedOutTime.length
      this.formData.registerReason = this.registerReason
      this.hasLoggedOut = false
      this.showBtn = true
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.getProvinceHandler()
      this.getProvinceWithTownHandler()
    })
  }
}
</script>
