export default {
  data () {
    return {
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 当前每页展示数, 默认为10
      pageSizeOpt: [10, 20, 40], // 每页展示数配置，默认为10 20 40
      dataTotal: 0, // 数据总条数
      tableData: [], // 表格数据
      tableLoading: false // 表格loading
    }
  },
  methods: {
    // 点击搜索
    searchFn () {
      this.currentPageNum = 1
      this.getTableData()
    },
    // 当前页改变
    handleCurrentPageChange (page) {
      this.currentPageNum = page
      this.getTableData()
    },
    // 当前每页展示数改变
    handleCurrentPageSizeChange (pageSize) {
      this.currentPageSize = pageSize
      this.getTableData()
    }
  }
}
