<template>
  <section class="tal-yach-login-qrcode">
    <h2>知音楼扫码</h2>
    <Qrcode class="qr-code-img" :text="qrcodeUrl" :correctLevel="0" :size="size" :margin="10"></Qrcode>
    <p>{{qrcodeStatus}}<a href="void:javascipt(0)" @click="reGenQrcode">刷新</a></p>
    <a href="https://yach.zhiyinlou.com/">点此下载知音楼</a>
    <section class="exceed" v-show="exceed" @click="reGenQrcode">
      二维码已过期点击刷新
    </section>
  </section>

</template>

<script>
import {
  yachQrcodeStatus
} from '../../api/yach'
import {
  yachGenQrcode
} from '../../api/auth'
import Qrcode from 'vue-qr'
const NOTICETXT = {
  unScan: '请使用知音楼扫描二维码登录',
  scaned: '二维码已扫描',
  exceed: '二维码已过期',
  logined: '已登录'
}
export default {
  name: 'TalYachLoginQrcode',
  components: {
    Qrcode
  },
  props: {
    size: {
      type: [String, Number],
      default: 200
    }
  },
  data () {
    return {
      maxExceedTime: 60 * 60 * 1000,
      ticketGenTime: '',
      qrcodeUrl: '',
      qrcodeStatus: NOTICETXT.unScan,
      exceed: false,
      st: ''
    }
  },
  mounted () {
    this.genQrCodeHandle()
  },
  destroyed () {
    this.clearSt()
  },
  methods: {
    async genQrCodeHandle () {
      const GenQrcodeRes = await yachGenQrcode()
      if (GenQrcodeRes.qrcode) {
        this.qrcodeUrl = GenQrcodeRes.qrcode
        this.st = setInterval(async () => {
          const QrcodeStatusRes = await yachQrcodeStatus({
            uuid: GenQrcodeRes.uuid
          })
          const data = QrcodeStatusRes.data
          if (data.errcode === 0) {
            let qrcodeStatus = ''
            if (data.stage === 9 || data.stage === -2) {
              console.log(data)
              console.log('登录成功')
              qrcodeStatus = NOTICETXT.logined
              window.clearInterval(this.st)
              this.$emit('resData', {
                redirect: data.redirect,
                token: data.token
              })
              // 登录后台跳转
            } else if (data.stage === 0) {
              console.log('未扫码')
              qrcodeStatus = NOTICETXT.unScan
            } else if (data.stage === 1) {
              console.log('已扫码')
              qrcodeStatus = NOTICETXT.scaned
            } else if (data.stage === -1) {
              console.log('二维码已过期')
              qrcodeStatus = NOTICETXT.exceed
              window.clearInterval(this.st)
              this.exceed = true
            }
            this.qrcodeStatus = qrcodeStatus
          } else {
            console.log('登录异常')
            window.clearInterval(this.st)
          }
        }, 2000)
      }
    },
    reGenQrcode () {
      this.clearSt()
      this.genQrCodeHandle()
      this.exceed = false
    },
    clearSt () {
      clearInterval(this.st)
    }
  }
}
</script>

<style scoped lang="less">
  .tal-yach-login-qrcode {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    font-size: 14px;
    .qr-code-img {
      margin: 0 auto;
      min-height: 200px;
      min-width: 200px;
    }
    .exceed {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, .8);
      cursor: pointer;
      color: #fff;
      font-size: 18px;
    }
  }
