<template>
  <div class="tal-class-search-input">
    <Dropdown @on-click="handleClickItem">
      <Input :value="value" placeholder="搜索班级名称关键词" @input="handleInput" @on-change="handleChange" clearable>
        <Icon slot="suffix" type="ios-help-circle" class="help-icon" @click="handleClickHelp"/>
      </Input>
      <p class="error-msg" v-show="showError">请选择分校/年份</p>
      <DropdownMenu slot="list">
        <div class="example-wrapper" v-show="classesList.length === 0 && !value">
          <p>您可以搜索"难度/教材/学校/新区/标签等"</p>
          <p style="font-size: 12px;color: #ccc;">示例:</p>
          <p>勤思人教</p>
          <p>敏学苏教人大附中大兴衔接班</p>
        </div>
        <DropdownItem v-show="classesList.length <= 0 && value" disabled>
          <p class="class-item">暂无推荐结果，点击"查询"获取更多</p>
        </DropdownItem>
        <template v-show="classesList.length > 0 && value">
          <DropdownItem v-for="(item,index) in classesList" :key="index" :name="item.name" :selected="value === item.name">
            <p class="class-item" v-html="item.highlightName" />
          </DropdownItem>
          <DropdownItem v-show="classesList.length >= 5" disabled>
            <p class="class-item">以上为推荐结果, 点击"查询"获取更多</p>
          </DropdownItem>
        </template>
      </DropdownMenu>
    </Dropdown>
    <search-help :value="showSearchHelpModal" @close="showSearchHelpModal = false" />
  </div>
</template>

<script>
import SearchHelp from './modal/SearchHelp'
import { queryClassNameSuggest } from '../../api/clazz'
export default {
  name: 'tal-class-search-input',
  components: { SearchHelp },
  props: {
    value: {
      type: String,
      default: ''
    },
    formData: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      timer: null,
      classesList: [],
      showSearchHelpModal: false,
      showError: false
    }
  },
  methods: {
    handleInput (e) {
      this.$emit('input', e)
    },
    handleChange (e) {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!e.target.value) {
        this.$set(this, 'classesList', [])
        return
      }
      this.timer = setTimeout(() => {
        this.handleGetClassesList()
      }, 500)
    },
    handleClickHelp () {
      this.showSearchHelpModal = true
    },
    handleClickItem (name) {
      this.$emit('input', name)
      this.$emit('on-select', name)
    },
    handleGetClassesList () {
      // 判断是否选择分校、年级
      if (!this.formData.schoolCode || !this.formData.year) {
        this.showError = true
      } else {
        this.showError = false
        const params = Object.assign({
          name: this.value,
          highlightPreTag: '<span class="red">',
          highlightPostTag: '</span>'
        }, this.formData)
        queryClassNameSuggest(params, this.formData.schoolCode).then(response => {
          if (response.totalCount > 0) {
            this.classesList = response.totalCount > 5 ? response.records.slice(0, 5) : response.records
          } else {
            this.classesList = []
          }
        }).catch((error) => {
          this.$Message.error('获取班级名称失败：' + error.message)
        })
      }
    },
    handleReset () {
      this.$set(this, 'classesList', [])
      if (this.value) {
        this.$emit('input', '')
      }
    }
  }
}
</script>

<style scoped lang="less">
.tal-class-search-input {
  display: inline-block;
  .error-msg {
    text-align: left;
    color: #ff0000;
    transition: display .2s;
  }
  .help-icon {
    transition: color .5s;
    &:hover {
      color: #2d8cf0;
    }
  }
  /deep/.example-wrapper {
    padding: 0 10px;
    font-size: 12px;
    text-align: left;
    min-width: 250px;
  }
  /deep/.class-item {
    text-align: left;
    span.red {
      color: #ff0000;
    }
  }
}
</style>
