<template>
    <div>
        <div style="padding: 15px">
            <Table :columns="tableColumns" :data="tableData" height="500"></Table>
        </div>
    </div>
</template>
<script>
import { clazzQueryCurriculumInfo, registPlayBack } from '../../api/clazz'
export default {
  name: 'TalSendPlaybackBysessions',
  props: {
    classInfo: {
      type: Object,
      default: () => {
        return {}
      }
    },
    creatorEmpNo: {
      type: String,
      default: ''
    },
    studentUid: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      tableColumns: [
        {
          title: '课次',
          key: 'classNum',
          align: 'center'
        },
        {
          title: '上课日期',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDate)])
          }
        },
        {
          title: '星期',
          align: 'center',
          width: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDateWeek)])
          }
        },
        {
          title: '上课时间',
          key: 'classTimeName',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('span', params.row.classTimeName)
          }
        },
        {
          title: '科目',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '教师',
          key: 'teacherName',
          align: 'center'
        },
        {
          title: '授课类型',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color:
                    params.row.cucClassType !== this.classInfo.classType
                      ? '#ed4014'
                      : ''
                }
              },
              this.getClassType(params.row.cucClassType)
            )
          }
        },
        {
          title: '课堂模式',
          align: 'center',
          render: (h, params) => {
            return h('span', this.liveTypeMap[params.row.liveType])
          }
        },
        {
          title: '教室',
          key: 'classroomSimpleName',
          align: 'center'
        },
        {
          title: '辅导老师',
          key: 'tutorSysName',
          align: 'center'
        },
        {
          title: '班主任',
          key: 'classTeacherName',
          align: 'center'
        },
        {
          title: '课次状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color: params.row.status === 0 ? '#ed4014' : '#19be6b'
                }
              },
              params.row.status === 0 ? '未上课' : '已上课'
            )
          }
        },
        {
          title: '操作',
          key: 'caozuo',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                style: {
                  display: params.row.status === 0 ? 'none' : 'inline-block'
                },
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.sendPlayback(params.row)
                  }
                }
              }, '发放回放')
            ])
          }
        }
      ],
      tableData: [],
      changeClassType: {
        1: '在线',
        2: '双师',
        4: '面授',
        5: '自习'
      },
      liveTypeMap: {
        0: '通用课程',
        1: '小组课',
        2: '新课堂通用课程',
        3: '新课堂小组课',
        4: '新课堂考试',
        5: '新课堂讲座'
      }
    }
  },
  methods: {
    sendPlayback (row) {
      let params = {
        studentUid: this.studentUid,
        curriculumId: row.curriculumId,
        creator: this.creatorEmpNo,
        areaCode: this.classInfo.cityId,
        operationType: 14,
        registType: 4
      }
      registPlayBack(params).then(() => {
        this.$Message.success('发放成功')
        this.$emit('success')
      }).catch(error => {
        this.$Message.error('发送失败: ' + error)
      })
    },
    getClassType (val) {
      return this.changeClassType[val]
    },
    getTable () {
      const params = {
        areaCode: this.classInfo.cityId,
        classId: this.classInfo.classId ? this.classInfo.classId : this.classInfo.id ? this.classInfo.id : '',
        pageSize: 100
      }
      clazzQueryCurriculumInfo(params).then(res => {
        this.tableData = res.rows || []
      }).catch(error => {
        this.$Message.error('获取课次失败: ' + error)
      })
    }
  },
  created () {
    this.getTable()
  }
}
</script>
