<!--优惠信息-->
<template>
  <div class="preferential">
    <template v-if="promotionList.length===0">
      <Row class="no-data">
        <Col span="24">该班级暂无优惠活动</Col>
      </Row>
    </template>
    <template v-else>
      <div v-for="(promotion,index) in promotionList" :key="promotion.promotionId">
        <h2 style="margin-left:-30px">活动{{index+1}}：</h2>
        <Row class="border-dash">
          <Col span="3" class="spa">
            活动名称:
          </Col>
          <Col span="21" class="mess">
            {{promotion.promotionName}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            活动类型:
          </Col>
          <Col span="21" class="mess">
            {{promotion.promotionTypeName}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            活动开始时间:
          </Col>
          <Col span="21" class="mess">
            {{promotion.startTime}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            活动结束时间:
          </Col>
          <Col span="21" class="mess">
            {{promotion.endTime}}
          </Col>
        </Row>
        <template v-if="typeOne(promotion.promotionType)">
          <template v-for="(promotionClassGroupList) in promotion.mainClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="2" class="spa">
                  <template v-if="index === 0">
                    原价班:
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="2" class="spa">
                  <template v-if="index === 0">
                    班级:
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="20" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
          <template v-for="(item, index) in promotion.ruleClassGroupList">
            <div :key="index">
            <Row class="info">
              <Col span="2" class="spa">
                优惠班
              </Col>
              <Col span="2" class="spa">
                {{pType(promotion.promotionType)}}
              </Col>
              <Col span="20" class="spa">
                {{getPrice(item.rule, promotion.promotionType)}}
              </Col>
            </Row>
            <Row class="info">
              <Col span="2" class="spa">
                &nbsp;
              </Col>
              <Col span="2" class="spa">
                班级
              </Col>
              <Col span="20" class="mess">
                <template v-for="(promotionClass,index1) in item.promotionClassList">
                  <p :key="index1">{{buildClassInfo(promotionClass)}}</p>
                </template>
              </Col>
            </Row>
            </div>
          </template>
        </template>

        <template v-for="(item, index) in promotion.ruleList">
          <Row class="info" :key="index">
            <Col span="3" class="spa">
              <template v-if="index === 0">
                优惠规则
              </template>
              <template>
                &nbsp;
              </template>
            </Col>
            <Col span="21" class="mess">
              <p>{{buildRule(promotion.promotionType, item)}}</p>
            </Col>
          </Row>
        </template>
        <template v-if="promotion.mainClassGroupList && promotion.promotionType === 16">
          <template v-for="promotionClassGroupList in promotion.mainClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="3" class="spa">
                  <template v-if="index === 0">
                    购买班级
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="21" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
        </template>
        <template v-if="promotion.promotionClassGroupList">
          <template v-for="promotionClassGroupList in promotion.promotionClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="3" class="spa">
                  <template v-if="index === 0">
                    {{promotion.promotionType === 16 ?  (promotion.mainClassGroupList && promotion.mainClassGroupList.length ? '赠送班级' : '适用班级') : '优惠班级'}}
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="21" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
/**
   * promotionType 10-联报折扣 11-加价购 12-联报立减  13-第二件折扣 14-第二件立减 15-限时购 16-买送优惠
   */
import {
  queryClassPromotionActivity
} from '../../../api/clazz'
const PROMOTION = {
  LINKDISCOUNT: 10,
  ADD: 11,
  LINKSUB: 12,
  TWODISCOUNT: 13,
  TWOSUB: 14,
  TIMELIMIT: 15,
  BUYGIVING: 16
}
export default {
  name: 'preferentialInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      loading: false,
      promotionType: {
        10: '联报折扣',
        11: '加价购',
        12: '联报立减',
        13: '第二件折扣',
        14: '第二件立减',
        15: '限时购',
        16: '买送优惠'
      },
      promotionList: []
    }
  },
  computed: {

  },
  methods: {
    initView () {
      this.promotionList = []
      this.getData()
    },
    getData () {
      this.loading = true
      let req = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      queryClassPromotionActivity(req).then(res => {
        if (res) {
          this.promotionList = res
        }
      }).catch(err => {
        // this.$Message.warning({ content: '查看上课时间失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取优惠数据失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    // 显示 原价班 和 优惠班 的形式
    typeOne: function (type) {
      return [PROMOTION.ADD, PROMOTION.TWODISCOUNT, PROMOTION.TWOSUB].indexOf(Number(type)) >= 0
    },
    // 优惠类型
    pType: function (type) {
      if (type === PROMOTION.ADD) {
        return '加价金额'
      } else if (type === PROMOTION.TWODISCOUNT) {
        return '折扣'
      } else if (type === PROMOTION.TWOSUB) {
        return '立减'
      }
      return ' '
    },
    getPrice (item, type) {
      if (type === PROMOTION.ADD) {
        return item.increaseAmount + '元'
      } else if (type === PROMOTION.TWODISCOUNT) {
        return item.discount + '%'
      } else if (type === PROMOTION.TWOSUB) {
        return item.reduceAmount + '元'
      }
    },
    buildClassInfo (data) {
      /**
      classLevelId: "219"
      classLevelName: "敏学班6688"
      classType: 2
      classTypeName: "双师"
      gradeId: "8"
      gradeName: "初中二年级"
      gtId: "2"
      gtName: "初中部"
      subjectId: "ff80808127d77caa0127d7e10f1c00c4"
      subjectName: "数学"
      termId: "4"
      termName: "寒假班"
      year: "2019"
       */
      return [
        `(${data.classTypeName})`,
        `${data.year}年`,
        data.termName,
        data.gtName,
        data.gradeName,
        data.subjectName,
        data.classLevelName
      ].join('')
    },
    buildRule (type, data) {
      if (type === PROMOTION.BUYGIVING) {
        return `买 ${data.buyCount} 科送 ${data.giveCount} 科`
      } else if (type === PROMOTION.TIMELIMIT) {
        let res = `优惠时间段 ${data.startPeriod} 至 ${data.endPeriod} 课程`
        if (data.reduceAmount) {
          res += `立减 ${data.reduceAmount} 元`
        } else if (data.discount) {
          res += `折扣 ${data.discount} %`
        } else if (data.fixedAmount) {
          res += `价格 ${data.fixedAmount} 元`
        }
        return res
      } else if (type === PROMOTION.LINKSUB) {
        return `报班科目数量 ≥ ${data.buyCount} 单科立减 ${data.reduceAmount} 元`
      } else if (type === 10) {
        return `报班科目数量 ≥ ${data.buyCount} 单科折扣 ${data.discount} %`
      }
    }
  }
}
</script>

<style scoped lang="less">
  .preferential {
    padding: 0 50px;
  }
  .info {
    margin: 5px 0;
  }
  .border-dash {
    line-height: 35px;
    border-bottom: 1px dashed #ccc;
  }
  .spa {
    line-height: 30px;
    font-size: 14px;
    font-weight: bold;
  }
  .mess {
    font-size: 13px;
    line-height: 30px;
  }
  .no-data {
    margin: 100px auto;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
  }
</style>
