<template>
  <div>
    <div @keyup.enter="search">
      <div style="padding: 0 5px 5px 5px;">
        <Row>
          <Col span="10">
            <span>原班级信息:</span>
            <span class="stuRight">{{oldClassInfo.year + '+' + oldClassInfo.termName + '+' + oldClassInfo.gradeName + '+' + oldClassInfo.subjectNames + '+' + oldClassInfo.levelName + '+' + classTypeMap[oldClassInfo.classType]}}</span>
          </Col>
          <Col span="7">
            <span>已上 / 总课次:</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.passedCount}} / {{oldClassInfo.classCount}}</span>
          </Col>
          <Col span="7">
            <span>总金额:</span>
            <span class="stuRight">{{oldClassInfo.price}}</span>
          </Col>
        </Row>
        <Row style="margin-top: 10px">
          <Col span="10">
            <span>教师:</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.teacherNames}}</span>
          </Col>
          <Col span="7">
            <span>辅导老师:</span>
            <span class="stuRight">{{oldClassInfo.tutorRealName}}</span>
          </Col>
          <Col span="7">
            <span>教室:</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.classroomName}}</span>
          </Col>
        </Row>
        <Row style="margin-top: 10px">
          <Col span="10">
            <span>上课时间:</span>
            <span class="stuRight">{{oldClassInfo.classtimeNames}}</span>
          </Col>
          <Col span="7">
            <span>开课日期:</span>
            <span class="stuRight">{{oldClassInfo.startDate}}</span>
          </Col>
          <Col span="7">
            <span>结课日期:</span>
            <span class="stuRight">{{oldClassInfo.endDate}}</span>
          </Col>
        </Row>
      </div>
      <div style="position: relative">
        <Form @submit.native.prevent style="margin-top: 10px" ref="form" :model="formData" inline>
          <FormItem>
            <Select v-model="formData.gradeCustomId" @on-change="gradeChange" placeholder="年级" style="width:120px">
              <Option v-for="item in gradeList" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select placeholder="班次" v-model="formData.bookVersion" clearable filterable transfer style="width: 120px;">
              <Option v-for="option in levelList" :value="option.id" :key="option.id">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.categoryType" placeholder="授课类型" transfer style="width:120px">
              <Option v-for="item in classTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.maxPersonBoundary" placeholder="限额标签" transfer style="width:120px" clearable>
              <Option v-for="item in limitTags" :value="item.id" :key="item.value">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="teacherName">
            <Select placeholder="主讲老师" v-model="formData.teacherEmpNo" filterable clearable remote transfer
              @on-change="optionChange" :remote-method="getTeacherList" label-in-value style="width: 120px;" :loading="searchLoading">
              <Option v-for="option in teacherList" :value="option.empNo" :key="option.cityId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select placeholder="辅导老师" v-model="formData.tutorEmpNo" clearable filterable remote transfer
              @on-change="optionChange" :remote-method="getTeacherList2" label-in-value style="width: 120px;" :loading="searchLoading">
              <Option v-for="option in tutorTeacherList" :value="option.empNo" :key="option.cityId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Input :value="oldClassInfo.subjectNames" disabled placeholder="学科" style="width:120px"/>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomAreaId" @on-change="areaChange" placeholder="区域" filterable transfer clearable style="width:120px">
              <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomServiceCenterId" @on-change="serviceChange" filterable placeholder="服务中心" remote :remote-method="getServiceCenterList" transfer clearable style="width:120px">
              <Option v-for="item in serviceCenterList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomVenueId" transfer @on-change="changeVenue" filterable placeholder="教学点" remote :remote-method="getVenueList" clearable style="width:120px">
              <Option v-for="item in venueList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classTimeTypeId" clearable placeholder="上课时间类型"  style="width:120px">
              <Option v-for="(item,index) in classTimeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.period" placeholder="上课时间"  clearable style="width:120px">
              <Option value='上午'>上午</Option>
              <Option value='中午'>中午</Option>
              <Option value='下午'>下午</Option>
              <Option value='晚上'>晚上</Option>
            </Select>
          </FormItem>
          <FormItem>
            <DatePicker placeholder="开课日期" :editable="false" v-model="startDate" @on-change="startDateChange" type="date" style="width: 114px">
            </DatePicker>
            <span style="margin:0 5px;">至</span>
            <DatePicker placeholder="结课日期" :editable="false" v-model="endDate" @on-change="endDateChange" type="date" style="width: 113px">
            </DatePicker>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isSchoolTest" placeholder="入学测试" clearable style="width:120px">
              <Option :value=1>是</Option>
              <Option :value=0>否</Option>
            </Select>
          </FormItem>
          <FormItem>
            <InputNumber :precision="0" :min="-1000" :max="9999" v-model="formData.surplusPersonsStart" placeholder="剩余名额" style="width: 114px;"/>
            <span style="margin:0 5px;">至</span>
            <InputNumber :precision="0" :min="1" :max="10000" v-model="formData.surplusPersonsEnd" placeholder="剩余名额" style="width: 113px;"/>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="search">查询班级</Button>
          </FormItem>
        </Form>
      </div>
    </div>
    <div class="class_table">
      <Table :columns="tableColumns" :data="tableData"></Table>
      <div style="text-align: right">
        <Page
          :total="pageTotal"
          :page-size="pageSize"
          :current="pageNum"
          :page-size-opts="[5, 10, 20, 40]"
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
          show-sizer
          show-total
          style="margin-top: 10px;"/>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getLinkage,
  getAreaList,
  getServiceCenterList,
  getAllServiceCenterList,
  getVenueList,
  getTeacherByNameFuzzy,
  getTimeTypeList
} from '../../api/bd'
import { queryTargetChangeClassList, queryClazzDic } from '../../api/clazz'
import Cookie from 'js-cookie'

export default {
  name: 'TalSearchChangeClassTarget',
  props: {
    // 原班信息
    oldClassInfo: {
      type: Object,
      default: () => { }
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    clientType: {
      type: Number,
      required: true,
      default: 3 // 3盘古 17 呼叫
    }
  },
  data () {
    return {
      // 目标班级查询表单
      startDate: '',
      endDate: '',
      formData: {
        subjectCustomId: '', // 学科id
        gradeCustomId: '', // 年级id
        bookVersion: '', // 班次级别
        categoryType: '', // 授课类型
        teacherEmpNo: null, // 主讲老师
        tutorEmpNo: null, // 辅导老师
        classroomAreaId: '', // 区域id
        classroomServiceCenterId: '', // 服务中心id
        classroomVenueId: '', // 教学点id
        classTimeTypeId: '', // 上课时间类型
        isSchoolTest: '', // 是否入学测试
        surplusPersonsStart: null, // 剩余名额
        surplusPersonsEnd: null, // 剩余名额
        startDateFrom: '', // 开课日期
        startDateTo: '', // 结课日期
        maxPersonBoundary: '' // 限额标签
      },
      serviceCenterList: [], // 服务中心
      gradeList: [], // 年级
      levelList: [], // 班次级别
      classTypeList: [
        { value: '1', label: '在线' },
        { value: '2', label: '双师' },
        { value: '4', label: '面授' },
        { value: '16', label: '自学课' }
      ], // 授课类型
      teacherList: [], // 主讲老师
      tutorTeacherList: [], // 辅导老师
      areaList: [], // 区域
      venueList: [], // 教学点
      classTimeTypeList: [], // 上课时间类型
      // 班级类型映射
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授',
        16: '自学课'
      },
      limitTags: [], // 限额标签
      // 目标班级
      tableData: [],
      pageTotal: 0, // 总条数
      pageSize: 10, // 每页条数
      pageNum: 1, // 第几页
      searchLoading: false,
      tableColumns: [
        {
          title: '班级名称',
          key: 'className',
          minWidth: 130,
          align: 'center',
          render: (h, params) => {
            return h(
              'a',
              {
                props: {
                  href: 'javascript:void(0)'
                },
                on: {
                  click: () => {
                    this.$emit('showClassDetail', params.row.classId)
                  }
                }
              },
              params.row.className
            )
          }
        },
        {
          title: '授课类型',
          key: 'categoryType',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h(
              'span',
              {
                style: {}
              },
              this.getClassType(params.row.categoryType)
            )
          }
        },
        { title: '学科', key: 'subjectName', align: 'center', minWidth: 50 },
        {
          title: '主讲教师',
          key: 'teacherNames',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            let teachers = params.row.teachers
            if (teachers && teachers.length) {
              return h(
                'div',
                teachers.map((item, index) => {
                  return h(
                    'a',
                    {
                      props: {
                        href: 'javascript:void(0)'
                      },
                      on: {
                        click: () => {
                          this.$emit('showTeacherDetail', {
                            teacherEmpNo: item.teacherEmpNo,
                            cityCode: this.studentDetail.cityCode
                          })
                        }
                      }
                    },
                    item.teacherName + ' '
                  )
                })
              )
            }
          }
        },
        {
          title: '辅导教师',
          key: 'tutorRealName',
          align: 'center',
          minWidth: 50
        },
        { title: '开课日期', key: 'startDate', align: 'center', minWidth: 60 },
        { title: '结课日期', key: 'endDate', align: 'center', minWidth: 60 },
        {
          title: '上课时间',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            if (
              params.row.classTimeDisplay
            ) {
              return h(
                'Poptip',
                {
                  style: {
                    display: 'inline-block',
                    marginRight: '5px'
                  },
                  props: {
                    trigger: 'hover',
                    width: '300',
                    wordWrap: true,
                    content: params.row.classTimeDisplay,
                    options: {
                      eventsEnabled: false,
                      positionFixed: true,
                      modifiers: {}
                    }
                  }
                },
                this.getStrLength(params.row.classTimeDisplay, 40, true)
              )
            } else {
              return h('span', params.row.classTimeDisplay)
            }
          }
        },
        {
          title: '服务中心',
          key: 'classroomServiceCenterName',
          align: 'center',
          minWidth: 100
        },
        { title: '教室', key: 'classroomName', align: 'center', minWidth: 100 },
        {
          title: '上课进度',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h(
              'span',
              params.row.passedCount + '/' + params.row.classCount
            )
          }
        },
        {
          title: '报名进度',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h(
              'span',
              {},
              params.row.registPersons + '/' + params.row.maxPersons
            )
          }
        },
        {
          title: '剩余名额',
          key: 'surplusPersons',
          align: 'center',
          minWidth: 40
        },
        { title: '总金额', key: 'price', align: 'center', minWidth: 40 },
        {
          title: '入学测试',
          key: 'isSchooltest',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h('span', params.row.isSchooltest === 0 ? '否' : '是')
          }
        },
        {
          title: '操作',
          minWidth: 60,
          align: 'center',
          render: (h, params) => {
            if (
              params.row.classId === this.oldClassInfo.id ||
              params.row.classId === this.oldClassInfo.classId
            ) {
              return h('span', '')
            } else {
              return h('div', [
                h(
                  'Button',
                  {
                    props: {
                      size: 'small',
                      type: 'primary'
                    },
                    on: {
                      click: () => {
                        this.$emit('confirm', params.row)
                      }
                    }
                  },
                  '转入'
                )
              ])
            }
          }
        }
      ]
    }
  },
  mounted () {
    // 如果原班是全国班
    if (this.oldClassInfo.businessType === 1) {
      this.formData.cityId = '1111'
    } else {
      this.formData.cityId = this.studentDetail.cityCode
    }
    this.formData.subjectCustomId = this.oldClassInfo.subjectIds
    this.formData.gradeCustomId = this.oldClassInfo.gradeId
    this.formData.bookVersion = this.oldClassInfo.bookVersion
    this.getBaseData()
  },
  methods: {
    // 时间类型
    async getTimeTypeList () {
      try {
        const res = await getTimeTypeList()
        this.classTimeTypeList = Object.freeze(res)
      } catch (error) {
        this.$Message.error({ message: error.message, duration: 5 })
      }
    },
    // 限额标签字典表接口
    async queryClazzDic () {
      try {
        const params = {
          platform: this.clientType,
          enName: 'limitLabel',
          cityCode: this.formData.cityId
        }
        const res = await queryClazzDic(params)
        if (res.value) {
          this.limitTags = JSON.parse(res.value)
        }
      } catch (error) {
        this.$Message.error({
          content: `获取限额标签失败: ${error.message}`,
          duration: 5
        })
      }
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    getStrLength (str, len, ellipsis) {
      if (!str || !len) {
        return ''
      }
      if (str.length && str.length > len && ellipsis) {
        return str.substring(0, len) + '...'
      } else {
        return str.substring(0, len)
      }
    },
    // 查找目标班级
    queryTargetClass () {
      if (
        this.formData.surplusPersonsStart != null &&
        this.formData.surplusPersonsEnd != null
      ) {
        if (
          this.formData.surplusPersonsStart > this.formData.surplusPersonsEnd
        ) {
          this.$Message.warning('剩余名额填写错误')
          return
        }
      }

      const params = {
        ...this.formData,
        pageNo: this.pageNum,
        pageSize: this.pageSize,
        sourceClassId: this.oldClassInfo.classId,
        clientType: this.clientType,
        sellCity: this.studentDetail.cityCode
      }
      queryTargetChangeClassList(params)
        .then((res) => {
          this.tableData = res.records
          this.pageTotal = res.totalCount
          this.tableData.forEach((e) => {
            if (e.startDate) {
              e.startDate = e.startDate.substring(0, 10)
            }
            if (e.endDate) {
              e.endDate = e.endDate.substring(0, 10)
            }
          })
          this.$Message.success('查询成功')
        })
        .catch((err) => {
          console.log('查询报错', err)
          this.tableData = []
          this.$Message.warning({
            content: '查询错误:' + err,
            duration: 5
          })
        })
    },
    search () {
      this.pageNum = 1
      this.queryTargetClass()
    },
    changePage (num) {
      this.pageNum = num
      this.queryTargetClass()
    },
    pageSizeChange (size) {
      this.pageNum = 1
      this.pageSize = size
      this.queryTargetClass()
    },
    // 开课日期、结课日期改变
    startDateChange (val) {
      if (this.formData.startDateTo) {
        if (
          new Date(val).getTime() >
          new Date(this.formData.startDateTo).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData.startDateFrom = ''
          this.startDate = ''
          return
        }
      }
      this.formData.startDateFrom = val
    },
    endDateChange (val) {
      if (this.formData.startDateFrom) {
        if (
          new Date(this.formData.startDateFrom).getTime() >
          new Date(val).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData.startDateTo = ''
          this.endDate = ''
          return
        }
      }
      this.formData.startDateTo = val
    },

    // 区域改变
    areaChange (val) {
      this.serviceCenterList = []
      this.venueList = []
      this.formData.classroomServiceCenterId = ''
      this.formData.classroomVenueId = ''
      if (!val) return
      this.getServiceCenterList('', 'noAll')
    },
    serviceChange (val) {
      this.venueList = []
      this.formData.classroomVenueId = ''
      if (!val) return
      // 如果父级没有选中的话，默认把区域带出来
      let item = this.serviceCenterList.find((e) => {
        return e.schoolId === val
      })
      this.oldServiceName = item.name
      this.getVenueList()
    },
    changeVenue (val) {
      if (!val) return
      let item = this.venueList.find((e) => {
        return e.schoolId === val
      })
      this.oldVenueName = item.name
    },
    getServiceCenterListHandler (params) {
      return getServiceCenterList(params)
    },
    getAllServiceCenterListHandler (params) {
      return getAllServiceCenterList(params)
    },
    // 获取服务中心
    getServiceCenterList (name = '', type) {
      name = name.trim()
      if (!this.formData.cityId || (name && this.oldServiceName === name)) {
        return
      }
      // let req = {
      //   cityCode: this.formData.cityId,
      //   areaCode: this.formData.cityId,
      //   areaId: this.formData.classroomAreaId
      // }
      console.log(name, 'ss')
      let ser = {}
      // if (name) req.servLikeName = name
      // getServiceCenterList(req)
      //   .then((data) => {
      //     this.serviceCenterList = data
      //   })
      //   .catch((err) => {
      //     this.$Message.warning({
      //       content: '获取服务中心失败:' + err.message,
      //       duration: 5
      //     })
      //   })
      if (type === 'all') {
        ser = {
          areaCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }
        if (name) ser.likeName = name
        this.getAllServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: '获取服务中心失败:' + err.message, duration: 5 })
        })
      } else {
        ser = {
          areaCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          areaId: this.formData.classroomAreaId
        }
        if (name) ser.likeName = name
        this.getServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: '获取服务中心失败:' + err.message, duration: 5 })
        })
      }
    },
    // 获取教学点
    getVenueList (name = '') {
      name = name.trim()
      if (!this.formData.cityId || (name && this.oldVenueName === name)) {
        return
      }
      let req = {
        cityCode: this.formData.cityId,
        areaCode: this.formData.cityId,
        serviceId: this.formData.classroomServiceCenterId
      }
      if (name) req.venueName = name
      getVenueList(req)
        .then((data) => {
          this.venueList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: '获取教学点失败:' + err.message,
            duration: 5
          })
        })
    },

    // 获取主讲教师列表  教师类型（0：主讲老师 1：辅导老师 2：班主任）
    getTeacherList (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          businessType: this.formData.categoryType,
          sysName: query,
          teacherType: 0,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then((data) => {
            this.teacherList = data.records
            this.searchLoading = false
          })
          .catch((err) => {
            this.$Message.warning({
              content: '获取老师失败:' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    getTeacherList2 (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          businessType: this.formData.categoryType,
          sysName: query,
          teacherType: 1,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then((data) => {
            this.tutorTeacherList = data.records
            this.searchLoading = false
          })
          .catch((err) => {
            this.$Message.warning({
              content: '获取老师失败:' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    // 年部改变
    gradeTypeChange (val) {
      this.formData.gradeCustomId = ''
      this.formData.bookVersion = ''
      this.gradeList = []
      this.levelList = []
      if (!val) {
        return
      }
      this.getGradeList()
    },
    // 年级改变
    gradeChange (val) {
      this.formData.bookVersion = ''
      this.levelList = []
      if (!val) {
        return
      }
      this.getLevelList()
    },
    // 获取年级
    getGradeList () {
      let req = {
        schoolCode: this.formData.cityId,
        cityCode: this.formData.cityId,
        queryType: 'g',
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData.gradeTypeId
      }
      getLinkage(req)
        .then((data) => {
          this.gradeList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: '获取年级失败:' + err.message,
            duration: 5
          })
        })
    },
    // 获取班次
    getLevelList () {
      let req = {
        schoolCode: this.formData.cityId,
        cityCode: this.formData.cityId,
        queryType: 'c',
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeCustomId
      }
      getLinkage(req)
        .then((data) => {
          this.levelList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: '获取班次失败:' + err.message,
            duration: 5
          })
        })
    },

    // 获取基础数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // 获取年级
      this.getGradeList()
      // 获取班次
      this.getLevelList()
      // 获取区域
      let req4 = {
        areaCode: this.formData.cityId,
        cityCode: this.formData.cityId
      }
      getAreaList(req4)
        .then((data) => {
          this.areaList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: '获取区域失败:' + err.message,
            duration: 5
          })
        })
      this.getServiceCenterList('', 'all')
      this.getVenueList()
      // 获取限额标签
      this.queryClazzDic()
      this.getTimeTypeList()
    }
  }
}
</script>

