<!--变动信息-->
<template>
  <div class="changeInfo">
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div>加载中</div>
    </Spin>
    <Row>
      <Col span="2" style="border-right: 1px solid gainsboro">
        <div class="table-tag" :class="tabContent === 'changeList'? 'tabColor' : 'changeBtn'"  @click="changeTable('changeList')">变动信息</div>
        <div class="table-tag" :class="tabContent === 'preChangeList'? 'tabColor' : 'changeBtn'"  @click="changeTable('preChangeList')">预变动信息</div>
      </Col>
      <Col span="22" style="padding: 10px">
        <div class="newTab">
          <Table stripe :columns="tableColumns" :data="tableData" height="500"></Table>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import {
  queryClassChangeInfo,
  queryClassPrechangeInfo
} from '../../../api/clazz'
export default {
  name: 'changeInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      loading: false,
      tabContent: '',
      tableColumns: [],
      tableData: [],
      changeListColumns: [ // 变动信息
        {
          title: '编号',
          key: 'index',
          align: 'center'
        },
        {
          title: '变动课次',
          key: 'totalClassNum',
          align: 'center'
        },
        {
          title: '变动类型',
          key: 'classTeacherName',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getChangetype(params.row.changeType))
          }
        },
        {
          title: '通知类型',
          key: 'classTeacherType', // 暂时没有
          align: 'center'
        },
        {
          title: '通知方式',
          key: 'msmType',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getAdvice(params.row.msmType))
          }
        },
        {
          title: '变动信息',
          key: 'changeContent',
          align: 'center',
          render: (h, params) => {
            return h('Poptip', {
              style: {
                display: 'inline-block',
                marginRight: '5px'
              },
              props: {
                'popper-class': 'tal-popup',
                trigger: 'hover',
                width: '300',
                wordWrap: true,
                placement: 'left',
                content: params.row.changeContent
              }
            }, this.getClassChangeName(params.row.changeContent))
          }
        },
        {
          title: '变动原因',
          key: 'changeReason',
          align: 'center'
        },
        {
          title: '变动操作日期',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '变动操作人',
          key: 'createrName',
          align: 'center'
        }
      ],
      preChangeListColumns: [
        {
          title: '编号',
          key: 'index',
          align: 'center',
          width: 70
        },
        {
          title: '变动课次',
          key: 'classNum',
          align: 'center',
          width: 100
        },
        {
          title: '预变动信息',
          key: 'preInfo',
          align: 'center'
        },
        {
          title: '预变动进度',
          key: 'changeProgress',
          align: 'center'
        },
        {
          title: '班级变动时间',
          key: 'classChangeAuditDate',
          align: 'center'
        },
        {
          title: '任务申请时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '发起人',
          key: 'createName',
          align: 'center',
          width: 120
        }
      ],
      advice: {
        1: '呼叫中心通知',
        2: '服务中心通知',
        3: '学科通知',
        4: '师训通知',
        5: '不通知'
      },
      changeType: {
        0: '临时变动',
        1: '永久变动',
        2: '批量变动'
      }
    }
  },
  methods: {
    initView () {
      this.changeTable('changeList')
    },
    changeTable (a) {
      this.tableData = []
      this.tabContent = a
      switch (a) {
        case 'changeList':
          this.changeListHandler()
          break
        case 'preChangeList':
          this.preChangeListHandler()
          break
      }
    },
    changeListHandler () {
      this.loading = true
      this.tableData = []
      this.tableColumns = this.changeListColumns
      let changeData = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      queryClassChangeInfo(changeData).then((res) => {
        if (res.length <= 0) {
          this.tableData = res
        } else {
          this.tableData = res
          this.tableData.forEach((e, index) => {
            e.index = index + 1
          })
        }
      }).catch(err => {
        // this.$Message.error({ content: err, duration: 5 })
        this.$emit('errMsg', '获取变动信息列表失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    preChangeListHandler () {
      this.loading = true
      this.tableData = []
      let req = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.tableColumns = this.preChangeListColumns
      queryClassPrechangeInfo(req).then(res => {
        if (res) {
          this.buildPreChangeData(res)
        }
      }).catch(err => {
        // this.$Message.error({ content: err, duration: 5 })
        this.$emit('errMsg', '获取预变动信息列表失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    buildPreChangeData (data) {
      let res = []
      data.forEach((obj, idx) => {
        // 编号	从1开始一次递增
        let index = idx + 1
        // 变动课次
        let classNum = obj.callTask.startClassNum + (obj.callTask.endClassNum !== obj.callTask.startClassNum ? `-${obj.callTask.endClassNum}` : '')
        // 预变动信息 通过 变动课次 和 变动条目遍历 拼装组成
        let preInfo = `变动课次：${obj.callTask.startClassNum}-${obj.callTask.endClassNum}；`
        obj.callChangeItemList.forEach(item => {
          preInfo += `${item.changeTypeName}：${item.newName}；`
        })
        /** 预变动进度 如果是callTask.status 等于4（外呼中） 则添加 进度 callPercentage ，其他仅使用callTask.changeProgress显示
         *
         * WAIT_AUDIT("1", "待审核"),
         WAIT_PUSH("2", "待发布"),
         AUDIT_REFUSE("3", "审核驳回"),
         CALL("4", "外呼中"),
         PUSH_REFUSE("5", "发布驳回"),
         WAIT_TEA_CONFIRM("6", "待教学确认"),
         WAIT_CHANGE("7", "待变动"),
         TASK_DONE("8", "任务完成");
         * @type {string}
         */
        let changeProgress = ''
        if (Number(obj.callTask.status) === 4) {
          changeProgress = `${obj.callTask.changeProgress}：${obj.callPercentage}%`
        } else {
          changeProgress = obj.callTask.changeProgress
        }
        // 班级变动时间
        let createDate = obj.callTask.createDate || '--'
        // 任务申请时间
        let classChangeAuditDate = obj.callTask.classChangeAuditDate || '--'
        // 发起人
        let createName = obj.callTask.createName || '--'
        res.push({
          index,
          classNum,
          preInfo,
          changeProgress,
          createDate,
          classChangeAuditDate,
          createName
        })
      })
      this.tableData = res
    },
    getAdvice (val) {
      return this.advice[val]
    },
    getChangetype (val) {
      return this.changeType[val]
    },
    // 获取变动记录
    getClassChangeName (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    }
  }
}
</script>

<style lang="less">
  .changeInfo {
    position: relative;
    .table-tag {
      margin-top: 20px;
      text-align: center;
      cursor: pointer;
      padding: 3px;
      &.tabColor{
        color: #008CF8;
      }
    }
    .ivu-table-wrapper{
      overflow: unset;
    }
  }
  body /deep/ .tal-popup {
    z-index: 29999 !important;
  }
</style>
