<template>
  <div class="pos-relative">
    <div class="rate-wrapper">
      <Spin fix v-if="percentLoading">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div>加载中</div>
      </Spin>
      <div class="circle-wrapper">
        <i-circle :size="85" :percent="satisfyPercent" stroke-color="#5cb85c">
          <div class="circle-custom">
            <div>
              <span class="num">{{satisfyPercent}}</span>
              <span class="percent">%</span>
            </div>
            <div class="text">满意</div>
          </div>
        </i-circle>
        <i-circle :size="85" :percent="normalPercent">
          <div class="circle-custom">
            <div>
              <span class="num">{{normalPercent}}</span>
              <span class="percent">%</span>
            </div>
            <div class="text">一般</div>
          </div>
        </i-circle>
        <i-circle :size="85" :percent="unSatisfyPercent" stroke-color="#ff5500">
          <div class="circle-custom">
            <div>
              <span class="num">{{unSatisfyPercent}}</span>
              <span class="percent">%</span>
            </div>
            <div class="text">不满意</div>
          </div>
        </i-circle>
      </div>
      <div class="text-wrapper">
        <span class="text">来自学员的{{rateTotal}}条综合评价</span>
        <Tooltip content="4星以上为满意，2.5星到3.5星为一般，2.5星以下为不满意" theme="light" placement="right">
          <Icon type="ios-help-circle" size="18"/>
          <div slot="content">
            <p>满意：4星（包含）以上</p>
            <p>一般：2.5星（包含）到3.5星（包含）</p>
            <p>不满意：2星（包含）以下</p>
          </div>
        </Tooltip>
      </div>
    </div>
    <div style="position: relative">
      <Spin fix v-if="commentLoading">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div>加载中</div>
      </Spin>
      <div class="comment-wrapper" v-for="(item, idx) in rate.evaluateList" :key="idx">
        <div class="name-rate">
          <div class="name-time">
            <span class="name">{{item.stuName}}</span><span class="time">{{item.evaluateTime}}</span>
          </div>
          <div class="rate">
            <Rate allow-half disabled :value="item.starNum" />
          </div>
        </div>
        <div class="comment" v-html="item.evaluateContent"></div>
      </div>
      <div v-if="(!rate.evaluateList || rate.evaluateList.length === 0) && !commentLoading" class="no-data" >
        <span class="no-comment"></span>
        <span class="text">暂无学员评论</span>
      </div>
      <div v-else class="pager-wrapper">
        <Page :total="rateTotal" :current.sync="ratePageIndex" :page-size="ratePageSize" @on-change="getRate" show-total/>
      </div>
    </div>
  </div>
</template>

<script>
import { getTeacherEvaluateList, getTeaCommentPercent } from '../../../api/teacher'
export default {
  name: 'teacherEvaluationInfo',
  data () {
    return {
      rate: {},
      satisfyPercent: 0,
      normalPercent: 0,
      unSatisfyPercent: 0,
      ratePageIndex: 1,
      ratePageSize: 10,
      rateTotal: 0,
      percentLoading: false,
      commentLoading: false
    }
  },
  props: {
    teacherId: { // 老师id
      type: String,
      required: true
    },
    teacherEmpNo: { // 教师员工编号
      type: String,
      default: ''
    },
    schoolCode: { // 老师分校id
      type: String,
      required: true
    }
  },
  methods: {
    initView () {
      this.ratePageIndex = 1
      this.rate = {}
      this.getRatePercent()
      this.getRate()
    },
    async getRate () {
      try {
        this.commentLoading = true
        this.rate = await getTeacherEvaluateList({
          current: this.ratePageIndex,
          size: this.ratePageSize,
          teacherIds: [ this.teacherId ],
          teacherEmpNo: this.teacherEmpNo,
          schoolCode: this.schoolCode
        })
        this.commentLoading = false
        this.rateTotal = this.rate.totalCount
      } catch (e) {
        this.$emit('errMsg', e)
      } finally {
        this.commentLoading = false
      }
    },
    async getRatePercent () {
      try {
        this.percentLoading = true
        const data = await getTeaCommentPercent({
          teacherIds: [ this.teacherId ],
          teacherEmpNo: this.teacherEmpNo,
          schoolCode: this.schoolCode
        })
        this.percentLoading = false
        this.satisfyPercent = parseInt((data.satisfyPercent * 100).toFixed(0))
        this.normalPercent = parseInt((data.normalPercent * 100).toFixed(0))
        this.unSatisfyPercent = parseInt((data.unSatisfyPercent * 100).toFixed(0))
      } catch (e) {
        this.$emit('errMsg', e)
      } finally {
        this.percentLoading = false
      }
    }
  }
}
</script>

