<template>
  <Form
    class="service-venue-classroom-wrap self-form"
    ref="areaServiceVenueClassroomForm"
    :model="areaServiceVenueClassroomForm"
    inline
  >
    <FormItem
      class="form-item-wrap mr10"
      prop="area"
    >
      <label v-if="showNameLabel" class="ivu-form-item-label fz12-imp" :style="nameLabelStyle">地区</label>
      <Select
        transfer
        filterable
        clearable
        label-in-value
        :style="(isEmpty(setAreaStyle)?false:setAreaStyle) || selectStyleComputed('a')"
        :disabled="disabled"
        :placeholder="setPlaceholder&&setPlaceholder.area?setPlaceholder.area:'地区'"
        v-model="areaServiceVenueClassroomForm.areaId"
        :class="loadingMap.area?'global-select-loading':''"
        @on-change="areaChange"
      >
        <Option
          v-for="option in areaList"
          :key="option.value"
          :label="option.label"
          :value="option.value"
        ></Option>
      </Select>
    </FormItem>

    <FormItem prop="serviceCenter" class="form-item-wrap">
      <label v-if="showNameLabel" class="ivu-form-item-label fz12-imp" :style="nameLabelStyle">服务中心</label>
      <Select
        transfer
        filterable
        clearable
        label-in-value
        :style="selectStyleComputed('s')"
        :disabled="disabled"
        :placeholder="setPlaceholder&&setPlaceholder.serviceCenter?setPlaceholder.serviceCenter:'服务中心'"
        v-model="areaServiceVenueClassroomForm.serviceCenterId"
        :class="loadingMap.serviceCenter?'global-select-loading':''"
        @on-change="serviceCenterChange"
      >
        <!--@on-query-change="queryServiceList"-->
        <Option
          v-for="option in serviceCenterList"
          :key="option.value"
          :label="option.label"
          :value="option.value"
        ></Option>
      </Select>
    </FormItem>

    <FormItem prop="venue" class="form-item-wrap">
      <label v-if="showNameLabel" class="ivu-form-item-label" :style="nameLabelStyle">教学点</label>
      <Select
        transfer
        filterable
        clearable
        label-in-value
        :style="selectStyleComputed('v')"
        :disabled="disabled"
        :placeholder="setPlaceholder&&setPlaceholder.venue?setPlaceholder.venue:'教学点'"
        v-model="areaServiceVenueClassroomForm.venueId"
        :class="loadingMap.venue?'global-select-loading':''"
        @on-change="venueChange"
      >
        <Option
          v-for="option in venueList"
          :key="option.value"
          :label="option.label"
          :value="option.value"
        >
        </Option>
      </Select>
    </FormItem>

    <FormItem :error="error" prop="classroom" class="form-item-wrap">
      <label v-if="showNameLabel" class="ivu-form-item-label" :style="nameLabelStyle">教室</label>
      <Select
        transfer
        filterable
        clearable
        label-in-value
        :style="selectStyleComputed('c')"
        v-model="classroomSelect"
        :disabled="disabled"
        :placeholder="setPlaceholder&&setPlaceholder.classroom?setPlaceholder.classroom:'教室'"
        :class="loadingMap.classroom?'global-select-loading':''"
        @on-change="classroomChange"
      >
        <Option
          v-for="option in classroomList"
          :key="option.value"
          :label="option.label"
          :value="option.value"
        ></Option>
      </Select>
    </FormItem>
  </Form>
</template>

<script>
/*
  * 地区联动到教室查询
  *
  * isAutoGetData 默认自动返回数据
  * getData 返回数据
  *
  * */
import {
  queryAreaList,
  queryServiceCenterList,
  queryVenueList,
  queryClassroomList
} from '../../api/masterBD'

import courseBase from '../../mixin/courseBase'

export default {
  name: 'TalLinkQueryClassroom',
  mixins: [courseBase],
  props: {
    error: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    cityId: {
      type: String,
      required: true
    },
    isSingleGetData: {
      type: Boolean,
      default: false
    },
    getAllDataStatus: {
      type: Boolean,
      default: false
    },
    resetFormStatus: {
      type: Boolean,
      default: false
    },
    showNameLabel: {
      type: Boolean,
      default: false
    },
    nameLabelStyle: {
      type: Object,
      default () {
        return {}
      }
    },
    initData: { // 初始值 回显
      type: Object,
      default () {
        return {}
      }
    },
    initDataSingle: { // 初始值 回显
      type: Object,
      default () {
        return {
          type: 'classroom',
          data: {
            id: '',
            name: ''
          }
        }
      }
    },
    courseParentData: {
      type: Object,
      default () {
        return { // 根据状态筛选类型，无传入则默认全部
          categoryType: '',
          contentType: ''
        }
      }
    },
    setSelectStyle: {
      type: Array,
      default () {
        return []
      }
    },
    selectStyle: {
      type: Object,
      default () {
        return {
          width: '135px'
        }
      }
    },
    setPlaceholder: {
      type: Object,
      default () {
        return {}
      }
    },
    isAutoGetData: { // 是否提取数据
      type: Boolean,
      default: true
    },
    isClassSide: { // 默认课程规则 为true时启用班级规则
      type: Boolean,
      default: false
    },
    setAreaStyle: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      areaList: [],
      serviceCenterList: [],
      venueList: [],
      classroomList: [],
      liveRoomList: [],
      classroomDataMap: {},
      loadingMap: {
        area: false,
        serviceCenter: false,
        venue: false,
        classroom: false
      },
      classroomSelect: '',
      areaServiceVenueClassroomForm: {
        areaId: '',
        areaName: '',
        serviceCenterId: '',
        serviceCenterName: '',
        venueId: '',
        venueName: '',
        classroomId: '',
        classroomName: '',
        liveRoomId: '',
        liveRoomName: '',
        roomName: '', // 不区分教室 直播间
        roomId: ''// 不区分教室 直播间
      },
      stopSign: false
    }
  },
  methods: {
    resetSelect (clearType, opType) {
      clearType = clearType || 1
      opType = opType || 1

      /*
        * clearType
        * 1-area,
        * 2-serviceCenter,
        * 3-venue,
        * 4-classroom、liveRoom
        *
        * opType
        * 1-classroom
        * 2-liveRoom
        * */

      if (clearType === 4) {
        this.classroomList = []
        this.classroomSelect = ''
        this.areaServiceVenueClassroomForm.classroomId = ''
        this.areaServiceVenueClassroomForm.classroomName = ''
        this.areaServiceVenueClassroomForm.liveRoomId = ''
        this.areaServiceVenueClassroomForm.liveRoomName = ''

        this.areaServiceVenueClassroomForm.roomId = ''
        this.areaServiceVenueClassroomForm.roomName = ''
        if (!this.stopSign) this.$emit('getData', this.areaServiceVenueClassroomForm)
        return
      }

      if (clearType === 3) {
        this.venueList = []
        this.areaServiceVenueClassroomForm.venueId = ''
        this.areaServiceVenueClassroomForm.venueName = ''
        return
      }

      if (clearType === 2) {
        this.serviceCenterList = []
        this.areaServiceVenueClassroomForm.serviceCenterId = ''
        this.areaServiceVenueClassroomForm.serviceCenterName = ''
        return
      }

      if (clearType === 1) {
        this.areaServiceVenueClassroomForm.areaId = ''
        this.areaServiceVenueClassroomForm.areaName = ''
      }
    },
    queryAreaAjax () {
      this.resetSelect(1, 1)

      if (!this.cityId) {
        return
      }

      this.loadingMap.area = true

      queryAreaList({
        schoolCode: this.cityId,
        status: 0
      }).then(res => {
        if (res) {
          res = res || []
          this.areaList = res.map(item => {
            return {
              value: item.schoolId,
              label: item.name
            }
          })
        }

        this.initDataFn()
      }).catch((err) => {
        this.$Message.error(err.message || '获取地区失败')
      }).finally(() => {
        this.loadingMap.area = false
      })
    },
    queryServiceCenterAjax (val, opType) {
      opType = opType || 1

      this.resetSelect(2, opType)

      if (!this.cityId) {
        return
      }

      return new Promise((resolve, reject) => {
        this.loadingMap.serviceCenter = true

        queryServiceCenterList({
          areaid: val,
          schoolCode: this.cityId,
          status: 0
        }).then(res => {
          if (res) {
            res = res || []
            this.serviceCenterList = res.map(item => {
              return {
                value: item.schoolId,
                label: item.name
              }
            })
            resolve()
          } else {
            this.$Message.error('获取服务中心失败')
            reject()
          }
        }).catch((err) => {
          this.$Message.error(err.message || '获取服务中心失败')
          reject()
        }).finally(() => {
          this.loadingMap.serviceCenter = false
        })
      })
    },
    queryVenueAjax (val, opType) {
      opType = opType || 1

      this.resetSelect(3, opType)

      if (!this.cityId) {
        return
      }

      return new Promise((resolve, reject) => {
        this.loadingMap.venue = true

        queryVenueList({
          areaid: this.areaServiceVenueClassroomForm.areaId,
          serviceid: val,
          schoolCode: this.cityId,
          status: 0
        }).then(res => {
          if (res) {
            res = res || []
            this.venueList = res.map(item => {
              return {
                value: item.schoolId,
                label: item.name
              }
            })
            resolve()
          } else {
            this.$Message.error('获取教学点失败')
            reject()
          }
        }).catch((err) => {
          this.$Message.error(err.message || '获取教学点失败')
          reject()
        }).finally(() => {
          this.loadingMap.venue = false
        })
      })
    },
    queryRoomAjax (val, opType) {
      opType = opType || 1

      /*
        * 教室、直播间 课程部分逻辑
        * */

      /*
       * 教室、直播间 classType liveType 传参释义
       * classType 定义
       * 1:普通教室，2:双师教室，3:直播间，4:AI，5:虚拟
       *
       * liveType 定义
       * 1:双师直播教室，2:在线直播教室
       * 若传3 则表示1+2 都可以
       * */

      const cate = this.courseParentData.categoryType
      const content = this.courseParentData.contentType

      let classType = ''
      let liveType = ''
      if (cate - 1 === 0 || cate - 16 === 0) {
        classType = this.isClassSide ? 5 : 3
        liveType = this.isClassSide ? '' : 2
      } else if (cate - 2 === 0) {
        if (content - 1 === 0) {
          /* 普通 */
          classType = this.isClassSide ? 2 : 3
          liveType = this.isClassSide ? '' : 1
        } else if (content - 2 === 0) {
          /* AI */
          classType = this.isClassSide ? 4 : ''
          liveType = ''
        }
      } else if (cate - 4 === 0) {
        if (content - 1 === 0) {
          /* 普通 */
          classType = 1
          liveType = ''
        } else if (content - 2 === 0) {
          /* AI */
          classType = 4
          liveType = ''
        }
      }

      if (!this.cityId) {
        return
      }

      this.resetSelect(4, opType)

      return new Promise((resolve, reject) => {
        this.loadingMap.classroom = true

        queryClassroomList({
          venueId: val,
          schoolCode: this.cityId,
          classType,
          liveType,
          status: 0
        }).then(res => {
          res = res || []
          this.classroomDataMap = {}
          const list = res.map(item => {
            this.classroomDataMap[item.schoolId] = item

            return {
              value: item.schoolId,
              label: item.name
            }
          })

          if (opType === 1) {
            this.classroomList = list
          } else {
            this.liveRoomList = list
          }
          resolve()
        }).catch((err) => {
          this.$Message.error(err.message || '获取教室失败')
          reject()
        }).finally(() => {
          this.loadingMap.classroom = false
        })
      })
    },
    areaChange (val) {
      if (val) {
        this.areaServiceVenueClassroomForm.areaId = val.value
        this.areaServiceVenueClassroomForm.areaName = val.label
        this.queryServiceCenterAjax(val.value, 1)
      } else {
        this.resetSelect(2, 1)
        this.areaServiceVenueClassroomForm.areaId = ''
        this.areaServiceVenueClassroomForm.areaName = ''
      }

      if (this.isAutoGetData && !this.stopSign) {
        this.$emit('getData', this.areaServiceVenueClassroomForm)
      }
    },
    serviceCenterChange (val) {
      if (val) {
        this.areaServiceVenueClassroomForm.serviceCenterId = val.value
        this.areaServiceVenueClassroomForm.serviceCenterName = val.label
        this.queryVenueAjax(val.value, 1)
      } else {
        this.resetSelect(3, 1)
        this.areaServiceVenueClassroomForm.serviceCenterId = ''
        this.areaServiceVenueClassroomForm.serviceCenterName = ''
      }

      if (this.isAutoGetData && !this.stopSign) {
        this.$emit('getData', this.areaServiceVenueClassroomForm)
      }
    },
    venueChange (val) {
      if (val) {
        this.areaServiceVenueClassroomForm.venueId = val.value
        this.areaServiceVenueClassroomForm.venueName = val.label
        this.queryRoomAjax(val.value, 1)
      } else {
        this.resetSelect(4, 1)
        this.areaServiceVenueClassroomForm.venueId = ''
        this.areaServiceVenueClassroomForm.venueName = ''
      }
    },
    classroomChange (val) {
      if (val) {
        /* 当只有3的时候 为直播间 否则为教室 */
        if (this.classroomDataMap[val.value].classType - 3 === 0) {
          this.areaServiceVenueClassroomForm.classroomId = ''
          this.areaServiceVenueClassroomForm.classroomName = ''
          this.areaServiceVenueClassroomForm.liveRoomId = val.value
          this.areaServiceVenueClassroomForm.liveRoomName = val.label
        } else {
          this.areaServiceVenueClassroomForm.classroomId = val.value
          this.areaServiceVenueClassroomForm.classroomName = val.label
          this.areaServiceVenueClassroomForm.liveRoomId = ''
          this.areaServiceVenueClassroomForm.liveRoomName = ''
        }

        this.areaServiceVenueClassroomForm.roomId = val.value
        this.areaServiceVenueClassroomForm.roomName = val.label
      } else {
        this.areaServiceVenueClassroomForm.classroomId = ''
        this.areaServiceVenueClassroomForm.classroomName = ''
        this.areaServiceVenueClassroomForm.liveRoomId = ''
        this.areaServiceVenueClassroomForm.liveRoomName = ''

        this.areaServiceVenueClassroomForm.roomId = ''
        this.areaServiceVenueClassroomForm.roomName = ''
      }

      if (this.isAutoGetData || this.isSingleGetData) {
        this.$emit('getData', this.areaServiceVenueClassroomForm)
      }
    },
    findItemFn (list, val) {
      return list.find(item => {
        return item.schoolId === val
      })
    },
    initDataFn () {
      console.log('init')

      if (!this.cityId) {
        return
      }

      let val = this.initData.areaId
      let findItem
      if (val) {
        findItem = this.findItemFn(this.areaList)
        let areaName = findItem ? findItem.name : ''
        this.queryServiceCenterAjax(val).then(() => {
          val = this.initData.serviceCenterId
          if (val) {
            findItem = this.findItemFn(this.serviceCenterList)
            let serviceCenterName = findItem ? findItem.name : ''
            this.queryVenueAjax(val).then(() => {
              val = this.initData.venueId
              if (val) {
                findItem = this.findItemFn(this.venueList)
                let venueName = findItem ? findItem.name : ''
                this.queryRoomAjax(val).then(() => {
                  const {
                    areaId,
                    serviceCenterId,
                    venueId,
                    classroomId
                  } = this.initData
                  this.classroomSelect = classroomId
                  findItem = this.findItemFn(this.classroomList)
                  let roomName = findItem ? findItem.name : ''
                  this.areaServiceVenueClassroomForm = {
                    ...this.areaServiceVenueClassroomForm,
                    areaId,
                    areaName,
                    serviceCenterId,
                    serviceCenterName,
                    venueId,
                    venueName,
                    roomId: classroomId,
                    roomName
                  }
                })
              }
            })
          }
        })
      }
    },
    initDataSingleFn () {
      const initData = this.initDataSingle.data || {}
      const initId = initData.id || ''
      const initName = initData.name || ''
      const initList = [{
        label: initName,
        value: initId
      }]

      /*
      * 默认只回显教室 联动查询后续 需要前序数据
      * */

      if (this.initDataSingle.type === 'area') {
        this.areaServiceVenueClassroomForm.areaId = initId
        this.areaServiceVenueClassroomForm.areaName = initName
      }

      if (this.initDataSingle.type === 'serviceCenter') {
        this.serviceCenterList = initList
        this.areaServiceVenueClassroomForm.serviceCenterId = initId
        this.areaServiceVenueClassroomForm.serviceCenterName = initName
      }

      if (this.initDataSingle.type === 'venue') {
        this.venueList = initList
        this.areaServiceVenueClassroomForm.venueId = initId
        this.areaServiceVenueClassroomForm.venueName = initName
      }

      if (this.initDataSingle.type === 'classroom') {
        this.classroomSelect = initId
        this.classroomList = initList
      }
    },
    selectStyleComputed (type) {
      return this.setSelectStyle.indexOf(type) === -1 ? {
        width: '135px'
      } : this.selectStyle
    },
    isEmpty (obj) {
      return Object.keys(obj).length === 0
    }
  },
  watch: {
    cityId () {
      this.queryAreaAjax()
    },
    isSingleGetData () {
      // console.log(this.isSingleGetData);
    },
    getAllDataStatus () {
      if (this.getAllDataStatus && !this.stopSign) {
        this.$emit('getData', this.areaServiceVenueClassroomForm)
      }
    },
    resetFormStatus () {
      if (this.resetFormStatus) {
        this.stopSign = true
        this.areaServiceVenueClassroomForm = {
          areaId: '',
          areaName: '',
          serviceCenterId: '',
          serviceCenterName: '',
          venueId: '',
          venueName: '',
          classroomId: '',
          classroomName: '',
          liveRoomId: '',
          liveRoomName: '',
          roomId: '',
          roomName: ''
        }

        this.$nextTick(() => {
          this.$emit('resetFormDone', false)
          this.stopSign = false
        })
      }
    },
    initData: {
      deep: true,
      handler () {
        this.initDataFn()
      }
    },
    initDataSingle: {
      deep: true,
      handler () {
        this.initDataSingleFn()
      }
    }
  },
  mounted () {
    this.queryAreaAjax()
  }
}
</script>

<style lang="less">
  .service-venue-classroom-wrap {
    .ivu-form-item-label {
      text-align: right;
    }

    .global-select-loading {
      /deep/ .ivu-select-arrow {
        transition: none;
      }

      i.ivu-icon {
        &:before {
          content: "\F45B";
        }

        font-size: 18px;
        top: 7px;
        animation: ani-load-loop 1s linear infinite;
      }

      &.ivu-select-small {
        i.ivu-icon {
          font-size: 16px;
          top: 3px;
        }
      }
    }
  }
</style>
