/*
* 教师查询
* params
*   schoolCode,
*   teacherType, // 0：主讲老师 1：辅导老师 2：班主任
*   pageNo: 1,
*   pageSize: 20,
*   sysName: 模式查询名称
* */
export const queryTeacher = params => {
  params = {
    ...params,
    pageSize: 100
  }
  return teacher.request({
    url: '/admin/teacher/getTeacherByNameFuzzy',
    method: 'post',
    data: params
  })
}

/*
* 通过员工编号查询教师信息
* params
*   schoolCode
*   type // 0：主讲老师 1：辅导老师 2：班主任
*   empNo
* */
export const getTeacherByEmpNo = params => {
  return teacher.request({
    url: '/teacherBase/getTeacherByEmpNo',
    method: 'post',
    data: params,
    headers: {
      platformKey: '3945bc115be5c4792e3b6564c7b47814'
    }
  })
}

