<script>
import { queryChangeCourseClassList, queryClazzDic } from '../../api/clazz'
import { getStrLength } from '../../utils/utils'

import {
  getLinkage,
  getTimeTypeList,
  getTeacherByNameFuzzy,
  getAreaList as getArea,
  getVenueList as getVenue,
  getServiceCenterList,
  getAllServiceCenterList
} from './../../api/bd'
import Cookie from 'js-cookie'

export default {
  name: 'TalChangeCourseTargetClass',
  props: {
    // 是否特殊调课
    isSpecial: {
      type: Number,
      default: 0
    },
    // 原班信息
    oldClassInfo: {
      type: Object,
      default: function () {
        return {}
      }
    },
    changeCountTarget: {
      type: Number
    },
    clientType: {
      type: Number,
      required: true,
      default: 3 // 3盘古 17 呼叫
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      classtimeList: [],
      teacherEmpNos: '',
      tutorEmpNos: '',
      cityId: '',
      pageNum: 1,
      pageSize: 5,
      pageTotal: 0,
      startDate: '',
      searchLoading: false, // 老师模糊查询loading
      maxPersonBoundaryList: [], // 限额标签list
      endDate: '',
      venueList: [], // 区域列表
      // 上课时间类型
      classTypeMap: {
        '1': '在线',
        '2': '双师',
        '4': '面授',
        '16': '自学课'
      },
      classTypeList: [
        { value: '1', label: '在线' },
        { value: '2', label: '双师' },
        { value: '4', label: '面授' },
        { value: '16', label: '自学课' }
      ],
      classTimeTypeList: [],
      tutorTeacherList: [],
      changeCourseTypeList: [],
      gradeTypeList: [], // 年部列表
      gradeList: [], // 年级列表
      levelList: [], // 班次列表
      areaList: [], // 城市列表
      teacherList: [], // 老师列表
      serviceCenterList: [], // 服务中心列表
      // 班级查找弹框表单
      formData2: {
        classId: '', // 原班id
        maxPersonBoundary: '', // 限额标签
        termId: '', // 学期id	是
        studentId: '', // 学生ID	是 ff808081669b036b01669be7ca9500d5
        srcClassId: '', // 原班级id	是 ff808081669b036b01669be7ca9500d5
        // studentGradeId: "", // 学生在读年级	是
        // changeCountTarget: null, // 调课课次号	是
        subjectCustomId: '', // 学科id 是
        gradeTypeId: this.oldClassInfo.gradeTypeId, // 年部id	是
        gradeCustomId: this.oldClassInfo.gradeId,	// String	年级id
        bookVersion: '', // String	班次id
        categoryType: '1', // 授课类型
        teacherEmpNos: '', // 老师id
        tutorEmpNos: '', // 辅导老师id
        classroomAreaId: '', // 区域
        classroomServiceCenterId: '', // 服务中心id
        classRoomVenueId: '', // 教学点id
        classTimeTypeId: '', // 请选择上课时间类型id
        period: '', // 上课时间
        curriculumTimesStartTime: {}, // 开课日期start
        curriculumTimesEndTime: {}, // 开课日期end
        isSchoolTest: null, // 是否进行入学测试
        // claStartCount: null, // 剩余课次区间start
        // claEndCount: null, // 剩余课次区间end
        remainCountStart: null, // 剩余名额区间start
        isChangeNumZero: 0, // 是否展示可调入人数为0的班 0否 1是
        remainCountEnd: null // 剩余名额区间end
      },
      tableData: [],
      tableColumns: [
        {
          title: '班级名称',
          key: 'className',
          minWidth: 100,
          align: 'center',
          render: (h, params) => {
            return h(
              'a',
              {
                props: {
                  href: 'javascript:void(0)'
                },
                on: {
                  click: () => {
                    this.$emit('showClassDetail', params.row.classId)
                  }
                }
              },
              params.row.className
            )
          }
        },
        {
          title: '授课类型',
          key: 'categoryType',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h(
              'span',
              {
                style: {}
              },
              this.getClassType(params.row.categoryType)
            )
          }
        },
        // {
        //     title:"限额标签",
        //     key: "maxPersonBoundary",
        //     align: "center"
        // },
        { title: '学科', key: 'subjectCustomName', align: 'center', minWidth: 50 },
        {
          title: '主讲教师',
          key: 'teacherName',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            let teacherName = params.row.teacherName
            // let teacherEmpNos = params.row.teacherEmpNos
            //   ? params.row.teacherEmpNos.split(',')
            //   : []
            if (teacherName) {
              return h('a', {
                on: {
                  click: () => {
                    this.$emit('showTeacherDetail', {
                      teacherEmpNo: params.row.teacherEmpNo,
                      cityCode: this.studentDetail.cityCode
                    })
                  }
                }
              }, params.row.teacherName)
            }
          }
        },
        {
          title: '辅导教师',
          key: 'tutorName',
          align: 'center',
          minWidth: 50
        },
        { title: '开课日期', key: 'startDate', align: 'center', minWidth: 70 },
        { title: '结课日期', key: 'endDate', align: 'center', minWidth: 70 },
        {
          title: '上课时间',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            if (
              params.row.classTimeDisplay
            ) {
              return h(
                'Poptip',
                {
                  style: {
                    display: 'inline-block',
                    marginRight: '5px'
                  },
                  props: {
                    trigger: 'hover',
                    width: '300',
                    wordWrap: true,
                    content: params.row.classTimeDisplay,
                    options: {
                      eventsEnabled: false,
                      positionFixed: true,
                      modifiers: {}
                    }
                  }
                },
                getStrLength(params.row.classTimeDisplay, 40, true)
              )
            } else {
              return h('span', params.row.classTimeDisplay)
            }
          }
        },
        {
          title: '服务中心',
          key: 'classRoomServiceCenterName',
          align: 'center',
          minWidth: 100
        },
        { title: '教室', key: 'classRoomName', align: 'center', minWidth: 100 },
        {
          title: '上课进度',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h(
              'span',
              params.row.passedCount + '/' + params.row.classCount
            )
          }
        },
        {
          title: '报名进度',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h(
              'span',
              {},
              params.row.maxPersons -
                params.row.remainCount +
                '/' +
                params.row.maxPersons
            )
          }
        },
        // { title: "剩余名额", key: "remainNum", align: "center", minWidth: 40 },
        { title: '总金额', key: 'price', align: 'center', minWidth: 40 },
        {
          title: '可调入人数',
          key: 'canChangeInPersons',
          align: 'center',
          minWidth: 50
        },
        {
          title: '操作',
          minWidth: 80,
          align: 'center',
          render: (h, params) => {
            if (params.row.canChangeInPersons > 0) {
              if (
                this.oldClassInfo.classId === params.row.classId ||
                this.oldClassInfo.targetClassId === params.row.classId
              ) {
                return h('span', '')
              } else {
                return h('div', [
                  h(
                    'Button',
                    {
                      props: {
                        size: 'small',
                        type: 'primary'
                      },
                      on: {
                        click: () => {
                          this.$emit('confirm', params.row)
                        }
                      }
                    },
                    '调入'
                  )
                ])
              }
            }
          }
        }
      ]
    }
  },
  mounted () {
    // 如果原班是全国班
    if (this.oldClassInfo.businessType === 1) {
      this.cityId = '1111'
    } else {
      this.cityId = this.studentDetail.cityCode
    }
    this.getTimeTypeList()
    this.getBaseData()
    this.queryTargetClass()
    this.getClassDic()
  },
  methods: {
    // 时间类型
    async getTimeTypeList () {
      try {
        const res = await getTimeTypeList()
        this.classTimeTypeList = Object.freeze(res)
      } catch (error) {
        this.$Message.error({ message: error.message, duration: 5 })
      }
    },
    // 获取限额标签
    getClassDic () {
      try {
        let reqParams = {
          platform: this.clientType,
          enName: 'limitLabel',
          cityCode: this.cityId
        }
        queryClazzDic(reqParams).then(res => {
          if (res !== '') {
            this.maxPersonBoundaryList = JSON.parse(res.value)
          }
        })
      } catch (error) {
        this.$Message.error({
          content: `获取限额标签失败: ${error.message}`,
          duration: 5
        })
      }
    },
    getVenue (name = '') {
      name = name.trim()
      if (!this.cityId || (name && this.oldVenueName === name)) {
        return
      }
      let req = {
        areaCode: this.cityId,
        cityCode: this.cityId,
        serviceId: this.formData2.classroomServiceCenterId
      }
      if (name) req.venueName = name
      getVenue(req)
        .then(data => {
          this.venueList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取教学点失败:' + err.message,
            duration: 5
          })
        })
    },
    // 年部改变
    gradeTypeChange (val) {
      this.formData2.gradeCustomId = ''
      this.formData2.bookVersion = ''
      this.gradeList = []
      this.levelList = []
      if (!val) {
        return
      }
      this.getGradeList()
    },
    // 年级改变
    gradeChange (val) {
      this.formData2.bookVersion = ''
      this.levelList = []
      if (!val) {
        return
      }
      this.getLevelList()
    },
    handleVisibleChange2 (show) {
      if (show) {
        this.getBaseData()
      } else {
        this.tableData = []
        this.pageTotal = 0
        this.pageNum = 1
        this.pageSize = 10
      }
    },
    // 开课日期、结课日期改变
    startDateChange (val) {
      if (this.formData2.endDate) {
        if (
          new Date(val).getTime() > new Date(this.formData2.endDate).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData2.startDate = ''
          this.startDate = ''
          return
        }
      }
      this.formData2.startDate = val
    },
    endDateChange (val) {
      if (this.formData2.startDate) {
        if (
          new Date(this.formData2.startDate).getTime() > new Date(val).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData2.endDate = ''
          this.endDate = ''
          return
        }
      }
      this.formData2.endDate = val
    },
    areaChange (val) {
      this.serviceCenterList = []
      this.venueList = []
      this.formData2.classroomServiceCenterId = ''
      this.formData2.classRoomVenueId = ''
      if (!val) {
        return false
      }
      this.getServiceCenter('', 'noAll')
    },
    serviceChange (val) {
      this.venueList = []
      this.formData2.classRoomVenueId = ''
      if (!val) return
      // 如果父级没有选中的话，默认把区域带出来
      let item = this.serviceCenterList.find(e => {
        return e.schoolId === val
      })
      // this.formData2.classroomAreaId = item.classroomAreaId
      this.oldServiceName = item.name
      this.getVenue()
    },
    getGradeList () {
      let req = {
        schoolCode: this.cityId,
        cityCode: this.cityId,
        queryType: 'g',
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId
      }
      getLinkage(req)
        .then(data => {
          this.gradeList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取年级失败:' + err.message,
            duration: 5
          })
        })
    },
    getServiceCenterListHandler (params) {
      return getServiceCenterList(params)
    },
    getAllServiceCenterListHandler (params) {
      return getAllServiceCenterList(params)
    },
    getServiceCenter (name, type) {
      name = name.trim()
      if (!this.cityId || (name && this.oldServiceName === name)) {
        return
      }
      // let req = {
      //   cityCode: this.cityId,
      //   areaCode: this.cityId,
      //   areaId: this.formData2.classroomAreaId
      // }
      let ser = {}
      // getServiceCenter(req)
      //   .then(data => {
      //     this.serviceCenterList = data
      //   })
      //   .catch(err => {
      //     this.$Message.warning({
      //       content: '获取服务中心失败:' + err.message,
      //       duration: 5
      //     })
      //   })
      if (type === 'all') {
        ser = {
          areaCode: this.cityId,
          cityCode: this.cityId,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }
        if (name) ser.likeName = name
        this.getAllServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: '获取服务中心失败:' + err.message, duration: 5 })
        })
      } else {
        ser = {
          areaCode: this.cityId,
          cityCode: this.cityId,
          areaId: this.formData2.classroomAreaId
        }
        if (name) ser.likeName = name
        this.getServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: '获取服务中心失败:' + err.message, duration: 5 })
        })
      }
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    changeVenue (val) {
      if (!val) return
      let item = this.venueList.find(e => {
        return e.schoolId === val
      })
      this.oldVenueName = item.name
    },
    // 查找目标班级
    queryTargetClass () {
      if (
        this.formData2.claStartCount != null &&
        this.formData2.claEndCount != null
      ) {
        if (this.formData2.claStartCount > this.formData2.claEndCount) {
          this.$Message.warning('剩余课次填写错误')
          return
        }
      }
      if (
        this.formData2.remainCountStart != null &&
        this.formData2.remainCountEnd != null
      ) {
        if (this.formData2.remainCountStart > this.formData2.remainCountEnd) {
          this.$Message.warning('剩余名额填写错误')
          return
        }
      }
      //   if (!this.formData2.capacityLabel) {
      //     this.$Message.warning("班级容量必填");
      //     return;
      //   }
      this.formData2.year = this.oldClassInfo.year
      this.formData2.clientType = this.clientType
      //   this.formData2.termId = this.oldClassInfo.termId;
      this.formData2.subjectCustomId = this.oldClassInfo.subjectIds
      this.formData2.studentId = this.studentDetail.studentId
      this.formData2.classId = this.oldClassInfo.classId
      this.formData2.curriculumNo = this.changeCountTarget
      this.formData2.cityCode = this.cityId
      if (this.teacherEmpNos) {
        let teacherEmpNos = []
        teacherEmpNos.push(this.teacherEmpNos)
        this.formData2.teacherEmpNos = teacherEmpNos
      } else {
        this.formData2.teacherEmpNos = []
      }

      if (this.tutorEmpNos) {
        let tutorEmpNos = []
        tutorEmpNos.push(this.tutorEmpNos)
        this.formData2.tutorEmpNos = tutorEmpNos
      } else {
        this.formData2.tutorEmpNos = []
      }
      //   this.formData2.studentGradeId = this.oldClassInfo.gradeId;
      this.formData2.pageIndex = this.pageNum
      this.formData2.pageSize = this.pageSize
      queryChangeCourseClassList(this.formData2)
        .then(res => {
          this.formData2.teacherEmpNos = ''
          this.formData2.tutorEmpNos = ''
          this.pageTotal = res.totalCount
          this.tableData = res.records
          this.tableData.forEach(e => {
            if (e.startDate) {
              e.startDate = e.startDate.substring(0, 10)
            }
            if (e.endDate) {
              e.endDate = e.endDate.substring(0, 10)
            }
          })
          this.$Message.success('查询成功')
        })
        .catch(err => {
          this.formData2.teacherEmpNos = ''
          this.formData2.tutorEmpNos = ''
          this.$Message.warning({
            content: '查询错误:' + err,
            duration: 5
          })
        })
    },
    getTeacherList2 (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.cityId,
          cityCode: this.cityId,
          businessType: this.formData2.categoryType,
          sysName: query,
          teacherType: 1,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then(data => {
            this.tutorTeacherList = data.records

            this.searchLoading = false
          })
          .catch(err => {
            this.$Message.warning({
              content: '获取老师失败:' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    getLevelList () {
      let req = {
        schoolCode: this.cityId,
        queryType: 'c',
        cityCode: this.cityId,
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId,
        gradeIds: this.formData2.gradeCustomId
      }
      getLinkage(req)
        .then(data => {
          this.levelList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取班次失败:' + err.message,
            duration: 5
          })
        })
    },

    // 获取基础数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // 获取年部
      let req1 = {
        schoolCode: this.cityId,
        queryType: 'gt',
        cityCode: this.cityId,
        years: this.oldClassInfo.year,
        termIds: this.oldClassInfo.termId
      }
      getLinkage(req1)
        .then(data => {
          this.gradeTypeList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取年部失败:' + err.message,
            duration: 5
          })
        })
      // 获取年级
      this.getGradeList()
      // 获取班次
      this.getLevelList()
      // 获取区域
      let req4 = {
        cityCode: this.cityId,
        areaCode: this.cityId
      }
      getArea(req4)
        .then(data => {
          this.areaList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取区域失败:' + err.message,
            duration: 5
          })
        })
      this.getServiceCenter('', 'all')
      this.getVenue()
    },
    getTeacherList (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.cityId,
          cityCode: this.cityId,
          businessType: this.formData2.categoryType,
          sysName: query,
          teacherType: 0,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then(data => {
            this.teacherList = data.records

            this.searchLoading = false
          })
          .catch(err => {
            this.$Message.warning({
              content: '获取老师失败:' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    pageChange (val) {
      this.pageNum = val
      this.queryTargetClass()
    },
    search () {
      this.pageNum = 1
      this.queryTargetClass()
    },
    pageSizeChange (size) {
      this.pageNum = 1
      this.pageSize = size
      this.queryTargetClass()
    }
  }
}
</script>
