<template>
  <Form label-position="right" :label-width="100">
    <h3 style="margin-bottom: 10px;font-size:14px">查看发票</h3>
    <div style='position:relative'>
      <Spin v-if="loading" fix=""></Spin>
      <Tabs type="card" >
        <TabPane :label="'发票' + (index + 1)" v-for="item, index in invoiceData" :key="item.id">
          <Row class="ivu-top">
            <Col span="24">
              <FormItem label="销售方名称: ">
                <span>{{item.sellerName}}</span>
              </FormItem>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <FormItem label="发票号码: ">
                <span>{{item.invoiceNo}}</span>
              </FormItem>
            </Col>
            <Col span="12">
              <FormItem label="发票金额: ">
                <span>{{item.invoiceAmount}}</span>
              </FormItem>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <FormItem label="购买方名称: ">
                <span>{{item.purchaser}}</span>
              </FormItem>
            </Col>
            <Col span="12">
              <FormItem label="领取人: ">
                <span>{{item.receiverName}}</span>
              </FormItem>
            </Col>
          </Row>
        </TabPane>
      </Tabs>
    </div>
    <h3 style="margin-bottom: 10px;margin-top: 15px;font-size:14px">基本信息</h3>
    <Row class="ivu-top">
      <Col span="12">
        <FormItem label="学员姓名: ">
          <span>{{studentName}}</span>
        </FormItem>
      </Col>
      <Col span="12">
        <FormItem label="学员编号: ">
          <span>{{studentCard}}</span>
        </FormItem>
      </Col>
    </Row>
    <Row class="ivu-top">
      <Col span="12">
        <FormItem label="班级名称: ">
          <span>{{className}}</span>
        </FormItem>
      </Col>
      <Col span="12">
        <FormItem label="教师: ">
          <span>{{teacherNames}}</span>
        </FormItem>
      </Col>
    </Row>
    <Row class="ivu-top">
      <Col span="12">
        <FormItem label="分期名称: ">
          <span>{{stagesTypeName}}</span>
        </FormItem>
      </Col>
      <Col span="12">
        <FormItem label="缴费金额: ">
          <span>{{actuallyPaidAmount}}</span>
        </FormItem>
      </Col>
    </Row>
  </Form>
</template>

<script>
import { getInvoiceDetail } from '../../api/finance'
export default {
  name: 'TalFinanceInvoiceDetails',
  props: {
    registId: {
      type: String,
      required: true
    },
    stagesNum: {
      type: String,
      required: true
    },
    studentName: {
      type: [String, Number],
      required: true
    },
    studentCard: {
      type: [String, Number],
      required: true
    },
    className: {
      type: String,
      required: true
    },
    cityCode: {
      type: [String, Number],
      required: true
    },
    stagesTypeName: {
      type: String,
      required: true
    },
    actuallyPaidAmount: {
      type: [String, Number],
      required: true
    },
    teacherNames: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      invoiceData: [{}, {}, {}, {}],
      loading: false
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      this.loading = true
      let req = {
        registId: this.registId,
        stagesNum: this.stagesNum,
        cityCode: this.cityCode
      }
      getInvoiceDetail(req).then((res) => {
        this.invoiceData = res
        console.log(res)
      }).catch((err) => {
        this.$Message.warning({ content: '获取发票失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.loading = false
      })
    }
  }
}
</script>

<style scoped lang="less">
  .ivu-form {
    /deep/ .ivu-form-item-label {
      font-weight: bold;
    }
    /deep/ .ivu-form-item {
      margin-bottom: 12px;
    }
  }
</style>
