<template>
  <div>
    <div>
      <span style="font-size: 14px">优惠券兑换码：</span><Input v-model="bindCode" placeholder="请输入优惠券兑换码" style="width: 400px"/>
    </div>
    <div style="margin-top: 10px;text-align: center">
      <Button type="primary" @click="bindHandler" style="margin-right: 10px">兑换</Button>
      <Button @click="$emit('close')">取消</Button>
    </div>
  </div>

</template>

<script>
import { bindCoupon } from '../../api/coupon'
export default {
  name: 'TalAddCoupon',
  props: {
    cityCode: String,
    studentId: String,
    uid: String
  },
  data () {
    return {
      bindCode: ''
    }
  },
  methods: {
    bindHandler () {
      let req = {
        couponNo: this.bindCode,
        studentId: this.studentId,
        cityCode: this.cityCode,
        uid: this.uid
      }
      bindCoupon(req).then(res => {
        this.$Message.success({ content: '绑定成功！', duration: 5 })
        this.$emit('success')
      }).catch(err => {
        this.$Message.warning({ content: err.message, duration: 5 })
      })
    }
  },
  watch: {

  }
}
</script>

