<template>
    <div>
        <Form
        ref="form"
        :model="formData"
        label-position="right"
        :label-width="90"
        style="text-align: left;"
        inline>
            <FormItem label="年份">
                <Select v-model="formData.year" style="width: 120px;" clearable>
                    <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
                </Select>
            </FormItem>
            <FormItem label="学期">
                <Select v-model="formData.termId" style="width: 120px;" clearable>
                    <Option v-for="(item,index) in termList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
                </Select>
            </FormItem>
            <FormItem label="学科">
                <Select
                    v-model="formData.subjectIds"
                    style="width: 120px;"
                    clearable
                >
                    <Option
                    v-for="(item,index) in subjectList"
                    :value="item.schoolId"
                    :key="index"
                    >{{ item.customName }}</Option>
                </Select>
            </FormItem>
            <Button type="primary" style="margin-right: 10px;" @click="search">查询</Button>
            <Button style="margin-right: 10px;" @click="resetFields">重置</Button>
            <Button style="margin-right: 10px;float: right" type="primary" v-if="isShowSend" @click="modalValue=true">赠送</Button>
        </Form>
        <Table
        border
        :loading="tableLoading"
        ref="selection"
        :columns="columns"
        :data="tableData"
        ></Table>
        <Modal
        :mask-closable="false"
        title="发放回放课链接-选班级"
        v-model="modalValue"
        width="960"
        >
            <TalSendPlaybackByclass v-if="modalValue" :studentUid="studentUid" :creatorEmpNo="creatorEmpNo" :cityCode="cityCode"/>
        </Modal>
        <TalClassDetail
        :class-id="classId"
        :show="classDetailShow"
        @close="classDetailShow = false"
        ></TalClassDetail>
        <TalTeacherDetail
          ref="teacherRef"
          v-if="teacherDetail.teacherEmpNo"
          :schoolCode="cityCode || ''"
          :teacherId="''"
          :teacherEmpNo="teacherDetail.teacherEmpNo"
          :type="teacherDetail.type"
        />
        <Page
        style="float:right;padding-right: 20px;"
        show-elevator
        :total="pageTotal"
        :current="pageNo"
        @on-change="pageChangeHandler"
        @on-page-size-change="pageSizeChangeHandler"
        :page-size="pageSize"
        show-total
        show-sizer
        transfer
      />
    </div>
</template>
<script>
import { getYearList, getTermList, getSubjectList } from '../../api/bd'
import { playBackList } from '../../api/clazz'
import { dateFormat } from '../../utils/utils'
import TalClassDetail from '../tal-class-details'
import TalTeacherDetail from '../tal-teacher-details'
import TalSendPlaybackByclass from '../tal-send-playback-byclass'
export default {
  name: 'TalSendPlaybackRecord',
  props: {
    cityCode: {
      type: String,
      default: '010'
    },
    studentUid: {
      type: String,
      default: '1020604265'
    },
    isShowSend: {
      type: Boolean,
      default: true
    },
    creatorEmpNo: {
      type: String,
      default: ''
    }
  },
  components: { TalClassDetail, TalTeacherDetail, TalSendPlaybackByclass },
  data () {
    return {
      columns: [
        {
          title: '班级名称',
          key: 'className',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.showClassDetail(params.row.classId)
                }
              }
            }, params.row.className)
          }
        },
        {
          title: '课次',
          key: 'courseNo',
          align: 'center'
        },
        {
          title: '授课类型',
          key: 'classTypeName',
          align: 'center'
        },
        {
          title: '学科',
          key: 'course',
          align: 'center'
        },
        {
          title: '主讲教师',
          key: 'lecturer',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.showTeacherDetail(params.row.lecturerEmpNo)
                }
              }
            }, params.row.lecturer)
          }
        },
        {
          title: '辅导教师',
          key: 'counselor',
          align: 'center'
        },
        {
          title: '视频赠送时间',
          key: 'createTime',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, dateFormat(params.row.createTime, 'yyyy-MM-dd HH:mm:ss'))
          }
        },
        {
          title: '视频赠送人',
          key: 'creatorName',
          align: 'center'
        },
        {
          title: '视频赠送平台',
          key: 'platformName',
          align: 'center'
        }

      ],
      tableData: [],
      tableLoading: false,
      formData: {
        year: '',
        termId: '',
        subjectIds: ''
      },
      modalValue: false,
      classDetailShow: false,
      classId: '',
      classInfo: {
        cityId: '010',
        classId: '87454e63ebf74091b936cbb2c15b2acf'
      },
      yearList: [],
      subjectList: [],
      termList: [],
      pageSize: 10,
      pageTotal: 0,
      pageNo: 1,
      teacherDetail: {}
    }
  },
  methods: {
    search () {
      this.pageNo = 1
      this.pageSize = 10
      this.getTable()
    },
    getTable () {
      let params = Object.assign({}, this.formData, {
        area: this.cityCode,
        studentUid: this.studentUid,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      })
      this.tableLoading = true
      playBackList(params).then(res => {
        this.tableData = res.records || []
        this.pageTotal = res.totalCount
        this.tableLoading = false
      }).catch((err) => {
        this.$Message.warning({ content: '查询失败:' + err, duration: 5 })
        this.tableLoading = false
      })
    },
    resetFields () {
      this.formData = {
        year: '',
        termId: '',
        subjectIds: ''
      }
    },
    getYear () {
      getYearList().then((data) => {
        this.yearList = data.sort((y1, y2) => Number(y2) - Number(y1))
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    },
    getTerm () {
      let params = {
        cityCode: this.cityCode
      }
      getTermList(params).then(res => {
        this.termList = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: '获取学期失败:' + err.message, duration: 5 })
      })
    },
    getSubject () {
      let baseSubject = {
        cityCode: this.cityCode
      }
      getSubjectList(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error({ content: '获取学科失败:' + err.message, duration: 5 })
      })
    },
    pageChangeHandler (page) {
      this.pageNo = page
      this.getTable()
    },
    pageSizeChangeHandler (pageSize) {
      this.pageSize = pageSize
      this.pageCurrent = 1
      this.getTable()
    },
    success () {
      this.modalValue = false
    },
    showClassDetail (id) {
      this.classId = id
      this.classDetailShow = true
    },
    showTeacherDetail (empNo) {
      let params = {
        type: '0',
        teacherEmpNo: empNo
      }
      this.teacherDetail = params
      this.$nextTick(() => {
        this.$refs.teacherRef.show()
      })
    }
  },
  created () {
    this.getYear()
    this.getTerm()
    this.getSubject()
    this.search()
  }
}
</script>
