<template>
  <Modal
    :value="modalValue"
    :title="`${teacherSysName}老师${teacherSysName?`（${teacherQueryInfo.empNo || ''}）`:''}`"
    width="960"
    @on-cancel="closeModal"
    footer-hide
  >
    <div class="teacher-detail-wrap-course" style="min-height: 300px">
      <Spin fix v-if="loading"></Spin>

      <Tabs>
        <TabPane label="教师简介">
          <Row>
            <Col>
              <baseInfo :info="teacherBaseDetail" />
            </Col>
          </Row>
        </TabPane>
      </Tabs>

      <div class="text-center modal-footer">
        <Button @click="closeModal">关闭</Button>
      </div>
    </div>
  </Modal>
</template>

<script>
import './teacherDetail.less'
import { getTeacherByEmpNo } from '../../api/courseBase'
import baseInfo from './baseInfo/baseInfo'

export default {
  name: 'TalCourseTeacherDetail',
  components: {
    baseInfo
  },
  props: {
    teacherQueryInfo: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      queryData: {},
      teacherBaseDetail: {},
      teacherSysName: '', // 教师系统名称
      modalValue: false,
      loading: false
    }
  },
  methods: {
    showTeacherDetail () {
      this.queryData = this.teacherQueryInfo
      let schoolCode = this.queryData.schoolCode || ''
      let empNo = this.queryData.empNo || ''
      let type = String(this.queryData.type) || ''
      if (!schoolCode || !empNo || !type) {
        this.$Message.error('参数错误，请检查入参')
        return
      }

      this.loading = true
      getTeacherByEmpNo({
        schoolCode,
        type,
        empNo
      }).then((res) => {
        if (res) {
          this.modalValue = true
          this.teacherBaseDetail = res
          console.log(this.teacherBaseDetail)
          this.teacherSysName = this.teacherBaseDetail.sysName || ''
        } else {
          this.$Message.error({
            content: '获取教师详情信息失败',
            onClose: () => {
              this.closeModal()
            }
          })
        }
      }).catch(() => {
        this.$Message.error({
          content: '调用教师详情接口失败',
          onClose: () => {
            this.closeModal()
          }
        })
      }).finally(() => {
        this.loading = false
      })
    },
    closeModal (val) {
      // console.log(val)
      this.$emit('close', val)
    }
  },
  watch: {
    teacherQueryInfo: {
      deep: true,
      handler () {
        if (!this.teacherQueryInfo.flag) {
          this.modalValue = false
        } else {
          this.showTeacherDetail()
        }
      }
    }
  }
  // computed: {
  //   modalValue: {
  //     get: function () {
  //       console.log(this.teacherQueryInfo)
  //       if (!this.teacherQueryInfo.flag) {
  //         return false
  //       }
  //
  //       return true
  //     },
  //     set: function (val) {
  //       this.closeModal(val)
  //     }
  //   }
  // }
}
</script>
