<style lang="less" scoped>
.stuRight {
  margin-left: 10px;
  color: black;
}
.ivu-top {
  margin-top: 20px;
}
</style>
<template>
  <div class="box">
    <Tabs :animated="false" type="card" v-model="tabPaneName" name="studentDetailTabs">
      <TabPane label="学员基本资料" name="studentMessage">
        <div class="stuMes" style="padding: 0 20px 20px 20px;font-size: 14px;">
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">姓名:</span>
              <span class="stuRight">{{studentDetail.realName}}</span>
            </Col>
            <Col span="12">
              <span class="stuLeft">出生日期:</span>
              <span class="stuRight">
                <decode-text type="birthday" encode-text="**********" :uid="studentDetail.uid"></decode-text>
              </span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">性别:</span>
              <span class="stuRight">{{getSexName(studentDetail.sex)}}</span>
            </Col>
            <Col span="12">
              <span>经验值:</span>
              <span class="stuRight">{{studentDetail.points}}</span>
              <span style="margin-left: 10px">等级</span>
              <span class="stuRight">{{studentDetail.level}}</span>
              <span style="margin-left: 10px">经验折扣率</span>
              <span class="stuRight">{{studentDetail.agio}}</span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">年级:</span>
              <span class="stuRight">{{studentDetail.gradeName}}</span>
            </Col>
            <Col span="12">
              <span class="stuLeft">首次报班日期:</span>
              <span class="stuRight">
                {{studentDetail.firstPayDate}}
                <span
                  @click="seeClass"
                  style="color: #008CF8;cursor: pointer"
                >查看已报班级</span>
              </span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">所在城市:</span>
              <span class="stuRight">{{studentDetail.cityName}}</span>
            </Col>
            <Col span="12">
              <span class="stuLeft">绑定手机号:</span>
              <span class="stuRight">
                <decode-text
                  :encode-text="studentDetail.phone"
                  type="phone"
                  :uid="studentDetail.uid"
                ></decode-text>
              </span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">第1联系人手机号:</span>
              <span class="stuRight">
                <decode-text
                  :encode-text="studentDetail.attentionReqList && studentDetail.attentionReqList.length ? studentDetail.attentionReqList[0].tel : ''"
                  type="phone"
                  :uid="studentDetail.uid"
                ></decode-text>
              </span>
            </Col>
            <Col span="12">
              <span class="stuLeft">第1联系人:</span>
              <span
                class="stuRight"
              >{{studentDetail.attentionReqList&&studentDetail.attentionReqList[0] ? studentDetail.attentionReqList[0].typeName : ''}}</span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">电子凭证接收邮箱:</span>
              <span class="stuRight">
                <decode-text
                  type="email"
                  :encode-text="studentDetail.accessEmail"
                  :uid="studentDetail.uid"
                ></decode-text>
              </span>
            </Col>
            <Col span="12">
              <span class="stuLeft">注册时间:</span>
              <span class="stuRight">{{studentDetail.createDate}}</span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">最后修改人:</span>
              <span class="stuRight">{{studentDetail.modifierName}}</span>
            </Col>
            <Col span="12">
              <span class="stuLeft">注册渠道:</span>
              <span class="stuRight">{{studentDetail.registtype}}</span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <Col span="12">
              <span class="stuLeft">最后修改时间:</span>
              <span class="stuRight">{{studentDetail.modifyDate}}</span>
            </Col>
          </Row>
          <Row class="ivu-top">
            <h3>邮寄地址</h3>
            <div
              v-for="(i,index) in studentDetail.recipientReqList"
              :key="index"
              style="clear: both"
            >
              <div style="float: left">
                <p style="margin-top: 20px">
                  <span>收件人:</span>
                  <span class="stuRight">{{i.name}}</span>
                  <span style="margin-left: 20px">联系电话:</span>
                  <span class="stuRight">{{i.tel}}</span>
                </p>
                <p style="margin-top: 20px">
                  <span>收货地址:</span>
                  <span
                    class="stuRight"
                  >{{i.provinceName}} {{i.cityName}} {{i.districtName}} {{i.address}}</span>
                </p>
              </div>
              <div style="float: left;margin-left: 30px;margin-top: 35px">
                <Button type="primary" ghost v-if="i.isDefault===1">默认地址</Button>
              </div>
            </div>
          </Row>
          <div style="margin-top: 10px;text-align: center">
            <a v-if="showMore && !moreLoading" @click="loadMore">查看更多</a>
            <a v-else @click="showMore = true">收起</a>
            <span v-if="moreLoading">
              加载中
              <Icon type="ios-loading" />
            </span>
            <Row
              v-show="!showMore"
              v-for="item in schoolList"
              style="text-align: left"
              :key="item.id"
            >
              <span class="stuLeft">{{schoolType[String(item.gradeType)]}}:</span>
              <span
                class="stuRight"
              >{{item.provinceName}} {{item.cityName}} {{item.districtName}} {{item.schoolName}} {{item.gradeName}} {{ item.className}}</span>
            </Row>
          </div>
        </div>
      </TabPane>
      <TabPane label="电子钱包" name="wallet" v-if="studentDetail.cityCode !== '1111'">
        <Table stripe :columns="columns1" :data="data1"></Table>
      </TabPane>
    </Tabs>

    <!--查看已报班级-->
    <SubmitClass v-model="reportShow" @close="closeReport" :reportData1="reportData"></SubmitClass>
  </div>
</template>

<script>
import SubmitClass from '../tal-stu-submit-class'
import {
  getReadingClass,
  getStudentDetail,
  getUserSchoolList
} from '../../api/student'
import {
  queryRegisterClassList
} from '../../api/register'
import DecodeText from '../tal-decode-text'
import { queryStudentCard } from '../../api/finance'
export default {
  name: 'TalStuDetails',
  props: {
    cityCode: {
      type: String,
      require: true
    },
    uid: {
      type: Number,
      require: true
    }
  },
  components: {
    SubmitClass,
    DecodeText
  },
  data () {
    return {
      reportShow: false, // 查看已报班级
      columns1: [
        {
          title: '钱包账号',
          key: 'cardNo',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'name',
          align: 'center'
        },
        {
          title: '账户状态',
          key: 'cardStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.cardStatus === 0 ? '未冻结' : '已冻结')
          }
        },
        {
          title: '账户余额',
          key: 'cardBalance',
          align: 'center'
        }
      ],
      data1: [],
      studentDetail: {},
      tabPaneName: 'studentMessage', // 选项卡
      reportData: [],
      showMore: true,
      moreLoading: false,
      schoolList: [],
      schoolType: {
        '5': '在读幼儿园',
        '1': '在读小学',
        '2': '在读初中',
        '3': '在读高中'
      }
    }
  },
  mounted () {
    getStudentDetail({
      cityCode: this.cityCode,
      uid: this.uid
    }).then(data => {
      this.studentDetail = data
      this.getWalletHandler()
    }).catch(err => {
      this.$Message.error(err.message)
    })
  },
  methods: {
    seeClass () {
      this.getRoportClass()
    },
    closeReport () {
      this.reportShow = false
      this.reportData = []
    },
    getRoportClass () {
      let data = {
        cityCode: this.studentDetail.cityCode,
        mode: 4,
        studentId: this.studentDetail.studentId
      }
      queryRegisterClassList(data).then(res => {
        if (res.records.length > 0) {
          res.records.forEach((e, i) => {
            if (e.stageList && e.stageList.length) {
              e.rowspan = e.stageList.length
            }
            if (e.startDate) {
              e.startDate = e.startDate.substring(0, 10)
            }
            if (e.endDate) {
              e.endDate = e.endDate.substring(0, 10)
            }
          })
          this.reportData = res.records
          this.reportShow = true
        } else {
          this.$Message.warning('暂无数据')
        }
      }).catch(err => {
        this.$Message.error('获取已报班级失败' + err.message)
      })
    },
    getWalletHandler () {
      if (this.studentDetail.cityCode === '1111') {
        return
      }
      let data = {
        card: this.studentDetail.card,
        cityCode: this.studentDetail.cityCode
      }
      queryStudentCard(data).then(res => {
        // 返回结果可能为null
        if (res) {
          res.name = this.studentDetail.realName
          this.data1.push(res)
        }
      }).catch(err => {
        this.$Message.error({ content: '查询电子钱包失败' + err.message, duration: 5 })
      })
    },
    openReset () {
      this.$emit('resetPassword')
    },
    showAdd () {
      this.$emit('showAdd')
    },
    showUpdate () {
      this.$emit('showUpdate')
    },
    getSexName (sex) {
      let name = ''
      if (sex === 2 || sex === 0) {
        name = '女'
      } else if (sex === 1) {
        name = '男'
      }
      return name
    },
    loadMore () {
      if (this.schoolList && this.schoolList.length) {
        this.showMore = false
      } else {
        this.moreLoading = true
        getUserSchoolList({
          cityCode: this.cityCode,
          uid: this.uid
        }).then(data => {
          if (data && data.length) {
            const school = data.find(d => d.gradeType === 1) // 只有小学有年级
            if (school) {
              this.getReadingClass(school)
            }
            this.schoolList = data
            this.showMore = false
          } else {
            this.$Message.warning({
              content: '无更多信息查看',
              duration: 5
            })
            this.showMore = true
          }
        }).catch(err => {
          this.$Message.error({
            content: '获取学员就读学校失败：' + err.message,
            duration: 5
          })
        }).finally(() => {
          this.moreLoading = false
        })
      }
    },
    getReadingClass (school) {
      getReadingClass({
        cityCode: this.cityCode,
        uid: this.uid
      }).then(data => {
        if (data) {
          this.$set(school, 'className', data.className)
          this.$set(school, 'gradeName', data.schoolGradeName)
        }
      }).catch(err => {
        this.$Message.error({
          content: '查询就读班级失败：' + err.message,
          duration: 5
        })
      })
    }
  }
}
</script>
