<template>
  <div>
    <Modal
      :mask-closable="false"
      title="报名资格查询"
      v-model="modalValue"
      @on-visible-change="onVisibleChange"
      :footer-hide="true"
      width="1100px">
      <div class="certificate_top">
        <Form ref="formMessage" :model="formMessage" label-position="right" :label-width="50" inline>
          <FormItem label="年份" required>
            <Select v-model="formMessage.year" @on-change="changeYear">
              <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem label="学期" required>
            <Select v-model="formMessage.term" @on-change="changeTerm">
              <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="年部" required>
            <Select v-model="formMessage.gradeType" @on-change="changeGradeType">
              <Option v-for="(item,index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="年级" required>
            <Select v-model="formMessage.grade" clearable @on-change="changeGrade">
              <Option v-for="(item,index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="学科" required>
            <Select v-model="formMessage.subject" clearable @on-change="changeSubject">
              <Option v-for="(item,index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem label="班次">
            <Select
              v-model="formMessage.classNum"
              filterable
              remote
              clearable
              :loading="loading1">
              <Option v-for="(option,index) in classNumList" :value="option.id" :key="index">{{option.name}}</Option>
            </Select>
          </FormItem>
          <Button type="primary" @click="search" style="margin-left: 15px;float: right">查询</Button>
        </Form>
      </div>
      <div class="certificate_article" style="clear: both;padding: 15px">
        <Table stripe :columns="columns1" :data="data1" :loading="tableLoading"></Table>
      </div>
    </Modal>
  </div>
</template>

<script>

import { getLinkage, setFrom } from '../../api/bd'
import { getQualification } from '../../api/student'

export default {
  name: 'certificate',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    yearList: {
      type: Array,
      default: function () {
        return []
      }
    },
    cityCode: String,
    stuId: String,
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      tableLoading: false,
      currentPageNum: 1,
      currentPageSize: 10,
      pageTotal: 0,
      formMessage: {
        year: '',
        term: '',
        gradeType: '',
        grade: '',
        subject: '',
        classNum: ''
      },
      termList: [],
      gradeTypeList: [],
      gradeList: [],
      subjectList: [],
      classNumList: [],
      loading1: false,
      columns1: Object.freeze([
        {
          title: '年份',
          key: 'year',
          align: 'center'
        },
        {
          title: '学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '班次名称',
          key: 'classlevelName',
          align: 'center'
        },
        {
          title: '是否满足跨报条件',
          key: 'limitSign',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.limitSign === false ? '否' : '是')
          }
        },
        {
          title: '是否达到入学测试',
          key: 'testSign',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.testSign === false ? '否' : '是')
          }
        }
      ]),
      data1: []
    }
  },
  methods: {
    onVisibleChange (status) {
      if (status) {
        setFrom({
          apiName: 'exchangeChangeClassPage'
        })
      }
    },
    search () {
      this.currentPageNum = 1
      this.getTableData()
    },
    getTableData () {
      if (this.formMessage.year === '' || this.formMessage.year === undefined) {
        this.$Message.warning('年份不能为空')
        return
      }
      if (this.formMessage.term === '' || this.formMessage.term === undefined) {
        this.$Message.warning('学期不能为空')
        return
      }
      if (this.formMessage.gradeType === '' || this.formMessage.term === undefined) {
        this.$Message.warning('年部不能为空')
        return
      }
      if (this.formMessage.grade === '' || this.formMessage.grade === undefined) {
        this.$Message.warning('年级不能为空')
        return
      }
      if (this.formMessage.subject === '' || this.formMessage.subject === undefined) {
        this.$Message.warning('学科不能为空')
      } else {
        this.tableLoading = true
        let data = {
          cityCode: this.cityCode,
          gradeId: this.formMessage.grade ? this.formMessage.grade : 'undefined',
          gradeTypeId: this.formMessage.gradeType ? this.formMessage.gradeType : 'undefined',
          levelId: this.formMessage.classNum ? this.formMessage.classNum : '',
          studentId: this.stuId,
          subjectIds: this.formMessage.subject ? this.formMessage.subject : 'undefined',
          termId: this.formMessage.term ? this.formMessage.term : 'undefined',
          year: this.formMessage.year ? this.formMessage.year : 'undefined',
          pageNo: this.currentPageNum,
          pageSize: this.currentPageSize
        }
        getQualification(data).then(res => {
          // this.pageTotal = res.totalCount
          this.data1 = Object.freeze(res)
        }).catch(err => {
          this.$Message.error({ content: '查询失败:' + err.message, duration: 5 })
        }).finally(() => {
          this.tableLoading = false
        })
      }
    },
    changeYear (val) {
      this.formMessage.term = ''
      this.formMessage.gradeType = ''
      this.formMessage.grade = ''
      this.formMessage.subject = ''
      this.formMessage.classNum = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.classNumList = []
      if (!val) {
        return
      }
      let baseTerm = {
        cityCode: this.cityCode,
        years: this.formMessage.year,
        queryType: 't'
      }
      getLinkage(baseTerm).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error({ content: '获取学期失败:' + err.message, duration: 5 })
      })
    },
    changeTerm (val) {
      this.formMessage.grade = ''
      this.gradeList = []
      this.formMessage.gradeType = ''
      this.gradeTypeList = []
      this.formMessage.subject = ''
      this.subjectList = []
      this.formMessage.classNum = ''
      this.subjectList = []
      if (!val) {
        return
      }
      let baseGradeType = {
        cityCode: this.cityCode,
        years: this.formMessage.year,
        termId: this.formMessage.term,
        queryType: 'gt'
      }
      getLinkage(baseGradeType).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error({ content: '获取年部失败:' + err.message, duration: 5 })
      })
    },
    changeGradeType (val) {
      this.formMessage.grade = ''
      this.gradeList = []
      this.formMessage.subject = ''
      this.subjectList = []
      this.formMessage.classNum = ''
      this.classNumList = []
      if (!val) {
        return
      }
      let baseClass = {
        cityCode: this.cityCode,
        years: this.formMessage.year,
        termIds: this.formMessage.term,
        gtIds: this.formMessage.gradeType,
        queryType: 'g'
      }
      getLinkage(baseClass).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error({ content: '获取年级失败:' + err.message, duration: 5 })
      })
    },
    changeGrade (val) {
      this.formMessage.subject = ''
      this.subjectList = []
      this.formMessage.classNum = ''
      this.classNumList = []
      if (!val) {
        return
      }
      let baseSubject = {
        cityCode: this.cityCode,
        years: this.formMessage.year,
        termIds: this.formMessage.term,
        gtIds: this.formMessage.gradeType,
        gradeIds: this.formMessage.grade,
        queryType: 's'
      }
      getLinkage(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error({ content: '获取学科失败:' + err.message, duration: 5 })
      })
    },
    changeSubject (val) {
      this.formMessage.classNum = ''
      this.classNumList = []
      if (!val) {
        return
      }
      let baseClassCount = {
        cityCode: this.cityCode,
        years: this.formMessage.year,
        termIds: this.formMessage.term,
        gtIds: this.formMessage.gradeType,
        gradeIds: this.formMessage.grade,
        subjectIds: this.formMessage.subject,
        queryType: 'c'
      }
      getLinkage(baseClassCount).then(res => {
        this.classNumList = res
      }).catch(err => {
        this.$Message.error({ content: '获取班次失败:' + err.message, duration: 5 })
      })
    },
    // 换页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageSize = size
      this.getTableData()
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.$emit('close', val)
        this.data1 = []
        this.formMessage = {
          year: '',
          term: '',
          gradeType: '',
          grade: '',
          subject: '',
          classNum: ''
        }
      }
    }
  }
}
</script>

<style scoped>

