<template>
  <div v-if="dataList && dataList.length">
    <div style="text-align: center">
      <img width="246" height="43" src="../../assets/images/recommend.png"/>
    </div>
    <Tabs  type="card" name="recommendTabs" :animated="false" v-model="activeTab">
      <TabPane v-for="(item,index) in dataList" :label="'同'+ item.typeName + '班级 ' + item.count" :key="index" tab="recommendTabs" :name="'tabName' + index">
        <div class="ivu-table ivu-table-default ivu-table-stripe ivu-table-wrapper">
          <div class="ivu-table-body">
            <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
              <thead>
              <tr class="ivu-table-row">
                <th class="ivu-table-column-center" width="200px"><div class="ivu-table-cell">操作</div></th>
                <th class="ivu-table-column-center" width="200px"><div class="ivu-table-cell">班级名称</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">授课类型</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">学科</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">主讲教师</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">辅导教师</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">开课日期</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">结课日期</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">上课时间</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">服务中心</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">教室</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">上课进度</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">报名进度</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">剩余名额</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">总金额</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">变动记录</div></th>
                <th class="ivu-table-column-center"><div class="ivu-table-cell">是否进行入学测试</div></th>
              </tr>
              </thead>
              <tbody class="ivu-table-tbody">
                <template v-if="item.classList && item.classList.length > 0">
                  <tr class="ivu-table-row" v-for="clazz in item.classList" :key="clazz.id + index">
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <a @click="$emit('apply', clazz)">报名</a><br>
                        <a @click="$emit('reserveClass', clazz)">预留转班名额</a><br>
                        <a @click="$emit('enterClass', clazz)">转入此班</a>
                      </div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <a @click="$emit('showClassDetail', clazz.id)">{{clazz.name}}</a>
                        <Tag color="green" v-if="clazz.businessType==1">全国班</Tag>
                        <Tag color="orange">{{clazz.recommendRemark}}</Tag>
                      </div>
                    </td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ classTypeMap[clazz.classType] }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.subjectNames }}</div></td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <template v-for="(tea, index) in clazz.teacherNames.split(',')">
                          <a @click="$emit('showTeacherDetail',clazz.cityCode, clazz.teacherIds.split(',')[index])" :key="tea">{{ tea }}</a>
                          <template v-if="index != clazz.teacherNames.split(',').length - 1"> ,<br :key="index" /></template>
                        </template>
                      </div>
                    </td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.tutorRealName }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.startDate }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.endDate }}</div></td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <div v-if="clazz.classtimeNames && clazz.classtimeNames.length > 30">
                          <Tooltip max-width="300" :content="clazz.classtimeNames" placement="right" transfer>
                            {{clazz.classtimeNames.substring(0, 30) + '...'}}
                          </Tooltip>
                        </div>
                        <div v-else>{{clazz.classtimeNames}}</div>
                      </div>
                    </td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.serviceCenterName }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.classroomName }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.passedCount + '/' + clazz.classCount }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ (clazz.maxPersons - clazz.remainNum) + '/' + clazz.maxPersons }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell" style="color: red;font-weight: bold">{{ clazz.remainNum }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.price }}</div></td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <div v-if="clazz.classChangeName && clazz.classChangeName.length > 30">
                          <Tooltip max-width="300" :content="clazz.classChangeName" placement="right" transfer>
                            {{clazz.classChangeName.substring(0, 30) + '...'}}
                          </Tooltip>
                        </div>
                        <div v-else>{{clazz.classChangeName}}</div>
                      </div>
                    </td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.isSchooltest === 0 ? '否' : '是' }}</div></td>
                  </tr>
                </template>
                <tr v-if="!item.classList || !item.classList.length">
                <td class="ivu-table-column-center" colspan="6"><div class="ivu-table-cell">暂无推荐记录</div></td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import { getRecommendClassInfo } from '../../api/clazz'
export default {
  name: 'TalRecommendClass',
  data () {
    return {
      dataList: [],
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      activeTab: 'tabName0'
    }
  },
  props: {
    params: Object
  },
  methods: {
    initView () {
      this.activeTab = 'tabName0'
      this.dataList = []
      this.$emit('setHasRecommend', false)
    },
    search () {
      this.activeTab = 'tabName0'
      this.$nextTick(() => {
        this.params.business = 'recommend'
        getRecommendClassInfo(this.params).then(data => {
          if (data && data.length) {
            this.$emit('setHasRecommend', true)
            this.dataList = data
          } else {
            this.$emit('setHasRecommend', false)
            this.dataList = []
          }
        }).catch(err => {
          this.$emit('setHasRecommend', false)
          this.dataList = []
          this.$Message.error({
            content: '查询推荐班级错误：' + err.message,
            duration: 5
          })
        })
      })
    }
  }
}
</script>

