<template>
  <div class="order">
    <div class="no-data" v-if="orderList.length === 0">暂无工单记录</div>
    <Timeline v-else style="margin-top: 10px">
      <TimelineItem v-for="(order, index) in orderList" :key="index">
        <p class="time">{{order.title}}<span>[{{String(order.advisorType) === '5' ? '家庭顾问' : String(order.advisorType) === '8' ? '在线顾问': '未知顾问类型'}}]</span></p>
        <div class="tags">
          <span>学员标签：</span>
          <Tag class="tag" color="#83BEFF" v-for="(tag, index) in order.studentTagList" :key="index">{{tag.personalityTag}}</Tag>
        </div>
        <div class="content">
          <Collapse simple>
            <Panel name="1">
              <a>展开更多</a>
              <div slot="content">
                <p><span class="bold-text">沟通结果：</span>{{order.contactResult || '无'}}</p>
                <div style="margin-top: 10px" v-if="order.userInfo">
                  <span class="bold-text">{{order.userInfo.readStatusName || '无'}} | </span>
                  <span class="bold-text">{{order.userInfo.stuGradeName || '无'}} | </span>
                  <span class="bold-text">{{order.userInfo.stuSex ? '男' : '女'}} | </span>
                  <span class="bold-text">注册城市：{{order.userInfo.cityName || '无'}} | </span>
                  <span class="bold-text">归属城市：{{order.userInfo.belongCityName || '无'}} | </span>
                  <span class="bold-text">归属服务中心：{{order.userInfo.serviceCenterName || '无'}} | </span>
                  <span class="bold-text">绑定人：{{order.userInfo.consultant || '无'}}</span>
                  <span v-if="order.userInfo.title" class="bold-text"> | 标题：{{order.userInfo.title || '无'}}</span>
                  <template v-if="order.userInfo.diagnosisUrl">
                    <span class="bold-text"> | 诊断报告：<a v-if='order.userInfo.diagnosisUrl' :href='order.userInfo.diagnosisUrl' target='_blank'>{{order.userInfo.cityName }}{{ order.userInfo.stuGradeName }}诊断</a> | </span>
                    <span>诊断年级：{{order.userInfo.gradeName || '无'}} | </span>
                    <span>科目：{{order.userInfo.subjectName  || '无'}} | </span>
                    <span>分数：{{order.userInfo.score  || '无'}} | </span>
                    <span>班次级别：{{order.userInfo.levelDegreeName || '无'}} | </span>
                    <span>班次名称：{{order.userInfo.levelName || '无'}} | </span>
                    <span>成绩来源：{{order.userInfo.sourceTypeName || '无'}} | </span>
                    <span>成绩是否有效：有效 | </span>
                    <span>作答时间：{{order.userInfo.answerTime|dateFilter}}</span>
                  </template>
                </div>
              </div>
            </Panel>
          </Collapse>
        </div>
      </TimelineItem>
      <TimelineItem>
        <a v-if="showMore" @click="more">查看更多...</a>
        <span v-else>没有更多了</span>
      </TimelineItem>
    </Timeline>
  </div>
</template>
<script>
  import { dateFormat } from '../../utils/utils'
  import { getWorkOrderHistory } from '../../api/adviser'
  export default {
    name: 'TalOnlineAdviserRecord',
    data () {
      return {
        orderList: [],
        showMore: true,
        pageNo: 1
      }
    },
    props: {
      cityCode: {
        type: String,
        default: ''
      },
      uid: {
        type: [String, Number],
        default: ''
      }
    },
    filters: {
      dateFilter (val) {
        return dateFormat(val)
      }
    },
    methods: {
      refresh () {
        this.orderList = []
        this.showMore = true
        this.pageNo = 1
        this.queryOrder()
      },
      more () {
        this.pageNo += 1
        this.queryOrder()
      },
      queryOrder () {
        getWorkOrderHistory({
          cityCode: this.cityCode,
          pyUid: this.uid,
          pageNo: this.pageNo
        }).then(data => {
          if (data) {
            this.orderList = this.orderList.concat(data.map(d => {
              if (d.studentJson) {
                d.userInfo = JSON.parse(d.studentJson)
              }
              return d
            }))
            if (this.pageNo > 1 && data.length < 5) {
              this.showMore = false
            }
          } else {
            this.orderList = []
          }
        })
      }
    },
    watch: {
      uid () {
        if (this.uid && this.cityCode) {
          this.refresh()
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .order {
    overflow-y: auto;
  }
  .no-data {
    margin-top: 10px;
  }
  .time{
    font-size: 14px;
    font-weight: bold;
    span {
      margin-left: 5px;
      color: #2d8cf0;
      font-weight: normal;
    }
  }
  .tags {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    margin: 10px 0;
    .tag {
      border-radius: 30px;
    }
  }
  .bold-text {
    font-weight: bold;
  }

</style>
