export function runEnv () {
  if (window) {
    const location = window.location.hostname
    if (location.indexOf('localhost') > -1 || location.indexOf('dev') > -1 || process.env.NODE_ENV === 'development') {
      return 'dev'
    } else if (location.indexOf('one-ext') > -1 || location.indexOf('external') > -1 || location.indexOf('-ext') > -1) {
      return 'external'
    } else if (location.indexOf('.net') > -1 || location.indexOf('beta') > -1) {
      return 'beta'
    } else if (location.indexOf('release') > -1) {
      return 'release'
    } else {
      return 'prod'
    }
  }
}

export function getQueryString (name) {
  const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  const regRewrite = new RegExp('(^|/)' + name + '/([^/]*)(/|$)', 'i')
  const r = window.location.search.substr(1).match(reg)
  const q = window.location.pathname.substr(1).match(regRewrite)
  if (r != null) {
    return unescape(r[2])
  } else if (q != null) {
    return unescape(q[2])
  } else {
    return null
  }
}

// 深拷貝
export const depClone = (obj1, obj2) => {
  obj1 = obj1 || {}
  for (var k in obj2) {
    if (obj2[k] && typeof obj2[k] === 'object') {
      obj1[k] = obj2[k].constructor === Array ? [] : {}
      depClone(obj1[k], obj2[k])
    } else {
      obj1[k] = obj2[k]
    }
  }
}

// 时间格式化
export const dateFormat = function (time, format) {
  if (!time) return ''
  time = typeof time === 'string' ? parseInt(time) : time
  time = time.toString().length === 10 ? time * 1000 : time
  let t = new Date(time)
  let tf = function (i) {
    return (i < 10 ? '0' : '') + i
  }
  /* 添加冗余判断 */
  format = format || 'yyyy-MM-dd HH:mm:ss'

  return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function (a) {
    switch (a) {
      case 'yyyy':
        return tf(t.getFullYear())
      case 'MM':
        return tf(t.getMonth() + 1)
      case 'mm':
        return tf(t.getMinutes())
      case 'dd':
        return tf(t.getDate())
      case 'HH':
        return tf(t.getHours())
      case 'ss':
        return tf(t.getSeconds())
    }
  })
}

export const countModalHeight = () => {
  let winHeight = document.documentElement.clientHeight
  /* 220 留白 */
  const list = document.querySelectorAll('.ivu-modal-body')
  if (list.length) {
    for (let i = 0; i < list.length; i++) {
      const item = list[i]
      item.style.maxHeight = (winHeight - 220 < 300 ? 300 : winHeight - 220) + 'px'
      item.style.overflow = 'auto'
      // item.scrollTop = 0;
    }
  }
}
/**
 * @param {Number} times 回调函数需要执行的次数
 * @param {Function} callback 回调函数
 */
export const doCustomTimes = (times, callback) => {
  let i = -1
  while (++i < times) {
    callback(i)
  }
}
