/*
* 格式化数字为数学计数法
* */
export const formatSplitNumber = (val) => {
  if (!val && val !== 0) return ''
  if (isNaN(Number(val))) return ''
  val = Number(val) || 0
  val = String(val)
  // 1000000000  ->  1,000,000,000
  val = val.toString().replace(/(^|\s)\d+/g, (m) => m.replace(/(?=(?!\b)(\d{3})+$)/g, ','))
  return val
}

/*
* 反格式化数学计数法
* */
export const unFormatSplitNumber = (val) => {
  if (val === '') return ''
  val = String(val)
  // 1,000,000,000 -> 1000000000
  val = val.replace(/\D/g, '')
  return val
}

/**
 * @description 绑定事件 on(element, event, handler)
 */
export const on = (function () {
  if (document.addEventListener) {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent('on' + event, handler)
      }
    }
  }
})()

/**
 * @description 解绑事件 off(element, event, handler)
 */
export const off = (function () {
  if (document.removeEventListener) {
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent('on' + event, handler)
      }
    }
  }
})()

/**
 * desc: base64对象转blob文件对象
 * @param urlData  ：数据的base64对象
 * @param type  ：类型 png,pdf,doc,mp3等;
 * @returns {Blob}：Blob文件对象
*/
function base64ToBlob (urlData, type) {
  let arr = urlData.split(',')
  let array = arr[0].match(/:(.*?);/)
  let mime = (array && array.length > 1 ? array[1] : type) || type
  // 去掉url的头，并转化为byte
  let bytes = window.atob(arr[1])
  // 处理异常,将ascii码小于0的转换为大于0
  let ab = new ArrayBuffer(bytes.length)
  // 生成视图（直接针对内存）：8位无符号整数，长度1个字节
  let ia = new Uint8Array(ab)
  for (let i = 0; i < bytes.length; i++) {
    ia[i] = bytes.charCodeAt(i)
  }
  return new Blob([ab], {
    type: mime
  })
}

/**
 * desc: 下载导出文件
 * @param blob  ：返回数据的blob对象或链接
 * @param fileName  ：下载后文件名标记
 * @param fileType  ：文件类 word(docx) excel(xlsx) ppt等
*/
function downloadExportFile (blob, fileName, fileType) {
  let downloadElement = document.createElement('a')
  let href = blob
  if (typeof blob === 'string') {
    downloadElement.target = '_blank'
  } else {
    href = window.URL.createObjectURL(blob) // 创建下载的链接
  }
  downloadElement.href = href
  downloadElement.download = fileName + '.' + fileType // 下载后文件名
  document.body.appendChild(downloadElement)
  downloadElement.click() // 触发点击下载
  document.body.removeChild(downloadElement) // 下载完成移除元素
  if (typeof blob !== 'string') {
    window.URL.revokeObjectURL(href) // 释放掉blob对象
  }
}

/**
 * desc: base64转文件并下载
 * @param base64 {String} : base64数据
 * @param fileType {String} : 要导出的文件类型png,pdf,doc,mp3等
 * @param fileName {String} : 文件名
*/
export function downloadFile (base64, fileName, fileType) {
  let typeHeader = 'data:application/' + fileType + ';base64,' // 定义base64 头部文件类型
  let converedBase64 = typeHeader + base64 // 拼接最终的base64
  let blob = base64ToBlob(converedBase64, fileType) // 转成blob对象
  downloadExportFile(blob, fileName, fileType) // 下载文件
}

export const generateZipDownload = (data, className) => {
  var zip = new JSZip()
  // 创建zip文件夹
  var zipFolder = zip.folder('images')

  // let urls = [
  //   'https://funsion-video.oss-cn-beijing.aliyuncs.com/dev/course/20210825/pdf/326c6476998348a5b686eb429943be99-79a2970371884640a17b531fd1786820.pdf',
  //   'https://funsion-video.oss-cn-beijing.aliyuncs.com/dev/course/20210825/pdf/326c6476998348a5b686eb429943be99-79a2970371884640a17b531fd1786820.pdf'
  // ]
  let urls = []
  data.forEach(item => {
    urls.push(...item.contractPdfUrlList)
  })
  let flag = 0 //  判断加载了几张图片的标识
  for (let i = 0; i < urls.length; i++) {
    const url = urls[i]
    urlToPromise(url).then(function (binary) {
      const fileName = data[i].stuName || '学生姓名'
      zipFolder.file(fileName, binary, { binary: true })
      if (flag === urls.length - 1) {
        zip.generateAsync({ type: 'blob' }).then((blob) => {
          saveAs(blob, `${className}.zip`)
        })
      }
      flag++
    }, function (err) {
      console.log(err)// 打印异常信息
    })
  }

  function urlToPromise (url) {
    return new Promise(function (resolve, reject) {
      JSZipUtils.getBinaryContent(url, function (err, data) {
        if (err) {
          reject(err)
        } else {
          resolve(data)
        }
      })
    })
  }
}

/*
 * 根据是否显示自建课 返回授课类型
 * */

export const filterCourseTypeBySelfCourse = (list = [], isShowSelfCourse = '') => {
  const courseTypeList = []
  isShowSelfCourse = String(isShowSelfCourse)
  list.forEach(z => {
    const hasTrue = isShowSelfCourse === '1'
    const hasFalse = isShowSelfCourse === '0'
    // console.log(hasTrue, z, hasFalse)
    if (hasTrue) {
      // hasTrue 展示所有
      courseTypeList.push(z.id)
    } else if (hasFalse && z.id - 518 !== 0) {
      // hasFalse 只过滤自建课
      courseTypeList.push(z.id)
    } else if (!hasTrue && !hasFalse) {
      // 所有
      courseTypeList.push(z.id)
    }
  })
  console.log(courseTypeList)
  return courseTypeList
}
