<template>
  <div class="diff-refund">
    <stu-search :attendanceSearch="false" onlyCode @stuInfo="setStuInfo">
    </stu-search>
    <div class="tb-wrapper">
      <div class="notice" style="color:red">特别提示：如用户已开发票，退费时会自动扣除税金部分</div>
      <Table :columns="cols" :data="dataList" no-data-text="当前学员无差价退费记录" :loading="tableLoading"></Table>
      <div style="margin: 10px;" :class="{ 'page-fix': dataList.length < 3 }">
        <div style="text-align: right;">
          <Page
            :total="totalCount"
            :page-size="pageSize"
            :current="pageNo"
            show-total
            @on-change="changePage"></Page>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import StuSearch from '../tal-stu-search'
import expandRow from './table-expand'
import { queryRefundDiff } from '../../api/finance'
import CountTo from 'vue-count-to'
export default {
  name: 'TalDiffRefund',
  components: {
    StuSearch,
    CountTo
  },
  data () {
    return {
      cols: [
        {
          type: 'expand',
          width: 50,
          render: (h, params) => {
            return h(expandRow, {
              props: {
                row: params.row,
                cityCode: this.studentDetail.cityCode
              }
            })
          }
        },
        { title: '报名ID', align: 'center', key: 'registryId' },
        { title: '城市', align: 'center', key: 'cityName' },
        { title: '年份', align: 'center', key: 'year' },
        { title: '学期', align: 'center', key: 'termName' },
        { title: '年级', align: 'center', key: 'gradeName' },
        { title: '学科', align: 'center', key: 'subjectNames' },
        { title: '班次', align: 'center', key: 'levelName' },
        {
          title: '应退退款',
          align: 'center',
          key: 'shouldRefundPrice',
          render: (h, params) => {
            const count = params.row.exceedReceiptDeductionPrice + params.row.actualShouldRefundPrice
            return h(CountTo, {
              props: {
                startValue: 0,
                endVal: count < 0 ? 0 : count,
                duration: 1,
                suffix: '元',
                decimals: 2
              }
            })
          }
        },
        {
          title: '双师优惠券核减',
          align: 'center',
          key: 'reduceCouponPromotionPriceWhileTutor',
          render: (h, params) => {
            return h(CountTo, {
              props: {
                startValue: 0,
                endVal: params.row.reduceCouponPromotionPriceWhileTutor,
                duration: 1,
                suffix: '元',
                decimals: 2
              }
            })
          }
        },
        {
          title: '实际退款',
          align: 'center',
          key: 'actualShouldRefundPriceAfterTutor',
          render: (h, params) => {
            return h(CountTo, {
              props: {
                startValue: 0,
                endVal: params.row.actualShouldRefundPriceAfterTutor,
                duration: 1,
                suffix: '元',
                decimals: 2
              }
            })
          }
        },
        { title: '退款状态', align: 'center', key: 'refundStatusName' }
      ],
      dataList: [],
      studentDetail: {},
      tableLoading: false,
      pageNo: 1,
      pageSize: 4,
      totalCount: 0
    }
  },
  props: {
  },
  methods: {
    setStuInfo (stuInfo) {
      this.studentDetail = stuInfo
      this.pageNo = 1
      this.getTableData()
    },
    getTableData () {
      this.tableLoading = true
      queryRefundDiff({
        cityCode: this.studentDetail.cityCode,
        pageNo: this.pageNo,
        pageSize: this.pageSize,
        studentUid: this.studentDetail.uid
      }).then(data => {
        this.tableLoading = false
        this.dataList = data.records
        this.totalCount = data.totalCount
      }).catch(err => {
        this.tableLoading = false
        this.tableData = []
        this.totalCount = 0
        this.pageSize = 10
        this.pageNo = 1
        this.$Message.error({ content: '查询差价退费记录失败:' + err.message, duration: 5 })
      })
    },
    // 分页
    changePage (num) {
      this.pageNo = num
      this.getTableData()
    }
  },
  watch: {
  },
  computed: {

  }
}
</script>

<style scoped lang="less">
  .diff-refund {
    .tb-wrapper {
      .notice {
        font-size: 16px;
        color: red
      }
    }
  }
</style>
