<script>
import { verificationCodeSend, checkPhoneLastLogout } from '../api/student'

export default {
  data () {
    return {
      sendBtnT: false, // 验证码已发送
      msgNumT: '', // 发送验证码后的倒计时
      smsCodeT: '', // 验证码
      hasLoggedOut: false, // 是否注销过-控制是否展示Modal
      // registerReason: '', // 再次注册原因
      loggedOutTime: '', // 上次注销时间
      lastLoggedOut: false
    }
  },
  methods: {
    // 发送验证码
    sendMessageHandlerT (phone, mode) {
      if (!this.sendBtnT) {
        if (!phone) {
          this.$Message.warning({
            content: '请输入手机号',
            duration: 3
          })
          return
        }
        this.sendBtnT = true
        this.msgNumT = 30
        let set
        let reqForChecking = {
          phone
        }
        checkPhoneLastLogout(reqForChecking)
          .then(res => {
            if (res !== '') {
              this.$Message.warning({ content: '此手机号进行过注册后注销操作', duration: 3 })
              this.hasLoggedOut = true
              this.loggedOutTime = res
            } else {
              let req = {
                phone,
                mode // 模式，regCode【注册】bindCode【绑定手机】
              }
              verificationCodeSend(req)
                .then(res => {
                  this.$Message.success({ content: '发送成功', duration: 3 })
                  set = setInterval(() => {
                    if (this.msgNumT <= 0) {
                      clearInterval(set)
                      this.sendBtnT = false
                    } else {
                      this.msgNumT -= 1
                    }
                  }, 1000)
                }).catch(err => {
                  clearInterval(set)
                  this.sendBtnT = false
                  this.$Message.error({
                    content: err.message,
                    duration: 5
                  })
                })
            }
          }).catch(err => {
            this.sendBtnT = false
            this.$Message.error({
              content: err.message,
              duration: 5
            })
          })
      }
    },
    resetSendBtn () {
      this.sendBtnT = false
    }
  }
}
</script>
