<template>
  <div class="invoice-query">
    <Form
      :label-width="75"
      label-position="left"
      ref="form"
      :model="formData"
      :rules='ruleData'
      inline
      style="text-align: left"
    >
      <FormItem label="分校：" prop="areaCode">
        <Select v-model="formData.areaCode" placeholder="请选择分校" clearable class="w120" filterable>
          <Option v-for="(item) in allAreaCode" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="业务线：" prop="businessCode">
        <Select v-model="formData.businessCode" placeholder="请选择业务线" clearable class="w120">
          <Option v-for="(item) in allBusiness" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="费用类型：" prop="queryType" :label-width="85">
        <Select v-model="formData.queryType" placeholder="请选费用类型" clearable class="w120">
          <Option v-for="(item) in feeTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="开票类型：" prop="invoiceType">
        <Select v-model="formData.invoiceType" placeholder="请选择开票类型" clearable class="w120" filterable>
          <Option v-for="(item, key) in invoiceTypeList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="开票状态：" prop="invoiceStatus">
        <Select v-model="formData.invoiceStatus" placeholder="请选择开票状态" clearable class="w120">
          <Option v-for="(item, key) in invoiceStateList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="来源：" prop="source" :label-width="65">
        <Select v-model="formData.source" placeholder="请选择来源" clearable class="w120">
          <Option v-for="(item, key) in sourceList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
        <FormItem label="学员姓名："  >
          <Input v-model="formData.studentName" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="学员编号："  >
          <Input v-model="formData.studentNo" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="开票人姓名：" :label-width="85">
          <Input v-model="formData.operatorName" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="发票号码：">
          <Input v-model="formData.invoiceNum" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="发票代码：">
          <Input v-model="formData.invoiceCode" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="原发票号码：" :label-width="85">
          <Input v-model="formData.originalInvoiceNum" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="原发票代码：" :label-width="85">
          <Input v-model="formData.originalInvoiceCode" placeholder="请输入" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="开票日期：">
          <Date-picker
            type="date"
            :value="formData.invoiceTime"
            placeholder="请选择日期"
            placement="bottom-end"
            format="yyyy-MM-dd"
            @on-change="changeDate"
            class="w120"
          ></Date-picker>
        </FormItem>
        <FormItem label="报名ID：">
          <Input v-model="formData.subOrderId" placeholder="请输入" :maxlength="400" clearable class="w120 ml10"/>
        </FormItem>
        <FormItem label="开票主体：">
          <Input v-model="formData.subjectName" placeholder="请输入" :maxlength="400" clearable class="w120 ml10"/>
        </FormItem>
      <div class="invoice-all-data">
        <p>
          数据合计：蓝字发票共{{tableList.countBlue || 0}}张，合计金额{{tableList.sumBlueAmount || 0}}元；红字发票共{{tableList.countRed || 0}}张，合计金额{{tableList.sumRedAmount || 0}}元
        </p>
        <div class="invoice-all-button">
          <Button type="primary" @click="getTableData(true)" :loading="tableLoading">查询</Button>
          <Button type="primary" style="margin-left: 15px;" v-if="importButtonFlag" @click="importTableData">导出</Button>
        </div>
      </div>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableList.data" :loading="tableLoading">
      <template slot-scope="{ row }"  slot="sp">
        <a @click="getDetailData(row)">查看详情</a>
      </template>
      <template slot-scope="{ row }"  slot="invoiceType">
        {{row.invoiceType|filterInvoiceName}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceSubjectType">
        {{row.invoiceSubjectType|filterInvoiceTypeName}}
      </template>
       <template slot-scope="{ row }"  slot="source">
        {{{source: row.source, sourceList: sourceList}|filterSourceName}}
      </template>
      <template slot-scope="{ row, index }"  slot="index">
        {{(formData.pageNum - 1) * formData.pageSize + index + 1}}
      </template>
      <template slot-scope="{ row }"  slot="buyer">
        {{row.buyer || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="createTime">
        {{row.createTime|formatTimeFilter}}
      </template>
      <template slot-scope="{ row }"  slot="originalInvoiceCode">
        {{row.originalInvoiceCode || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="originalInvoiceNum">
        {{row.originalInvoiceNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="receiver">
        {{row.receiver || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceCode">
        {{row.invoiceCode || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceNum">
        {{row.invoiceNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceRate">
        {{row.invoiceRate|filterRate}}
      </template>
      <template slot-scope="{ row }"  slot="buyerIdentifyNum">
        {{row.buyerIdentifyNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="buyerAddressPhone">
        {{row.buyerAddressPhone || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="buyerOpeningBank">
        {{row.buyerOpeningBank || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceId">
        {{row.invoiceId || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="subOrderId">
        {{row.subOrderId || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="resultMsg">
        {{row.resultMsg || '--'}}
      </template>
    </Table>
    <div style="text-align: right;margin-top: 10px;">
      <Page
        :total="tableList.count"
        :page-size="formData.pageSize"
        :current.sync="formData.pageNum"
        show-elevator
        show-sizer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"></Page>
    </div>
    <div v-if="this.formData.queryType !== 3">
      <detail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></detail>
    </div>
    <div v-if="this.formData.queryType === 3">
      <scholarshipDetail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></scholarshipDetail>
    </div>
  </div>
</template>

<script>
import detail from './detailModal/detail'
import scholarshipDetail from '../tal-invoice-query-detail-scholarship/detail'
import filters from '../../filters/index'
export default {
  name: 'talInvoiceQuery',
  data () {
    return {
      formData: {
        areaCode: '',
        businessCode: '',
        queryType: '',
        invoiceStatus: '',
        source: '',
        invoiceType: '',
        studentName: '',
        studentNo: '',
        operatorName: '',
        invoiceNum: '',
        invoiceCode: '',
        originalInvoiceNum: '',
        originalInvoiceCode: '',
        invoiceTime: '',
        subOrderId: '',
        subjectName: '',
        pageSize: 10,
        pageNum: 1
      },
      searchLoading: false,
      cols: [
        { title: '操作', align: 'center', width: 85, slot: 'sp' },
        { title: '序号', align: 'center', width: 60, slot: 'index' },
        { title: '分校', align: 'center', width: 100, key: 'areaCode' },
        { title: '业务线', align: 'center', width: 100, key: 'businessCode' },
        { title: '发票订单号', align: 'center', width: 100, slot: 'invoiceId' },
        {
          // title: '报名ID',
          align: 'center',
          width: 120,
          slot: 'subOrderId',
          renderHeader: (h, params) => {
            return h('div', [
              h('p', {
                style: {
                  textAlign: 'center'
                }
              }, this.formData.queryType !== 3 ? '报名ID' : '凭证ID')
            ])
          }
        },
        { title: '发票代码', align: 'center', width: 120, slot: 'invoiceCode' },
        { title: '发票号码', align: 'center', width: 120, slot: 'invoiceNum' },
        { title: '开票主体（销售方）', align: 'center', width: 150, key: 'sellerName' },
        { title: '开票金额', align: 'center', width: 90, key: 'invoiceAmount' },
        { title: '税率', align: 'center', width: 60, slot: 'invoiceRate' },
        { title: '收票主体（购买方）', align: 'center', width: 150, slot: 'invoiceSubjectType' },
        { title: '收票主体名称', align: 'center', width: 120, slot: 'buyer' },
        { title: '纳税人识别号', align: 'center', width: 170, slot: 'buyerIdentifyNum' },
        { title: '地址电话', align: 'center', width: 150, slot: 'buyerAddressPhone' },
        { title: '开户行及账户', align: 'center', width: 110, slot: 'buyerOpeningBank' },
        { title: '开票时间', align: 'center', width: 165, slot: 'createTime' },
        { title: '开票类型', align: 'center', width: 90, slot: 'invoiceType' },
        { title: '发票状态', align: 'center', width: 90, key: 'invoiceStateDesc' },
        { title: '来源', align: 'center', width: 110, slot: 'source' },
        { title: '失败原因', align: 'center', width: 280, slot: 'resultMsg' },
        { title: '原发票代码', align: 'center', width: 100, slot: 'originalInvoiceCode' },
        { title: '原发票号码', align: 'center', width: 100, slot: 'originalInvoiceNum' },
        { title: '开票人姓名', align: 'center', width: 100, key: 'operatorName' },
        { title: '领票人姓名', align: 'center', width: 100, slot: 'receiver' },
        { title: '领票人邮箱', align: 'center', width: 160, key: 'receiverEmail' },
        { title: '领票人手机', align: 'center', width: 120, key: 'receiverPhone' },
        { title: '学员姓名', align: 'center', width: 90, key: 'studentName' },
        { title: '学员编号', align: 'center', width: 90, key: 'studentNo' }
      ],
      tableLoading: false,
      ruleData: {
        areaCode: [
          { required: true, message: '请选择分校', trigger: 'change' }
        ],
        businessCode: [
          { required: true, message: '请选择业务线', trigger: 'change' }
        ],
        queryType: [
          { required: true, message: '请选择费用类型', trigger: 'change', type: 'number' }
        ]
      },
      detailFlag: false,
      invoiceTypeList: [
        { id: 0, name: '蓝字发票' },
        { id: 1, name: '红字发票' }
      ],
      invoiceStateList: [
        { id: 1, name: '未开' },
        { id: 2, name: '开票中' },
        { id: 3, name: '已开' },
        { id: 4, name: '开票失败' },
        { id: 5, name: '红冲中' },
        { id: 6, name: '部分红冲' },
        { id: 7, name: '全部红冲' },
        { id: 8, name: '红冲失败' }
      ]
    }
  },
  components: {
    detail,
    scholarshipDetail
  },
  filters: filters,
  props: {
    classDetailMessage: {
      type: Object,
      default: function () {
        return {}
      }
    },
    importButtonFlag: { // 导出按钮是否展示
      type: Boolean,
      default: true
    },
    ifRedDashed: { // 是否有红冲和发送操作
      type: Boolean,
      default: false
    },
    allAreaCode: { // 分校
      type: Array,
      default () {
        return []
      }
    },
    allBusiness: { // 业务线
      type: Array,
      default () {
        return []
      }
    },
    feeTypeList: {
      type: Array,
      default () {
        return []
      }
    },
    sourceList: {
      type: Array,
      default () {
        return []
      }
    },
    tableList: {
      type: Object,
      default: function () {
        return {
          count: 0,
          data: [],
          countBlue: 0,
          sumBlueAmount: 0,
          countRed: 0,
          sumRedAmount: 0
        }
      }
    },
    inDetailData: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    pageSizeChange (val) {
      this.formData.pageNum = 1
      this.formData.pageSize = val
      this.getTableData()
    },
    changePage (val) {
      this.formData.pageNum = val
      this.getTableData()
    },
    getTableData (mark) {
      // 获取列表数据
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (mark) {
            this.formData.pageNum = 1
            this.tableLoading = true
          }
          this.$emit('getInvoiceList', this.formData, () => {
            this.tableLoading = false
          })
        }
      })
    },
    getDetailData (row) {
      let params = {
        invoiceId: row.invoiceId
      }
      this.$emit('getInvoiceDetail', params, this.formData.queryType, () => {
        this.detailFlag = true
      })
    },
    importTableData () {
      // 导出
      if (this.tableList.data.length === 0) {
        this.$Message.warning('数据列表为空')
        return
      }
      this.$emit('importInvoiceList', this.formData)
    },
    closeModal (val) {
      this.detailFlag = val
    },
    changeDate (date) {
      this.formData.invoiceTime = date
    }
  }
}
</script>

<style scoped lang="less">
  .invoice-query{
    box-sizing: border-box;
    padding:0 20px;
  }
  .invoice-all-data{
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
      text-align: left;
    }
    .invoice-all-button{

    }
  }
  .w120{
    width: 125px;
  }
  .ml10{
    margin-left: 10px;
  }
</style>
<style lang="less">
  .invoice-query{
    .ivu-page{
      position: relative;
    }
    .ivu-form-item-label{
      /*padding-left:6px;*/
    }
  }
</style>
