<template>
  <div>
    <Modal
      :mask-closable="false"
      title="组合优惠退费班级列表"
      width="1180px"
      @on-visible-change="initModal"
      v-model="modalValue">
      <div>
        <div style="font-size: 14px">
          <p style="color: #FF0000">温馨提示：退费班级遇到以下情况，原则上享受同组合优惠的班级都需要进行退费处理，具体退费班级请与家长沟通。</p>
          <p> （1）退费班级享受联报立减、联报折扣；</p>
          <p> （2）退费班级享受买送优惠、第二件立减、第二件折扣，且退费班级为主班或优惠必选班；</p>
          <p> （3）退费班级享受加价购，但不享受OMO优惠，且退费班级为主班或优惠必选班；</p>
          <p style="color: #FF0000">如对退费课次有疑问，需修正课次特殊退费，请驳回后让家长到前台去退费</p>
        </div>
        <div style="margin-top: 10px">
          <h2>当前选择退费的班级</h2>
          <Table :columns="tableColumns1" :data="refundClassData" style="margin-top: 10px"/>
          <div v-for="(item,index) in tableData" :key="index">
            <h2 style="margin-top: 10px">{{item.name}}</h2>
            <Table
              :columns="tableColumns2"
              :data="tableData[index].data"
              :row-class-name="rowClassName"
              style="margin-top: 10px"/>
          </div>
        </div>
      </div>
      <div slot="footer">
        <Button type="primary" :loading="appealing" @click="handleAppeal">确认通过</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { refundOperate } from '../../../api/refund'

export default {
  name: 'combineRefundAppeal',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    refundClassData: {
      type: Array,
      default () {
        return []
      }
    },
    combineClassData: {
      type: Object,
      default () {
        return {}
      }
    },
    appealResult: {
      type: Array,
      default () {
        return []
      }
    },
    remark: {
      type: String,
      default: ''
    },
    areaCode: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    userNumber: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    studentId: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    cityCode: {
      type: [String, Number],
      default: function () {
        return ''
      }
    }
  },
  data () {
    return {
      tableColumns1: [
        {
          title: '班级名称',
          key: 'clazzName',
          align: 'center'
        }, {
          title: '授课类型',
          render: (h, params) => {
            return h('span', {}, this.classTypeMap[params.row.classType])
          },
          align: 'center'
        }, {
          title: '学科',
          key: 'subjectNames',
          align: 'center'
        }, {
          title: '主讲老师',
          key: 'teacherNames',
          align: 'center'
        }, {
          title: '辅导老师',
          key: 'tutorRealName',
          align: 'center'
        }, {
          title: '上课时间',
          key: 'classTimeNames',
          align: 'center'
        }, {
          title: '服务中心',
          key: 'servicecenterName',
          align: 'center'
        }, {
          title: '实缴金额',
          key: 'paymentAmount',
          align: 'center'
        }, {
          title: '应退金额',
          key: 'shouldRefundAmount',
          align: 'center'
        }, {
          title: '班级状态',
          align: 'center',
          render: (h, params) => h('span', this.clazzStatusMap[params.row.clazzStatus] || '')
        }, {
          title: '优惠名称',
          key: 'promotionName',
          align: 'center'
        }
      ],
      tableColumns2: [
        {
          width: 50,
          title: '操作',
          align: 'center',
          render: (h, params) => {
            return h('div', {
              style: {
                display: params.row.clazzStatus === '2' ? 'none' : 'inline-block'
              },
              on: {
                change: () => {
                  this.handleSelectionChange(params.row)
                }
              }
            }, [
              h('Checkbox', {
                props: {
                  disabled: params.row.clazzStatus !== '0',
                  value: params.row._checked
                }
              }, '')
            ])
          }
        },
        {
          title: '班级名称',
          key: 'clazzName',
          align: 'center'
        }, {
          title: '授课类型',
          render: (h, params) => {
            return h('span', {}, this.classTypeMap[params.row.classType])
          },
          align: 'center'
        }, {
          title: '学科',
          key: 'subjectNames',
          align: 'center'
        }, {
          title: '主讲老师',
          key: 'teacherNames',
          align: 'center'
        }, {
          title: '辅导老师',
          key: 'tutorRealName',
          align: 'center'
        }, {
          title: '上课时间',
          key: 'classTimeNames',
          align: 'center'
        }, {
          title: '服务中心',
          key: 'servicecenterName',
          align: 'center'
        }, {
          title: '实缴金额',
          key: 'actualRefundAmount',
          align: 'center'
        }, {
          title: '应退金额',
          key: 'shouldRefundAmount',
          align: 'center'
        }, {
          title: '班级状态',
          align: 'center',
          render: (h, params) => h('span', this.clazzStatusMap[params.row.clazzStatus] || '')
        }, {
          title: '优惠名称',
          key: 'promotionName',
          align: 'center'
        }
      ],
      tableData: {},
      tableSelection: [],
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      clazzStatusMap: {
        0: '已缴费',
        1: '已结课',
        2: '已退费'
      },
      appealing: false
    }
  },
  methods: {
    initModal (isShow) {
      if (!isShow) {
        this.tableSelection = []
        this.tableData = {}
      } else {
        const classId = this.refundClassData[0].classId
        const tableData = {}
        for (const key in this.combineClassData) {
          if (this.combineClassData.hasOwnProperty(key)) {
            if (!tableData[key]) {
              tableData[key] = {}
              tableData[key].name = this.combineClassData[key].name
              tableData[key].data = []
            }
            this.combineClassData[key].data.forEach(item => {
              const newItem = item
              newItem._checked = newItem.clazzStatus !== '2' // 除了退费的班级，其他的班级默认勾选
              newItem._disabled = newItem.classId === classId
              newItem.tableIndex = key
              tableData[key].data.push(newItem)
            })
          }
        }
        this.tableData = tableData
      }
    },
    handleSelectionChange (val) {
      console.log(val)
      const tempList = []
      this.tableData[val.tableIndex].data.forEach(item => {
        if (val.classId === item.classId) {
          item._checked = !item._checked
        }
        if (item._checked) {
          tempList.push(item)
        }
      })
      const selectIdList = tempList.map(item => item.classId)
      const classIdList = this.tableData[val.tableIndex].data.map(item => item.classId)
      const { tableData } = this
      for (const key in tableData) {
        if (tableData.hasOwnProperty(key)) {
          if (key !== val.tableIndex) {
            tableData[key].data.forEach(row => {
              if (row.classId !== this.refundClassData[0].classId && classIdList.indexOf(row.classId) > -1 && row.clazzStatus !== '2') {
                row._checked = selectIdList.indexOf(row.classId) > -1
              }
            })
          } else {
            tableData[key].data.forEach(row => {
              row._checked = selectIdList.indexOf(row.classId) > -1
            })
          }
        }
      }
      this.tableData = JSON.parse(JSON.stringify(tableData))
    },
    handleAppeal () {
      const appealResult = {
        reviewResult: '',
        refundReasonOne: '',
        refundReasonTwo: '',
        rejectReason: ''
      }
      appealResult.reviewResult = this.appealResult[0]
      if (this.appealResult[0] === '1') {
        appealResult.refundReasonOne = this.appealResult[1]
        appealResult.refundReasonTwo = this.appealResult[2]
      } else if (this.appealResult[2] === '2') {
        appealResult.rejectReason = this.appealResult[1]
      }
      const sampleUpdateReqs = []
      this.tableSelection.forEach(classItem => {
        if (sampleUpdateReqs.filter(item => item.applyId === classItem.applyId).length === 0) {
          const item = {
            refundId: classItem.applyId,
            reviewResult: appealResult.reviewResult,
            refundReasonOne: appealResult.refundReasonOne,
            refundReasonTwo: appealResult.refundReasonTwo,
            rejectReason: appealResult.rejectReason,
            promotionId: classItem.promotionId,
            promotionType: classItem.promotionType,
            remark: this.remark,
            registId: classItem.registId,
            comboId: classItem.submitId
          }
          sampleUpdateReqs.push(item)
        }
      })
      if (sampleUpdateReqs.filter(item => item.registId === this.refundClassData[0].registId).length === 0) {
        sampleUpdateReqs.push({
          refundId: this.refundClassData[0].applyId,
          reviewResult: appealResult.reviewResult,
          refundReasonOne: appealResult.refundReasonOne,
          refundReasonTwo: appealResult.refundReasonTwo,
          rejectReason: appealResult.rejectReason,
          remark: this.remark,
          registId: this.refundClassData[0].registId,
          comboId: this.refundClassData[0].submitId,
          promotionId: this.refundClassData[0].promotionId,
          promotionType: this.refundClassData[0].promotionType
        })
      }
      const params = {
        handler: this.userNumber,
        studentId: this.studentId,
        // area: this.areaCode, 全国版退费问题修复
        area: this.cityCode === '1111' ? this.cityCode : this.areaCode,
        sampleUpdateReqs
      }
      this.appealing = true
      refundOperate(params).then(() => {
        this.$Message.success('更新成功')
        this.modalValue = false
        this.$emit('success')
        this.appealing = false
      }).catch(error => {
        this.$Message.error('更新退费状态失败: ' + error.message)
        this.appealing = false
      })
    },
    rowClassName (row, index) {
      if (row.clazzStatus === '2') {
        return 'special-row'
      } else {
        return ''
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  },
  watch: {
    tableData (val) {
      this.tableSelection = []
      for (const key in val) {
        if (val.hasOwnProperty(key)) {
          val[key].data.forEach(item => {
            if (item._checked) {
              this.tableSelection.push(item)
            }
          })
        }
      }
      console.log(this.tableSelection)
    }
  }
}
</script>

<style scoped lang="less">
  /deep/.ivu-table {
    .special-row {
      td {
        background-color: #ddd;
      }
    }
    td {
      .special-column {
        display: none;
      }
    }
  }
</style>
