<template>
  <div class="tal-teacher-attendance">
    <div class="form-wrapper">
      <Form
        ref="form"
        inline
        class="ivu-common-shadow"
        style="padding: 18px 0;"
        :model="formMessage"
        :rules="formRules"
        :label-position="'right'"
        :label-width="90">
        <div>
          <FormItem label="城市" prop="cityId">
            <Select style="width: 120px" v-model="formMessage.cityId" filterable @on-change="handleCityCodeChange">
              <Option v-for="item in allCityList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="年份" prop="year">
            <Select style="width: 120px" v-model="formMessage.year" filterable @on-change="handleYearChange">
              <Option v-for="item in yearList" :key="item" :value="item" :label="item">{{item}}</Option>
            </Select>
          </FormItem>
          <FormItem label="学期" prop="termId">
            <Select style="width: 120px" v-model="formMessage.termId" filterable @on-change="handleTermChange">
              <Option v-for="item in termList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="年部">
            <Select style="width: 120px" v-model="formMessage.gradeTypeId" clearable filterable @on-change="handleGradeTypeChange">
              <Option v-for="item in gradeTypeList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="年级">
            <Select style="width: 120px" v-model="formMessage.gradeId" clearable filterable @on-change="handleGradeChange">
              <Option v-for="item in gradeList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="学科">
            <Select style="width: 120px" v-model="formMessage.subjectId" clearable filterable @on-change="handleSubjectChange">
              <Option v-for="item in subjectList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="班次">
            <Select style="width: 120px" v-model="formMessage.levelId" clearable filterable @on-change="handleClassLevelChange">
              <Option v-for="item in classLevelList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="课次" prop="classNum">
            <Input style="width: 120px;" v-model.trim="formMessage.classNum" clearable/>
          </FormItem>
          <FormItem label="教师姓名">
            <Input style="width: 120px" v-model.trim="formMessage.teacherName" clearable filterable></Input>
          </FormItem>
          <FormItem label="区域">
            <Select style="width: 120px" v-model="formMessage.areaId" clearable filterable @on-change="handleAreaChange">
              <Option v-for="item in areaList" :key="item.schoolId" :value="item.schoolId" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="服务中心">
            <Select style="width: 120px" v-model="formMessage.servicecenterId" clearable filterable @on-change="handleServiceCenterChange">
              <Option v-for="item in serviceCenterList" :key="item.schoolId" :value="item.schoolId" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="教学点">
            <Select style="width: 120px" v-model="formMessage.venueId" clearable filterable>
              <Option v-for="item in venueList" :key="item.schoolId" :value="item.schoolId" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课时间类型">
            <Select style="width: 120px" v-model="formMessage.classtimeTimeClassify" clearable filterable>
              <Option v-for="item in classtimeTypeList" :key="item.id" :value="item.name" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课时间">
            <Select style="width: 120px" v-model="formMessage.classTimeId" clearable filterable label-in-value @on-change="handleClassTimeChange">
              <Option v-for="item in classtimeList" :key="item.id" :value="item.id" :label="item.name">{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="上课日期">
            <DatePicker style="width: 120px" v-model="formMessage.classDate" clearable filterable></DatePicker>
          </FormItem>
        </div>
        <div class="tip-wrapper">
          <p>修改教师考勤应在当次课结束当天24:00前进行修改，如需更正过期教师考勤请发送邮件至：kaoqin@edu.xueersi.com </p>
        </div>
        <div style="text-align: right;">
          <FormItem>
            <Button type="primary" style="margin-right: 20px;" :loading="tableLoading" @click="searchFn">查询</Button>
            <Button @click="handleResetForm">重置</Button>
          </FormItem>
        </div>
      </Form>
    </div>
    <div class="table-wrapper">
      <Table
        size="small"
        :columns="tableColumns"
        :data="tableData"
        :loading="tableLoading">
        <template slot="operation" slot-scope="{ row }">
          <div style="padding: 5px;">
            <Button type="text" size="small" style="margin-bottom: 10px;" :disabled="handleCanSet(row.teaLaterType, row.classDate)" @click="handleShowSetAttendance(row)">设置</Button>
            <Button type="text" size="small" :disabled="handleCanEdit(row.teaLaterType, row.classDate)" @click="handleShowEditAttendance(row)">修改</Button>
          </div>
        </template>
        <template slot="className" slot-scope="{ row }">
          <a @click="showClassDetail(row.classId)">{{row.className}}</a>
        </template>
        <template slot="teacherName" slot-scope="{ row }">
          <a @click="showTeacherDetail(formMessage.cityId,row.teacherId,row.teacherType)">{{row.teacherName}}</a>
        </template>
        <template slot="classTeacherName" slot-scope="{ row }">
          <a @click="showTeacherDetail(formMessage.cityId,row.classTeacherId,row.classTeacherType)">{{row.classTeacherName}}</a>
        </template>
        <template slot="teacherAttendStatus" slot-scope="{row}">
          <span>{{getTeacherAttendStatus(row.teaLaterType, row.teaIsNoneClock, row.teaIsAbsent)}}</span>
        </template>
        <template slot="assistantAttendStatus" slot-scope="{row}">
          <span>{{getTeacherAttendStatus(row.assistantLaterType, row.assistantIsNoneClock, row.assistantIsAbsent)}}</span>
        </template>
      </Table>
    </div>
    <div class="page-wrapper">
      <div style="float: right;">
        <Page
          :total="dataTotal"
          :page-size="currentPageSize"
          :page-size-opts="pageSizeOpt"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="handleCurrentPageSizeChange"
          @on-change="handleCurrentPageChange"></Page>
      </div>
    </div>
    <!--设置、修改考勤-->
    <set-attendance
      :value="showSetAttendance"
      :status="setAttendanceStatus"
      :detail-info="detailInfo"
      :city-code="formMessage.cityId"
      @success="searchFn"
      @close="showSetAttendance = false">
    </set-attendance>
    <!--班级详情-->
    <tal-class-detail
      :show="classDetail.show"
      :class-id="classDetail.id"
      @close="classDetail.show = false">
    </tal-class-detail>
    <!--教师详情-->
    <tal-teacher-detail
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :creatorEmpNo="teacherDetail.empNo">
    </tal-teacher-detail>
    <!-- 查看上课时间 -->
    <TalSchooltime ref="talSchooltime" />
  </div>
</template>

<script>
import teacherDetail from '../../mixin/teacherDetail'
import SetAttendance from './set-attendance/index.vue'
import TalClassDetail from '../tal-class-details'
import TalTeacherDetail from '../tal-teacher-details'
import table from '../../mixin/table'
import baseData from '../../mixin/baseData'
import classDetail from '../../mixin/classDetail'
import { getCityList, getLinkage } from '../../api/bd'
import { queryCurriculumInfo } from '../../api/clazz'
import { validateInt } from '../../utils/validate'
import { getTeacherAttendanceList } from '../../api/teacher'
import { dateFormat, getStrLength } from '../../utils/utils'
import TalSchooltime from '../tal-schooltime'
export default {
  name: 'TalTeacherAttendance',
  components: { SetAttendance, TalClassDetail, TalTeacherDetail, TalSchooltime },
  mixins: [table, baseData, classDetail, teacherDetail],
  data () {
    return {
      formMessage: {
        cityId: '',
        year: '',
        termId: '',
        gradeTypeId: '',
        gradeId: '',
        subjectId: '',
        levelId: '',
        classNum: null,
        teacherName: '',
        areaId: '',
        servicecenterId: '',
        venueId: '',
        classTimeId: '',
        classtimeTimeClassify: '',
        classDate: ''
      },
      classTimeName: '',
      formRules: {
        cityId: [{ required: true, message: '城市必填', trigger: 'blur|change' }],
        year: [{ required: true, message: '年份必填', trigger: 'blur|change' }],
        termId: [{ required: true, message: '学期必填', trigger: 'blur|change' }],
        classNum: [{ trigger: 'blur', validator: this.handleValidateInt }]
      },
      tableColumns: [
        {
          title: '操作',
          width: 70,
          align: 'center',
          slot: 'operation'
        },
        {
          title: '班级名称',
          width: 100,
          align: 'center',
          slot: 'className'
        },
        {
          title: '服务中心',
          key: 'servicecenterName',
          align: 'center'
        },
        {
          title: '教师姓名',
          align: 'center',
          slot: 'teacherName'
        },
        {
          title: '班主任',
          align: 'center',
          slot: 'classTeacherName'
        },
        {
          title: '上课时间',
          key: 'classTimeName',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: async () => {
                  this.showSchooltime({ cityId: this.formMessage.cityId, id: params.row.classId })
                }
              }
            }, getStrLength(params.row.classTimeName, 15, true))
          }
        },
        {
          title: '开课日期',
          key: 'classStartDate',
          align: 'center'
        },
        {
          title: '结课日期',
          key: 'classEndDate',
          align: 'center'
        },
        {
          title: '上课日期',
          key: 'classDate',
          align: 'center'
        },
        {
          title: '总课次',
          key: 'classCount',
          align: 'center'
        },
        {
          title: '课次',
          key: 'classNum',
          align: 'center'
        },
        {
          title: '教师考勤状态',
          align: 'center',
          slot: 'teacherAttendStatus'
        },
        {
          title: '教师考勤时间',
          key: 'teaModifyDate',
          align: 'center'
        },
        {
          title: '教师考勤操作人',
          key: 'teaModifyName',
          align: 'center'
        },
        {
          title: '班主任考勤状态',
          align: 'center',
          slot: 'assistantAttendStatus'
        },
        {
          title: '班主任考勤时间',
          key: 'assistantModifyDate',
          align: 'center'
        },
        {
          title: '班主任考勤操作人',
          key: 'assistantModifyName',
          align: 'center'
        }
      ],
      showSetAttendance: false,
      setAttendanceStatus: 'SET',
      detailInfo: {},
      termList: [],
      gradeTypeList: [],
      gradeList: [],
      subjectList: [],
      classLevelList: [],
      classNumList: [],
      areaList: [],
      serviceCenterList: [],
      venueList: []
    }
  },
  methods: {
    handleValidateInt (rule, value, callback) {
      if (!value) {
        callback()
      }
      const valid = validateInt(value)
      if (!valid) {
        callback(new Error('只能输入整数'))
      } else {
        callback()
      }
    },
    getTableData () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const condition = []
          const formMessageKeys = Object.keys(this.formMessage)
          formMessageKeys.forEach(key => {
            if (this.formMessage[key]) {
              let val
              let pushKey = key
              if (key === 'classDate') {
                val = dateFormat(this.formMessage[key].valueOf() / 1000, 'yyyy-MM-dd')
              } else if (key === 'teacherName') {
                pushKey = 'teacherName.keyword'
                val = this.formMessage[key]
              } else if (key === 'classTimeId') {
                return
              } else {
                val = this.formMessage[key]
              }
              condition.push({
                field: pushKey,
                val
              })
            }
          })
          const sortFields = [{
            name: 'classNum',
            isAsc: true
          }]
          const body = {
            selectSearch: 'Curriculum',
            includeField: [
              'curriculumId',
              'classId',
              'className',
              'servicecenterName',
              'classNum',
              'cityId',
              'classDate',
              'classTimeName',
              'classCount',
              'classTeacherId',
              'classTeacherName',
              'classTeacherEmpNo',
              'teacherId',
              'teacherEmpNo',
              'teacherName',
              'venueName',
              'year',
              'termId',
              'termName',
              'absentReason'
            ],
            condition,
            sortFields,
            pageIndex: this.currentPageNum,
            pageSize: this.currentPageSize
          }
          const params = {
            cityCode: this.formMessage.cityId
          }
          let mustFields = []
          if (this.classTimeName) {
            mustFields.push({
              field: 'classTimeName.text',
              val: this.classTimeName ? this.classTimeName : null,
              opType: 'match'
            })
          }
          if (mustFields.length > 0) {
            body.mustFields = mustFields
          }
          this.tableLoading = true
          getTeacherAttendanceList({ params, body }).then(response => {
            this.tableData = response.rows || []
            this.dataTotal = response.totalCount
            this.tableLoading = false
          }).catch(error => {
            this.$Message.error('获取教师考勤列表失败: ' + error.message)
            this.tableLoading = false
          })
        }
      })
    },
    handleResetForm () {
      this.$refs.form.resetFields()
    },
    handleCityCodeChange (val) {
      this.formMessage.termId = ''
      this.formMessage.gradeTypeId = ''
      this.formMessage.gradeId = ''
      this.formMessage.subjectId = ''
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.formMessage.areaId = ''
      this.formMessage.servicecenterId = ''
      this.formMessage.venueId = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.classLevelList = []
      this.classNumList = []
      this.areaList = []
      this.serviceCenterList = []
      this.venueList = []
      if (!val) {
        return
      }
      if (this.formMessage.year) {
        const params1 = {
          cityCode: this.formMessage.cityId,
          years: this.formMessage.year,
          queryType: 't'
        }
        getLinkage(params1).then(response => {
          if (response) {
            this.termList = response
          }
        }).catch(error => {
          this.$Message.error('获取学期失败: ' + error.message)
        })
      }
      const params2 = {
        cityCode: this.formMessage.cityId
      }
      this.getAreaListHandler(params2).then(response => {
        if (response) {
          this.areaList = response
        }
      }).catch(error => {
        this.$Message.error('获取区域失败: ' + error.message)
      })
    },
    handleYearChange (val) {
      this.formMessage.termId = ''
      this.formMessage.gradeTypeId = ''
      this.formMessage.gradeId = ''
      this.formMessage.subjectId = ''
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.classLevelList = []
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        years: this.formMessage.year,
        queryType: 't'
      }
      getLinkage(params).then(response => {
        if (response) {
          this.termList = response
        }
      }).catch(error => {
        this.$Message.error('获取学期失败: ' + error.message)
      })
    },
    handleTermChange (val) {
      this.formMessage.gradeTypeId = ''
      this.formMessage.gradeId = ''
      this.formMessage.subjectId = ''
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.classLevelList = []
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        years: this.formMessage.year,
        termId: this.formMessage.termId,
        queryType: 'gt'
      }
      getLinkage(params).then(response => {
        if (response) {
          this.gradeTypeList = response
        }
      }).catch(error => {
        this.$Message.error('获取年部失败: ' + error.message)
      })
    },
    handleGradeTypeChange (val) {
      this.formMessage.gradeId = ''
      this.formMessage.subjectId = ''
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.gradeList = []
      this.subjectList = []
      this.classLevelList = []
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        years: this.formMessage.year,
        termId: this.formMessage.termId,
        gradeTypeId: this.formMessage.gradeTypeId,
        queryType: 'g'
      }
      getLinkage(params).then(response => {
        if (response) {
          this.gradeList = response
        }
      }).catch(error => {
        this.$Message.error('获取年级失败: ' + error.message)
      })
    },
    handleGradeChange (val) {
      this.formMessage.subjectId = ''
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.subjectList = []
      this.classLevelList = []
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        years: this.formMessage.year,
        termId: this.formMessage.termId,
        gradeTypeId: this.formMessage.gradeTypeId,
        gradeId: this.formMessage.gradeId,
        queryType: 's'
      }
      getLinkage(params).then(response => {
        if (response) {
          this.subjectList = response
        }
      }).catch(error => {
        this.$Message.error('获取学科失败: ' + error.message)
      })
    },
    handleSubjectChange (val) {
      this.formMessage.levelId = ''
      this.formMessage.classNum = ''
      this.classLevelList = []
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        years: this.formMessage.year,
        termId: this.formMessage.termId,
        gradeTypeId: this.formMessage.gradeTypeId,
        gradeId: this.formMessage.gradeId,
        subjectId: this.formMessage.subjectId,
        queryType: 'c'
      }
      getLinkage(params).then(response => {
        if (response) {
          this.classLevelList = response
        }
      }).catch(error => {
        this.$Message.error('获取班次失败: ' + error.message)
      })
    },
    handleClassLevelChange (val) {
      this.formMessage.classNum = ''
      this.classNumList = []
      if (!val) {
        return
      }
      const params = {
        area: this.formMessage.cityId,
        classId: this.formMessage.levelId
      }
      queryCurriculumInfo(params).then(response => {
        console.log(response)
      }).catch(error => {
        this.$Message.error('获取课次失败: ' + error.message)
      })
    },
    handleAreaChange (val) {
      this.formMessage.servicecenterId = ''
      this.formMessage.venueId = ''
      this.serviceCenterList = []
      this.venueList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        areaId: this.formMessage.areaId
      }
      this.getServiceCenterListHandler(params).then(response => {
        if (response) {
          this.serviceCenterList = response
        }
      }).catch(error => {
        this.formMessage.error('获取服务中心失败: ' + error.message)
      })
    },
    handleServiceCenterChange (val) {
      this.formMessage.venueId = ''
      this.venueList = []
      if (!val) {
        return
      }
      const params = {
        cityCode: this.formMessage.cityId,
        serviceId: this.formMessage.servicecenterId
      }
      this.getVenueListHandler(params).then(response => {
        if (response) {
          this.venueList = response
        }
      }).catch(error => {
        this.$Message.error('获取教学点失败: ' + error.message)
      })
    },
    handleShowEditAttendance (row) {
      this.setAttendanceStatus = 'EDIT'
      this.detailInfo = row
      this.showSetAttendance = true
    },
    handleShowSetAttendance (row) {
      this.setAttendanceStatus = 'SET'
      this.detailInfo = row
      this.showSetAttendance = true
    },
    handleClassTimeChange (val) {
      if (!val) {
        this.classTimeName = ''
      } else {
        this.classTimeName = val.label
      }
    },
    getTeacherAttendStatus (laterType, isNoneClock, isAbsent) {
      if (isNoneClock === 1) {
        return '未打卡'
      }
      if (isAbsent === 1) {
        return '缺勤'
      }
      switch (laterType) {
        case 0: return '正常上课'
        case 1: return '课前迟到'
        case 2: return '课后迟到'
        default: return ''
      }
    },
    handleCanEdit (laterType, classDate) {
      const currentDate = dateFormat(new Date().valueOf(), 'yyyy-MM-dd')
      if (currentDate !== classDate) return true
      return laterType !== 0 && laterType !== 1 && laterType !== 2
    },
    handleCanSet (laterType, classDate) {
      const currentDate = dateFormat(new Date().valueOf(), 'yyyy-MM-dd')
      if (currentDate !== classDate) return true
      return laterType === 0 || laterType === 1 || laterType === 2
    }
  },
  async mounted () {
    this.tableData = []
    await this.getYearListHandler()
    getCityList().then((data) => {
      this.allCityList = data
      this.formMessage.cityId = this.allCityList && this.allCityList.length > 0 ? this.allCityList[0].id : ''
      this.handleCityCodeChange(this.formMessage.cityId)
    }).catch((err) => {
      this.$Message.warning({ content: '获取所有城市失败:' + err.message, duration: 5 })
    })
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
    console.log(this.yearList, this.allCityList)
    this.formMessage.year = new Date().getFullYear().toString()
  }
}
</script>

<style scoped lang="less">
  .tal-teacher-attendance {
    .form-wrapper {
      .tip-wrapper {
        margin-left: 10px;
        p {
          color: #ff0000;
        }
      }
    }
    .table-wrapper {
      margin-top: 10px;
    }
    .page-wrapper {
      margin-top: 10px;
      height: 40px;
    }
  }
</style>
