<template>
  <Modal v-model="showModal" title="短信发送" :z-index="9999" :mask-closeable="false" @on-cancel="handleClose" @on-visible-change="handleVisibleChange">
    <div class="send-weChat">
      <div>
        <span style="padding-right:20px;">接收人：</span>
        <Input v-model="phone" placeholder="请输入..." style="width: 300px" clearable></Input>
      </div>
      <div style="margin-top:20px; display: flex">
        <span style="padding-right:8px;">短信内容：</span>
        <div style="font-size:14px">
          <Input v-model="messageText" type="textarea" :disabled="isMessageDisabled" placeholder="短信内容" style="width: 400px;margin: 5px 0" :autosize="{maxRows: 6,minRows: 2}"/>
        </div>
      </div>
      <Spin v-show="initialing" fix/>
    </div>
    <template slot="footer">
      <div style="overflow: hidden;">
        <div style="float: left;color: #ff0000;" v-show="!getWeChat">未获取到相应的教师微信，无法发送</div>
        <div style="float: right;">
          <Button type="primary" :disabled="!getWeChat" :loading="submitting" @click="handleSendMsg">确认发送</Button>
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import { getClassTchQrCode } from '../../../api/teacher'
import { getSecretPhoneNum, sendMsg, getMsgTemplateById } from '../../../api/clazz'
import { validatePhoneNumber } from '../../../utils/validate'
import { runEnv } from '../../../utils/utils'

export default {
  name: 'sendWeChat',
  inject: ['isMessageDisabled'],
  props: {
    show: {
      type: Boolean,
      default: false
    },
    classInfo: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      showModal: false,
      phone: '',
      initialing: false,
      submitting: false,
      messageText: '',
      getWeChat: false
    }
  },
  methods: {
    async handleVisibleChange (val) {
      if (val) {
        const params = {
          classId: this.classInfo.classId
        }
        this.initialing = true
        try {
          let response = await getClassTchQrCode(params)
          const weChat = response
          response = await getMsgTemplateById({ templateId: runEnv() === 'prod' ? '11000000111' : '11000000270' })
          let messageText = response.templateContent || ''
          this.getWeChat = true
          messageText = messageText.replace('{1}', this.classInfo.className)
          messageText = messageText.replace('{2}', this.classInfo.teacherNames)
          messageText = messageText.replace('{3}', weChat)
          this.messageText = messageText
        } catch (err) {
          console.log(err)
        } finally {
          this.initialing = false
        }
      }
    },
    async handleSendMsg () {
      if (this.phone.length !== 11) {
        this.$Message.warning('请输入正确的手机号码')
        return
      }
      if (!this.messageText) {
        this.$Message.warning('短信内容不能为空')
        return
      }
      const params = {
        content: this.messageText
      }
      this.submitting = true
      let response
      try {
        if (validatePhoneNumber(this.phone)) {
          response = await getSecretPhoneNum({ phone: this.phone })
          if (response) {
            params.phone = response
          }
        } else {
          params.phone = this.phone
        }
        response = await sendMsg(params)
        if (response.rlt) {
          this.$Message.success('发送成功')
          this.handleClose()
        } else {
          this.$Message.error(response.message)
        }
      } catch (err) {
        console.log(err)
        this.$Message.error('发送失败，请重试。')
      } finally {
        this.submitting = false
      }
    },
    handleClose () {
      this.phone = ''
      this.getWeChat = false
      this.messageText = ''
      this.$emit('close')
    }
  },
  watch: {
    show (val) {
      this.showModal = val
    }
  }
}
</script>

<style lang="less" scoped>
.send-weChat {
  position: relative;
}
</style>
