/* 时间戳转日期-时间 */
const formatTimeFilter = (secs) => {
  if (!secs) {
    return '--'
  }
  const newdate = new Date(secs)
  const year = newdate.getFullYear()
  const month = addZeroMonthDate(newdate.getMonth() + 1)
  const date = addZeroMonthDate(newdate.getDate())
  const h = addZeroMonthDate(newdate.getHours())
  const m = addZeroMonthDate(newdate.getMinutes())
  const s = addZeroMonthDate(newdate.getSeconds())
  return `${year}-${month}-${date} ${h}:${m}:${s}`
}
/* 补零 */
const addZeroMonthDate = (val) => {
  const newVal = val.toString()
  return newVal.length > 1 ? val : `0${val}`
}
const filterInvoiceName = (type) => {
  let typeList = {
    0: '蓝字发票',
    1: '红字发票'
  }
  return typeList[type] || '--'
}
const filterInvoiceTypeName = (type) => {
  let typeList = {
    0: '个人/非企业单位',
    1: '企业'
  }
  return typeList[type] || '--'
}
// 税率
const filterRate = (number) => {
  if (number === 0) {
    return '免税'
  } else if (number) {
    return number * 100 + '%'
  } else {
    return '--'
  }
}
// 来源
const filterSourceName = (info) => { // 列表来源显示name
  const { source, sourceList } = info
  if (source || source === 0) {
    const filterArr = sourceList.filter((item) => { return item.id === source })
    if (filterArr.length) {
      return filterArr[0].name
    } else {
      return '--'
    }
  } else {
    return '--'
  }
}
const filters = {
  formatTimeFilter,
  filterInvoiceName,
  filterInvoiceTypeName,
  filterRate,
  filterSourceName
}
export default filters
