<script>
import filters from '../../../filters/index'
import { toChinesNum } from '../../../utils/utils'

export default {
  name: 'detail',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    inDetailData: {
      type: Array,
      default: function () {
        return []
      }
    },
    ifRedDashed: { // 一键红冲和重新发送功能（供盘古使用）
      type: Boolean,
      default: false
    },
    isNew: { // 是否新版发票
      type: Boolean,
      default: true
    }
  },
  filters: filters,
  data () {
    return {
      tableData: [],
      cols: [
        {
          title: '班级D-上期',
          children: [
            {
              title: '已购课次',
              width: 100,
              align: 'center',
              key: 'year'
            },
            {
              title: '实缴金额',
              width: 100,
              align: 'center',
              key: 'year'
            },
            {
              title: '上课时间',
              width: 100,
              align: 'center',
              key: 'year'
            },
            {
              title: '归属班级',
              width: 100,
              align: 'center',
              key: 'year'
            },
            {
              title: '对应主体',
              width: 150,
              align: 'center',
              key: 'year'
            },
            {
              title: '税率',
              width: 100,
              align: 'center',
              key: 'year'
            },
            {
              title: '是否已红冲',
              align: 'center',
              key: 'year',
              minWidth: 120
            }
          ]
        }
      ],
      spanArr: {},
      sendFlag: false,
      sendData: {
        oldEmail: '',
        newEmail: '',
        invoiceId: ''
      },
      redFlag: false,
      redDashedData: {
        invoiceDetail: {}
      },
      redDashedIndex: null,
      checkList: [],
      // 发票红冲状态
      hasOffsetText: ['红冲中', '已红冲', '未红冲', '红冲失败']
    }
  },
  computed: {
    modalValue: {
      get: function () {
        this.dealTableData()
        return this.value
      },
      set: function (val) {
        this.checkList = []
        this.$emit('close', val)
      }
    }
  },
  methods: {
    // 下载发票
    donwloadInvoice (url) {
      window.open(url)
    },
    // 重新发送邮箱确认按钮
    confirmRetrySendHandler () {
      const params = Object.assign({}, this.sendData, {
        receiverEmail: this.sendData.newEmail || ''
      })
      const reg = /^[a-zA-Z0-9_.-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (params.receiverEmail && !reg.test(params.receiverEmail)) {
        this.$Message.warning({
          content: '请输入正确的邮箱地址',
          duration: 5
        })
        return
      }
      this.sendFlag = true
      this.$emit('sendEmail', params)
    },
    dealTableData () {
      // 计算表格单元格合并数量
      if (this.inDetailData.length === 0) return
      this.inDetailData.forEach((v, k) => {
        this.spanArr[k] = []
        let number = 0
        let allLength = 0
        if (v.invoiceInfo && v.invoiceInfo.length > 0) {
          v.invoiceInfo.forEach((item, index) => {
            this.spanArr[k][index] = 0
            if (item.classDetail && item.classDetail.length > 0) {
              item.classDetail.forEach((val, key) => {
                this.spanArr[k][index] += (val.classInfo ? val.classInfo.length : 0)
                if (val.classInfo && val.classInfo.length > 0) {
                  val.classInfo.forEach((va, ke) => {
                    // va._check = false
                    this.$set(this.inDetailData[k].invoiceInfo[index].classDetail[key].classInfo[ke], '_check', false)
                    allLength += 1
                    if (va.hasOffset === 1 || va.hasOffset === 0) {
                      // 计算已红冲的条数，如果全部已红冲，一键红冲按钮不展示
                      number += 1
                    }
                  })
                }
              })
            }
          })
        }
        v.allHasOffset = !(allLength === number)
      })
    },
    sendEmail (row) {
      // 重新发送
      this.sendData.oldEmail = row.invoiceDetail.receiverEmail
      this.sendData.invoiceId = row.invoiceDetail.invoiceId
      this.sendFlag = true
    },
    redDashed (item, index) {
      // 一键红冲
      this.redDashedData = item
      if (!this.redDashedData.invoiceDetail) {
        this.$Message.warning('发票信息为空')
        return
      }
      let arr = []
      let selectPrice = 0
      this.checkList.forEach((val, key) => {
        if (val.invoiceId === this.redDashedData.invoiceDetail.invoiceId) {
          arr.push(val)
          selectPrice += Number(val.courseAmount)
        }
      })
      if (arr.length === 0) {
        this.$Message.warning('请先选择课次后再点击一键红冲')
        return
      }
      this.redDashedData.selectPrice = selectPrice.toFixed(2)
      this.redDashedData.offsetInfo = arr
      this.redDashedIndex = index
      this.redFlag = true
    },
    setOffset () {
      let params = {
        invoiceId: this.redDashedData.invoiceDetail.invoiceId,
        offsetInfo: this.redDashedData.offsetInfo,
        invoiceTypeName: this.redDashedData.invoiceDetail.invoiceType === 0 ? '蓝字发票' : '红字发票'
      }
      this.$emit('setOffset', params)
    },
    changeCheck (index, v, va, val) {
      // 选择
      if (!this.inDetailData[index].invoiceDetail) return
      if (v._check) {
        let obj = {
          subOrderId: val.subOrderId,
          stagesNum: va.stageNum,
          classNum: v.courseNum,
          classType: v.classType,
          invoiceId: this.inDetailData[index].invoiceDetail.invoiceId,
          courseAmount: v.courseAmount
        }
        this.checkList.push(obj)
      } else {
        // 移除的时候，确保移除数据准确性
        this.checkList = this.checkList.filter(item => {
          return !(item.invoiceId === this.inDetailData[index].invoiceDetail.invoiceId &&
                      item.classNum === v.courseNum &&
                      item.stagesNum === va.stageNum &&
                      item.subOrderId === val.subOrderId &&
                      item.courseAmount === v.courseAmount &&
                      item.classType === v.classType)
        })
      }
    },
    toCN (num) {
      return toChinesNum(num)
    }
  }
}
</script>

