<template>
  <Modal
    :title="title"
    width="960"
    v-model="isShow"
    :mask-closable="false"
    @on-visible-change="handleVisibleChange">
    <div class="set-attendance">
      <div class="form-wrapper">
        <Row class="row">
          <Col span="12">
            <span class="label">班级</span>
            <span class="mess">{{detailInfo.className}}</span>
          </Col>
          <Col span="12">
            <span class="label">课次</span>
            <span class="mess">{{detailInfo.classNum}}</span>
          </Col>
        </Row>
        <Row class="row">
          <Col span="12">
            <span class="label">教学点</span>
            <span class="mess">{{detailInfo.venueName}}</span>
          </Col>
          <Col span="12">
            <span class="label">上课时间</span>
            <span class="mess">{{detailInfo.classTimeName}}</span>
          </Col>
        </Row>
        <Row class="row">
          <Col span="12">
            <span class="label">教师名称</span>
            <span class="mess">{{detailInfo.teacherName}}</span>
          </Col>
          <Col span="12">
            <span class="label">出勤</span>
            <Select v-model="formMessage.teaAttendStatus" placeholder="出勤状态" style="display: inline-block;width: 240px;" clearable @on-change="handleChangeStatus('tea')">
              <Option v-for="(item,index) in attendStatusMap" :key="index" :value="index" :label="item">{{item}}</Option>
            </Select>
          </Col>
        </Row>
        <Row class="row" v-show="formMessage.teaAttendStatus === '2-1' || formMessage.teaAttendStatus === '2-2'">
          <Col span="12">
            <span class="label">迟到分钟</span>
            <Input v-model="formMessage.teaLaterTimes" placeholder="迟到分钟，以分钟为单位" style="display: inline-block;width: 240px;" clearable></Input>
          </Col>
          <Col span="12">
            <span class="label">出勤时间</span>
            <TimePicker v-model="formMessage.teaLaterAttendHour" placeholder="出勤时间" style="display: inline-block;width: 240px;"></TimePicker>
          </Col>
        </Row>
        <Row class="row" v-show="formMessage.teaAttendStatus === '3'">
          <Col span="24">
            <span class="label">缺勤原因</span>
            <Input type="textarea" v-model="formMessage.teaAbsentReason" :autosize="{ minRows: 4, maxRows: 4 }" placeholder="缺勤原因" style="display: inline-block; width: 703px;"></Input>
          </Col>
        </Row>
      </div>
      <div class="form-wrapper">
        <Row class="row">
          <Col span="12">
            <span class="label">班主任姓名</span>
            <span class="mess">{{detailInfo.classTeacherName}}</span>
          </Col>
          <Col span="12" v-if="this.detailInfo.classTeacherId">
            <span class="label">出勤</span>
            <Select v-model="formMessage.claTeaAttendStatus" placeholder="出勤状态" style="display: inline-block;width: 240px;" clearable @on-change="handleChangeStatus('claTea')">
              <Option v-for="(item,index) in attendStatusMap" :key="index" :value="index" :label="item">{{item}}</Option>
            </Select>
          </Col>
        </Row>
        <Row v-if="this.detailInfo.classTeacherId">
          <Row class="row" v-show="formMessage.claTeaAttendStatus === '2-1' || formMessage.claTeaAttendStatus === '2-2'">
            <Col span="12">
              <span class="label">迟到分钟</span>
              <Input v-model="formMessage.claTeaLaterTimes" placeholder="迟到分钟，以分钟为单位" style="display: inline-block;width: 240px;" clearable></Input>
            </Col>
            <Col span="12">
              <span class="label">出勤时间</span>
              <TimePicker v-model="formMessage.claTeaLaterAttendHour" placeholder="出勤时间" style="display: inline-block;width: 240px;"></TimePicker>
            </Col>
          </Row>
          <Row class="row" v-show="formMessage.claTeaAttendStatus === '3'">
            <Col span="24">
              <span class="label">缺勤原因</span>
              <Input type="textarea" v-model="formMessage.claTeaAbsentReason" :autosize="{ minRows: 4, maxRows: 4 }" placeholder="缺勤原因" style="display: inline-block; width: 703px;"></Input>
            </Col>
          </Row>
        </Row>
      </div>
      <div class="tip-wrapper">
        <p>备注：根据出勤状态联动设置详细情况，规则如下：1.准时上课 2.迟到－－迟到具体时间 3.缺勤－缺勤原因 4.未打卡</p>
      </div>
    </div>
    <div slot="footer">
      <Button @click="handleCancel">取消</Button>
      <Button style="margin-right: 20px;" type="primary" :loading="submitting" @click="handleConfirm">提交考勤</Button>
    </div>
  </Modal>
</template>

<script>
import { setTeacherAttendance } from '../../../api/teacher'
import { validateInt } from '../../../utils/validate'

export default {
  name: 'index',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    status: {
      type: String,
      default: 'SET'
    },
    detailInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    cityCode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      formMessage: {
        teaAttendStatus: '',
        teaLaterTimes: '',
        teaLaterAttendHour: '',
        teaAbsentReason: '',
        claTeaAttendStatus: '',
        claTeaLaterTimes: '',
        claTeaLaterAttendHour: '',
        claTeaAbsentReason: ''
      },
      isShow: false,
      submitting: false,
      attendStatusMap: {
        '1': '准时上课',
        '2-1': '课前迟到',
        '2-2': '课后迟到',
        '3': '缺勤',
        '4': '未打卡'
      }
    }
  },
  computed: {
    title () {
      return this.status === 'SET' ? '设置考勤' : '修改考勤'
    }
  },
  methods: {
    handleVisibleChange (val) {
      if (val) {
        this.initDetail()
      } else {
        this.formMessage = {
          teaAttendStatus: '',
          teaLaterTimes: '',
          teaLaterAttendHour: '',
          teaAbsentReason: '',
          claTeaAttendStatus: '',
          claTeaLaterTimes: '',
          claTeaLaterAttendHour: '',
          claTeaAbsentReason: ''
        }
        this.$emit('close')
      }
    },
    handleConfirm () {
      if (!this.formMessage.teaAttendStatus) {
        this.$Message.error('请填写出勤状态')
        return
      }
      if (this.formMessage.teaAttendStatus === '2-1' || this.formMessage.teaAttendStatus === '2-2') {
        if (!this.formMessage.teaLaterTimes) {
          this.$Message.error('请填写迟到分钟')
          return
        }
        if (!validateInt(this.formMessage.teaLaterTimes)) {
          this.$Message.error('迟到分钟请填写整数')
          return
        }
        if (!this.formMessage.teaLaterAttendHour) {
          this.$Message.error('请填写出勤时间')
          return
        }
      }
      if (this.formMessage.teaAttendStatus === '3') {
        if (!this.formMessage.teaAbsentReason) {
          this.$Message.error('请填写缺勤原因')
          return
        }
      }
      if (this.detailInfo.classTeacherId) {
        if (!this.formMessage.claTeaAttendStatus) {
          this.$Message.error('请填写出勤状态')
          return
        }
        if (this.formMessage.claTeaAttendStatus === '2-1' || this.formMessage.claTeaAttendStatus === '2-2') {
          console.log(11111)
          if (!this.formMessage.claTeaLaterTimes) {
            this.$Message.error('请填写迟到分钟')
            return
          }
          if (!validateInt(this.formMessage.claTeaLaterTimes)) {
            this.$Message.error('迟到分钟请填写整数')
            return
          }
          if (!this.formMessage.claTeaLaterAttendHour) {
            this.$Message.error('请填写出勤时间')
            return
          }
        }
        if (this.formMessage.claTeaAttendStatus === '3') {
          if (!this.formMessage.claTeaAbsentReason) {
            this.$Message.error('请填写缺勤原因')
            return
          }
        }
      }
      let isNoneClock = this.formMessage.teaAttendStatus === '4' ? 1 : 0
      let isPunctual = this.formMessage.teaAttendStatus === '1' ? 1 : 0
      let isAbsent = this.formMessage.teaAttendStatus === '3' ? 1 : 0
      let laterType
      switch (this.formMessage.teaAttendStatus) {
        case '1': laterType = 0; break
        case '2-1': laterType = 1; break
        case '2-2': laterType = 2; break
        default: laterType = null
      }
      const teaParams = {
        entityId: this.detailInfo.teacherId,
        entityType: 1,
        cityCode: this.cityCode,
        claId: this.detailInfo.classId,
        curriculumId: this.detailInfo.curriculumId,
        year: this.detailInfo.year,
        termId: this.detailInfo.termId,
        isNoneClock,
        isPunctual,
        isAbsent,
        laterType,
        laterTimes: this.formMessage.teaLaterTimes ? this.formMessage.teaLaterTimes : null,
        laterAttendHour: this.formMessage.teaLaterAttendHour ? this.formMessage.teaLaterAttendHour : null,
        absentReason: this.formMessage.teaAbsentReason ? this.formMessage.teaAbsentReason : null
      }
      isNoneClock = this.formMessage.claTeaAttendStatus === '4' ? 1 : 0
      isPunctual = this.formMessage.claTeaAttendStatus === '1' ? 1 : 0
      isAbsent = this.formMessage.claTeaAttendStatus === '3' ? 1 : 0
      switch (this.formMessage.claTeaAttendStatus) {
        case '1': laterType = 0; break
        case '2-1': laterType = 1; break
        case '2-2': laterType = 2; break
        default: laterType = null
      }
      const claTeaParams = {
        entityId: this.detailInfo.classTeacherId ? this.detailInfo.classTeacherId : null,
        entityType: 2,
        cityCode: this.cityCode,
        claId: this.detailInfo.classId,
        curriculumId: this.detailInfo.curriculumId,
        year: this.detailInfo.year,
        termId: this.detailInfo.termId,
        isNoneClock,
        isPunctual,
        isAbsent,
        laterType,
        laterTimes: this.formMessage.claTeaLaterTimes ? this.formMessage.claTeaLaterTimes : null,
        laterAttendHour: this.formMessage.claTeaLaterAttendHour ? this.formMessage.claTeaLaterAttendHour : null,
        absentReason: this.formMessage.claTeaAbsentReason ? this.formMessage.claTeaAbsentReason : null
      }
      const params = [ teaParams, claTeaParams ]
      this.submitting = true
      setTeacherAttendance(params).then(response => {
        this.$Message.success(`${this.status === 'SET' ? '设置' : '修改'}考勤成功 `)
        this.$emit('success')
        this.isShow = false
        this.submitting = false
      }).catch(error => {
        this.$Message.error(`${this.status === 'SET' ? '设置' : '修改'}考勤失败: ` + error.message)
        this.submitting = false
      })
    },
    handleCancel () {
      this.isShow = false
    },
    initDetail () {
      if (this.status !== 'EDIT') {
        return
      }
      // 教师的考勤信息 start
      if (this.detailInfo.teaIsNoneClock === 1) {
        this.formMessage.teaAttendStatus = '4'
        return
      }
      if (this.detailInfo.teaIsAbsent === 1) {
        this.formMessage.teaAttendStatus = '3'
        this.formMessage.teaAbsentReason = this.detailInfo.absentReason
        return
      }
      switch (this.detailInfo.teaLaterType) {
        case 0: this.formMessage.teaAttendStatus = '1'; break
        case 1: this.formMessage.teaAttendStatus = '2-1'; break
        case 2: this.formMessage.teaAttendStatus = '2-2'; break
        default: return ''
      }
      if (this.detailInfo.teaLaterType === 1 || this.detailInfo.teaLaterType === 2) {
        this.formMessage.teaLaterTimes = this.detailInfo.laterTimes
        this.formMessage.teaLaterAttendHour = this.detailInfo.laterAttendHour
      }
      // 教师的考勤信息 end
      // 班主任的考勤信息 start
      if (this.detailInfo.assistantIsNoneClock === 1) {
        this.formMessage.claTeaAttendStatus = '4'
        return
      }
      if (this.detailInfo.assistantIsAbsent === 1) {
        this.formMessage.claTeaAttendStatus = '3'
        this.formMessage.claTeaAbsentReason = this.detailInfo.assistantAbsentReason
        return
      }
      switch (this.detailInfo.assistantLaterType) {
        case 0: this.formMessage.claTeaAttendStatus = '1'; break
        case 1: this.formMessage.claTeaAttendStatus = '2-1'; break
        case 2: this.formMessage.claTeaAttendStatus = '2-2'; break
        default: return ''
      }
      if (this.detailInfo.assistantLaterType === 1 || this.detailInfo.assistantLaterType === 2) {
        this.formMessage.claTeaLaterTimes = this.detailInfo.assistantLaterTimes
        this.formMessage.claTeaLaterAttendHour = this.detailInfo.assistantLaterAttendHour
      }
      // 班主任的考勤信息 end
    },
    handleChangeStatus (type) {
      switch (type) {
        case 'tea':
          this.formMessage.teaLaterTimes = ''
          this.formMessage.teaLaterAttendHour = ''
          this.formMessage.teaAbsentReason = ''
          break
        case 'claTea':
          this.formMessage.claTeaLaterTimes = ''
          this.formMessage.claTeaLaterAttendHour = ''
          this.formMessage.claTeaAbsentReason = ''
          break
        default: break
      }
    }
  },
  watch: {
    value (val) {
      this.isShow = val
    }
  }
}
</script>

<style scoped lang="less">
.set-attendance {
  .info-wrapper, .form-wrapper {
    font-size: 14px;
    .row {
      margin-bottom: 20px;
    }
    .label {
      display: inline-block;
      width: 100px;
      color: #666666;
      text-align: right;
      margin-right: 20px;
    }
    .mess {
      color: #333333;
      letter-spacing: 1.07px;
    }
  }
  .tip-wrapper {
    margin-top: 20px;
    color: #ff0000;
  }
}
</style>
