<template>
  <div class="refund-process">
    <!--<Spin v-if="tableLoading" size="large" fix></Spin>-->
    <div class="steps-bg">
      <p class="refund-process-title">
        <span>学员姓名:{{ studentName || '--' }}</span>
        <span>
          班级名称:
          <template v-if="className.length <= 25">{{ className || '--' }}</template>
          <template v-else>
            <Tooltip
              :content="className"
              max-width="200"
              placement="bottom"
            >{{className.substring(0, 25)}}</Tooltip>
          </template>
        </span>
        <span>退费平台:{{ refundData.refundPlatformName || '--' }}</span>
        <span>退费服务中心:{{ refundData.serviceCenterName || '--' }}</span>
      </p>
    </div>
    <Row type="flex" justify="center" class="refund-stages-info">
      <template v-if="refundBills.length === 1&&!refundBills">
        <Col span="16">
          <aside class="refund-stages-info-item">
            <span>分期：{{refundBills[0].stagesName}}</span>
            <span>退款金额：{{refundBills[0].refundAmount}}</span>
          </aside>
        </Col>
      </template>
      <Col v-else>
        <aside class="refund-stages-info-item" v-for="item in refundBills" :key="item.stagesNo">
          <span>分期：{{item.stagesName}}</span>
          <span>退款金额：{{item.refundAmount}}</span>
        </aside>
      </Col>
      <Col v-if="extraRefundDTOS&&extraRefundDTOS.length>0">
        <aside class="refund-stages-info-item" v-for="(item,i) in extraRefundDTOS" :key="i">
          <span>{{item.feeTypeName}}</span>
          <span>退款金额：{{item.totalAmount}}</span>
        </aside>
      </Col>
    </Row>
    <div class="steps-bg refund-process-list">
      <Spin v-if="tableLoading" size="large" fix></Spin>
      <p class="no-data-txt" v-if="nodeInfos.length === 0">暂无数据</p>
      <section v-for="(item, index) in nodeInfos" :key="item.id">
        <p class="refund-process-list-title">
          <span>第{{index + 1}}笔</span>
          <span>退款金额：{{item.refundAmount}}元</span>
          <span>退费方式：{{item.payTypeName || '--'}}</span>
          <span>退款路径：{{item.refundPlatformName || '--'}}</span>
          <span v-if="item.nodes && item.nodes.success">退费账户：{{item.nodes.success.bankNo || '--'}}</span>
          <span>退费类型：{{item.feeTypeName || '--'}}</span>
        </p>
        <div v-if="item.payType === 4">
          <p
            class="refund-process-list-dzqb"
          >{{item.description || '暂时不支持电子钱包退费进度查询，现金退费以实际为准，POS退费7-15个工作日到帐。'}}</p>
        </div>
        <div v-else-if="item.payType === 200">
          <p class="refund-process-list-dzqb">温馨提示：该笔现金退费未通过电子钱包，请直接给用户退还现金。</p>
        </div>
        <Steps v-else-if="item.nodes">
          <Step
            v-if="item.nodes.businessSubmit"
            :title="item.nodes.businessSubmit.name"
            :status="getRefundStatus(item.nodes.businessSubmit.status,item.nodes.businessSubmit.show)"
            :content="item.nodes.businessSubmit.completeTime"
          ></Step>
          <Step
            v-if="item.nodes.check"
            :title="item.nodes.check.name"
            :status="getRefundStatus(item.nodes.check.status,item.nodes.check.show)"
            :content="item.nodes.check.completeTime"
          ></Step>
          <Step
            v-if="item.nodes.accept"
            :title="item.nodes.accept.name"
            :status="getRefundStatus(item.nodes.accept.status,item.nodes.accept.show)"
            :content="item.nodes.accept.completeTime"
          ></Step>
          <Step
            v-if="item.nodes.refundSubmit"
            :title="item.nodes.refundSubmit.name"
            :status="getRefundStatus(item.nodes.refundSubmit.status,item.nodes.refundSubmit.show)"
            :content="item.nodes.refundSubmit.completeTime"
          ></Step>
          <Step
            v-if="item.nodes.success"
            :title="item.nodes.success.name"
            :status="getRefundStatus(item.nodes.success.status,item.nodes.success.show)"
            :content="item.nodes.success.completeTime"
          ></Step>
        </Steps>
      </section>
      <p v-if="nodeInfos.length !== 0" style="color: red">
        <span
          style="margin-right: 20px"
          v-for="(item, index) in refundProcessSums"
          :key="index"
        >{{item.payTypeName}}共退款：{{item.refundAmount}}元</span>
      </p>
    </div>
    <div class="refund-notice">
      <p class="orange">退款到账时间预计为【银行接收时间】加3~5个工作日</p>
      <p>
        如遇异常状态或有任何疑问请联系IT支持
        <!--<template v-if="contact">-->
        <!--联系电话：15810216021-->
        <!--</template>-->
      </p>
    </div>
  </div>
</template>

<script>
/*eslint-disable*/
import { queryRefundProcessDetail } from '../../api/finance'
export default {
  name: 'talFinanceRefundProcess',
  props: {
    cityCode: {
      required: true,
      type: String
    },
    refundBillId: {
      required: true,
      type: String
    },

    studentName: {
      required: true,
      type: String
    },
    className: {
      required: true,
      type: String
    },
    contact: {
      type: Boolean,
      default: function () {
        return false
      }
    },
    workOrderId: {
      type: String,
      default: ''
    }
  },
  computed: {
  },
  data() {
    return {
      refundData: {},
      nodeInfos: [],
      refundBills: [],
      refundProcessSums: [],
      tableLoading: true,
      extraRefundDTOS:[]
    }
  },
  mounted() {
    this.getRefundProcessDetailHandler()
  },
  methods: {
    getRefundProcessDetailHandler() {
      let req = {
        cityCode: this.cityCode,
        refundBillId: this.refundBillId,
        workOrderId: this.workOrderId
      }
      queryRefundProcessDetail(req).then((res) => {
        this.refundBills = res.refundBills || []
        this.nodeInfos = res.nodeInfos || []
        this.refundProcessSums = res.refundProcessSums || []
        this.refundData = res
        this.extraRefundDTOS = res.extraRefundDTOS
      }).catch((err) => {
        this.$Message.error({ content: '获取数据失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    getRefundStatus(status = 0, show = 0) {
      if (status === 1) {
        return 'error'
      } else if (show === 1 || status === 2) {
        return 'finish'
      } else {
        return 'wait'
      }
    }
  }
}
</script>

