<!--优惠券-->
<template>
  <div>
    <div style="text-align: right;margin-bottom: 10px">
      <Button type="primary" @click="addCoupon">绑定优惠券</Button>
    </div>
    <Table stripe :columns="tableColumns" :data="tableData" :loading="tableLoading"></Table>
    <div style="margin: 10px;overflow: hidden;">
        <div style="float: right;">
          <Page
            show-elevator
            :total="totalCount"
            :current="currentPage"
            @on-change="handlePageChange"
            @on-page-size-change="handlePageSizeChange"
            :page-size="pageSize"
            show-total
            show-sizer/>
        </div>
    </div>
    <Modal
      v-model="addCouponModal"
      :mask-closable="false"
      width="600"
      :footer-hide="true"
      title="绑定优惠券"
      class="has-tag">
      <TalAddCoupon v-if="addCouponModal" :studentId="studentDetail.studentId" :cityCode="studentDetail.cityCode" :uid="studentDetail.uid" @success="addCouponSuccess" @close="addCouponModal = false"></TalAddCoupon>
    </Modal>
    <!-- 优惠券详情 -->
    <TalCouponDetail :showCoupon="showCoupon" :releaseId="releaseId" @close="showCoupon=false" />
    <!--班级详情-->
    <TalClassDetail
      :show="classDetail.show"
      :classId="classDetail.id"
      @close="classDetail.show = false">
    </TalClassDetail>
  </div>
</template>
<script>

import { getCouponList, getClassByCouponId, unBindCoupon } from '../../api/coupon'
import ClassMixin from '../../mixin/classDetail'
import { copyText } from '../../utils/utils'
import TalCouponDetail from '../tal-coupon-detail'
import TalClassDetail from '../tal-class-details'
import TalAddCoupon from '../tal-add-coupon'
export default {
  name: 'TalDiscountCoupon',
  mixins: [ClassMixin],
  components: {
    TalCouponDetail,
    TalClassDetail,
    TalAddCoupon
  },
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data: function () {
    return {
      addCouponModal: false,
      tableLoading: false,
      modalValue: false,
      modalLoading: false,
      tableData: [], // 优惠券列表
      totalCount: 0,
      currentPage: 1,
      pageSize: 10,
      tableData2: [], // 适用班级
      showCoupon: false, // 优惠券详情
      releaseId: '', // 优惠券id
      statusMap: Object.freeze({
        13: '过期',
        14: '优惠券未到生效日期',
        16: '优惠券已使用',
        19: '平台不使用',
        21: '有效',
        24: '优惠券不是已发放状态',
        1: '未使用',
        2: '已使用',
        3: '已过期'
      }),
      typeMap: Object.freeze({
        1: '代金券',
        2: '22011寒-2012春',
        3: '满减券',
        4: '满折券',
        5: '折扣券'
      }),
      tableColumns: Object.freeze([{
        title: '操作',
        align: 'center',
        minWidth: 160,
        render: (h, params) => {
          return h('div',
            [
              h('a', {
                attrs: {
                  class: 'info-btn'
                },
                style: {
                  display: 'inline-block',
                  padding: '0 5px'
                },
                on: {
                  click: () => {
                    this.unBind(params.row)
                  }
                }
              }, '解绑优惠券 '),
              h('a', {
                attrs: {
                  class: 'info-btn'
                },
                style: {
                  display: 'inline-block',
                  padding: '0 5px'
                },
                on: {
                  click: () => {
                    this.releaseId = params.row.releaseId
                    this.showCoupon = true
                  }
                }
              }, '优惠券详情')
            ])
        }
      },
      {
        title: '优惠券名称',
        key: 'couponName',
        align: 'center'
      },
      {
        title: '优惠券类型',
        align: 'center',
        render: (h, params) => {
          return h('span', {
            style: {}
          }, this.typeMap[params.row.type] || '--')
        }
      },
      {
        title: '优惠券编码',
        key: 'couponCode',
        width: 200,
        align: 'center'
      },
      {
        title: '面值',
        key: 'amountName',
        align: 'center'
      },
      {
        title: '有效期',
        align: 'center',
        render: (h, params) => {
          return h('span', {
          }, params.row.beginTime + ' / ' + params.row.expireTime)
        }
      },
      {
        title: '状态',
        align: 'center',
        render: (h, params) => {
          return h('span', {
            style: {}
          }, this.statusMap[params.row.couponStatus])
        }
      },
      {
        title: '是否可叠加',
        align: 'center',
        render: (h, params) => {
          return h('span', {
          }, params.row.isCombine === 1 ? '是' : '否')
        }
      },
      {
        title: '是否退回',
        align: 'center',
        key: 'refundCoupon',
        render: (h, params) => {
          return h('span', params.row.refundCoupon === 1 ? '是' : '否')
        }
      },
      {
        title: '退回时间',
        align: 'center',
        key: 'refundCouponTime'
      },
      {
        title: '已使用班级',
        key: 'className',
        align: 'center',
        width: 200,
        render: (h, params) => {
          return h('div', [
            h('div', {
              style: {
                'text-align': 'right'
              }
            }, [
              h('Tag', {
                style: {
                  'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                },
                props: {
                  color: 'green'
                }
              }, '全国班')
            ]),
            h('a', {
              on: {
                click: () => {
                  this.showClassDetail(params.row.classId)
                }
              }
            }, params.row.className)
          ])
        }
      }])

    }
  },
  methods: {
    // 改变页码
    handlePageChange (page) {
      this.currentPage = page
      this.getTableData()
    },
    // 页码数量改变
    handlePageSizeChange (num) {
      this.pageSize = num
      this.currentPage = 1
      this.getTableData()
    },
    initView () {
      this.getTableData()
    },
    addCoupon () {
      if (window.XesAnalytics) {
        XesAnalytics.track('15-3629', {})
      }

      this.addCouponModal = true
    },
    addCouponSuccess () {
      this.addCouponModal = false
      this.getTableData()
    },
    getTableData () {
      this.tableLoading = true
      let req = {
        areaCode: this.studentDetail.cityCode,
        studentUid: this.studentDetail.uid,
        pageSize: this.pageSize,
        pageNo: this.currentPage
      }
      getCouponList(req, this.studentDetail.cityCode).then((res) => {
        if (res.records) {
          res.records.forEach((e, i) => {
            if (e.beginTime) {
              e.beginTime = e.beginTime.substring(0, 10)
            }
            if (e.expireTime) {
              e.expireTime = e.expireTime.substring(0, 10)
            }
          })
          this.tableData = Object.freeze(res.records)
          this.totalCount = res.totalCount
        } else {
          this.tableData = []
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    unBind (row) {
      this.$Modal.confirm({
        title: '解绑确认',
        content: `确定要解绑${row.couponCode}优惠券吗`,
        okText: '解绑',
        onOk: () => {
          unBindCoupon({
            areaCode: this.studentDetail.cityCode,
            cityCode: this.studentDetail.cityCode,
            password: row.couponCode,
            studentId: this.studentDetail.studentId,
            uid: this.studentDetail.uid
          }).then(() => {
            setTimeout(() => {
              this.$Modal.success({
                title: '解绑成功',
                content: `优惠券编码：${row.couponCode}，点击复制按钮可复制优惠券编码`,
                okText: '复制',
                onOk: () => {
                  copyText(row.couponCode)
                  this.getTableData()
                }
              })
            }, 500)
          }).catch(err => {
            this.$Message.error({
              content: '解绑优惠券错误：' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    getClassByCouponId (item) {
      if (item.scopeType === 3) {
        this.$Message.warning({ content: '该优惠券适用于通用班级', duration: 3 })
      } else if (item.scopeType === 2) {
        this.$Message.warning({ content: '该优惠券适用于部分课程', duration: 3 })
      } else {
        this.modalValue = true
        this.modalLoading = true
        let req = {
          cityCode: this.studentDetail.cityCode,
          password: item.password
        }
        getClassByCouponId(req).then((res) => {
          res.forEach((e, i) => {
            e.index = i + 1
          })
          this.tableData2 = Object.freeze(res)
          this.modalLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
          this.modalValue = false
        })
      }
    }
  }
}
</script>
