            </div>
            <div class="invoice-detail-flex">
              <span>领票人手机：</span>
              <p>{{item.invoiceDetail ? (item.invoiceDetail.receiverPhone || '--') : '--'}}</p>
            </div>
            <div class="invoice-detail-button" v-if="ifRedDashed">
              <template v-if="(item.invoiceDetail && item.invoiceDetail.invoiceType === 0)">
