              </template>
            </div>
          </li>
        </ul>

        <div class="invoice-table-box">
          <div class="ivu-table ivu-table-default ivu-table-stripe ivu-table-wrapper invoice-detail-table">
            <div class="ivu-table-body">
              <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
                <thead>
                  <tr class="ivu-table-row">
                    <th class="ivu-table-column-center" style="width:100px;"><div class="ivu-table-cell">活动名称</div></th>
                    <th class="ivu-table-column-center" v-if="ifRedDashed && (item.invoiceDetail && item.invoiceDetail.invoiceType === 0)" style="width:60px;"><div class="ivu-table-cell">选择</div></th>
                    <th class="ivu-table-column-center" style="width:130px;"><div class="ivu-table-cell">年级</div></th>
                    <th class="ivu-table-column-center" style="width:90px;"><div class="ivu-table-cell">学科</div></th>
                    <th class="ivu-table-column-center" style="width:90px;"><div class="ivu-table-cell">使用班级</div></th>
                    <th class="ivu-table-column-center" style="width:100px;"><div class="ivu-table-cell">实缴金额</div></th>
                    <th class="ivu-table-column-center" style="width:120px;"><div class="ivu-table-cell">对应主体</div></th>
                    <th class="ivu-table-column-center" style="width:60px;"><div class="ivu-table-cell">税率</div></th>
                    <th class="ivu-table-column-center" style="width:65px;">红冲状态</th>
                    <th class="ivu-table-column-center" style="width:150px;">备注</th>
                  </tr>
                </thead>
                <tbody class="ivu-table-tbody">
                <tr class="ivu-table-row" v-for="(_item, _index) in item.invoiceInfo" :key="_index">
                  <td class="ivu-table-column-center" v-if="spanArr[index][_index]" :rowspan="spanArr[index][_index]"><div class="ivu-table-cell">{{_item.activityName}}</div></td>
                  <td class="ivu-table-column-center" v-if="ifRedDashed && (item.invoiceDetail && item.invoiceDetail.invoiceType === 0)"><div class="ivu-table-cell"><Checkbox v-model="_item._check" @on-change="changeCheck(_item, index)" :disabled="_item.hasOffset === 1 || _item.hasOffset === 0"></Checkbox></div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.gradeName}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.subjectName}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.className}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.amount}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.ledgerSubjectName}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{_item.rate|filterRate}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{hasOffsetText[_item.hasOffset]}}</div></td>
                  <!-- 备注/红冲失败和红冲成功 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">
                      {{_item.hasOffset === 3 ? '失败原因: ' + _item.offsetFailReason
                        : (_item.hasOffset === 1 ? '发票代码：'+_item.offsetInvoiceCode+'，发票号码：'+_item.offsetInvoiceNum+'，开票时间：'+ _item.offsetInvoiceTime
                        : '--')}}
                    </div>
                  </td>
                </tr>
                <tr v-if="!item.invoiceInfo || !item.invoiceInfo.length">
                  <td class="ivu-table-column-center" colspan="10"><div class="ivu-table-cell">暂无数据</div></td>
                </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer">
        <Button @click="modalValue = false">关闭</Button>
      </div>
    </Modal>
    <!--重新发送弹框-->
    <Modal
      :mask-closable="false"
      title="重新发送"
      v-model="sendFlag"
    >
      <ul class="invoice-detail invoice-detail-send">
        <li class="invoice-detail-flex">
          <span>原领票人邮箱：</span>
          <p>{{this.sendData.oldEmail || '--'}}</p>
        </li>
        <li class="invoice-detail-flex">
          <span class="invoice-detail-span">新领票人邮箱：</span>
          <Input v-model="sendData.newEmail" type="email" placeholder="如不填写，则会向原邮箱重新发送" :maxlength="400"/>
        </li>
      </ul>
      <div slot="footer">
        <Button @click="confirmRetrySendHandler" type="primary">发送</Button>
      </div>
    </Modal>
    <!--一键红冲弹框-->
    <Modal
      :mask-closable="false"
      title="一键红冲-确认"
      v-model="redFlag"
      okText="确认红冲"
      @on-ok="setOffset"
    >
      <ul class="invoice-detail" v-if="redDashedData.invoiceDetail">
        <li class="invoice-detail-flex">
          <span>开票主体（销售方）：</span>
          <p>{{redDashedData.invoiceDetail.sellerName || '--'}}</p>
        </li>
        <li>
          <div class="invoice-detail-flex">
            <span>开票金额（人民币）：</span>
            <p>{{redDashedData.selectPrice}}</p>
          </div>
          <div class="invoice-detail-flex">
            <span>税率：</span>
            <p>{{redDashedData.invoiceDetail.invoiceRate|filterRate}}</p>
          </div>
        </li>
        <li class="invoice-detail-flex">
          <span>收票主体（购买方）：</span>
          <p>{{redDashedData.invoiceDetail.invoiceSubjectType|filterInvoiceTypeName}}</p>
        </li>
        <li>
          <div class="invoice-detail-flex">
            <span>收票主体名称：</span>
            <p>{{redDashedData.invoiceDetail.buyerName || '--'}}</p>
          </div>
          <div class="invoice-detail-flex">
            <span>纳税人识别号：</span>
            <p>{{redDashedData.invoiceDetail.buyerIdentifyNum || '--'}}</p>
          </div>
        </li>
        <li>
          <div class="invoice-detail-flex">
            <span>地址、电话：</span>
            <p>{{redDashedData.invoiceDetail.buyerAddressPhone || '--'}}</p>
          </div>
          <div class="invoice-detail-flex">
            <span>开户行及账号：</span>
            <p>{{redDashedData.invoiceDetail.buyerOpeningBank || '--'}}</p>
          </div>
        </li>
        <li class="invoice-detail-flex">
          <span>领票人姓名：</span>
          <p>{{redDashedData.invoiceDetail.receiverName || '--'}}</p>
        </li>
        <li class="invoice-detail-flex">
          <span>领票人邮箱：</span>
          <p>{{redDashedData.invoiceDetail.receiverEmail || '--'}}</p>
        </li>
        <li class="invoice-detail-flex">
          <span>领票人手机：</span>
          <p>{{redDashedData.invoiceDetail.receiverPhone || '--'}}</p>
