</template>

<script>
import filters from '../../filters/index'
import { toChinesNum } from '../../utils/utils'

export default {
  name: 'detail',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    inDetailData: {
      type: Array,
      default: function () {
        return []
      }
    },
    ifRedDashed: { // 一键红冲和重新发送功能（供盘古使用）
      type: Boolean,
      default: false
    }
  },
  filters: filters,
  data () {
    return {
      spanArr: {},
      sendFlag: false,
      sendData: {
        oldEmail: '',
        newEmail: '',
        invoiceId: ''
      },
      redFlag: false,
      redDashedData: {
        invoiceDetail: {}
      },
      checkList: [],
      // 发票红冲状态
      hasOffsetText: ['红冲中', '已红冲', '未红冲', '红冲失败']
    }
  },
  computed: {
    modalValue: {
      get: function () {
        this.dealTableData()
        return this.value
      },
      set: function (val) {
        this.checkList = []
        this.$emit('close', val)
      }
    }
  },
  methods: {
    // 下载发票
    donwloadInvoice (url) {
      window.open(url)
    },
    // 重新发送邮箱确认按钮
    confirmRetrySendHandler () {
      const params = Object.assign({}, this.sendData, {
        receiverEmail: this.sendData.newEmail || ''
      })
      const reg = /^[a-zA-Z0-9_.-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (params.receiverEmail && !reg.test(params.receiverEmail)) {
        this.$Message.warning({
          content: '请输入正确的邮箱地址',
          duration: 5
        })
        return
      }
      this.sendFlag = true
      this.$emit('sendEmail', params)
    },
    // 计算明细表格
    dealTableData () {
      // 计算表格单元格合并数量
      if (this.inDetailData.length === 0) return
      this.spanArr = []
      this.inDetailData.forEach((v, k) => {
        this.spanArr[k] = []
        let number = 0
        let allLength = 0
        if (v.invoiceInfo && v.invoiceInfo.length > 0) {
          v.invoiceInfo.forEach((item, index) => {
            allLength += 1
            // item._check = false
            this.$set(this.inDetailData[k].invoiceInfo[index], '_check', false)
            if (item.hasOffset === 1 || item.hasOffset === 0) {
              // 计算已红冲的条数，如果全部已红冲，一键红冲按钮不展示
              number += 1
            }
            if (index === 0) {
              this.spanArr[k].push(1)
              this.pos = 0
            } else {
              // 判断当前元素与上一个元素是否相同
              if (item.activityName === v.invoiceInfo[index - 1].activityName) {
                console.log('name 仙童', this.spanArr[k], this.pos)
                this.spanArr[k][this.pos] += 1
                this.spanArr[k].push(0)
              } else {
                this.spanArr[k].push(1)
                this.pos = index
              }
            }
          })
        }
        v.allHasOffset = !(allLength === number)
      })
    },
    // 重新发送发票
    sendEmail (row) {
      // 重新发送
      this.sendData.oldEmail = row.invoiceDetail.receiverEmail
      this.sendData.invoiceId = row.invoiceDetail.invoiceId
      this.sendFlag = true
    },
    // 一键红冲
    redDashed (item, index) {
      this.redDashedData = item
      if (!this.redDashedData.invoiceDetail) {
        this.$Message.warning('发票信息为空')
        return
      }
      let arr = []
      let selectPrice = 0
      this.checkList.forEach((val, key) => {
        if (val.invoiceId === this.redDashedData.invoiceDetail.invoiceId) {
          arr.push(val)
          selectPrice += Number(val.courseAmount)
        }
      })
      if (arr.length === 0) {
        this.$Message.warning('请先选择课次后再点击一键红冲')
        return
      }
      this.redDashedData.offsetInfo = arr
      this.redDashedData.selectPrice = selectPrice.toFixed(2)
      this.redFlag = true
    },
    setOffset () {
      let params = {
        invoiceId: this.redDashedData.invoiceDetail.invoiceId,
        offsetInfo: this.redDashedData.offsetInfo,
        invoiceTypeName: this.redDashedData.invoiceDetail.invoiceType === 0 ? '蓝字发票' : '红字发票'
      }
      this.$emit('setOffset', params)
    },
    // 选择要开票的活动
    changeCheck (invoiceItem, index) {
      if (!this.inDetailData[index].invoiceDetail) return
      if (invoiceItem._check) {
        let obj = {
          voucherId: invoiceItem.voucherId,
          classId: invoiceItem.classId,
          invoiceId: this.inDetailData[index].invoiceDetail.invoiceId,
          courseAmount: invoiceItem.amount
        }
        this.checkList.push(obj)
      } else {
        let num = this.checkList.findIndex(item =>
          (item.voucherId === invoiceItem.voucherId &&
            item.classId === invoiceItem.classId &&
            item.invoiceId === this.inDetailData[index].invoiceDetail.invoiceId)
        )
        if (num !== -1) {
          this.checkList.splice(num, 1)
        }
      }
    },
    toCN (num) {
      return toChinesNum(num)
    }
  }
}
</script>

