<template>
  <Modal
    v-model="transFlag"
    :title="title"
    :width="width"
    @on-cancel="cancel"
    @on-ok="confirm"
    :mask-closable="maskClosable"
    :loading="transLoading"
  >
    <div class="tal-trans-wrap" v-bind:style="emptyTitleStyle">
      <Row type="flex" justify="space-around">
        <Col class="col-222">
          <div class="trans-left-wrap">
            <div class="trans-title">
              <Checkbox
                v-model="allCheck"
                :indeterminate="indeterminate"
                @on-change="allCheckChange"
              ></Checkbox>
              <span>{{titles[0]?titles[0]:'全部选项'}}</span>
            </div>
            <div class="trans-select trans-select-left">
              <CheckboxGroup @on-change="checkboxChange" v-model="leftList">
                <Checkbox
                  class="trans-select-checkbox"
                  v-for="item in data"
                  :key="item.key"
                  :label="item.key">
                  <span class="trans-select-span">{{item.title}}</span>
                </Checkbox>
              </CheckboxGroup>
            </div>
          </div>
        </Col>
        <Col class="mlr10 col-middle">
          <div>
            <Button type="primary" size="small" icon="ios-arrow-forward"/>
          </div>
        </Col>
        <Col class="col-222">
          <div class="trans-right-wrap">
            <div class="trans-title">{{titles[1]?titles[1]:'已选择常用选项'}}（{{selectList.length}}/{{data.length}}）</div>
            <div class="trans-select trans-select-right">
              <div v-for="(item,index) in selectList" :key="item.key">
                <p class="trans-select-p">
                  <span class="trans-select-span">{{item.title}}</span>
                  <Icon type="ios-close" class="trans-item-remove-icon" size="20" @click="removeSelect(item,index)"/>
                </p>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  </Modal>
</template>

<script>
export default {
  name: 'TalSelfTransfer',
  props: {
    value: {
      type: Boolean,
      required: true
    },
    maskClosable: {
      type: Boolean,
      default: true
    },
    width: {
      type: [String, Number],
      default: '540'
    },

    title: {
      type: String,
      default: ''
    },
    data: {
      type: Array,
      default () {
        return []
      }
    },
    titles: {
      type: Array,
      default () {
        return []
      }
    },
    targetKeys: {
      type: Array,
      default () {
        return []
      }
    },
    cancelCheckStatus: { // 是否可以取消全部选项 已勾选的全选 当设置为false时 数据为空回显为全部
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      totalMap: {},
      selectList: [],
      leftList: [],
      transLoading: true,
      indeterminate: false,
      allCheck: false,
      emptyTitleStyle: {
        marginTop: !this.title ? '35px' : '0px'
      }
    }
  },
  watch: {
    value () {
      if (this.value) {
        this.rendSelectList()
      }
    },
    data () {
      this.rendDataMap()
    },
    targetKeys () {
      this.rendSelectList()
    }
  },
  methods: {
    rendDataMap () {
      if (this.data.length) {
        let obj = {}
        this.data.forEach(z => {
          obj[z.key] = z
        })
        this.totalMap = obj
      }
    },
    rendSelectList () {
      let ary = []
      let ary1 = []
      const list = (!this.cancelCheckStatus && this.targetKeys.length === 0) ? Object.keys(this.totalMap) : this.targetKeys
      list.forEach(z => {
        const item = this.totalMap[z]
        if (item) {
          ary.push(this.totalMap[z])
          ary1.push(z)
        }
      })
      this.selectList = ary
      this.leftList = ary1
      this.rendCheckStatus(ary1)
    },
    rendCheckStatus (list = []) {
      if (this.data.length !== 0 && list.length === this.data.length) {
        this.allCheck = true
        this.indeterminate = false
      } else if (list.length !== 0 && list.length !== this.data.length) {
        this.allCheck = false
        this.indeterminate = true
      } else {
        this.allCheck = false
        this.indeterminate = false
      }
    },
    removeSelect (item, index) {
      this.leftList.splice(this.leftList.indexOf(item.key), 1)
      this.selectList.splice(index, 1)
      this.rendCheckStatus(this.leftList)
    },
    checkboxChange (list) {
      let ary = []
      list.forEach(z => {
        const item = this.totalMap[z]
        if (item) ary.push(this.totalMap[z])
      })
      this.selectList = ary
      this.rendCheckStatus(list)
    },
    resetLoading () {
      this.transLoading = false
      this.$nextTick(() => {
        this.transLoading = true
      })
    },
    cancel () {
      this.resetLoading()
      this.$emit('close')
    },
    confirm () {
      const list = this.selectList.map(z => {
        return z.key
      })

      this.$emit('setList', list,
        () => {
          this.cancel()
        },
        () => {
          this.resetLoading()
        })
    },
    allCheckChange (val) {
      if (!val) {
        if (!this.cancelCheckStatus) {
          this.$nextTick(() => {
            this.allCheck = true
          })
          return
        }
        this.leftList = []
        this.selectList = []
        this.allCheck = false
        this.indeterminate = false
      } else {
        this.leftList = Object.keys(this.totalMap)
        this.selectList = JSON.parse(JSON.stringify(this.data))
        this.allCheck = true
        this.indeterminate = false
      }
    }
  },
  computed: {
    transFlag: {
      get () {
        return this.value
      },
      set () {
        this.$emit('close')
      }
    }
  },
  mounted () {
    this.rendDataMap()
  }
}
</script>

