<template>
  <div>
    <!--查看分期-->
    <Modal
      :mask-closable="false"
      title="查看分期"
      v-model="modalValue"
      class="modal-height1"
      :z-index="9999"
      width="960px">
      <Row style="padding: 15px;margin-top: 15px;border: 1px solid gainsboro"
           v-for="(item,index) in stageArr" :key="index">
        <Col span="6">
          <span style="margin-right: 10px">{{item.stagesName}}</span>
          <span style="display: inline-block;margin-left: 30px">第{{item.startCurriculumNum}}节到第{{item.endCurriculumNum}}节</span>
        </Col>
        <Col span="6">
          <span style="margin-right: 10px">开课时间</span>
          <span>{{item.startCurriculumTime}}</span>
        </Col>
        <Col span="6">
          <span style="margin-right: 10px">结课时间</span>
          <span>{{item.endCurriculumTime}}</span>
        </Col>
        <Col span="6">
          <span style="margin-right: 10px">价格</span>
          <span>{{item.price}}</span>
        </Col>
      </Row>
      <div slot="footer">
        <Button @click="close">关闭</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'seeStage',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    classDetailMessage: {
      type: Object,
      default: function () {
        return {}
      }
    },
    stageArr: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {}
  },
  methods: {
    close () {
      this.modalValue = false
    }
  },
  mounted () {

  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  }
}
</script>

