<template>
  <div>
    <Modal
      :mask-closable="false"
      title="查看退费申请"
      @on-visible-change="handleVisibleChange"
      v-model="modalValue"
      width="960px">
      <div class="table-wrapper">
        <p class="tip-text">*如对退费课次有疑问，需修正课次特殊退费，请驳回后让家长联系前台直接在盘古系统中退费。</p>
        <Table size="large" :columns="tableColumns" :data="tableData" style="margin-top: 20px"></Table>
      </div>
      <div slot="footer">
        <div class="modal-footer">
          <Form
            inline
            ref="formMessage"
            :model="formMessage"
            label-position="right"
            :label-width="80">
            <FormItem label="审核结果" prop="appealResult" :rules="{ validator: validateAppeal ,trigger: 'blur | change'}">
              <Cascader
                v-model="formMessage.appealResult"
                change-on-select
                :data="treeData"
                placeholder="请选择审核结果"
                style="width: 300px;">
              </Cascader>
            </FormItem>
            <FormItem label="备注" prop="remark" :rules="{required: true, message: '备注必填', trigger: 'blur|change'}">
              <Input style="width: 250px;" v-model.trim="formMessage.remark" placeholder="请输入备注" :maxlength="100" clearable />
            </FormItem>
          </Form>
        </div>
        <Button type="primary" :loading="saveRefunding" @click="saveRefundAppeal">保存</Button>
      </div>
    </Modal>
    <combine-refund-appeal
      v-model="combineRefundAppealModal"
      :refund-class-data="refundClassData"
      :combine-class-data="combineClassData"
      :appeal-result="formMessage.appealResult"
      :remark="formMessage.remark"
      :area-code="areaCode"
      :city-code="cityCode"
      :user-number="userNumber"
      :student-id="studentId"
      @success="handleCombineSuccess"
      @close="combineRefundAppealModal = false">
    </combine-refund-appeal>
  </div>
</template>

<script>
import CombineRefundAppeal from './combine-refund'
import { getRefundDetailByRegistId, getComboRefundDetail, getRefundReasonTree, refundOperate } from '../../api/refund'
export default {
  name: 'TalRefundAuditReasonDetails',
  components: { CombineRefundAppeal },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    registId: {
      type: String,
      default: function () {
        return ''
      }
    },
    areaCode: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    userNumber: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    studentId: {
      type: [String, Number],
      default: function () {
        return ''
      }
    },
    cityCode: {
      type: [String, Number],
      default: function () {
        return ''
      }
    }
  },
  data () {
    return {
      tableColumns: [
        { title: '退费申请单号', key: 'applyId', align: 'center' },
        {
          title: '申请时间',
          render: (h, params) => {
            const date = new Date(parseInt(params.row.createTime))
            const year = date.getFullYear()
            const month = date.getMonth() + 1
            const day = date.getDate()
            const hour = date.getHours()
            const minute = date.getMinutes()
            const second = date.getSeconds()
            return h('span', {}, `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day} ${hour < 10 ? '0' + hour : hour}:${minute < 10 ? '0' + minute : minute}:${second < 10 ? '0' + second : second}`)
          },
          align: 'center'
        },
        { title: '退费申请一级原因', key: 'refundFirstReason', align: 'center' },
        { title: '退费申请二级原因', key: 'refundReason', align: 'center' },
        { title: '实缴金额', key: 'paymentAmount', align: 'center' },
        { title: '应扣学费', key: 'endClassAmount', align: 'center' },
        { title: '应扣材料费', key: 'shouldMaterialsFeeAmount', align: 'center' },
        {
          title: '应退课次号',
          key: 'shouldRefundCurriculums',
          align: 'center'
        },
        { title: '应退金额', key: 'shouldRefundAmount', align: 'center' },
        { title: '优惠名称', key: 'promotionName', align: 'center' }
      ],
      tableData: [],
      formMessage: {
        appealResult: [],
        remark: ''
      },
      treeData: [],
      combineRefundAppealModal: false,
      refundClassData: [],
      combineClassData: {},
      saveRefunding: false
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.show
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  },
  methods: {
    handleVisibleChange (val) {
      if (val) {
        let req = { registId: this.registId }
        getRefundDetailByRegistId(req).then(response => {
          response.registId = this.registId
          response.areaCode = this.areaCode
          this.tableData = [response]
        }).catch(error => {
          this.$Message.error({ content: '获取数据失败：' + error.message, duration: 5 })
        })
        getRefundReasonTree().then(response => {
          this._normalizeData(response)
          this.treeData = response
        }).catch(error => {
          this.$Message.error('获取审核结果列表失败: ' + error.message)
        })
      } else {
        this.tableData = []
        this.$refs.formMessage.resetFields()
      }
    },
    _normalizeData (tree) {
      tree.forEach(item => {
        const node = item
        node.value = item.id
        node.label = item.reason || item.name
        if (node.children && node.children.length > 0) {
          this._normalizeData(node.children)
        }
      })
    },
    saveRefundAppeal () {
      this.$refs.formMessage.validate(valid => {
        if (valid) {
          if (this.formMessage.appealResult[0] === '1') {
            const params = {
              registId: this.tableData[0].registId,
              stuUid: this.tableData[0].stuUid,
              area: this.tableData[0].areaCode
            }
            this.saveRefunding = true
            getComboRefundDetail(params).then(response => {
              if (response.comboType === 1) {
                this.refundClassData = this.tableData
                if (response.refundDetailDtos.length && response.refundDetailDtos.length === 1) {
                  if (response.refundDetailDtos[0].classId === this.tableData[0].classId) {
                    this.handleUpdateRefund()
                    return
                  }
                } else {
                  const combineClassData = {}
                  response.refundDetailDtos.forEach(item => {
                    if (!combineClassData.hasOwnProperty(item.promotionId)) {
                      combineClassData[item.promotionId] = {
                        name: item.promotionName,
                        data: []
                      }
                    }
                    combineClassData[item.promotionId].data.push(item)
                  })
                  this.combineClassData = combineClassData
                }
                this.combineRefundAppealModal = true
                this.saveRefunding = false
              } else {
                this.handleUpdateRefund()
              }
            }).catch(error => {
              console.log(error)
              this.$Message.error('获取组合退费详情失败：' + error.message)
              this.saveRefunding = false
            })
          } else if (this.formMessage.appealResult[0] === '2') {
            this.saveRefunding = true
            this.handleUpdateRefund()
          }
        }
      })
    },
    validateAppeal (rule, value, callback) {
      if (!value || value.length === 0) {
        callback(new Error('审核结果必填'))
      } else if (value.length === 1) {
        if (value[0] === '1') {
          callback(new Error('请选择一级原因'))
        } else {
          callback(new Error('请选择驳回原因'))
        }
      } else if (value.length === 2) {
        if (value[0] === '1') {
          callback(new Error('请选择二级原因'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    },
    handleUpdateRefund () {
      const appealResult = {
        reviewResult: '',
        refundReasonOne: '',
        refundReasonTwo: '',
        rejectReason: ''
      }
      appealResult.reviewResult = this.formMessage.appealResult[0]
      if (appealResult.reviewResult[0] === '1') {
        appealResult.refundReasonOne = this.formMessage.appealResult[1]
        appealResult.refundReasonTwo = this.formMessage.appealResult[2]
      } else if (appealResult.reviewResult === '2') {
        appealResult.rejectReason = this.formMessage.appealResult[1]
      }
      const sampleUpdateReqs = []
      const item = {
        sourcePlatform: '',
        refundId: this.tableData[0].applyId,
        reviewResult: appealResult.reviewResult,
        refundReasonOne: appealResult.refundReasonOne,
        refundReasonTwo: appealResult.refundReasonTwo,
        rejectReason: appealResult.rejectReason,
        remark: this.formMessage.remark,
        comboId: this.tableData[0].submitId,
        registId: this.tableData[0].registId
      }
      sampleUpdateReqs.push(item)
      const params = {
        handler: this.userNumber,
        studentId: this.studentId,
        // area: this.areaCode, 全国版退费问题修复
        area: this.cityCode === '1111' ? this.cityCode : this.areaCode,
        sampleUpdateReqs
      }
      refundOperate(params).then(() => {
        this.$Message.success('更新成功')
        this.modalValue = false
        this.$emit('success')
        this.saveRefunding = false
      }).catch(error => {
        this.$Message.error('更新退费状态失败: ' + error.message)
        this.saveRefunding = false
      })
    },
    handleCombineSuccess () {
      this.modalValue = false
      this.$emit('success')
    }
  }
}
</script>

<style lang="less" scoped>
  .table-wrapper {
    .tip-text {
      color: #000;
    }
  }
  /deep/ .ivu-modal-footer {
    height: 70px;
  }
  .modal-footer {
    float: left;
    text-align: left;
  }
</style>
