<!-- OMO等价转班风险提醒 -->

<template>
    <Modal
      :mask-closable="false"
      title="风险提示"
      v-model="modalValue"
      width="560px">
        <p class="warn">OMO等价转班，涉及财务风险，请和运营主管确认是否可操作转班</p>
        <div>
            <p>原班班信息: {{ sourseClassDetail.name }}</p>
            <p>总金额: {{ sourseClassDetail.price}}（包含报名费：{{ sourseClassDetail.feetype === 1 ? '否' : '是'}}）</p>
            <p>授课类型: {{ classType[sourseClassDetail.classType]}}</p>
            <p v-if="promotionArray.length > 0 || couponArray.length">原班参与活动名称: </p>
            <p class="activity" v-for="(item, index) in promotionArray" :key="index">-{{ item.promotionName }}，优惠金额：{{ item.promotionActualPrice }}</p>
            <p class="activity" v-for="(item, index) in couponArray" :key="index">-优惠券，优惠金额：{{ item.couponActualValue }}</p>
        </div>
        <Divider />
        <div>
            <p>目标班信息: {{ targetClassDetail.name }}</p>
            <p>总金额: {{ targetClassDetail.price}}（包含报名费：{{ sourseClassDetail.feetype === 1 ? '否' : '是'}}）</p>
            <p>授课类型: {{ classType[targetClassDetail.classType]}}</p>
        </div>
        <div slot="footer">
            <Button @click="confirmChangeClass" type="primary">确定转班</Button>
            <Button @click="modalValue=false">关闭</Button>
        </div>
    </Modal>
</template>

<script>
import { getOMOChangeClass } from '../../api/register'
export default {
  name: 'TalOmoChangeclassRiskwarningModal',
  data () {
    return {
      modalValue: false,
      params: {
        registId: '', // 原班报名ID--
        sourceClassId: '', // 原班id --
        targetClassId: '', // 目标班id
        operationType: '', // 操作类型（0:普通转班 1:特殊转班 7:精品课转班 8:全国班转班 9:分班转班）
        targetNum: null, // 转班起始课次
        studentUid: '' // 学员UID --
      },
      sourseClassDetail: {},
      targetClassDetail: {},
      promotionArray: [],
      couponArray: [], // 优惠券
      classType: {
        1: '在线',
        2: '双师',
        4: '面授',
        8: '线上班'
      }
    }
  },
  methods: {
    confirmChangeClass () {
      this.modalValue = false
      this.$emit('confirm-change-class', this.params)
    },
    getOMOChangeClass (params) {
      // params = {
      //   registId: '683f3aa2918b4c17aae8121e8d56d24d',
      //   sourceClassId: '55dbf91c78f3442798b64415f93fac90',
      //   targetClassId: '84e362ca0d4f4dc2a7989b7b890211d7',
      //   operationType: '0',
      //   targetNum: 1,
      //   studentUid: '851727638'
      // }

      console.log('params', params)

      this.params = params

      return getOMOChangeClass(params).then(res => {
        // omo等价转班
        if (res.validateOmo) {
          this.modalValue = true
          const { sourseClassDetail, targetClassDetail, promotionArray, couponArray } = res
          this.sourseClassDetail = sourseClassDetail
          this.targetClassDetail = targetClassDetail
          this.promotionArray = promotionArray || []
          this.couponArray = couponArray || []
        } else {
          return true
        }
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.warn {
  color: red;
  font-size: 16px;
}

.activity {
  padding-left: 50px;
}
</style>
