<template>
    <Modal
      :mask-closable="false"
      class="modal-height1"
      title="优惠券详情"
      v-model="modalValue"
      @on-visible-change="inModal"
      width="960px">
      <Spin fix v-if="loading"></Spin>
        <h3>券基本信息</h3>
        <Row type="flex">
            <Col class="detail-item-label"><span>分校</span></Col>
            <Col class="detail-item" style="width: 260px;">{{this.couponDetail.areaCodeName}}</Col>
        </Row>
        <Row type="flex">
            <Col class="detail-item-label"><span>优惠券批次号</span></Col>
            <Col class="detail-item" style="width: 260px;">{{this.couponDetail.releaseNumber}}</Col>
        </Row>
        <Row type="flex">
        <Col class="detail-item-label"><span>优惠劵名称</span></Col>
        <Col class="detail-item" style="width: 600px;">
            {{this.couponDetail.name}}
        </Col>
        </Row>
        <Row type="flex" style="padding:1em 0;">
        <Col class="detail-item-label-des"><span>优惠券说明</span></Col>
        <Col class="detail-item-des" style="flex:1;">
            <pre>{{this.couponDetail.description}}</pre>
        </Col>
        </Row>
        <Row type="flex">
        <Col class="detail-item-label"><span>优惠券分期</span></Col>
        <Col class="detail-item" style="width: 600px;">
            {{this.couponDetail.stageNumName}}
        </Col>
        </Row>
        <Row type="flex">
        <Col class="detail-item-label"><span>时间类型</span></Col>
        <Col class="detail-item" style="width: 260px;">
            {{this.couponDetail.dateTypeName}}
        </Col>
        </Row>
        <Row type="flex" v-if="this.couponDetail.dateType === 2">
          <Col class="detail-item-label"><span>领劵周期</span></Col>
          <Col class="detail-item" style="width: 260px;">
            {{this.couponDetail.gainDay}}
          </Col>
        </Row>
        <Row type="flex" v-if="this.couponDetail.dateType === 1">
        <Col class="detail-item-label"><span>有效期</span></Col>
        <Col class="detail-item">
            {{this.couponDetail.validDate}}
        </Col>
        </Row>
        <h3>券详细信息</h3>
        <Row type="flex">
            <Col class="detail-item-label"><span>优惠劵类型</span></Col>
            <Col class="detail-item" style="width: 260px;">
              {{this.couponDetail.typeName}}
            </Col>
        </Row>
          <Row type="flex" v-if="this.couponDetail.type === 3 || this.couponDetail.type === 4">
            <Col class="detail-item-label"><span>消费金额</span></Col>
            <Col class="detail-item" style="width: 260px;">
              {{this.couponDetail.priceCondition}}
            </Col>
          </Row>
          <Row type="flex" v-if="this.couponDetail.type === 6 || this.couponDetail.type === 4 || this.couponDetail.type === 5">
