            {{this.couponDetail.amount}}
        </Col>
        </Row>
        <Row type="flex">
        <Col class="detail-item-label"><span>优惠券用途</span></Col>
        <Col class="detail-item" style="width: 120px;">
            {{this.couponDetail.gainTypeName}}
        </Col>
        <Col class="detail-item" v-if="this.couponDetail.gainType !== 1" style="display: flex;">
            <span style="width: 120px">每个账户装券张数</span>
            <Col class="detail-item" style="width: 30px;">
            {{this.couponDetail.maxCount}}
            </Col>
            <span class="detail-item" style="margin-left: 10px;">张</span>
        </Col>
        </Row>
        <Row type="flex">
        <Col class="detail-item-label"><span>优惠券数量</span></Col>
        <Col class="detail-item" style="width: 120px;">
            {{this.couponDetail.count}}
            <span>张</span>
        </Col>
