        <h3>券适用范围</h3>
        <Row type="flex">
            <Col class="detail-item-label"><span>适用渠道</span></Col>
            <Col class="detail-item">
              {{this.couponDetail.platformName}}
            </Col>
        </Row>
        <Row type="flex">
            <Col class="detail-item-label"><span>适用班级范围</span></Col>
            <Col class="detail-item" style="width: 260px;">
              {{this.couponDetail.scopeTypeName}}
            </Col>
        </Row>
        <Row type="flex">
        <Col offset="2" style="width:88%">
            <Table v-if="this.couponDetail.scopeType === 1" :columns="couponColumns" :data="this.couponDetail.couponClass"></Table>
            <Table v-if="this.couponDetail.scopeType === 2" :columns="applyIdColumns" :data="this.couponDetail.couponCourse"></Table>
        </Col>
        </Row>
        <Row>
            <Col class="detail-item-label"><span>适用地区</span></Col>
          </Row>
          <Row>
            <Col offset="2">
            <div style="border: 1px solid #dcdee2; width: 96%; padding: 15px; border-radius: 3px">
              <div v-for="(value, index) in this.couponDetail.couponArea" :key="index">
                <Tag>{{value.cityName+''+value.serviceCenterName+''+value.stationName}}</Tag>
              </div>
            </div>
            </Col>
        </Row>
      <div slot="footer">
        <Button style="margin:0 auto;display:block" @click="modalValue = false">关闭</Button>
      </div>
    </Modal>
</template>
<script>
import { getCouponDetail } from '../../api/refund'
export default {
  name: 'TalCouponDetail',
  props: {
    showCoupon: {
      type: Boolean,
      default: false
    },
    releaseId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      couponColumns: [
        { 'title': '授课类型', 'key': 'classTypeName', 'align': 'center', 'width': 80 },
        { 'title': '业务类型', 'key': 'bizTypeName', 'align': 'center', 'width': 80 },
        { 'title': '年 份', 'key': 'year', 'align': 'center', 'width': 60 },
        { 'title': '学 期', 'key': 'termName', 'align': 'center', 'width': 80 },
        { 'title': '年 部', 'key': 'gtName', 'align': 'center', 'width': 100 },
        { 'title': '年 级', 'key': 'gradeName', 'align': 'center', 'width': 100 },
        { 'title': '科 目', 'key': 'subjectName', 'align': 'center', 'width': 100 },
        { 'title': '班次名称', 'key': 'classLevelName', 'align': 'center', 'width': 100 },
        { 'title': '容量类别', 'key': 'capacityLabelName', 'align': 'center', 'width': 100 }
      ],
      applyIdColumns: [
        { 'title': '序号', 'type': 'index', 'align': 'center', 'width': 60 },
        { 'title': '课程ID', 'key': 'id', 'align': 'center', 'min-width': 100 },
        { 'title': '课程名称', 'key': 'name', 'align': 'center', 'min-width': 100 }
      ],
      loading: false,
      couponDetail: []
    }
  },
  methods: {
    getCouponDetail () {
      this.loading = true
      getCouponDetail({
        releaseId: this.releaseId
      }).then((res) => {
        this.couponDetail = Object.freeze(res) || []
        this.loading = false
      }).catch((err) => {
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
        this.modalValue = false
      })
    },
    inModal (val) {
      if (val) {
        this.getCouponDetail()
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.showCoupon
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  }
}
</script>
