<template>
  <Modal
    v-model="modalValue"
    title="短信发送"
    :z-index="9999"
    @on-visible-change="handleVisibleChange"
    class-name="vertical-center-modal"
  >
    <div>
      <span style="padding-right:20px;">接收人：</span>
      <Input v-model="phone" placeholder="请输入..." style="width: 300px" clearable></Input>
    </div>
    <div style="margin-top:20px; display: flex">
      <span style="padding-right:8px;">短信内容：</span>
      <div style="font-size:14px">
        <p>【学而思培优】家长您好：</p>
        <Input v-model="messageText" type="textarea" :disabled="isMessageDisabled" placeholder="短信内容" style="width: 400px;margin: 5px 0" :autosize="{maxRows: 6,minRows: 2}"/>
        <p>感谢您对学而思的支持。</p>
      </div>
    </div>
    <div slot="footer">
      <Button type="primary" :loading="modalLoading" @click="sendUrl">确认发送</Button>
    </div>
  </Modal>

</template>

<script>
import { getMsgTemplate, getSecretPhoneNum, sendMsg } from '../../../api/clazz'
import { validatePhoneNumber } from '../../../utils/validate'

export default {
  name: 'SendPositionMsg',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    serviceCenterName: {
      type: String,
      default: ''
    },
    serviceCenterAddress: {
      type: String,
      default: ''
    },
    serviceCenterPhone: {
      type: String,
      default: ''
    }
  },
  inject: ['isMessageDisabled'],
  data () {
    return {
      phone: '',
      messageText: '',
      contentPre: '',
      modalLoading: false
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function () {
      }
    }
  },
  methods: {
    handleVisibleChange (val) {
      if (val) {
        this.getModalData()
      }
    },
    getModalData () {
      this.messageText = ''
      this.contentPre = ''
      const { serviceCenterName, serviceCenterAddress } = this
      const params = {
        type: 1,
        serviceCenterAddress,
        serviceCenterName
      }
      getMsgTemplate(params).then(data => { // 获取短信模版
        this.messageText = data
        this.contentPre = data
      }).catch(err => {
        console.log(err)
        this.$Message.warning('短信模版内容获取失败，请重试。')
      })
    },
    async sendUrl () { // 发送短信
      if (this.phone.length !== 11) {
        this.$Message.warning('请输入正确的手机号码')
        return
      }
      if (!this.messageText) {
        this.$Message.warning('短信内容不能为空')
        return
      }
      const params = {
        content: this.messageText,
        type: 1,
        contentPre: this.contentPre
      }
      this.modalLoading = true
      let response
      try {
        if (validatePhoneNumber(this.phone)) {
          response = await getSecretPhoneNum({ phone: this.phone })
          if (response) {
            params.phone = response
          }
        } else {
          params.phone = this.phone
        }
        response = await sendMsg(params)
        if (response.rlt) {
          this.$Message.success('发送成功')
          this.handleClose()
        } else {
          this.$Message.error(response.message)
        }
      } catch (err) {
        console.log(err)
        this.$Message.error('发送失败，请重试。')
      } finally {
        this.modalLoading = false
      }
    },
    handleClose () {
      this.phone = ''
      this.messageText = ''
      this.contentPre = ''
      this.$emit('close')
    }
  }
}
</script>

<style scoped>
.sendPositionMsg {
}
</style>
