          </Col>
        </Row>
        <Row class="row">
          <Col span="12">
            <span class="spa">毕业院校</span>
            <span class="mess">{{teacher.graduationSchool}}</span>
          </Col>
          <Col span="12">
            <span class="spa">专业</span>
            <span class="mess">{{teacher.profession}}</span>
          </Col>
        </Row>
        <Row>
          <Col span="12">
            <span class="spa">学历</span>
            <span class="mess">{{teacher.schoolDegreeName}}</span>
          </Col>
          <Col span="12">
