    </div>
    <div class="info-wrapper">
      <div class="title-wrapper">
        <span class="icon"></span>
        <span class="title">教学特点</span>
      </div>
      <div class="context" v-html="teacher.features"></div>
    </div>
    <div class="info-wrapper">
      <div class="title-wrapper">
