      </div>
      <div v-if="teacher.url">
        <a :href="teacher.url" target="_blank" style="margin-left:15px;margin-right: 10px;font-size: 14px">{{teacher.url}}</a>
        <Button type="primary" size="small" @click="copy(teacher.url)">复制</Button>
      </div>
      <div class="video-wrapper" v-if="teacher.videoUrl.includes('<')" v-html="teacher.videoUrl">
      </div>
      <div class="video-wrapper" v-else>
        <video :src="teacher.videoUrl" width="720" controls></video>
      </div>
    </div>
  </div>
</template>

<script>
import { copyText } from '../../../utils/utils'
import { queryTeacherByTeaId } from '../../../api/teacher'
export default {
  name: 'teacherBaseInfo',
  data () {
    return {
      loading: false,
      teacher: {}
    }
  },
  props: {
    schoolCode: { // 老师分校id
      type: String,
      required: true
    },
    teacherId: { // 老师id
      type: String,
      required: true
    },
    teacherEmpNo: { // 教师员工编号
      type: String,
      default: ''
    }
  },
  methods: {
    async initView () {
      this.loading = true
      this.teacher = {}
      try {
        this.teacher = await queryTeacherByTeaId({
          schoolCode: this.schoolCode,
          teacherId: this.teacherId,
          teacherEmpNo: this.teacherEmpNo
        })
        this.$emit('teacherInfo', this.teacher)
      } catch (e) {
        this.$emit('errMsg', e)
      } finally {
        this.loading = false
      }
    },
    copy (text) {
      copyText(text)
    }
  }
}
</script>

