<template>
  <div style="position: relative;">
    <Button class="btn-invoice" @click.stop="handleInvoice">一键开票</Button>
    <div class="ivu-table ivu-table-default ivu-table-border relative">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <CheckboxGroup
          style="font-size: 12px"
          v-model="checkAllGroup"
          @on-change="handleCheckGroupChange">
          <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
            <thead>
              <tr class="ivu-table-row">
                <th
                  v-for="(item,index) in tableColumns"
                  :key="index"
                  class="ivu-table-column-center"
                  :width="item.width"
                >
                  <template v-if="item.isCheckBox">
                    <label class="tal-ipt-checkbox" style="margin-left: 0">
                      <input
                        type="checkbox"
                        class="ivu-checkbox-inner"
                        style="margin-left: -9px;"
                        :checked="checkAll"
                        @click="checkAllHandler"
                      />
                      <span></span>
                    </label>
                  </template>
                  <div class="ivu-table-cell" v-else>{{item.title}}</div>
                </th>
              </tr>
            </thead>
            <tbody class="ivu-table-tbody">
              <tr class="ivu-table-row" v-for="(item) in tableData" :key="item.voucherId">
                <!-- 活动名称 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">
                    {{ item.name }}
                  </div>
                </td>
                <!-- 凭证ID -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">
                    {{ item.voucherId }}
                  </div>
                </td>
                <!-- 年级 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.gradeName }}</div>
                </td>
                <!-- 学科 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.subjectName }}</div>
                </td>
                <!-- 活动类型 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.typeName  }}</div>
                </td>

                <!-- 缴费时间 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.createTime || '--' }}</div>
                </td>
                <!-- 使用班级 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.registerIds || '--' }}</div>
                </td>
                <!-- 状态 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{getStatusName(item.status)}}</div>
                </td>
                <!-- 退费时间 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{ item.refundTime || '--' }}</div>
                </td>
                <!-- 发票状态 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">
                    <template v-if="item.invoiceStatus && item.invoiceStatus.length">
                      <p v-for="(text, index) in item.invoiceStatus" :key="index">{{ text }}</p>
                    </template>
                    <!-- 未使用或者已退费 -->
                    <p v-if="item.status === 1 || item.status === 3" style="color: gray;">联报金未使用，不支持开发票</p>
                  </div>
                </td>
                <!-- 失败原因 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">
                    <p v-for="(text, index) in item.failReasons" :key="index">{{ text }}</p>
                  </div>
                </td>
                <!-- 操作 -->
                <td class="ivu-table-column-center">
                  <a
                    href="javascirpt:void(0);"
                    v-if="item.haveDetail"
                    @click="viewInvoiceDetail(item.voucherId)"
                  >查看发票</a>
                </td>
                <!-- 选择 -->
                <td class="ivu-table-column-center">
                  <div class="ivu-table-cell">
                    <Checkbox
                      :label="item.label"
                      v-if="item.operational === 1"
                      :disabled="disabledCheckBox(item.label)"
                    >{{ }}</Checkbox>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </CheckboxGroup>
        <div v-show="!tableData.length">
          <p style="text-align: center;line-height: 50px;width: 100%">
            <span>暂无数据</span>
          </p>
        </div>
      </div>
    </div>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }" v-show="!tableLoading">
      <div style="float: right;">
        <Page
          :total="pageTotal"
          :page-size="currentPageSize"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        ></Page>
      </div>
    </div>
    <!-- 一键开票 -->
    <Modal v-model="invoiceModal" footer-hide :width="1200" :transfer="false" title="一键开票">
      <TalFinanceInvoiceToolScholarship
        v-if="invoiceModal"
        :city-code="cityCode"
        :uid="uid"
        :student-no="card"
        :student-name="realName"
        :student-id="studentId"
        :openInvoiceParams="calOpenInvoiceParams"
      ></TalFinanceInvoiceToolScholarship>
    </Modal>
    <!-- 查看发票详情 -->
    <TalInvoiceQueryDetailScholarship v-bind="invoiceDetail" :cityCode="cityCode"></TalInvoiceQueryDetailScholarship>
  </div>
</template>

<script>
import { getVoucherList } from '../../api/voucher'
import { copyText } from '../../utils/utils'
import TalFinanceInvoiceToolScholarship from '../tal-finance-invoice-tool-scholarship'
import TalInvoiceQueryDetailScholarship from '../tal-invoice-query-detail-scholarship'
const SEPARATOR = '|'
export default {
  name: 'TalFinanceScholarship',
  components: {
    TalFinanceInvoiceToolScholarship,
    TalInvoiceQueryDetailScholarship
  },
  props: {
    formData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    uid: {
      type: [Number, String],
      default: ''
    },
    cityCode: {
      type: String,
      default: ''
    },
    studentId: {
      type: String,
      required: true
    },
    realName: {
      type: String,
      required: true
    },
    card: {
      type: String,
      required: true
    }
  },
  computed: {
    // 一键开票查看详情入参计算
    calOpenInvoiceParams () {
      let params = []
      this.checkAllGroup.forEach(item => {
        // `${item.voucherId}${SEPARATOR}${item.depositAmount}${SEPARATOR}${item.activityId}${SEPARATOR}${item.name}${classIds}`
        let arrs = item.split(SEPARATOR)
        let classIds = []
        for (let i = 4; i < arrs.length; i++) {
          classIds.push(arrs[i])
        }
        let param = {
          voucherId: arrs[0],
          depositAmount: arrs[1],
          activityId: arrs[2],
          name: arrs[3],
          classIds
        }
        params.push(param)
      })
      return params
    }
  },
  data () {
    return {
      tableColumns: Object.freeze([
        {
          title: '活动名称'
          // width: 150
        },
        {
          title: '凭证ID'
          // width: 200
        },
        {
          title: '年级'
          // width: 100
        },
        {
          title: '学科'
          // width: 100
        },
        {
          title: '活动类型'
          // width: 100
        },
        {
          title: '缴费时间'
          // width: 100
        },
        {
          title: '使用班级（报名ID）'
          // width: 100
        },
        {
          title: '状态'
          // width: 100
        },
        {
          title: '退费时间'
          // width: 165
        },
        {
          title: '发票状态'
          // width: 165
        },
        {
          title: '失败原因'
          // width: 165
        },
        {
          title: '操作'
          // width: 165
        },
        {
          title: 'checkbox',
          isCheckBox: true,
          width: 150
        }
      ]),
      tableData: [],
      tableLoading: false,
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数

      checkAllGroup: [], // checkboxGroup选中项
      allItem: [], // 所有可选项
      checkAll: false, // 是否全选
      maxClassNum: 10, // 最多勾选10个
      invoiceModal: false,
      invoiceDetail: {
        detailParams: {
          voucherId: null
        },
        ifRedDashed: true
      }
    }
  },
  methods: {
    // 查看发票详情
    viewInvoiceDetail (voucherId) {
      Object.assign(this.invoiceDetail.detailParams, {
        voucherId
      })
    },
    /****
     * 一键开票
     */
    handleInvoice () {
      if (this.checkAllGroup.length === 0) {
        this.$Message.warning({ content: '请选择' })
        return
      }
      this.invoiceModal = true
    },
    // 禁选 10个班级
    disabledCheckBox (label) {
      let overLimit = this.checkAllGroup.length >= this.maxClassNum
      if (this.checkAllGroup.indexOf(label) === -1 && overLimit) return true
      return false
    },
    // 全选
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (this.allItem.length === 0) {
        this.$nextTick(() => {
          this.checkAll = false
        })

        return this.$Message.warning({ content: '没有可选项' })
      }
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        // 全选，限制班级数量为10
        this.allItem.forEach((item) => {
          if (this.checkAllGroup.indexOf(item) === -1 &&
            this.checkAllGroup.length < this.maxClassNum) {
            this.checkAllGroup.push(item)
          }
        })
      }
    },
    // checkGroup on-change
    handleCheckGroupChange () {
      // 是否处于全选状态
      let checkAll = true
      if (this.allItem.length <= this.maxClassNum) {
        if (this.checkAllGroup.length !== this.allItem.length) checkAll = false
      }

      if (this.allItem.length > this.maxClassNum) {
        if (this.checkAllGroup.length !== this.maxClassNum) checkAll = false
      }

      this.checkAll = checkAll
    },
    // 重置可选操作
    resetChecks () {
      this.checkAll = false
      this.checkAllGroup = []
      this.allItem = []
    },
    // 设置可选项
    setCheckableItem (data) {
      const allItem = []
      data.records.forEach(item => {
        if (item.operational === 1) {
          let classIds = SEPARATOR + 'null'
          if (item.classIdList && item.classIdList.length) {
            classIds = item.classIdList.map(classid => classid).reduce((p, c) => p + SEPARATOR + c, '')
          }
          const label = `${item.voucherId}${SEPARATOR}${item.depositAmount}${SEPARATOR}${item.activityId}${SEPARATOR}${item.name}${classIds}`
          item.label = label // 'voucherId|classid1|classid2'
          allItem.push(label)
        }
      })
      this.allItem = Object.freeze(allItem)
    },
    // 获取授课类型
    getStatusName (type) {
      switch (type) {
        case 1: return '未使用'
        case 2: return '已使用'
        case 3: return '已退费'
        default: return '--'
      }
    },
    // 复制班级报名id
    copy (text) {
      copyText(text)
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    // 查询奖学金列表
    async getTableData () {
      this.tableLoading = true
      this.tableData = []
      this.resetChecks()
      const params = {
        studentUid: this.uid,
        cityCode: this.cityCode,
        pageNo: this.currentPageNum,
        pageSize: this.currentPageSize,
        status: this.formData.scholarshipStatus ? this.formData.scholarshipStatus : undefined,
        gradeId: this.formData.gradeId ? this.formData.gradeId : undefined,
        subjectId: this.formData.subject ? this.formData.subject : undefined
      }
      try {
        const data = await getVoucherList(params)
        this.setCheckableItem(data)
        this.tableData = Object.freeze(data.records)
        this.pageTotal = data.totalCount
      } catch (error) {
        this.tableData = []
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
      this.tableLoading = false
    }
  },
  watch: {
    checkAllGroup (n, o) {
      if (n.length === this.maxClassNum) {
        this.$Message.warning({ content: `最多可选${this.maxClassNum}条`, duration: 5 })
      }
    }
  },

  mounted () {
    // this.getTableData()
  }
}
</script>

<style lang="less" scoped>
.btn-invoice {
  position: absolute;
  right: 50px;
  top: 0;
  transform: translate(0, -50px);
}
.ivu-table:after,
.ivu-table:before {
  height: 0;
  width: 0;
}
.gary {
  color: #ccc;
}
.orange {
  color: #f59a23;
}
</style>
