<template>
  <div>
    <!--<BaseSearch></BaseSearch>-->
    <div class="ivu-table ivu-table-default ivu-table-border relative">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
          <thead>
            <tr class="ivu-table-row">
              <th
                v-for="(item,index) in tableColumns"
                :key="index"
                class="ivu-table-column-center"
                :width="item.width"
              >
                <div class="ivu-table-cell">{{item.title}}</div>
              </th>
            </tr>
          </thead>
          <tbody class="ivu-table-tbody">
            <template v-for="(item) in tableData">
              <template v-if="item.stageList && item.stageList.length">
                <tr
                  class="ivu-table-row"
                  v-for="(item2,index2) in item.stageList"
                  :key="item2.workOrderId"
                >
                  <template v-if="index2 === 0">
                    <!-- 班级名称 -->
                    <td class="ivu-table-column-center" :rowspan="item.stageList.length">
                      <div class="ivu-table-cell">
                        <a @click="showClassDetail(item.classId)">{{ item.className || '--'}}</a>
                        <!-- <div
                          style="text-align: right;display: inline-block;"
                          v-if="item.businessType === 1"
                        >
                          <Tag color="green">全国班</Tag>
                        </div>
                        <div
                          style="text-align: right; display: inline-block;"
                          v-if="item.orderType==3"
                        >
                          <Tag type="border" color="warning">拼团</Tag>
                        </div>-->
                      </div>
                    </td>
                    <!-- 业务属性 -->
                    <td class="ivu-table-column-center" :rowspan="item.stageList.length">
                      <div class="ivu-table-cell">{{ businessTypeMap[item.businessBelong] }}</div>
                    </td>
                    <!-- 授课类型 -->
                    <td class="ivu-table-column-center" :rowspan="item.stageList.length">
                      <div class="ivu-table-cell">{{ item.classTypeName }}</div>
                    </td>
                    <!-- 班级总退费课次 -->
                    <td class="ivu-table-column-center" :rowspan="item.stageList.length">
                      <div
                        class="ivu-table-cell"
                        v-if="item.totalRefund"
                      >{{ item.totalRefund.join("讲，")}}讲</div>
                    </td>
                  </template>
                  <!-- 分期名称 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{ item2.stageName || '--' }}</div>
                  </td>
                  <!-- 分期退费课次 -->
                  <td class="ivu-table-column-center">
                    <div
                      class="ivu-table-cell"
                      v-if="item2.stageRefund"
                    >{{ item2.stageRefund.join("讲，")}}讲</div>
                  </td>
                  <!-- 退费金额 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{ item2.refundAmount }}</div>
                  </td>
                  <!-- 操作人 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{ item2.empName || '--'}}</div>
                  </td>
                  <!-- 操作时间 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{ item2.operateTime || '--'}}</div>
                  </td>
                  <!-- 工单状态 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{getStatus(item2.status)}}</div>
                  </td>
                  <!-- 退费类型 -->
                  <td class="ivu-table-column-center">
                    <div
                      class="ivu-table-cell"
                    >{{item2.refundType === 0 ? '原路退回' : item2.refundType === 1 ? '网银' : '--' }}</div>
                  </td>
                  <!-- 退费状态 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">{{getRefundStatus(item2.refundStatus)}}</div>
                  </td>
                  <!-- 操作 -->
                  <td class="ivu-table-column-center">
                    <div class="ivu-table-cell">
                      <template
                        v-if="getOperateArr(item2.status, item2.refundStatus).viewTicket && item2.billCode"
                      >
                        <a href="javascirpt:void(0);" @click="viewTicket(item2.billCode)">查看工单</a>
                        <br />
                      </template>
                      <!-- -->
                      <template v-if="getOperateArr(item2.status, item2.refundStatus).updateTicket">
                        <a
                          href="javascirpt:void(0);"
                          @click="updateTicket(item2.workOrderId, item2.stageNo,item2.registerId, item.studentUid, item.cityCode,item.studentId, 2, item2.refundNo)"
                        >修改工单</a>
                        <br />
                      </template>
                      <!-- // 网银不存在退费类型 -->
                      <template
                        v-if="getOperateArr(item2.status, item2.refundStatus).viewProcess && item2.refundType === 0"
                      >
                        <a
                          href="javascirpt:void(0);"
                          @click="viewProcess(item.className, item.cityCode, item2.workOrderId, item.studentName)"
                        >退费进度查询</a>
                        <br />
                      </template>
                      <template v-if="getOperateArr(item2.status, item2.refundStatus).refundAgain">
                        <a
                          href="javascirpt:void(0);"
                          v-if="!item2.deleted"
                          @click="updateTicket(item2.workOrderId, item2.stageNo,item2.registerId, item.studentUid, item.cityCode,item.studentId, 3, item2.refundNo)"
                        >再次发起退费</a>
                        <span v-else style="color: #aaa;">再次发起退费</span>
                      </template>
                    </div>
                  </td>
                </tr>
              </template>
            </template>
          </tbody>
        </table>
        <div v-show="!tableData.length">
          <p style="text-align: center;line-height: 50px;width: 100%">
            <span>暂无数据</span>
          </p>
        </div>
      </div>
    </div>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }" v-show="!tableLoading">
      <div style="float: right;">
        <Page
          :total="pageTotal"
          :page-size="currentPageSize"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        ></Page>
      </div>
    </div>
    <!-- 退费进度 -->
    <Modal
      v-model="refundProcessShow"
      :mask-closable="false"
      width="960"
      :footer-hide="true"
      title="退费进度查询"
      class="has-tag"
    >
      <talFinanceRefundProcess v-if="refundProcessShow" v-bind="refundProcess"></talFinanceRefundProcess>
    </Modal>
    <!-- 特殊退费申请 -->
    <TalFinanceRefundSpecialApply
      :show="refundSpecialApplyShow"
      @close="refundSpecialApplyShow = false"
      @success="onSuccess"
      v-bind="specialRefundApplyDetail"
    ></TalFinanceRefundSpecialApply>
    <!-- 班级详情 -->
    <TalClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></TalClassDetail>
  </div>
</template>

<script>
import { querySpecialRefundClassList, getOaFormDetailUrl } from '../../api/specialRefund'
import { auditMissingReportTip } from '../../api/finance'
export default {
  name: 'TalFinanceRefundSpecial',
  props: {
    uid: {
      type: [Number, String],
      default: ''
    },
    cityCode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      businessTypeMap: {
        1: '学而思培优',
        3: '学而思素养中心',
        5: '学而思国际'
      },
      tableColumns: Object.freeze([
        {
          title: '班级名称',
          width: 120
        },
        {
          title: '业务属性',
          width: 120
        },
        {
          title: '授课类型',
          width: 150
        },
        {
          title: '班级总退费课次',
          width: 150
        },
        {
          title: '分期名称'
          // width: 100
        },
        {
          title: '分期退费课次'
          // width: 100
        },
        {
          title: '退费金额'
          // width: 100
        },
        {
          title: '操作人'
          // width: 100
        },
        {
          title: '操作时间'
          // width: 165
        },
        {
          title: '工单状态'
          // width: 100
        },
        {
          title: '退费类型'
          // width: 100
        },
        {
          title: '退费状态'
          // width: 100
        },
        {
          title: '操作'
          // width: 200
        }
      ]),
      tableData: [{
        stageList: [1, 2]
      }, {
        stageList: [1, 2]
      }],
      tableLoading: false,
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数,
      refundProcessShow: false, // 退费进度
      refundProcess: {
        cityCode: '010',
        refundBillId: '3f41e528c2ed4ce79c0f021d41641d11',
        className: '秋季班小学二年级数学勤思A',
        studentName: '石小锤',
        contact: true
      },
      refundSpecialApplyShow: false,
      classDetail: { // 班级详情
        id: '78217601bebb4e7e8e4cf49bf4d0db31',
        show: false
      },
      // 特殊申请详情
      specialRefundApplyDetail: {
        workOrderId: '',
        classStagesNo: 0,
        registerId: '',
        studentUid: '',
        cityCode: '',
        studentId: '',
        refundNo: '',
        applyAgain: null // 再次退费 2 修改 3逆向
      }
    }
  },
  methods: {
    // 获取操作栏功能
    getOperateArr (status, refundStatus) {
      const obj = {
        viewTicket: true, // 查看工单
        updateTicket: false, // 修改工单
        viewProcess: false, // 查看进度
        refundAgain: false // 再次发起特殊退费
      }

      if (status === 3 && refundStatus === 0) {
        obj.updateTicket = true
      }

      if ((status === 2 && (refundStatus !== 0)) || (status === 4 && refundStatus === 2)) {
        obj.viewProcess = true
      }

      if (refundStatus === 3 && (status === 2 || status === 4)) {
        obj.refundAgain = true
      }

      return obj
    },
    // 工单状态
    getStatus (num) {
      num = Number(num)
      let statusName = '--'
      switch (num) {
        case 1: statusName = '审核中'; break
        case 2: statusName = '审核通过'; break
        case 3: statusName = '驳回'; break
        case 4: statusName = '归档'; break
      }
      return statusName
    },
    // 退费状态
    getRefundStatus (num) {
      num = Number(num)
      let statusName = '--'
      switch (num) {
        case 0: statusName = '--'; break
        case 1: statusName = '退款中'; break
        case 2: statusName = '退款成功'; break
        case 3: statusName = '退款失败'; break
      }
      return statusName
    },
    // 展示班级详情
    showClassDetail (classId) {
      this.classDetail.id = classId
      this.$nextTick(() => {
        this.classDetail.show = true
      })
    },
    viewProcess (className, cityCode, refundBillId, studentName) {
      this.refundProcess = {
        refundBillId,
        workOrderId: refundBillId,
        className,
        studentName,
        cityCode,
        contact: true
      }

      this.refundProcessShow = true
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    // 查询特殊退费班级
    async getTableData () {
      this.tableLoading = true
      this.tableData = []
      const params = {
        uid: this.uid,
        cityCode: this.cityCode,
        pageNo: this.currentPageNum,
        pageSize: this.currentPageSize
      }
      try {
        const data = await querySpecialRefundClassList(params)
        this.tableData = Object.freeze(data.records)
        this.pageTotal = data.totalCount
      } catch (error) {
        this.tableData = []
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
      this.tableLoading = false
    },
    // 查看工单
    async viewTicket (billCode) {
      try {
        const params = {
          billCode,
          cityCode: this.cityCode
        }
        const url = await getOaFormDetailUrl(params)
        this.openUrlByNewWindow(url)
      } catch (error) {
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
    },
    openUrlByNewWindow (url) {
      const aEle = document.createElement('a')
      aEle.href = url
      aEle.target = '_blank'
      const e = document.createEvent('MouseEvents')
      e.initEvent('click', true, true)
      aEle.dispatchEvent(e)
    },
    // 修改或者再次发起退费
    async updateTicket (workOrderId, classStagesNo, registerId, studentUid, cityCode, studentId, applyAgain, refundNo) {
      try {
        let interval = this.$store.state.app.missReportInterval
        if (interval) {
          clearInterval(interval)
        }
        await auditMissingReportTip({ isAudit: 0 })
        this.specialRefundApplyDetail = {
          workOrderId,
          classStagesNo,
          registerId,
          studentUid,
          cityCode,
          studentId,
          applyAgain,
          refundNo
        }
        this.refundSpecialApplyShow = true
        this._observe().then(() => {
          this.getTableData()
        })
      } catch (err) {
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              this.$emit('report')
            }
          })
        } else {
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        }
      }
    },
    _observe () {
      return new Promise(resolve => {
        this._observe.resolve = resolve
      })
    },
    onSuccess () {
      this._observe.resolve()
    }
  },
  mounted () {
    this.getTableData()
  }
}
</script>

<style lang="less" scoped>
.ivu-table:after,
.ivu-table:before {
  height: 0;
  width: 0;
}
.gary {
  color: #ccc;
}
.orange {
  color: #f59a23;
}
</style>
