<template>
  <div class="search-history-list" v-if="show && historyList.length" @click.stop="">
    <div class="search-history-item" v-for="(item, index) in historyList" :key="index"
      @click="handleHistorySelect(item)" @mouseover="item.isHover = true" @mouseleave="item.isHover = false">
      <span>{{ item.name }}</span>
      <span v-if="!item.isHover" class="gray" style="margin-left: 10px;white-space: nowrap;">历史</span>
      <a v-else href="javascript:void(0)" @click.stop="delHistory(item)" style="margin-left: 10px;white-space: nowrap;">删除</a>
    </div>
  </div>
</template>

<script>
import { on, off } from '../../utils/utils'
export default {
  name: 'SearchHistory',
  props: {
    name: String,
    triggerElementId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      historyList: [],
      show: false
    }
  },
  mounted () {
    const localHistoryList = this.getLocalListHistory()
    if (localHistoryList && localHistoryList[this.name]) {
      this.historyList = localHistoryList[this.name]
    }

    this.element = document.querySelector(`#${this.triggerElementId} input`)
    on(this.element, 'focus', this.showHistory)
    on(document.body, 'click', this.hideHistory)
  },
  beforeDestroy () {
    off(this.element, 'focus', this.showHistory)
    off(document.body, 'click', this.hideHistory)
  },
  methods: {
    getLocalListHistory () {
      return localStorage['localHistoryList']
        ? JSON.parse(localStorage['localHistoryList'])
        : {}
    },
    hideHistory (e) {
      if (e.target !== this.element) {
        this.show = false
      }
    },
    showHistory () {
      this.show = true
    },
    handleHistorySelect (data) {
      this.$emit('select-history', data.name)
      this.show = false
    },
    setHistory (name) {
      if (!name || (typeof name === 'string' && !name.trim())) {
        return
      }
      // 排重
      this.historyList = this.historyList.filter((item) => item.name !== name)
      if (this.historyList.length >= 10) this.historyList.pop()
      this.historyList.unshift({
        name,
        isHover: false,
        id: Date.now()
      })
      this.setStroy()
    },
    delHistory (data) {
      this.historyList = this.historyList.filter((item) => item.id !== data.id)
      this.setStroy()
    },
    setStroy () {
      let localHistoryList = this.getLocalListHistory()
      localHistoryList[this.name] = this.historyList
      localStorage['localHistoryList'] = JSON.stringify(localHistoryList)
    }
  }
}
</script>

<style lang="less" scoped>
// 搜索历史
.search-history-list {
  position: absolute;
  min-width: 130px;
  padding: 7px 0;
  left: 0;
  top: 34px;
  z-index: 10001;
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  max-height: 150px;
  overflow: auto;
  .search-history-item {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    padding: 0 7px;
    &:hover {
      background-color: #edf4ff;
    }
  }
  .gray {
    color: rgba(0, 0, 0, 0.5);
  }
}
</style>
