<template>
  <section>
    <Form :model="formData" :rules="formValidate" ref="form" inline :label-width="130">
      <Row type="flex" :justify="hasTable? 'start': 'center'">
        <FormItem label="电子凭证接收邮箱" prop="email">
          <Input
            type="email"
            clearable
            placeholder="请输入"
            style="width: 200px;margin-right: 10px"
            v-model="formData.email"
          />
          <Button
            v-if="supportedFunctions === 'all' || supportedFunctions === 'send'"
            type="primary"
            :loading="loading"
            @click="send"
            style="margin-right: 10px"
          >发送</Button>
          <Button
            v-if="supportedFunctions === 'all' || supportedFunctions === 'print'"
            type="primary"
            :loading="loading"
            @click="print"
          >打印</Button>
        </FormItem>
      </Row>
    </Form>
    <Table v-if="hasTable" stripe :columns="tableColumns" :data="tableData"></Table>
  </section>
</template>

<script>
import {
  findMessageSendRecords,
  sendPaymentVoucher,
  sendRefundVoucher,
  batchSendHKPaymentVouchers
} from '../../api/message'
import {
  getUserExtInfoByType
} from '../../api/bd'
export default {
  name: 'TalFinanceVoucher',
  props: {
    from: { // ChangeCourse：调课班级，ReadingClass：在读班级
      type: String,
      default: ''
    },
    hasTable: {
      type: Boolean,
      default: false
    },
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    },
    classId: String,
    registId: String,
    stageId: String,
    refundBillId: String,
    isPay: {
      type: Boolean,
      default: true
    },
    stagesNum: {
      type: [Number, String],
      default: ''
    },
    supportedFunctions: {
      type: String, // send 发送邮件 print 打印 all都支持
      default: 'all'
    },
    batch: { // 批量发送邮件
      type: Boolean,
      default: false
    },
    isHk: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      loading: false,
      formData: {
        email: ''
      },
      formValidate: {
        email: [
          { required: true, message: '邮箱不能为空', trigger: 'blur', type: 'string' },
          { type: 'email', message: 'email格式不正确', trigger: 'blur' }

        ]
      },
      tableColumns: [
        {
          title: '发送时间',
          key: 'createTime'
        },
        {
          title: '接收邮箱',
          key: 'businessNum'
        },
        {
          title: '操作人',
          key: 'creatorName'
        }
      ],
      tableData: []
    }
  },
  mounted () {
    if (!this.batch) {
      getUserExtInfoByType({
        cityCode: this.studentDetail.cityCode,
        uid: this.studentDetail.uid,
        type: 'accessEmail'
      }).then(data => {
        this.formData.email = data
      }).catch(err => {
        this.$Message.error({
          content: '查询学员电子凭证邮箱失败：' + err.message,
          duration: 5
        })
      })
    }

    if (this.hasTable) {
      findMessageSendRecords({
        cityCode: this.studentDetail.cityCode,
        businessId: this.isPay ? `${this.studentDetail.uid}_${this.stageId}` : this.refundBillId,
        type: this.isPay ? 3 : 5
        // stageId: '7af9e3b998a94c76a98ed63a53de9a4b'
      }).then(data => {
        this.tableData = data
      }).catch(err => {
        this.$Message.error({
          content: '查询发送记录失败：' + err.message,
          duration: 5
        })
      })
    }
  },
  methods: {
    // 发送
    send () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.isHk) {
            this.sendOrPrintHk(0)
          } else {
            this.sendOrPrint(0)
          }
        }
      })
    },
    // 打印
    print () {
      if (this.isHk) {
        this.sendOrPrintHk(1)
      } else {
        this.sendOrPrint(1)
      }
    },
    // 发送或者打印
    sendOrPrint (isPrint) {
      // 发送缴费凭证
      if (this.isPay) {
        sendPaymentVoucher({
          'X-Ca-referer': this.from,
          cityCode: this.studentDetail.cityCode,
          card: this.studentDetail.card,
          classId: this.classId,
          email: isPrint ? 'print@tal.com' : this.formData.email,
          isPaidRegfee: this.studentDetail.isPaidRegfee,
          registId: this.registId,
          stageId: this.stageId,
          studentName: this.studentDetail.realName,
          uid: this.studentDetail.uid,
          isPrint,
          stageNum: this.stagesNum
        }).then(data => {
          if (isPrint) {
            this.printData(data)
          } else {
            this.$Message.success('发送成功')
            this.$emit('success')
          }
        }).catch((err) => {
          this.$emit('fail')
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      } else {
        // 发送退费凭证
        sendRefundVoucher({
          'X-Ca-referer': this.from,
          cityCode: this.studentDetail.cityCode,
          email: isPrint ? 'print@tal.com' : this.formData.email,
          refundBillId: this.refundBillId,
          stageId: this.stageId,
          isPrint,
          stageNum: this.stagesNum
        }).then(data => {
          if (isPrint) {
            this.printData(data)
          } else {
            this.$Message.success('发送成功')
            this.$emit('success')
          }
        }).catch((err) => {
          this.$emit('fail')
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      }
    },
    // 发送或者打印凭证-香港-批量
    // isPrint 0 发送邮件 1打印
    sendOrPrintHk (isPrint) {
      // 发送缴费凭证
      let params = {
        cityCode: this.studentDetail.cityCode,
        email: this.formData.email,
        classId: this.classId,
        isPrint,
        type: 2
      }

      // 单个发送
      if (!this.batch) {
        Object.assign(params, {
          registerId: this.registId,
          email: isPrint ? 'print@tal.com' : this.formData.email,
          studentName: this.studentDetail.realName,
          uid: this.studentDetail.uid,
          stagesNum: this.stagesNum,
          card: this.studentDetail.card,
          isPaidRegfee: this.studentDetail.isPaidRegfee,
          type: 1,
          stagesId: this.stageId
        })
      }

      batchSendHKPaymentVouchers(params).then(data => {
        if (isPrint) {
          this.printData(data)
        } else {
          this.$Message.success('发送成功')
          this.$emit('success')
        }
      }).catch((err) => {
        this.$emit('fail')
        this.$Message.warning({ content: err.message, duration: 5 })
      })
    },
    printData (data) {
      var wind = window.open('', 'newwindow', 'height=700, width=1000, top=100, left=100, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=n o, status=no')
      wind.document.body.innerHTML = data
      wind.print()
    }
  }
}
</script>

