<template>
  <section class="fast-link">
    <section class="links" v-if="links.links.length">
      <template v-for="item in links.links">
        <span :key="item.path" class="normal" v-if="!item.needStu || item.needStu && card" @click.stop="routerTo(item)">{{item.name}}</span>
        <span :key="item.path" class="dis" v-else>{{item.name}}</span>
      </template>
      <!--<span v-for="item in links.links" @click.stop="routerTo(item)">{{item.name}}</span>-->
    </section>
    <!--<Button v-if="showSendMsg" class="send-message" type="primary" @click="sendMsg">发送消息</Button>-->
  </section>
</template>

<script>
export default {
  name: 'TalFastLink',
  props: {
    links: {
      type: Object,
      required: true
    },
    card: {
      type: [String, Number]
    },
    cityCode: {
      type: [String, Number]
    }
    // },
    // showSendMsg: {
    //   type: Boolean,
    //   required: true
    // }
  },
  data () {
    return {
    }
  },
  methods: {
    sendMsg () {
      this.$emit('sendMsg')
    },
    routerTo (item) {
      // 这个跳转逻辑是盘古独有的
      // 有学员uid 则打开新的tab， 没有uid ，无论点多少次，都跳到固定的某一个tab
      const isMulti = item.multiPath && this.card
      let name = item.multiPath && this.card ? item.multiPath : item.path
      let options = {
        name,
        query: {
          cityCode: this.cityCode,
          card: this.card
        }
      }
      if (item.isHref) {
        top.open(item.path + `?card=${this.card || ''}&cityCode=${this.cityCode || ''}`)
      } else {
        if (isMulti) {
          options['params'] = {
            id: this.card
          }
        }
        this.$router.push(options)
      }
      /**
       * 后续开发 需要兼容呼叫的多开方案
       */
      // if (this.links.multiType === 'query') {
      //   path = item.multiPath ? item.multiPath : item.path
      //   this.$router.push({
      //     path,
      //     query: {
      //       cityCode: this.cityCode,
      //       uid: this.uid
      //     }
      //   })
      // } else if (this.links.multiType === 'params') {
      //   path = item.multiPath ? `${item.multiPath}/${this.uid}` : item.path
      //   this.$router.push({
      //     path,
      //     query: {
      //       cityCode: this.cityCode,
      //       uid: this.uid
      //     }
      //   })
      // }
    }
  }
}
</script>

