<template>
  <div class="pay-details">
    <div class="row">
      <h3>课次单价详情</h3>
      <section class="legends">
        <Tag color="#000" type="border" class="legend"></Tag>未缴费
        <Tag color="#999" class="legend"></Tag>缴费已刷课时
        <Tag color="#2d8cf0" class="legend"></Tag>缴费未刷课时
      </section>
    </div>
    <Row type="flex" justify="space-between" class="price-infos">
      <Spin size="large" fix v-if="classCountLoading"></Spin>
      <Col span="2" class="lineHeight30">
        <aside>课次号</aside>
        <aside>课次应缴学费</aside>
        <aside>课次实缴学费</aside>
      </Col>
      <template v-if="classTimes.length">
        <Col span="20">
          <ul class="price-info-list" :class="{'pd17': classTimes.length > 16}">
            <li v-for="item in classTimes" :key="item.curriculumNum">
              <aside>
                <span class="circle" :class="{dis: item.curriculumStatus === 1, can: item.curriculumStatus === 2}">{{item.curriculumNum}}</span>
              </aside>
              <aside :title="112999">{{fixedPrice(item.shouldPrice)}}</aside>
              <aside>{{fixedPrice(item.actualPrice)}}</aside>
            </li>
          </ul>
        </Col>
        <Col span="2" class="lineHeight30 txt-right">
          <aside>总计</aside>
          <aside>{{totalShouldPrice}}</aside>
          <aside>{{totalActualPrice}}</aside>
        </Col>
      </template>
      <Col span="22" v-else >
        <p class="no-data">暂无数据</p>
      </Col>
    </Row>
    <div class="row">
      <h3>历史缴费详情</h3>
    </div>
    <div class="history-table">
      <TalTable
        :tableData="tableData"
        :columns="columns"
        :loading="tableLoading"
      ></TalTable>
    </div>
    <div class="row">
      <h3>说明:</h3>
      <ol class="desc">
        <li>课次号空心代表未交费，灰色代表缴费后已刷课时，蓝色代表缴费后未刷课时。</li>
        <li>退费时蓝色课次为默认可退课次（未考虑调课情况，退费时以实际计算为准）。</li>
        <li>表中课次应缴、实缴学费，总计应缴、实缴学费，只包含学费，均不包含报名费。</li>
        <li>课次单价表中的课次实缴、应缴学费精确到“分”，二者差值即为优惠金额，真实缴退费时四舍五入到“元”。</li>
        <li>历史缴费详情中只记录缴费记录以及涉及到多退少补的转班的记录。</li>
      </ol>
    </div>
  </div>
</template>

<script>
import { queryClassPayments, queryCurriculumPayments } from '../../api/order'
import TalTable from '../tal-table'
export default {
  name: 'talFinancePaymentDetails',
  components: {
    TalTable
  },
  props: {
    cityCode: {
      required: true,
      type: String
    },
    registId: {
      type: String,
      required: true
    },
    uid: {
      type: String,
      required: true
    }
  },
  computed: {
  },
  data () {
    return {
      classTimes: [],
      tableData: [],
      columns: [
        {
          title: '类型',
          width: 50,
          align: 'center',
          slot: 'payType'
        },
        {
          title: '班级名称',
          align: 'center',
          key: 'className',
          width: 150
        },
        {
          title: '授课类型',
          align: 'center',
          key: 'classTypeName',
          width: 30
        },
        {
          title: '分期名称',
          align: 'center',
          slot: 'subjectName',
          width: 60
        },
        {
          title: '缴费课次',
          align: 'center',
          slot: 'curriculumNum',
          width: 100
        },
        {
          title: '班级/分期原价',
          align: 'center',
          width: 100,
          slot: 'classInstallment'
        },
        {
          title: '优惠金额',
          align: 'center',
          width: 100,
          slot: 'discountAmount'
        },
        {
          title: '实缴金额',
          align: 'center',
          slot: 'price',
          width: 55
        },
        {
          title: '转班起/止课次',
          align: 'center',
          slot: 'availableClass',
          width: 150,
          renderHeader: (h, params) => {
            return h('span', [
              h('span', '转班起/止课次'),
              h('Tooltip', {
                props: {
                  content: '转班起/止课次指的是本次转班中多退或者少补所涉及到的课次',
                  'max-width': 200,
                  transfer: true,
                  placement: 'top'
                }
              }, [h('Icon', {
                style: {
                  marginTop: '-2px'
                },
                props: {
                  type: 'md-help-circle',
                  size: 16
                }
              })])
            ])
          }
        },
        {
          title: '转班多退少补金额',
          align: 'center',
          slot: 'dtsbDescription',
          width: 60
        },
        {
          title: '操作时间',
          align: 'center',
          key: 'createDate',
          width: 75
        },
        {
          title: '支付方式',
          align: 'center',
          slot: 'payTypeName',
          width: 55
        },
        {
          title: '订单号',
          width: 200,
          align: 'center',
          key: 'orderNo'
        }
      ],
      classCountLoading: true,
      tableLoading: true,
      totalShouldPrice: 0,
      totalActualPrice: 0
    }
  },
  mounted () {
    this.initData()
  },
  methods: {
    initData () {
      let req = {
        registId: this.registId,
        uid: this.uid,
        empNo: '076757',
        cityCode: this.cityCode
      }
      this.queryClassPaymentsHandler(req)
      this.queryCurriculumPaymentsHandler(req)
    },
    queryClassPaymentsHandler (req) {
      queryClassPayments(req).then(rlt => {
        console.log(rlt)
        this.tableData = rlt
      }).catch(err => {
        this.$Message.error({ content: '查询历史缴费详情失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    queryCurriculumPaymentsHandler (req) {
      queryCurriculumPayments(req).then(rlt => {
        this.classTimes = rlt
        this.totalActualPrice = this.sumPrice(rlt, 'actualPrice')
        this.totalShouldPrice = this.sumPrice(rlt, 'shouldPrice')
      }).catch(err => {
        this.$Message.error({ content: '查询缴费课次列表失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.classCountLoading = false
      })
    },
    sumPrice (data, attr) {
      let sum = 0
      data.forEach(obj => {
        sum += Number(obj[attr])
      })
      return sum.toFixed(2)
    },
    /**
     与产品沟通表示最高不会超过千位， 所以加上这个函数，以防止测试加入无限制长度数字
     @params val 金额
     @params max 最大显示数 默认 每节课单价不超过9999，总金额不超过 99999，超过后显示 --
    */
    fixedPrice (val, max = 9999) {
      let _p = Number(val)
      if (_p) {
        return _p > max ? '--' : _p
      }
      return 0
    }
  }
}
</script>

