import $ from '../utils/api.request'

/**
 * @张晨
 *  诊断闭环用户意向、是否参诊、是否缴费、是否报名，是否新学员可选项
 *  http://apidoc.speiyou.cn/api/view/16187#
 */

export const condition = params => {
  return $.splatAxios.request({
    url: '/diagnosis/condition',
    params,
    method: 'GET'
  })
}

/**
 * @张晨
 *  接待顾问搜索
 *  http://apidoc.speiyou.cn/api/view/16195#
 *  @param userName
 */

export const getUserList = params => {
  return $.splatAxios.request({
    url: '/diagnosis/getUserList',
    data: params,
    method: 'POST'
  })
}

