<template>
  <div>
    <Modal
      :mask-closable="false"
      title="缴费详情"
      v-model="modalValue"
      width="960px">
      <div>
        <div class="ivu-table ivu-table-default ivu-table-border">
          <div class="ivu-table-body">
            <table cellspacing="0" cellpadding="0" border="0" style="min-width: 100%">
              <thead>
              <tr class="ivu-table-row">
                <th v-for="(item,index) in columns1" :key="index" class="ivu-table-column-center">
                  <div class="ivu-table-cell">{{item.title}}</div>
                </th>
              </tr>
              </thead>
              <tbody class="ivu-table-tbody">
              <template v-for="item in parDetailList">
                <template v-if="item.stagesInfos && item.stagesInfos.length">
                  <tr class="ivu-table-row" v-for="(item2,index2) in item.stagesInfos" :key="index2">
                    <template v-if="index2 === 0">
                      <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.className}}</div></td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.studentRealName}}</div></td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.classPrice}}</div></td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.classCount}}</div></td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.passedCount}}</div></td>
                    </template>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item2.stagesName}}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item2.curriculumCount}}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item2.registCount}}</div></td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <span style="color: red" v-if="item2.payed == '0'">未缴费</span><span v-else>已缴费</span>
                      </div>
                    </td>
                  </tr>
                </template>
              </template>
              </tbody>
            </table>
            <div v-show="!parDetailList.length">
              <p style="text-align: center;line-height: 50px;width: 100%"><span>暂无数据</span></p>
            </div>
          </div>
        </div>

      </div>
      <div slot="footer">
         <Button @click="modalValue = false">关闭</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'payDetail',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    parDetailList: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      columns1: [
        {
          title: '班级名称'
        },
        {
          title: '学员姓名'
        },
        {
          title: '总金额'
        },
        {
          title: '总课次'
        },
        {
          title: '已上课次'
        },
        {
          title: '分期名称'
        },
        {
          title: '分期课次'
        },
        {
          title: '报班课次'
        },
        {
          title: '分期缴费状态'
        }
      ]
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  }
}
</script>

<style scoped lang="less">
  .ivu-table:after,.ivu-table:before{
    height:0;
    width:0
  }
</style>
