import HttpRequest from './axios.course'
import { runEnv } from './utils'
import config from '../config'
const env = runEnv()
const Platform = {
  TEACHER: 'TEACHER/bedrock-teacher',
  AUTH: 'AUTH',
  COURSE: 'course',
  MASTERBD: 'MASTER-BD/bd-web'
}
const baseURL = `${config.baseUrl[env]}/` // 一站式网关地址

const requestParams = {
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000 // request timeout
}

const teacherAxios = new HttpRequest({
  baseURL: baseURL + Platform.TEACHER,
  ...requestParams
})

const authAxios = new HttpRequest({
  baseURL: baseURL + Platform.AUTH,
  ...requestParams
})

const courseAxios = new HttpRequest({
  baseURL: baseURL + Platform.COURSE,
  ...requestParams
})

const masterBDAxios = new HttpRequest({
  baseURL: baseURL + Platform.MASTERBD,
  ...requestParams
})

export default {
  teacherAxios,
  authAxios,
  courseAxios,
  masterBDAxios
}
