<template>
<!-- 在读班级列表 -->
  <div>
    <div class="ivu-table ivu-table-default ivu-table-border relative">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <CheckboxGroup
          v-model="checkAllGroup"
          style="font-size: 12px"
          @on-change="checkCheckboxStatus"
        >
          <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
            <thead>
              <tr class="ivu-table-row">
                <template v-for="(item, index) in tableColumns">
                  <th
                    class="ivu-table-column-center"
                    :width="item.width"
                    :key="index"
                    v-if="showColumn(item)"
                  >
                    <div class="ivu-table-cell">{{item.title}}</div>
                  </th>
                </template>
              </tr>
            </thead>
            <tbody class="ivu-table-tbody">
              <template v-for="(item) in tableData">
                <template v-if="item.stageList && item.stageList.length">
                  <tr
                    class="ivu-table-row"
                    v-for="(item2,index2) in item.stageList"
                    :key="item2.id"
                  >
                    <template v-if="index2 === 0">
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <Button
                            size="small"
                            type="primary"
                            ghost
                            @click="copy(item.registId)"
                            style="margin-right: 5px"
                          >复制</Button>
                          <span>{{item.registId}}</span>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <a @click="showClassDetail(item.classId)">{{item.className}}</a>
                          <div
                            style="text-align: right;display: inline-block;"
                            v-if="item.businessType === 1"
                          >
                            <Tag color="green">全国班</Tag>
                          </div>
                          <div
                            style="text-align: right; display: inline-block;"
                            v-if="item.orderType==3"
                          >
                            <Tag type="border" color="warning">拼团</Tag>
                          </div>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{getClassType(item.classType)}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.subjectNames}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <!--{{item.teacherNames}}-->
                          <template v-for="(tea, index) in item.teacherNames.split(',')">
                            <a
                              @click="showTeacherDetail(studentDetail.bindingCode, item.teacherIds.split(',')[index])"
                              :key="tea"
                            >{{ tea }}</a>
                            <template v-if="index != item.teacherNames.split(',').length - 1">
                              ,
                              <br :key="index" />
                            </template>
                          </template>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.tutorRealName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">
                          <Poptip
                            trigger="hover"
                            width="300"
                            word-wrap
                            :content="item.classtimeNames"
                            transfer
                          >{{getClasstimeNames(item.classtimeNames)}}</Poptip>
                        </div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.serviceCenterName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.classroomName}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.passedCount + '/' + item.classCount}}</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell" v-if="item.businessType!=1">
                          <Poptip
                            trigger="hover"
                            width="300"
                            word-wrap
                            :content="item.classChangeName"
                            transfer
                          >{{getClassChangeName(item.classChangeName)}}</Poptip>
                        </div>
                        <div class="ivu-table-cell" v-if="item.businessType==1">——</div>
                      </td>
                      <td class="ivu-table-column-center" :rowspan="item.rowspan">
                        <div class="ivu-table-cell">{{item.price}}</div>
                      </td>
                      <td
                        :rowspan="item.rowspan"
                        class="ivu-table-column-center"
                        v-if="studentDetail.cityCode === '00852'"
                      >
                        <div class="ivu-table-cell">{{ getClassPayStatus(item.bookFeePayStatus) }}</div>
                      </td>
                    </template>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.stagesName}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.startCurriculumTime}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.endCurriculumTime}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div
                        class="ivu-table-cell"
                        v-if="item2.startCurriculumNum && item2.endCurriculumNum"
                      >{{item2.startCurriculumNum + '-' + item2.endCurriculumNum}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{item2.paidAmount}}</div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <span>{{ getOperateByStatus(item2.invoiceStatus).label }}</span>
                      </div>
                    </td>
                  </tr>
                </template>
              </template>
            </tbody>
          </table>
        </CheckboxGroup>
        <div v-show="!tableData.length">
          <p style="text-align: center;line-height: 50px;width: 100%">
            <span>暂无数据</span>
          </p>
        </div>
      </div>
    </div>
    <!-- <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }" v-show="!tableLoading">
      <div style="float: right;">
        <Page
          :total="pageTotal"
          :page-size="currentPageSize"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        ></Page>
      </div>
    </div> -->
    <tal-class-detail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></tal-class-detail>
    <tal-teacher-details
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :stuId="studentDetail.studentId"
      :stuSchoolCode="studentDetail.cityCode"
      :creatorEmpNo="teacherDetail.empNo"
    ></tal-teacher-details>
  </div>
</template>
<script>
// InvoiceData

import { getGradeList } from '../../api/bd' // auditMissingReportTip
import { queryRegisterClassList } from '../../api/register'
// import { queryRefundUnion } from '../../api/refund'
// import { modifyInvoiceStatus, generateCashOutWakeUp } from '../../api/finance'
import TeacherMixin from '../../mixin/teacherDetail'
import ClassMixin from '../../mixin/classDetail'
import { copyText, getUserInfoByCookies } from '../../utils/utils'
export default {
  name: 'TalReadingClassModal',
  mixins: [TeacherMixin, ClassMixin],
  props: {
    formData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      // createInvoiceModel: false,
      tableLoading: false,
      isSpecial: 0, // 是否特殊调课
      changeClassShow: false, // 互换转班
      gradeList: [],
      currentPageNum: 1, // 当前页
      currentPageSize: 100, // 显示条数
      pageTotal: 0, // 总条数
      changeClassModal: false,
      changeCourseModal: false,
      oldClassInfo: {},
      tableData: [],
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      tableColumns: Object.freeze([
        {
          title: '报名ID',
          width: 150
        },
        {
          title: '班级名称',
          width: 150
        },
        {
          title: '授课类型'
          // width: 100
        },
        {
          title: '学科'
          // width: 100
        },
        {
          title: '主讲教师'
          // width: 100
        },
        {
          title: '辅导教师'
          // width: 100
        },
        {
          title: '上课时间'
          // width: 165
        },
        {
          title: '服务中心'
          // width: 100
        },
        {
          title: '教室'
          // width: 100
        },
        {
          title: '上课进度'
          // width: 100
        },
        {
          title: '变动记录'
          // width: 200
        },
        {
          title: '总金额'
          // width: 120
        },
        {
          title: '书籍费缴费状态',
          isHk: true,
          width: 150
        },
        {
          title: '分期类型'
          // width: 100
        },
        {
          title: '开课日期'
          // width: 140
        },
        {
          title: '结课日期'
          // width: 140
        },
        {
          title: '分期课次'
          // width: 100
        },
        // {
        //   title: '应缴金额'
        //   // width: 100
        // },
        {
          title: '实缴金额'
          // width: 100
        },
        {
          title: '发票状态'
          // width: 100
        }
      ]),
      mutualChangeClassMessage: {},
      classInfo: [],
      invoiceInfoTodo: {},
      invoiceShow: false,
      linkRefundModal: false,
      refundClassId: '',
      refundModal: false,
      refundData: [{
        classId: '',
        registId: '',
        studentId: '',
        cflat: '',
        repairNo: ''
      }],
      refundProcessData: {
        show: false,
        refundList: [],
        contact: true
      },
      VoucherModal: false,
      checkAll: false,
      checkAllGroup: [],
      allItem: [],
      paymentDetailModal: false,
      paymentDetailData: {},
      refundApplyDetails: {
        show: false,
        registId: '',
        areaCode: '',
        userNumber: '',
        studentId: ''
      },
      isLoaded: false,
      row: {},
      // invoiceId: '',
      // createInvoiceData: {}
      invoiceModal: {
        show: false,
        content: ''
      }
    }
  },
  mounted () {
    this.getTableData()
  },
  methods: {
    // 获取开票状态对应操作
    getOperateByStatus (status) {
      switch (status) {
        // 未开
        case 0:
          return {
            label: '未开发票',
            view: false,
            check: true
          }
        // 部分已开
        case 1:
          return {
            label: '部分已开',
            view: true,
            check: true
          }
        // 全部已开
        case 2:
          return {
            label: '已开发票',
            view: true,
            check: false
          }
        default:
          return {}
      }
    },
    // 书籍费缴费状态映射
    getClassPayStatus (status) {
      switch (status) {
        case 0: return '未缴费'
        case 1: return '已缴费'
        case 3: return '已退费'
        case 4: return '待缴费'
      }
    },
    // 是否显示书籍费缴费状态
    showColumn (column) {
      if (column.isHk && this.studentDetail.cityCode !== '00852') {
        return false
      }
      return true
    },
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        this.checkAllGroup = this.allItem
      }
    },
    getClasstimeNames (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    },
    changeSuccess () {
      this.getTableData()
    },
    getTableData () {
      this.checkAll = false
      this.allItem = []
      this.tableLoading = true
      this.checkAllGroup = []
      let req = {
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId,
        pageSize: this.currentPageSize,
        pageNo: this.currentPageNum,
        mode: 4,
        year: this.formData.year ? this.formData.year : undefined,
        termId: this.formData.term ? this.formData.term : undefined,
        classType: this.formData.classType ? this.formData.classType : undefined,
        subjectId: this.formData.subject ? this.formData.subject : undefined,
        termList: this.formData.termList ? this.formData.termList : undefined
      }
      queryRegisterClassList(req).then((res) => {
        this.tableData = []
        if (res) {
          this.pageTotal = res.totalCount
          if (res.records) {
            this.tableData = res.records
            this.tableData.forEach((e) => {
              if (e.stageList && e.stageList.length) {
                e.rowspan = e.stageList.length
                e.stageList.forEach(res => {
                  if (res.invoiceStatus !== 0) {
                    this.allItem.push(res.id)
                  }
                })
              }
              if (e.startDate) {
                e.startDate = e.startDate.substring(0, 10)
              }
              if (e.endDate) {
                e.endDate = e.endDate.substring(0, 10)
              }
            })
          }
        }
      }).catch((err) => {
        this.tableData = []
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    // 转班
    changeClass (params) {
      this.changeClassModal = true
      this.oldClassInfo = params
    },
    // 转班成功
    changeClassSuccess () {
      this.changeClassModal = false
      this.getTableData()
    },
    // 调课
    changeCourse (params, isSpecial) {
      this.isSpecial = isSpecial
      this.changeCourseModal = true
      this.oldClassInfo = params
    },
    // 调课成功
    changeCourseSuccess () {
      this.changeCourseModal = false
      this.getTableData()
    },

    // 互换转班
    mutualChangeClass (item) {
      this.mutualChangeClassMessage = item
      let data = {
        cityCode: this.studentDetail.cityCode
      }
      getGradeList(data).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error({ content: '获取年级失败' + err.message, duration: 5 })
      })
      this.changeClassShow = true
    },
    // 获取授课类型
    getClassType (type) {
      return this.classTypeMap[type]
    },
    // 获取变动记录
    getClassChangeName (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    async refundMoney (item) {
      try {
        await auditMissingReportTip({ isAudit: 0 })
        this.row = item
        let req = {
          cityCode: this.studentDetail.cityCode,
          isQueryDetail: 1,
          // registId: '32b55505ca7e49d1ba15b398df74119a' || item.registId,
          // uid: 1000103181 || Number(this.studentDetail.uid)
          registId: item.registId,
          uid: Number(this.studentDetail.uid),
          'X-Ca-referer': 'ReadingClass'
        }
        queryRefundUnion(req).then(res => {
          this.refundClassId = item.classId
          if (res && res.refundCompoundType === 0) {
            this.refundData = [{
              className: item.className,
              classId: item.classId,
              registId: item.registId,
              studentId: this.studentDetail.studentId,
              cflat: '',
              repairNo: item.repairNo
            }]
            this.refundModal = true
          } else if (res && res.refundCompoundType === 1) {
            this.refundData = res.details.map(d => {
              if (d.promotionId === '20') {
                this.$Message.warning({
                  content: '该订单包含的奖学金将会返回到原账户',
                  duration: 5
                })
              }
              d.details.forEach(obj => {
                obj.studentId = this.studentDetail.studentId
                return obj
              })
              return d
            })
            this.linkRefundModal = true
          }
        }).catch(err => {
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
      } catch (err) {
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              this.$emit('report')
            }
          })
        } else {
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        }
      }
    },
    nextStep (data) {
      this.refundData = data
      this.$nextTick(() => {
        this.refundModal = true
        this.linkRefundModal = false
      })
    },
    refundHandler (list) {
      this.refundModal = false
      this.refundProcessData.className = this.row.className
      this.refundProcessData.refundList = list
      setTimeout(() => { // 延时两秒打开退费进度和电子钱包
        this.refundProcessData.show = true
        let stageList = this.row.stageList.filter(s => s.payType === 4) // 判断分期里面有没有电子钱包支付
        if (stageList.length > 0) {
          generateCashOutWakeUp({
            cityCode: this.studentDetail.cityCode,
            studentId: this.studentDetail.studentId,
            uid: this.studentDetail.uid,
            studentCardId: this.studentDetail.stuCardId,
            regId: list[0].registId,
            refundAmount: list[0].refundAmount
          }).then(data => {
            if (data && data.url) {
              window.open(data.url)
            }
          }).catch(err => {
            this.$Message.error({
              content: err.message,
              duration: 5
            })
          })
        }
      }, 2000)
    },
    fundSuccessHandler (list) {
      this.refundHandler(list)
      // 香港分校没有发票
      if (this.studentDetail.cityCode !== '00852') {
        this.autoOffsetInvoice(this.row, this.studentDetail.cityCode, this.invoiceModal)
      }
      setTimeout(() => { // 延时两秒刷新数据
        this.getTableData()
      }, 2000)
    },
    modifyInvoiceStatusHandler (item, item2) {
      this.$Modal.confirm({
        title: '确认退还发票吗？',
        onOk: () => {
          let req = {
            cityCode: this.studentDetail.cityCode,
            'X-Ca-referer': 'ReadingClass',
            registId: item.registId,
            stagesId: item2.id,
            stagesNum: item2.stagesNum
          }
          modifyInvoiceStatus(req).then(res => {
            this.$Message.success({ content: '退还成功', duration: 3 })
            this.getTableData()
          }).catch(err => {
            this.$Message.error({ content: '退还发票失败：' + err.message, duration: 5 })
          })
        },
        onCancel: () => {

        }
      })
    },
    checkCheckboxStatus () {
      if (this.checkAllGroup.length === this.allItem.length) {
        this.checkAll = true
      } else {
        this.checkAll = false
      }
    },
    paymentDetail (item) {
      this.paymentDetailModal = true
      this.paymentDetailData = {
        registId: item.registId + '',
        uid: this.studentDetail.uid + '',
        cityCode: this.studentDetail.cityCode + ''
      }
    },
    refundReasonDetail (item) {
      this.refundApplyDetails = {
        show: false,
        registId: item.registId,
        areaCode: this.studentDetail.cityCode,
        userNumber: getUserInfoByCookies('columbusUserInfo').id,
        studentId: this.studentDetail.studentId
      }
      this.$nextTick(() => {
        this.refundApplyDetails.show = true
      })
    },
    copy (text) {
      copyText(text)
    },
    refundAuditSuccess () {
      this.currentPageNum = 1
      this.getTableData()
    }
  },
  computed: {
    tableWidth: function () {
      return '100%'
    }
  }
}
</script>
<style scoped lang="less">
.ivu-table:after,
.ivu-table:before {
  height: 0;
  width: 0;
}
.gary {
  color: #ccc;
}
.orange {
  color: #f59a23;
}
</style>
