<template>
  <div class="dib oss-loader-wrap">
    <div class="dib" v-show="btnLoading">
      <Button
        type="primary"
        :loading="btnLoading"
      >{{bindName}}
      </Button>
    </div>

    <div class="dib" v-show="!btnLoading">
      <Form>
        <FormItem :error="btnErr">
          <Button
            type="primary"
            :disabled="disabledMark"
            :id="btnId"
          >{{bindName}}
          </Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
/*
  * 按钮 单文件上传
  *
  * uploadSuccess 上传成功返回方法
  *
  * */

import plupload from 'plupload'
import { uploadGetAuth } from '../../api/upload'
import { runEnv } from '../../utils/utils'
import Cookie from 'js-cookie'

export default {
  name: 'TalOssUpload',
  props: {
    bindName: {
      type: String,
      default: '上传'
    },
    size: {
      type: String,
      default: '50KB'// KB MB
    },
    disabledMark: {
      type: Boolean,
      default: false
    },
    errorMsg: {
      type: String,
      default: ''
    },
    /*
    * 设置为all不设定限制格式（常用格式都能传）、
    * 设置为have内置的所有格式
    * */
    limitFile: {
      type: String,
      default: 'excel'
    },
    btnId: {
      type: String,
      default: 'talOssUpload-' + Math.round(Math.random() * 1000000)
    }
  },
  data () {
    return {
      empNo: '',
      params: {
        fileUrl: '', // oss链接
        fileName: ''// 文件名字
      },
      limitFileType: {
        excel: { title: 'Excel files', extensions: 'XLS,XLSX' },
        doc: { title: 'Doc files', extensions: 'DOC,DOCX' },
        ppt: { title: 'Power Point files', extensions: 'PPT,PPTX' },
        pdf: { title: 'PDF files', extensions: 'PDF' },
        image: { title: 'Doc files', extensions: 'jpg,png,jpeg' },
        jpg: { title: 'Image files', extensions: 'jpg' },
        png: { title: 'Image files', extensions: 'png' },
        mp4: { title: 'video files', extensions: 'mp4' }
      },
      limitItem: {}, // 目前允许单规则
      limitExt: '',
      btnLoading: false,
      btnErr: ''
    }
  },
  methods: {
    uploadFn () {
      // oss
      let uploader = new plupload.Uploader({
        runtimes: 'html5,flash,silverlight,html4',
        browse_button: this.btnId,
        multi_selection: false, // 是否多选
        url: `${runEnv() === 'prod' ? 'https' : 'http'}://oss.aliyuncs.com`,
        filters: {
          mime_types: this.limitFile === 'all' ? [] : [this.limitItem], // 限制文件类型
          max_file_size: this.size, // 上传大小限制
          prevent_duplicates: false // 不允许选取重复文件
        },
        init: {
          PostInit: () => {
            // 当Init事件发生后触发
          },
          QueueChanged: () => {
            if (!this.btnLoading) {
              this.btnLoading = true
            }
          },
          FilesAdded: (up, files) => {
            let name = files[files.length - 1].name
            // console.log(name)
            // 当文件添加到上传队列后触发
            uploadGetAuth({
              fileName: files[files.length - 1].name,
              empNo: this.empNo
            }).then((res) => {
              // console.log(res)
              let params = {
                'key': res.dir + '/' + files[files.length - 1].name,
                'policy': res.policy,
                'expire': res.expire,
                'OSSAccessKeyId': res.accessid,
                'success_action_status': '200', // 让服务端返回200,不然，默认会返回204
                // 'callback': res.callback, // 设置回调
                'signature': res.signature
              }
              this.params.fileUrl = `${res.host}/${res.dir}/${name}`
              up.setOption({
                'url': res.host,
                'multipart_params': params
              })
              up.start()
              this.$emit('loadingStatus', this.btnLoading)
            }).catch(() => {
              up.stop()
              up.removeFile(files[files.length - 1])
              this.$Message.warning({
                content: '文件上传失败，请重新操作',
                duration: 10
              })
              this.resetLoading()
            })
          },
          FileUploaded: (up, file) => {
            // 当队列中的某一个文件上传完成后触发
            this.params.fileName = file.name
            this.btnErr = ''
            // console.log(this.params)
            setTimeout(() => {
              /* 避免错误提示信息闪烁 */
              this.$emit('uploadSuccess', this.params)
              this.resetLoading()
            }, 500)
          },
          Error: (up, err) => {
            // 错误回调
            if (err.code === -600) {
              this.$Message.warning({
                content: '选择的文件过大了，限制' + this.size + '以内',
                duration: 10
              })
            } else if (err.code === -601) {
              this.$Message.warning({
                content: '选择的文件格式错误，目前仅支持 ' + this.limitExt,
                duration: 10
              })
            } else if (err.code === -602) {
              this.$Message.warning({
                content: '这个文件已经上传过一遍了',
                duration: 10
              })
            } else {
              this.$Message.warning({
                content: '上传错误，请稍后再试',
                duration: 10
              })
            }
            this.resetLoading()
          }
        }
      })
      uploader.init()
      // oss 结束
    },
    resetLoading () {
      this.$nextTick(() => {
        this.btnLoading = false
        this.$emit('loadingStatus', this.btnLoading)
      })
    },
    mountErr () {
      this.btnErr = this.errorMsg
    }
  },
  mounted: function () {
    let columbusUserInfo = Cookie.get('columbusUserInfo')
    try {
      this.empNo = JSON.parse(columbusUserInfo).id

      if (this.limitFileType[this.limitFile]) {
        /* 内置格式之一 */
        this.limitItem = this.limitFileType[this.limitFile]
        this.limitExt = this.limitItem.extensions.split(',').map(item => {
          return '.' + item.toLowerCase()
        }).join(',')
      } else {
        if (this.limitFile === 'all') {
          /* 不限定格式 */
        } else if (this.limitFile === 'have') {
          /* 内置所有格式 */
          let extList = []
          let extendList = []

          for (const key in this.limitFileType) {
            const limitItem = this.limitFileType[key]
            const list = limitItem.extensions.split(',').map(item => {
              extendList.push(item)
              return '.' + item.toLowerCase()
            })
            extList = [
              ...extList,
              ...list
            ]
          }

          this.limitItem = {
            title: 'file support',
            extensions: extendList.join(',')
          }
          this.limitExt = extList.join(',')
        } else {
          this.$Message.error({
            content: '传入格式有误',
            duration: 3
          })
        }
      }

      // console.log(this.limitItem)
      // console.log(this.limitExt)
      this.uploadFn()
    } catch (e) {
      console.log('用户信息获取失败')
    }
  },
  watch: {
    errorMsg () {
      this.mountErr()
    }
  }
}
</script>

<style scoped lang="less">
  .oss-loader-wrap {
    /deep/ .ivu-form-item {
      margin-bottom: 0 !important;
    }

    /deep/ .ivu-form-item.ivu-form-item-error {
      margin-bottom: 24px !important;
    }
  }

  .dib {
    display: inline-block;
  }
</style>
