import $ from '../utils/api.request'

/**
 * API 退费进度
 * 数据项        参数方式 属性名          是否必填 描述
 * 报名ID        body    registId       Y       报名ID
 * 退费单号       body    refundBillNo   Y       退费单号
 * 支付方式       body    payType        N       支付方式
 * 支付方式名称    body    payTypeName    N       支付方式名称
 * @param params
 */

export const queryRefundProcessDetail = params => {
  return $.splatAxios.request({
    url: '/finance/queryRefundProcessDetail',
    data: params,
    method: 'post'
  })
}
/**
 查看发票
 * @params registId 报名id query true string
 * @params stageNum 分期数 query true string
 */
export function getInvoiceDetail (params) {
  return $.splatAxios.request({
    url: 'finance/getInvoiceDetail',
    method: 'get',
    params
  })
}

