import $ from '../utils/api.request'
import { apiGateWayHeader } from '../utils/utils'

const { noticeAxios } = $

const apiKey = '6148a97fd77846da8f0eac1ce7a01eaa'
const apiAuthSalt = '60a16ec88548476ba35f09d61a94deb2'

const getHeaders = () => {
  const timestamp = Date.now()
  return {
    'X-Ca-Timestamp': timestamp,
    'X-Ca-Signature': apiGateWayHeader(timestamp + '', apiAuthSalt),
    'X-Ca-Key': apiKey,
    'X-Ca-Notice-Token': sessionStorage.getItem('X-Ca-Notice-Token')
  }
}

export const ERROR_TOKEN_CODE = '200004'

/**
 * 获取未读消息数量
 * @returns {*}
 */
export const getNoReadCount = () => {
  return noticeAxios.request({
    method: 'get',
    url: '/api/getNoReadCount',
    headers: getHeaders()
  })
}

/**
 * 获得平台菜单的树形结构
 * @returns {*}
 */
export const getPlatFormMenuTree = () => {
  return noticeAxios.request({
    method: 'get',
    url: '/api/getPlatFormMenuTree',
    headers: getHeaders()
  })
}

/**
 * 全部消息搜索
 * @param params
 * @returns {*}
 */
export const queryMessage = params => {
  return noticeAxios.request({
    method: 'post',
    url: '/api/queryMessage',
    data: params,
    headers: getHeaders()
  })
}

