<template>
  <Modal
    :mask-closable="false"
    title="查看上课时间"
    v-model="seeClasstime"
    :z-index="9999"
    width="960px">
    <div class="seeClasstimeNav" style="padding: 15px">
      <Table :columns="seeTime" :data="seeTimeList" height="500"></Table>
    </div>
    <div slot="footer">
      <Button @click="closeClassTime()">关闭</Button>
    </div>
  </Modal>
</template>

<script>
import { clazzQueryCurriculumInfo } from '../../api/clazz'
export default {
  name: 'TalSchooltime',
  data () {
    return {
      seeClasstime: false,
      seeTimeList: [],
      changeClassType: {
        1: '在线',
        2: '双师',
        4: '面授',
        5: '自习'
      },
      lockStatus: {
        0: '未锁定',
        1: '已锁定',
        2: '已解锁'
      },
      liveTypeMap: {
        0: '通用课程',
        1: '小组课',
        2: '新课堂通用课程',
        3: '新课堂小组课',
        4: '新课堂考试',
        5: '新课堂讲座'
      },
      seeTime: [
        {
          title: '课次',
          key: 'classNum',
          align: 'center'
        },
        {
          title: '上课日期',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDate)])
          }
        },
        {
          title: '星期',
          align: 'center',
          width: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDateWeek)])
          }
        },
        {
          title: '上课时间',
          key: 'classTimeName',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('span', params.row.classTimeName)
          }
        },
        {
          title: '科目',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '教师',
          key: 'teacherName',
          align: 'center'
        },
        {
          title: '授课类型',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color:
                    params.row.cucClassType !== this.classInfo.classType
                      ? '#ed4014'
                      : ''
                }
              },
              this.getClassType(params.row.cucClassType)
            )
          }
        },
        {
          title: '课堂模式',
          align: 'center',
          render: (h, params) => {
            return h('span', this.liveTypeMap[params.row.liveType])
          }
        },
        {
          title: '教室',
          key: 'classroomSimpleName',
          align: 'center'
        },
        {
          title: '辅导老师',
          key: 'tutorSysName',
          align: 'center'
        },
        {
          title: '班主任',
          key: 'classTeacherName',
          align: 'center'
        },
        {
          title: '课次状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color: params.row.status === 0 ? '#ed4014' : '#19be6b'
                }
              },
              params.row.status === 0 ? '未上课' : '已上课'
            )
          }
        },
        {
          title: '锁定状态',
          key: 'lockStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getlockStatus(params.row.lockStatus))
          }
        }
      ]
    }
  },
  methods: {
    getClassType (val) {
      return this.changeClassType[val]
    },
    getlockStatus (val) {
      return this.lockStatus[val]
    },
    // 关闭上课时间查看详情 时清空缓存
    closeClassTime () {
      this.seeTimeList = []
      this.seeClasstime = false
    },
    // 查看上课时间
    seeDetail (classInfo, fromClassDetail = false) {
      let req = {
        areaCode: classInfo.cityId,
        classId: classInfo.classId,
        pageSize: 100
      }
      this.classInfo = classInfo
      if (window.XesAnalytics) {
        XesAnalytics.track('206-5123', {
          user_role: this.$store.state.roleRelativeData.userInfo.crmRoleId,
          user_id: this.$store.state.roleRelativeData.roleInfo.userWorkCode,
          city_code: this.$store.state.roleRelativeData.userInfo.cityCode,
          user_name: this.$store.state.roleRelativeData.userInfo.empName,
          title: fromClassDetail
            ? '查看上课时间弹窗-班级详情'
            : '查看上课时间弹窗-列表'
        })
      }

      return clazzQueryCurriculumInfo(req)
        .then((res) => {
          this.seeClasstime = true
          this.seeTimeList = res.rows || []
        })
        .catch((err) => {
          return err
        })
    }
  }
}
</script>

