import HttpRequest from './axios'
import { runEnv } from './utils'
import config from '../config'
const env = runEnv()
const Platform = {
  NORMAL: '',
  SPLAT: 'SPLAT',
  BETA: 'BETA',
  ALADDIN: 'ALADDIN',
  CALLCENTER: 'call-center',
  TEACHER: 'TEACHER/bedrock-teacher',
  AUTH: 'AUTH',
  SPECIALREFUND: 'specialRefund',
  COURSE: 'course',
  MASTERBD: 'MASTER-BD/bd-web',
  NOTICE: 'notice-plat',
  UNIFYBOUND: 'unify-bound'

}
const baseURL = `${config.baseUrl[env]}/` // 一站式网关地址
const apiURL = `${config.apiUrl[env]}/` // 一站式网关地址 // api网关地址

const requestParams = {
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000 // request timeout
}
const splatAxios = new HttpRequest({
  baseURL: baseURL + Platform.SPLAT,
  ...requestParams
})
const callCenterAxios = new HttpRequest({
  baseURL: baseURL + Platform.CALLCENTER,
  ...requestParams
})
const Axios = new HttpRequest({
  baseURL: baseURL + Platform.NORMAL,
  ...requestParams
})
const teacherAxios = new HttpRequest({
  baseURL: baseURL + Platform.TEACHER,
  ...requestParams
})

const authAxios = new HttpRequest({
  baseURL: baseURL + Platform.AUTH,
  ...requestParams
})

const courseAxios = new HttpRequest({
  baseURL: baseURL + Platform.COURSE,
  ...requestParams
})

const masterBDAxios = new HttpRequest({
  baseURL: baseURL + Platform.MASTERBD,
  ...requestParams
})

const http = new HttpRequest({
  baseURL: baseURL + Platform.ALADDIN,
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000 // request timeout
})
function aladdingAxios (option) {
  return new Promise((resolve, reject) => {
    http.request(option).then(data => {
      resolve(data)
    }).catch(err => {
      reject(err.message || err)
    })
  })
}

const noticeAxios = new HttpRequest({
  baseURL: apiURL + Platform.NOTICE,
  withCredentials: true
  // timeout: 30000
})

const outboundAxios = new HttpRequest({
  baseURL: apiURL + Platform.UNIFYBOUND,
  withCredentials: false
})

export default {
  splatAxios,
  aladdingAxios,
  Axios,
  callCenterAxios,
  teacherAxios,
  authAxios,
  courseAxios,
  masterBDAxios,
  noticeAxios,
  outboundAxios
}
