<template>
  <div>
    <!--班级详情-->
    <Modal
      class='talClassDetail has-tag'
      :mask-closable="false"
      :title="classdetailName"
      v-model="isShow"
      :z-index="8888"
      :scrollable="true"
      @on-visible-change="onVisibleChange"
      width="80%">
      <Tabs :value="tabName" :animated="false" @on-click="tabsChange" name="classDetails">
        <TabPane tab="classDetails" label="基本信息" name="baseInfo" :index="1">
          <BaseInfo v-if="tabName === 'baseInfo'" ref="baseInfo" :classId="classId" @errMsg="errMsgHandler" @classInfo="setClassInfoHandler" @show-source-class-detail="handleShowSourceClassDetail"></BaseInfo>
        </TabPane>
        <TabPane tab="classDetails" label="变动信息" name="changeInfo"  :disabled="paneDis" :index="2" v-if="classInfo.businessType !== 1">
          <ChangeInfo v-if="tabName === 'changeInfo'" ref="changeInfo" :classInfo="classInfo" @errMsg="errMsgHandler"></ChangeInfo>
        </TabPane>
        <TabPane tab="classDetails" label="班级学员" name="stuInfo" :disabled="paneDis" :index="3">
          <StuInfo v-if="tabName === 'stuInfo'" ref="stuInfo" :classInfo="classInfo" :isDisplayExportBtn="isDisplayExportBtn" @errMsg="errMsgHandler"></StuInfo>
        </TabPane>
        <TabPane tab="classDetails" label="优惠信息" name="preferentialInfo" :disabled="paneDis" :index="4">
          <PreferentialInfo v-if="tabName === 'preferentialInfo'" ref="preferentialInfo" :classInfo="classInfo" @errMsg="errMsgHandler"></PreferentialInfo>
        </TabPane>
        <TabPane tab="classDetails" label="课程详情" name="courseInfo" :disabled="paneDis" :index="5">
          <course-info v-if="tabName === 'courseInfo'" ref="courseInfo" :classInfo="classInfo" @errMsg="errMsgHandler"></course-info>
        </TabPane>
      </Tabs>
      <div slot="footer">
        <Button @click="closeDetail">关闭</Button>
      </div>
    </Modal>
    <Modal
      v-model="errModal"
      :z-index="30000"
      :footer-hide="true"
      :draggable="true">
      <p class="errTxt">{{errMsg}}</p>
    </Modal>
    <TalClassDetail
      v-if="showSourceClassDetailModal"
      :show-source-class-detail-modal="false"
      :show-source-class-detail="false"
      :show="sourceClassDetailModalShow"
      :class-id="sourceClassId"
      @close="showSourceClassDetail = false" />
  </div>
</template>

<script>
import BaseInfo from './baseInfo'
import ChangeInfo from './changeInfo'
import StuInfo from './stuInfo'
import PreferentialInfo from './preferentialInfo'
import CourseInfo from './courseInfo'
export default {
  name: 'TalClassDetail',
  components: {
    BaseInfo,
    ChangeInfo,
    StuInfo,
    PreferentialInfo,
    CourseInfo
  },
  props: {
    show: { // 是否显示
      type: Boolean,
      default: false
    },
    classId: { // 班级id
      type: String,
      required: true
    },
    showSourceClassDetailModal: {
      type: Boolean,
      default: true
    },
    showSourceClassDetail: {
      type: Boolean,
      default: true
    },
    isDisplayExportBtn: {
      type: Boolean,
      default: false
    },
    isMessageDisabled: {
      type: Boolean,
      default: false
    }
  },
  provide () {
    return {
      isMessageDisabled: this.isMessageDisabled
    }
  },
  data () {
    return {
      errModal: false,
      errMsg: '',
      classInfo: {},
      classdetailName: ' 班级详情',
      tabName: '',
      isShow: false,
      errSt: '',
      stTime: 3000,
      paneDis: true,
      sourceClassDetailModalShow: false,
      sourceClassId: ''
    }
  },
  beforeDestroy () {
    this.errSt = ''
  },
  methods: {
    initView (name) {
      this.tabName = name
      this.$nextTick(() => {
        this.$refs[name].initView()
      })
    },
    errMsgHandler (msg) {
      if (this.errSt) {
        clearTimeout(this.errSt)
      }
      this.errModal = true
      this.errMsg = msg
      this.errSt = setTimeout(() => {
        this.errModal = false
      }, this.stTime)
    },
    setClassInfoHandler (obj) {
      this.classInfo = obj
      this.$nextTick(() => {
        this.paneDis = false
      })
    },
    // 点击选项卡
    tabsChange (name) {
      this.tabName = name
      this.initView(name)
    },
    onVisibleChange (status) {
      if (status) {
        this.initView('baseInfo')
      } else {
        this.paneDis = true
        this.tabName = ''
        this.closeDetail()
      }
    },
    closeDetail () {
      this.$emit('close')
    },
    handleShowSourceClassDetail (id) {
      console.log(22222)
      this.sourceClassDetailModalShow = true
      this.sourceClassId = id
    }
  },
  watch: {
    show: function (val) {
      this.isShow = val
    }
  }
}
</script>

<style scoped lang="less">
.talClassDetail {
  /deep/ .ivu-modal-body {
    position: relative;
  }
}
/deep/ .ivu-table:after,/deep/ .ivu-table:before{
  height:0;
  width:0
}
.detailMessage {
  padding: 20px;
}
.seeClasstimeNav /deep/ .ivu-table-wrapper {
  position: static !important;
}
.errTxt {
  text-align: center;
  color: #ed4014;
  margin: 0 10px;
  font-size: 14px;
}
/deep/ .ivu-message {
  z-index: 60000 !important;
}
</style>
