import axios from 'axios'
import { getPlatform, getQueryString } from './utils'
import Cookie from 'js-cookie'
const userInfo = Cookie.get('columbusUserInfo')
class HttpRequest {
  constructor (config) {
    this.baseURL = config.baseURL
    this.withCredentials = config.withCredentials
    this.timeout = config.timeout
    this.queue = {}
  }
  getInsideConfig () {
    return {
      baseURL: this.baseURL,
      withCredentials: this.withCredentials,
      timeout: this.timeout,
      headers: {
        'X-Ca-platform': getPlatform(),
        platform: sessionStorage.getItem('authPlatform'),
        // 'X-Ca-Identity': '0c63641fb8cbf6289833c21b42d13490',
        creator: userInfo ? JSON.parse(userInfo).id : null,
        empNo: userInfo ? JSON.parse(userInfo).id : null,
        userName: userInfo ? encodeURI(JSON.parse(userInfo).name) : null,
        userId: userInfo ? JSON.parse(userInfo).id : null
      }
    }
  }
  destroy (url) {
    delete this.queue[url]
    if (!Object.keys(this.queue).length) {
      // Spin.hide()
    }
  }
  interceptors (instance, url) {
    // 请求拦截
    instance.interceptors.request.use(
      config => {
        // 添加全局的loading...
        if (!Object.keys(this.queue).length) {
          // Spin.show() // 不建议开启，因为界面不友好
        }
        this.queue[url] = true
        config.params = config.params || {}
        if (config.method === 'get') {
          config.params.rn = Math.random()
        }
        // let _cityCode = config.params['cityCode'] || config.data['cityCode']
        if (config.params['cityCode']) {
          config.headers.cityCode = config.params['cityCode']
        } else if (config.data && config.data['cityCode']) {
          config.headers.cityCode = config.data['cityCode']
        }
        if (config.params['empNo']) {
          config.headers.empNo = config.params['empNo']
          config.headers.emplId = config.params['empNo'] // api网关uid换phone使用
        } else if (config.data && config.data['empNo']) {
          config.headers.empNo = config.data['empNo']
          config.headers.emplId = config.params['empNo'] // api网关uid换phone使用
        } else {
          config.headers.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
          config.headers.emplId = JSON.parse(Cookie.get('columbusUserInfo')).id // api网关uid换phone使用
        }
        if (config.params['X-Ca-referer']) {
          config.headers['X-Ca-referer'] = config.params['X-Ca-referer']
        } else if (config.data && config.data['X-Ca-referer']) {
          config.headers['X-Ca-referer'] = config.data['X-Ca-referer']
        }
        if (config.params['business']) {
          config.headers['business'] = config.params['business']
        } else if (config.data && config.data['business']) {
          config.headers['business'] = config.data['business']
        }
        if (config.params['uid']) {
          config.headers.uid = config.params['uid']
        } else if (config.data && config.data['uid']) {
          config.headers.uid = config.data['uid']
        } else {
          config.headers.uid = Cookie.get('currentStudent')
            ? JSON.parse(Cookie.get('currentStudent')).uid
            : ''
        }
        config.headers['X-Ca-userId'] = Cookie.get('currentStudent')
          ? JSON.parse(Cookie.get('currentStudent')).studentId
          : ''
        config.headers['X-Ca-platfrom'] =
          getQueryString('from') ||
          sessionStorage.getItem('X-Ca-platform') ||
          'dev'
        config.headers.PGReferer = '1234'
        return config
      },
      error => {
        debugger
        return Promise.reject(error)
      }
    )
    // 响应拦截
    instance.interceptors.response.use(
      res => {
        // debugger
        const errCode = res.data.code

        if (errCode === 'ACCESS_DENIED') {
          return Promise.reject({
            message: '您无此功能权限'
          })
        } else if (
          errCode === 'HTTP_COOKIE_VALUE_ERROR' ||
          errCode === 'HTTP_COOKIE_REQUIRED'
        ) {
          if (top === window) {
            localStorage.loginToken = ''
            localStorage.headNavList = ''
            Cookie.remove('columbusJwt')
            Cookie.remove('columbusUserInfo')
            window.location.href = '/login?v=' + Math.random()
          } else {
            setTimeout(() => {
              window.parent.postMessage(`${errCode}_sys_${getPlatform()}`, '*')
            }, 3000)
            return Promise.reject({
              message: '登录超时'
            })
          }
          /* 一站式网关异常code 需要通知父级 将该code用postmessage 冒出去就可以了 父级接受进行处理 */
        } else if (
          errCode === 'ENCODE_ERROR_INTERNAL' ||
          errCode === 'HTTP_HEADER_REQUIRED' ||
          errCode === 'HTTP_HEADER_VALUE_ERROR' ||
          errCode === 'HTTP_COOKIE_REQUIRED' ||
          errCode === 'HTTP_HEADER_FORBIDDEN' ||
          errCode === 'HTTP_REQUEST_PATH_FORBIDDEN' ||
          errCode === 'HTTP_REQUEST_ERROR' ||
          errCode === 'TOO_MANY_REQUESTS' ||
          errCode === 'CID_NOT_REGIST' ||
          errCode === 'CID_HAS_NOT_SECURET' ||
          errCode === 'SIGNATURE_NOT_MATCH' ||
          errCode === 'LOAD_PRINCIPAL_ERROR' ||
          errCode === 'LOAD_AUTHORITY_COLLECTION_ERROR' ||
          errCode === 'LOAD_RATELIMITER_ERROR'
        ) {
          // sendErrorToParentFrame(errCode)
          // "_sys_service" 为 系统标识
          if (top === window) {
            localStorage.loginToken = ''
            localStorage.headNavList = ''
            Cookie.remove('columbusJwt')
            Cookie.remove('columbusUserInfo')
            top.location.href = '/login?v=' + Math.random()
          } else {
            window.parent.postMessage(`${errCode}_sys_${getPlatform()}`, '*')
            return Promise.reject({
              message: '查询异常'
            })
          }
        } else {
          if (url === 'clazz/exportRegistListExportExcel' || url === 'reason/resultExport' || url === '/upload/getAuth' ||
          url === 'register/exportWaitListExportExcel') {
            if (res.data.error) {
              return Promise.reject({
                message: res.data.message
              })
            } else {
              return res.data
            }
          }

          // rlt 为 false 直接提示 message
          // rlt 为 true 状态 000000
          this.destroy(url)
          // const { data, status } = res
          // return { data, status }
          // console.log('@@@@', res)
          if (!res.data.rlt) {
            // iView.Message.error({
            //   content: res.data.message,
            //   duration: 3
            // })
            return Promise.reject(
              res.data || {
                message: '系统错误'
              }
            )
          } else {
            if (res.data) {
              return res.data.data
            } else {
              return Promise.reject({
                message: '系统错误'
              })
            }
          }
        }
      },
      error => {
        let jwt = Cookie.get('columbusJwt')
        if (!jwt || jwt === undefined || jwt === 'undefined') {
          top.location.href = '/login?v=' + Math.random()
        } else {
          this.destroy(url)
          return Promise.reject({
            message:
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              '前端请求错误'
          })
        }
      }
    )
  }
  request (options) {
    const instance = axios.create()
    const insideConfig = this.getInsideConfig()
    options = Object.assign({}, insideConfig, options, {
      headers: {
        ...insideConfig.headers,
        ...options.headers
      }
    })
    this.interceptors(instance, options.url)
    return instance(options)
  }
}
export default HttpRequest
