<template>
  <div id="rtcDom"></div>
</template>

<script>
// import UdcVoipRtc from '@udc/tal-voip-jssdk'
// import md5 from 'md5'
import { getPhoneByUid, callRecordInsert, callRecordUpdate } from '../../api/unify-outbound'
import { runEnv, dateFormat } from '../../utils/utils'
export default {
  name: 'TalCall',
  props: {
    appId: String,
    signature: {
      type: String,
      default: ''
    },
    timestamp: String,
    agentNo: [String, Number],
    talNo: [String, Number],
    platform: String
  },
  data () {
    return {
      phone: '',
      rtc: {},
      callIsDisable: true,
      uuid: '', // 通话记录唯一ID
      callUniqId: '', // 呼叫唯一标识（中台提供）
      uid: '',
      cityCode: ''
    }
  },
  watch: {
    signature (val) {
      if (val) {
        this.init()
      }
    }
  },
  methods: {
    /**
     * 打电话
     * @param cityCode
     * @param uid
     * @returns {Promise<void>}
     */
    async call (cityCode, uid) {
      try {
        this.cityCode = cityCode
        this.uid = uid
        await this._getPhoneByUid(uid)
        await this._callRecordInsert(cityCode, uid)
        const callInfo = await this.rtc.startCallingPhone({
          talId: '',
          callee_number: this.phone,
          pass_content: JSON.stringify({
            talId: '',
            stuUid: uid,
            cityCode,
            platFormCode: this.platform,
            emplId: this.talNo
          })
        })
        if (callInfo.errcode !== 0) {
          this.$Message.error('外呼失败：' + callInfo.errmsg)
          console.error(callInfo.errmsg)
        } else {
          console.log(callInfo) // call_uniq_id: 呼叫记录唯一ID
          this.callUniqId = callInfo.call_uniq_id
        }
      } catch (e) {
        console.error(e)
      }
    },
    async _getPhoneByUid (uid) {
      try {
        const _data = await getPhoneByUid({
          uids: uid,
          empNo: this.talNo
        })
        this.phone = _data[uid.toString()]
      } catch (e) {
        this.$Message.error('查询学员电话失败：' + e.message)
      }
    },
    async _callRecordInsert (cityCode, uid) {
      try {
        this.uuid = await callRecordInsert({
          callDirection: 2,
          called: this.phone,
          callStartTime: dateFormat(new Date()),
          stuUid: uid,
          cityCode,
          agentNo: this.agentNo,
          agentTalNo: this.talNo,
          platformCode: this.platform
        })
      } catch (e) {
        this.$Message.error('插入话单失败：' + e.message)
      }
    },
    async _callRecordUpdate (params) {
      try {
        this.uuid = await callRecordUpdate({
          uuid: this.uuid,
          callUniqId: this.callUniqId,
          callDirection: 2,
          called: this.phone,
          stuUid: this.uid,
          cityCode: this.cityCode,
          agentNo: this.agentNo,
          agentTalNo: this.talNo,
          platformCode: this.platform,
          ...params
        })
      } catch (e) {
        this.$Message.error('更新话单失败：' + e.message)
      }
    },
    /**
     * 初始化外呼组件
     */
    init () {
      const UdcVoipRtc = window.UDC_VOIP_RTC
      const env = runEnv()
      const InitUdcParams = {
        initCallBarDom: '#rtcDom', // 电话条挂载到的元素
        // UDC_SDK_ENV: '',  // 默认传空访问线上环境
        UDC_SDK_ENV: ['prod', 'release'].includes(env) ? '' : 'sandbox', // 沙箱环境
        signature: this.signature, // 申请接入后会提供e
        appid: this.appId, // 申请接入后会提供
        timestamp: this.timestamp, // 时间戳
        emplId: this.talNo, // 工号
        initCallback: (res) => {
          if (res.errcode === 0) { // 初始化成功，可以拨打电话
            console.log(res)
            this.$emit('initSuccess')
            this.callIsDisable = false
          } else {
            this.$Message.error('初始化外呼组件失败：' + res.errmsg)
          }
        }
      }
      this.rtc = new UdcVoipRtc(InitUdcParams)
      console.log(InitUdcParams)
      this.rtc.on('status', ({ state, action }) => {
        this.$emit('callStateChange', action)
        switch (action) {
          case 'calling':

            break
          case 'seat.ring':
            this._callRecordUpdate({
              alerting: dateFormat(new Date()),
              action
            })
            break
          case 'customer.ring':
            this._callRecordUpdate({
              alerting: dateFormat(new Date()),
              action
            })
            break
          case 'customer.pickup':
            this._callRecordUpdate({
              answerTime: dateFormat(new Date()),
              action
            })

            break
          case 'customer.hangup':
          case 'customer.fail':
          case 'seat.handup':
          case 'seat.cancelCall':
            this._callRecordUpdate({
              callEndTime: dateFormat(new Date()),
              action
            })
            break
          default:
            console.log('未知事件')
        }
      })
    }
  },
  mounted () {
    // this.init()
  }
}
</script>

