import $ from '../utils/api.request'

/**
 * API 查询退费申请
 * 数据项        参数方式 属性名          是否必填 描述
 * 报名ID        params    registId       Y       报名ID
 * @param params
 */

export const getRefundApply = params => {
  return $.splatAxios.request({
    url: '/refund/getRefundApply',
    params,
    method: 'get'
  })
}

