<template>
  <Modal
    v-model="isShow"
    class="pos-relative"
    :mask-closable="false"
    title="查看退费申请"
    width="960"
    cancel-text=""
    ok-text="关闭"
    @on-visible-change="onVisibleChange"
  >
    <div>
      <Table
        size="large"
        :columns="tableColumns"
        :data="tableData"
        style="margin-top: 20px"
        :loading="tableLoading"
      ></Table>
    </div>
    <div slot="footer">
      <Button @click="closeModal">关闭</Button>
    </div>
  </Modal>
</template>

<script>
import {
  getRefundApply
} from '../../api/refund'
export default {
  name: 'talFinanceRefundApplyDetails',
  props: {
    cityCode: {
      type: String,
      required: true
    },
    registId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isShow: false,
      tableColumns:[
        {title: '申请人', key: 'applicant', align: 'center'},
        {title: '申请时间', key: 'createTime', align: 'center'},
        {title: '退费原因', key: 'refundReason', align: 'center'},
        {title: '应退金额', key: 'shouldRefundAmount', align: 'center'},
        {title: '实际退费金额', key: 'actualRefundAmount', align: 'center'},
      ],
      tableData:[],
      tableLoading: true

    }
  },
  methods: {
    show () {
      this.isShow = true
    },
    onVisibleChange (val) {
      if (!val) {
        this.closeModal()
      } else {
        this.initView()
      }
    },
    closeModal () {
      this.isShow = false
    },
    initView () {
      this.tableLoading = true
      getRefundApply({
        registId: this.registId,
        cityCode: this.cityCode
      }).then(res => {
        if (res) {
          this.tableData = [res]
          console.log(this.tableData)
        }
      }).finally(() => {
        this.$nextTick(() => {
          this.tableLoading = false
        })
      })
    }
  }
}
</script>

