<template>
  <div>
    <Modal v-model="modalVisible"
           class="pos-relative"
           :mask-closable="false"
           :z-index="9999"
           :title="teacher.realName + '老师'"
           width="960"
           cancel-text=""
           ok-text="关闭"
           @on-visible-change="onVisibleChange">
      <Tabs v-model="active" name="teacherDetailTab" :animated="false" @on-click="tabsChange">
        <TabPane label="教师简介" name="baseInfo" tab="teacherDetailTab">
          <Base-info
            ref="baseInfo"
            :teacherEmpNo="teacherEmpNo"
            :teacher-id="teacherId"
            :school-code="schoolCode"
            @teacherInfo="setTeacherInfo"
            @errMsg="errMsgHandler"
            @close="modalVisible = false">
          </Base-info>
        </TabPane>
        <TabPane label="所带课程" name="classInfo" tab="teacherDetailTab" :disabled="paneDis">
          <Class-info
            ref="classInfo"
            :school-code="schoolCode"
            :type="type"
            :teacher="teacher"
            :showApply="showApply"
            @apply="setApply"
            @errMsg="errMsgHandler">
          </Class-info>
        </TabPane>
        <TabPane label="学员评价" name="evaluationInfo" tab="teacherDetailTab" :disabled="paneDis">
          <Evaluation-info
            ref="evaluationInfo"
            :schoolCode="schoolCode"
            :teacherEmpNo="teacherEmpNo"
            :teacher-id="teacherId"
            @errMsg="errMsgHandler">
          </Evaluation-info>
        </TabPane>
      </Tabs>
      <BaoClass
        :show="applyVisible"
        :classId="applyClassId"
        :stuId="stuId"
        :stuCityCode="stuSchoolCode"
        :creatorEmpNo="creatorEmpNo"
        @close="applyVisible = false">
      </BaoClass>
    </Modal>
    <Modal
      v-model="errModal"
      :z-index="30000"
      :footer-hide="true"
      :draggable="true">
      <p class="errTxt">{{errMsg}}</p>
    </Modal>
  </div>
</template>

<script>
import BaoClass from './baoClass'
import BaseInfo from './baseInfo'
import ClassInfo from './classInfo'
import EvaluationInfo from './evaluationInfo'
export default {
  name: 'TalTeacherDetails',
  components: {
    BaoClass, BaseInfo, ClassInfo, EvaluationInfo
  },
  data () {
    return {
      errModal: false,
      errMsg: '',
      errSt: '',
      stTime: 3000,
      active: '',
      teacher: {
        realName: ''
      },
      applyClassId: '',
      applyVisible: false,
      loading: false,
      modalVisible: false,
      paneDis: true
    }
  },
  props: {
    schoolCode: { // 老师分校id
      type: String,
      required: true
    },
    teacherId: { // 老师id
      type: String,
      required: true
    },
    type: { // 老师类型：'0' 主讲，'1' 辅导
      type: String,
      required: true
    },
    stuId: { // 学生id
      type: String,
      default: ''
    },
    stuSchoolCode: { // 学生分校id
      type: String,
      default: ''
    },
    creatorEmpNo: { // 操作者钉钉id
      type: String,
      default: ''
    },
    teacherEmpNo: { // 教师员工编号
      type: String,
      default: ''
    }
  },
  methods: {
    errMsgHandler (msg) {
      if (this.errSt) {
        clearTimeout(this.errSt)
      }
      this.errModal = true
      this.errMsg = msg
      this.errSt = setTimeout(() => {
        this.errModal = false
      }, this.stTime)
    },
    show () {
      this.modalVisible = true
    },
    initView (name) {
      this.$refs[name].initView()
    },
    // 点击选项卡
    tabsChange (name) {
      if (name !== 'baseInfo') {
        this.initView(name)
      }
    },
    setTeacherInfo (teacher) {
      this.teacher = teacher
      this.paneDis = false
    },
    setApply (classId) {
      this.applyClassId = classId
      this.applyVisible = true
    },
    async onVisibleChange (status) {
      if (!status) {
        this.active = 'baseInfo'
        clearTimeout(this.errSt)
        this.paneDis = true
      } else {
        this.initView('baseInfo')
      }
    }
  },
  computed: {
    showApply () {
      return !!(this.stuId && this.stuSchoolCode && this.creatorEmpNo)
    }
  }
}
</script>

<style scoped lang="less">
  .errTxt {
    text-align: center;
    color: #ed4014;
    margin: 0 10px;
    font-size: 14px;
  }
</style>
