<template>
  <div class="class-wrapper">
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div>加载中</div>
    </Spin>
    <div class="title-wrapper">
      <span class="icon"></span>
      <span class="title">任教班级在售列表</span>
      <Select v-model="term" style="width:100px" @on-change="termChange">
        <Option value="0" :selected="true">全部学期</Option>
        <Option v-for="item in terms" :value="item.id" :key="item.id">{{ item.name }}</Option>
      </Select>
    </div>
    <div class="class-info" v-for="item in classes" :key="item.classId">
      <div class="class-name">{{item.className}}</div>
      <Row class="row">
        <Col span="12">
          <span class="spa">开课时间</span>
          <span class="mess">{{item.classStartDate}}</span>
        </Col>
        <Col span="12">
          <span class="spa">结课时间</span>
          <span class="mess">{{item.classEndDate}}</span>
        </Col>
      </Row>
      <Row class="row">
        <Col span="12">
          <span class="spa">上课时间</span>
          <span class="mess">{{item.classTimeName}}</span>
        </Col>
        <Col span="12">
          <span class="spa">上课地点</span>
          <span class="mess">{{item.classRoomName}}</span>
        </Col>
      </Row>
      <Row class="row">
        <Col span="12">
          <span class="spa">授课类型</span>
          <span class="mess">{{item.classTypeName}}</span>
        </Col>
        <Col span="12">
          <span class="spa">辅导老师</span>
          <span class="mess">{{item.tutorName}}</span>
        </Col>
      </Row>
      <Row>
        <Col span="12">
          <span class="spa" style="color:#4DA2FF">报名进度</span>
          <span class="mess" style="color:#4DA2FF">{{item.registCount}}/{{item.maxPersons}}</span>
        </Col>
        <Col span="12">
          <span class="spa" style="color:#4DA2FF">总金额</span>
          <span class="mess" style="color:#4DA2FF">{{item.classPrice}}</span>
          <Button v-if="showApply" type="primary" shape="circle" @click="apply(item)">报名</Button>
        </Col>
      </Row>
    </div>
    <div v-if="!classes || classes.length == 0" class="no-data" >
      <span class="no-class"></span>
      <span class="text">未找到对应班级，请重新筛选</span>
    </div>
    <div v-else class="pager-wrapper">
      <Page :total="classTotal" :current.sync="classPageIndex" :page-size="classPageSize" @on-change="getClasses" show-total />
    </div>
  </div>
</template>

<script>
import { queryTermList } from '../../../api/bd'
import {  getTeacherClass } from '../../../api/teacher'
export default {
  name: 'teacherClassInfo',
  data () {
    return {
      classes: [],
      classPageIndex: 1,
      classPageSize: 5,
      classTotal: 0,
      applyVisible: false,
      terms: [],
      term: '0',
      loading: false,
    }
  },
  props: {
    schoolCode: { // 老师分校id
      type: String,
      required: true
    },
    teacher: { // 教师信息
      type: Object,
      required: true
    },
    type: { // 老师类型：'0' 主讲，'1' 辅导
      type: String,
      required: true
    },
    showApply: {
      type: Boolean,
      required: false
    }
  },
  methods: {
    async initView () {
      this.classPageIndex = 1
      this.classes = []
      this.loading = true
      try {
        this.terms = await queryTermList({
          schoolCode: this.schoolCode
        })
        this.getClasses()
      } catch (e) {
        this.$emit('errMsg', e)
      }
    },
    async getClasses () {
      try {
        this.loading = true
        const data = await getTeacherClass({
          current: this.classPageIndex,
          size: this.classPageSize,
          schoolCode: this.schoolCode,
          empNo: this.teacher.empNo,
          // empNo: '001099',
          type: this.type,
          termId: this.term === '0' ? '' : this.term
        })
        this.classes = data.rows
        this.classTotal = data.totalCount
      } catch (e) {
        this.$emit('errMsg', e)
      } finally {
        this.loading = false
      }
    },
    apply (classInfo) {
      this.$emit('apply', classInfo.classId)
    },
    termChange () {
      this.classPageIndex = 1
      this.getClasses()
    },
    copy (text) {
      copyText(text)
    }
  }
}
</script>

