<template>
  <div class="score-recording">
    <Form :model="formData" :label-width='70' inline style="text-align: left">
      <FormItem label="年份">
        <Select v-model="formData.year" placeholder="请选择年份" class="score-select" clearable filterable @keyup.enter.native="searchTableData">
          <Option v-for="(item,index) in yearList" :value="item" :key="index">{{ item }}</Option>
        </Select>
      </FormItem>
      <FormItem label="学期">
        <Select v-model="formData.termId" placeholder="请选择学期" class="score-select" clearable filterable @keyup.enter.native="searchTableData">
          <Option v-for="(item,index) in termList" :value="item.schoolId" :key="index">{{ item.customName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="年级">
        <Select v-model="formData.gradeId" placeholder="请选择年级" class="score-select" clearable filterable @keyup.enter.native="searchTableData">
          <Option v-for="(item,index) in gradeList" :value="item.schoolId" :key="index">{{ item.customName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="学科">
        <Select v-model="formData.subjectId" placeholder="请选择学科" class="score-select" clearable filterable @keyup.enter.native="searchTableData">
          <Option v-for="(item,index) in subjectList" :value="item.schoolId" :key="index">{{ item.customName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="创建时间">
        <DatePicker style="width: 110px" :editable="false" v-model="formData.startDateVal" @on-change="setStartTime" type="date" format="yyyy-MM-dd" @keyup.enter.native="searchTableData"></DatePicker> &nbsp;&nbsp;至&nbsp;&nbsp;
        <DatePicker style="width: 110px" :editable="false" v-model="formData.endDateVal" @on-change="setEndTime" type="date" format="yyyy-MM-dd" @keyup.enter.native="searchTableData"></DatePicker>
      </FormItem>
      <FormItem style="float: right">
        <Button type="primary" style="margin-right: 10px;" @click="searchTableData" :loading="tableLoading">查询</Button>
        <Button v-if="showRecording" style="margin-right: 10px;" @click="scoreRecord">成绩录入</Button>
        <Button v-if="showQualification" style="margin-right: 10px;" @click="qualificationSearch">查询报名资格</Button>
      </FormItem>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableData" :loading="tableLoading" no-data-text="暂无入学测试成绩">
      <template slot-scope="{ row }"  slot="levelComment">
        <div v-if="row.levelComment && row.levelComment.length > 30">
          <Tooltip max-width="300" :content="row.levelComment" placement="right">
            {{row.levelComment.substring(0, 30) + '...'}}
          </Tooltip>
        </div>
        <div v-else>{{row.levelComment}}</div>
      </template>
      <template slot-scope="{ row }"  slot="operate">
        <a v-if="row.reportUrl" :href="row.reportUrl" target="_blank">查看详情</a>
      </template>
    </Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="text-align: right;">
        <Page
          :total="formData.totalCount"
          :page-size="formData.pageSize"
          :current.sync="formData.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"></Page>
      </div>
    </div>
    <Modal
      v-model="scoreRecordShow"
      :mask-closable="false"
      width="960"
      :footer-hide="true"
      title="成绩录入"
      class="has-tag">
      <TalScoreRecord v-if="scoreRecordShow"
                     :stuId="stuId"
                     :cityCode="cityCode"
                     :stuCard="stuCard"
                     :stuUid="stuUid"
                     :stuName="stuName"
                     :hiddenDiagnosisType="hiddenDiagnosisType"
                      @success="scoreRecordShow = false">
      </TalScoreRecord>
    </Modal>
    <Qualification
      v-model="qualificationShow"
      :yearList="yearList"
      :cityCode="cityCode"
      :stuId="stuId"
      @close="qualificationShow = false">
    </Qualification>
  </div>
</template>

<script>
import { getYearList, getTermList, getGradeList, getSubjectList } from '../../api/bd'
import { queryStudentScoreList } from '../../api/student'
import TalScoreRecord from '../tal-score-recording'
import Qualification from './qualification'
export default {
  name: 'TalScoreQuery',
  components: {
    TalScoreRecord,
    Qualification
  },
  data () {
    return {
      cityList: [],
      yearList: [],
      termList: [],
      gradeList: [],
      subjectList: [],
      tableData: [],
      cols: [
        { title: '年份', align: 'center', key: 'year' },
        { title: '学期', align: 'center', key: 'term' },
        { title: '年级', align: 'center', key: 'gradeName' },
        { title: '学科', align: 'center', key: 'subjectName' },
        { title: '诊断名称', align: 'center', key: 'examName' },
        { title: '试卷名称', align: 'center', key: 'paperName' },
        { title: '分数', align: 'center', key: 'score' },
        { title: '可报班次', align: 'center', key: 'levelInfo' },
        {
          title: '报班说明',
          align: 'center',
          slot: 'levelComment',
          renderHeader: h => {
            return h('div', {}, [
              h('span', {
                style: {
                  display: 'inline-block',
                  verticalAlign: 'middle'
                }
              }, '报班说明'),
              h('Tooltip', {
                props: {
                  placement: 'bottom',
                  maxWidth: '550',
                  content: '此处只显示单个班次，优秀体系中，高级别班次自动向下兼容低级别班次。创新体系中，只对指定班次名称生效，但同时自动向下兼容全部优秀体系。独立体系中，只对指定班次名称生效'
                }
              }, [
                h('Icon', {
                  style: {
                    verticalAlign: 'middle'
                  },
                  props: {
                    size: 14,
                    type: 'md-help-circle'
                  }
                }, '')
              ])
            ]
            )
          }
        },
        { title: '成绩有效期', align: 'center', key: 'validDate' },
        { title: '来源渠道', align: 'center', key: 'source' },
        { title: '创建人', align: 'center', key: 'creatorName' },
        { title: '创建时间', align: 'center', key: 'modifyTime' },
        { title: '操作', align: 'center', slot: 'operate' }
      ],
      formData: {
        cityCode: '',
        year: '',
        termId: '',
        gradeId: '',
        subjectId: '',
        startDateVal: '',
        startTime: '',
        endDateVal: '',
        endTime: '',
        totalCount: 0,
        pageSize: 20,
        pageNo: 1
      },
      scoreRecordShow: false,
      qualificationShow: false,
      tableLoading: false
    }
  },
  props: {
    stuId: String,
    cityCode: String,
    stuCard: String,
    stuUid: [String, Number],
    stuName: String,
    showRecording: {
      type: Boolean,
      default: true
    },
    showQualification: {
      type: Boolean,
      default: true
    },
    hiddenDiagnosisType: { // 隐藏诊断类型: 呼叫平台不展示诊断类型、用户意向和渠道标签
      type: Boolean,
      default: false
    },
    from: {
      type: String,
      default: ''
    }
  },
  methods: {
    initView () {
      this.formData.cityCode = this.cityCode
      this.getYear()
      this.getTerm()
      this.getGrade()
      this.getSubject()
      this.formData.year = ''
      this.formData.termId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.startDateVal = ''
      this.formData.startTime = ''
      this.formData.endDateVal = ''
      this.formData.endTime = ''
      this.formData.pageNo = 1
      this.formData.pageSize = 20
      this.formData.totalCount = 0
      this.getTableData()
      if (this.from === 'splat') {
        XesAnalytics.track('15-3623', {})
      }
    },
    scoreRecord () {
      if (this.from === 'splat') {
        XesAnalytics.track('15-3624', {})
      }
      // this.$Message.warning({
      //   content: '前台散诊成绩请在散诊管理模块预约后录入，否则无法计入转化数据',
      //   duration: 5
      // })
      this.scoreRecordShow = true
    },
    qualificationSearch () {
      if (this.from === 'splat') {
        XesAnalytics.track('15-3625', {})
      }
      this.qualificationShow = true
    },
    /**
     * "查询"
     */
    searchTableData () {
      if (this.from === 'splat') {
        XesAnalytics.track('15-3623', {})
      }
      this.formData.pageNo = 1
      this.getTableData()
    },
    getTableData () {
      if (this.formData.startDateVal && this.formData.endDateVal && this.formData.startDateVal > this.formData.endDateVal) {
        this.$Message.warning({
          content: '创建时间：开始时间不能晚于结束时间',
          duration: 3
        })
        return
      }
      this.tableLoading = true
      queryStudentScoreList(Object.assign({}, this.formData, {
        cityCode: this.cityCode,
        stuCard: this.stuCard,
        stuUid: this.stuUid
      })).then(data => {
        this.formData.totalCount = data.totalCount
        this.tableData = data.records
      }).catch(err => {
        this.tableData = []
        this.formData.totalCount = 0
        this.$Message.error({
          content: '获取成绩查询列表失败：' + err.message
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    /**
     * 明细翻页
     * @param index
     */
    changePage (index) {
      this.formData.pageNo = index
      this.getTableData()
    },
    /**
     * 变更pageSize
     * @param size
     */
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    // /**
    //  * 计算两个时间之间的天数
    //  * @param date1 小日期
    //  * @param date2 大日期
    //  * @returns {number}
    //  * @constructor
    //  */
    // DateMinus (date1, date2) {
    //   const start = new Date(date1)
    //   const end = new Date(date2)
    //   const days = end.getTime() - start.getTime()
    //   const day = parseInt(days / (1000 * 60 * 60 * 24))
    //   return day
    // },
    // validateDate (which) {
    //   if (this.formData.startTime && this.formData.endTime) {
    //     if (this.DateMinus(this.formData.startTime, this.formData.endTime) > 90) {
    //       this.$Message.warning({ content: '时间跨度不能大于90天，请重新选择', duration: 5 })
    //       if (which === 0) {
    //         this.formData.startDateVal = ''
    //         this.formData.startTime = ''
    //       } else if (which = 1) {
    //         this.formData.endDateVal = ''
    //         this.formData.endTime = ''
    //       } else {
    //
    //       }
    //     }
    //   }
    // },
    setStartTime (val) {
      this.formData.startTime = val
      // this.validateDate(0)
    },
    setEndTime (val) {
      this.formData.endTime = val
      // this.validateDate(1)
    },
    getYear () {
      getYearList().then((data) => {
        this.yearList = data.sort((y1, y2) => Number(y2) - Number(y1))
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    },
    getTerm () {
      let params = {
        cityCode: this.formData.cityCode
      }
      getTermList(params).then(res => {
        this.termList = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: '获取学期失败:' + err.message, duration: 5 })
      })
    },
    getGrade () {
      let params = {
        cityCode: this.formData.cityCode
      }
      getGradeList(params).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error({ content: '获取年级失败:' + err.message, duration: 5 })
      })
    },
    getSubject () {
      let baseSubject = {
        cityCode: this.formData.cityCode
      }
      getSubjectList(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error({ content: '获取学科失败:' + err.message, duration: 5 })
      })
    }
  }
}
</script>

<style scoped lang="less">
.score-recording {
  .required {
    color: red;
    font-size: 16px;
  }
  .label {
    font-size: 14px;
  }
  .score-select {
    width: 100px;
    margin-right: 10px;
  }
}
</style>
