import $ from '../utils/api.request'

/**
 * 绑定优惠券
 *     couponNo 优惠劵密码 body false string
 *     couponNum body false string
 *     password body false string
 *     studentId 学员ID body false string
 */

export function bindCoupon (params) {
  return $.splatAxios.request({
    url: 'coupon/bindCoupon',
    method: 'post',
    data: params
  })
}

/**
 *
 报名管理 - 优惠券 - 优惠券列表查询
 @param cityCode 地区 query false string
 @param studentId studentId query false string
  */
export function getCouponList (params, cityCode) {
  return $.splatAxios.request({
    url: 'coupon/getCoupon',
    method: 'get',
    headers: {
      cityCode
    },
    params
  })
}
/**
 *
 报名管理 - 优惠券 - 优惠券适用班级查询
 @param cityCode 地区 query false string
 @param password password query false string
 */
export function getClassByCouponId (params) {
  return $.splatAxios.request({
    url: 'coupon/getClassByCouponId',
    method: 'get',
    params
  })
}

/**
 * 班级可用优惠券列表
 * classId 班级ID body false string
 * studentId 学员ID body false string
 */
export function getClassCouponList (params) {
  return $.splatAxios.request({
    url: 'coupon/getClassCouponList',
    method: 'get',
    params
  })
}

/**
 * 解绑优惠券
 */
export function unBindCoupon (params) {
  return $.splatAxios.request({
    url: 'coupon/unBindCoupon',
    method: 'get',
    params
  })
}
