<template>
  <div>
    <Modal
      :mask-closable="false"
      title="查看已报班级"
      v-model="modalValue"
      width="1080px">
      <div class="ivu-table ivu-table-default ivu-table-border">
        <div class="ivu-table-body">
          <table cellspacing="0" cellpadding="0" border="0" style="table-layout: fixed">
            <thead>
            <tr class="ivu-table-row">
              <th v-for="(item,index) in tableColumns" :key="item.title" class="ivu-table-column-center">
                <div class="ivu-table-cell">{{item.title}}</div>
              </th>
            </tr>
            </thead>
            <tbody class="ivu-table-tbody">
            <template v-for="(item,index) in reportData1">
              <template v-if="item.stageList && item.stageList.length">
                <tr class="ivu-table-row" v-for="(item2,index2) in item.stageList" :key="item2.id">
                  <template v-if="index2 === 0">
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell"><div style="text-align: right;" v-if="item.businessType==1"><Tag color="green">全国班</Tag></div>{{item.className}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.subjectNames}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.teacherNames}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.classtimeNames}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.classroomName}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.startDate}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.endDate}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.classCount}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.passedCount}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan"><div class="ivu-table-cell">{{item.price}}</div></td>
                    <td class="ivu-table-column-center" :rowspan="item.rowspan">
                      <div class="ivu-table-cell" v-if="item.businessType!=1">
                        <Poptip trigger="hover" width="300" word-wrap  :content="item.classChangeName" transfer>
                          {{getClassChangeName(item.classChangeName)}}
                        </Poptip>
                      </div>
                      <div class="ivu-table-cell" v-if="item.businessType==1">——</div>
                    </td>
                  </template>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item2.stagesName}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item2.paidAmount}}</div></td>
                  <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ item2.curriculumCount }}</div></td>
                  <td v-if="index2 === 0" :rowspan="item.rowspan" class="ivu-table-column-center"><div class="ivu-table-cell">{{ item.remark }}</div></td>
                </tr>
              </template>
            </template>
            </tbody>
          </table>
          <div v-show="!reportData1.length">
            <p style="text-align: center;line-height: 50px;width: 100%"><span>暂无数据</span></p>
          </div>
        </div>
      </div>
      <div slot="footer">
        <Button @click="modalValue = false">关闭</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'TalStuSubmitClass',
    props: {
      value: {
        type: Boolean,
        default: false
      },
      reportData1: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data () {
      return {
        classTypeMap: {
          1: '在线',
          2: '双师',
          4: '面授'
        },
        tableColumns: [
          {
            title: '班级名称'
          },
          {
            title: '学科'
          },
          {
            title: '教师'
          },
          {
            title: '上课时间'
          },
          {
            title: '教室'
          },
          {
            title: '开课日期'
          },
          {
            title: '结课日期'
          },
          {
            title: '总课次'
          },
          {
            title: '已上课次'
          },
          {
            title: '总金额'
          },
          {
            title: '变动记录'
          },
          {
            title: '分期'
          },
          {
            title: '实缴金额'
          },
          {
            title: '分期课次'
          },
          {
            title: '备注'
          }
        ]
      }
    },
    methods: {
      // 获取变动记录
      getClassChangeName (name) {
        if (name && name.length > 30) {
          return name.substring(0, 30) + '...'
        } else {
          return name
        }
      }
    },
    computed: {
      modalValue: {
        get: function () {
          return this.value
        },
        set: function (val) {
          this.$emit('close', val)
        }
      },
      tableWidth: function () {
        let width = 0
        this.tableColumns.forEach((e) => {
          width += e.width
        })
        if (width) {
          return width + 'px'
        } else {
          return '100%'
        }
      }
    }

  }
</script>

