<!--班级学员-->
<template>
  <div class="stuInfo">
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size="18" class="demo-spin-icon-load"></Icon>
      <div>加载中</div>
    </Spin>
    <Row>
      <Col span="2" style="border-right: 1px solid gainsboro">
        <div
          :class="tabContent == 'attendClass'? 'tabColor' : 'changeBtn'"
          @click="changeTable('attendClass')"
        >报班学员</div>
        <div
          :class="tabContent == 'applicationClass'? 'tabColor' : 'changeBtn'"
          @click="changeTable('applicationClass')"
        >退费学员</div>
        <div
          v-if="classInfo.businessType !== 1"
          :class="tabContent == 'reverseStudent'? 'tabColor' : 'changeBtn'"
          @click="changeTable('reverseStudent')"
        >转班学员</div>
        <div
          v-if="classInfo.businessType !== 1"
          :class="tabContent == 'classSwitch'? 'tabColor' : 'changeBtn'"
          @click="changeTable('classSwitch')"
        >调课学员</div>
        <div
          :class="tabContent == 'alternateStudent'? 'tabColor' : 'changeBtn'"
          @click="changeTable('alternateStudent')"
        >候补学员</div>
      </Col>
      <Col span="22" style="padding: 10px">
        <div class="newTab-top"></div>
        <div class="newTab">
          <Button
            class="export-btn"
            v-if="tabContent == 'attendClass' && reverseList.length"
            type="primary"
            @click="exportHandle"
            :disabled="exportDisaled"
            :loading="exportLoading"
          >导出报班学员</Button>&nbsp;
          <Button
            class="export-btn"
            type="primary"
            v-if="tabContent == 'attendClass' && reverseList.length && isDisplayExportBtn"
            @click="exportContact"
            :loading="exportContactLoading"
          >导出合同</Button>
          <!-- 香港分校支持 -->
          <Button
            class="export-btn"
            v-if="tabContent == 'attendClass' && reverseList.length && classInfo.cityId === '00852'"
            type="primary"
            @click="voucherParam.show=true"
          >批量发送缴费凭证</Button>
          <Table stripe :columns="Columns" :data="reverseList" height="500"></Table>
          <div v-show="isPerson" style="text-align: right">
            <span style="margin-right: 15px">候补总人数: {{totalCount}}人</span>
            <span style="margin-right: 15px">已报同期班: {{isregCount}}人</span>
            <span style="margin-right: 15px">未报同期班: {{notregCount}}人</span>
          </div>
          <Row v-if="tabContent == 'attendClass'" style="margin-top: 20px;" type="flex" justify="center">
            <Col>
              <Button @click="loadMore">加载更多</Button>
            </Col>
          </Row>
        </div>
      </Col>
    </Row>
    <Modal
      :mask-closable="false"
      title="发送缴费电子凭证"
      v-model="voucherParam.show"
      width="960"
      :z-index="8889"
      :footer-hide="true"
    >
      <TalFinanceVoucher
        v-if="voucherParam.show"
        @success="voucherParam.show=false"
        :classId="classInfo.id"
        supportedFunctions="send"
        :studentDetail="{'cityCode': classInfo.cityId}"
        batch
        isHk
      ></TalFinanceVoucher>
    </Modal>
  </div>
</template>

<script>
/* eslint-disable */
