import { downloadFile } from './../../../utils/utils'
import TalFinanceVoucher from '../../tal-finance-voucher'
export default {
  name: 'stuInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    },
    isDisplayExportBtn:{
      type: Boolean,
      default:false
    }
  },
  components: {
    Phone
  },
  data() {
    return {
      loading: false,
      exportLoading: false,
      exportDisaled: false,
      exportContactLoading: false,
      st: '',
      tabContent: '',
      isPerson: false,
      Columns: [],
      notregCount: '',
      isregCount: '',
      totalCount: '',
      registerClassStudentInfoReqs:[], // 报名列表
      reverseList: [],
      reverseColumns: [ // 转班学员
        {
          title: '编号',
          key: 'count',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'traineeName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuNo',
          align: 'center'
        },
        {
          title: '转班课次',
          key: 'sourceNum',
          align: 'center'
        },
        {
          title: '转班后起始上课日期',
          key: 'targetClassDate',
          align: 'center'
        },
        {
          title: '转班类型',
          key: 'name',
          align: 'center'
        },
        {
          title: '转班原因',
          key: 'reason',
          align: 'center'
        },
        {
          title: '转入时间',
          key: 'enterDate',
          align: 'center'
        },
        {
          title: '转出时间',
          key: 'outDate',
          align: 'center'
        },
        {
          title: '辅导老师',
          key: 'tutorRealName',
          align: 'center'
        },
        {
          title: '操作人',
          key: 'employeeName',
          align: 'center'
        },
        {
          title: '操作平台',
          key: 'platformName',
          align: 'center'
        }

      ], // 退费学员
      attendStudentColumns: [
        {
          title: '编号',
          key: 'count',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'traineeName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuNo',
          align: 'center'
        },
        {
          title: '辅导教师',
          key: 'tutorRealName',
          align: 'center'
        },
        {
          title: '缴费状态',
          key: 'ispay',
          align: 'center',
          render: (h, params) => {
            return h('div', this.getIspay(params.row.ispay))
          }
        },
        {
          title: '书籍缴费状态',
          key: 'ispay',
          align: 'center',
          render: (h, params) => {
            return h('div', this.getIspay(params.row.bookFeePayStatus))
          }
        },
        {
          title: '是否预留转班',
          key: 'startDate',
          align: 'center',
          render: (h, params) => {
            if (this.classInfo.businessType === 1) {
              return h('span', '—————')
            } else {
              return h('span', {}, params.row.isReserved === 0 ? '否' : '是')
            }
          }
        },
        {
          title: '报名操作人',
          key: 'employeeName',
          align: 'center'
        },
        {
          title: '报班时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '缴费时间',
          key: 'payDate',
          align: 'center'
        },
        {
          title: '待签合同数',
          key: 'awaitSignContractCount',
          align: 'center'
        },
        {
          title: '已签合同数',
          key: 'signContractCount',
          align: 'center'
        }
      ],
      applicationClassList: [
        {
          title: '编号',
          key: 'count',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'traineeName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuNo',
          align: 'center'
        },
        {
          title: '报名操作人',
          key: 'regEmployeeName',
          align: 'center'
        },
        {
          title: '退费操作人',
          key: 'refundEmployeeName',
          align: 'center'
        },
        {
          title: '退费课次',
          key: 'classNum',
          align: 'center'
        },
        {
          title: '操作原因一级',
          key: 'actualFirstReason',
          align: 'center',
          width: 150
        }, {
          title: '操作原因二级',
          key: 'actualSecondReason',
          align: 'center',
          width: 150
        }, {
          title: '原因备注',
          key: 'reasonRemark',
          align: 'center',
          width: 150
        },
        {
          title: '退费平台',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.refundPlatform === '0' ? '业务系统' : '学而思app')
          }
        },
        {
          title: '退费时间',
          key: 'createDate',
          align: 'center'
        }
      ], // 调课学员
      classSwitchList: [
        {
          title: '编号',
          key: 'count',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'traineeName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuNo',
          align: 'center'
        },
        {
          title: '原班',
          key: 'sourceClassName',
          align: 'center'
        },
        {
          title: '原班课次',
          key: 'sourceNum',
          align: 'center'
        },
        {
          title: '目标班',
          key: 'targetClassName',
          align: 'center'
        },
        {
          title: '目标班课次',
          key: 'targetNum',
          align: 'center'
        },
        {
          title: '调入后上课时间',
          key: 'targetClassdateName',
          align: 'center'
        },
        {
          title: '调课原因',
          key: 'reason',
          align: 'center'
        },
        {
          title: '调课标识',
          key: 'changeInOutName',
          align: 'center'
        },
        {
          title: '调课日期',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '调课类型',
          key: 'name',
          align: 'center'
        },
        {
          title: '状态',
          key: 'changeStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getChangeStatus(params.row.changeStatus))
          }
        },
        {
          title: '操作人',
          key: 'employeeName',
          align: 'center',
          width: 100
        },
        {
          title: '操作平台',
          key: 'changeType',
          width: 100,
          render: (h, params) => {
            return h('span', this.getSwitchType(params.row.changeType))
          }
        }
      ],
      alternate: [
        {
          title: '编号',
          key: 'count',
          align: 'center',
          minWidth: 80
        },
        {
          title: '学员姓名',
          key: 'traineeName',
          align: 'center',
          minWidth: 100
        },
        {
          title: '状态',
          key: 'waitStatus',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', this.getWaitStatus(params.row.waitStatus))
          }
        },
        {
          title: '候补操作人',
          key: 'employeeName',
          align: 'center',
          width: 100
        },
        {
          title: '候补原因',
          key: 'description',
          align: 'center',
          minWidth: 100
        },
        {
          title: '联系方式',
          key: 'contact',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h(Phone, {
              props: {
                row: params.row
              }
            })
          }
        },
        {
          title: '是否报同期班',
          key: 'isreg',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('span', params.row.isreg === 0 ? '未报' : '已报')
          }
        },
        {
          title: '候补时间',
          key: 'createDate',
          align: 'center',
          minWidth: 130
        }
      ],
      ispay: {
        0: '未缴费',
        1: '已缴费',
        2: '缴费处理中',
        3: '已退费',
        4: '缴费中'
      }, // 调课学员
      changeStatus: {
        0: '调课',
        1: '取消调课',
        2: '再次调课'
      },
      getchangeType: {
        0: '前台',
        1: '学校',
        3: 'APP'
      },
      getwaitStatus: {
        0: '候补中',
        1: '已报名',
        2: '转班'
      },
      // 批量发送凭证
      voucherParam: {
        show: false,
      },
      pageNo: 1, // 分页
      pageSize: 50, // 单页数量

    }
  },
  mounted() {
    console.log(this.isDisplayExportBtn)
  },
  methods: {
    loadMore() {
      this.pageNo++;
      this.attendClass()
    },
    initView() {
      this.changeTable('attendClass')
    },
    changeTable(a) {
      this.tabContent = a
      switch (this.tabContent) {
        case 'attendClass':
          this.pageNo = 1;
          this.reverseList = [];
          this.attendClass()
          break
        case 'applicationClass':
          this.applicationClass()
          break
        case 'reverseStudent':
          this.reverseStudent()
          break
        case 'classSwitch':
          this.classSwitch()
          break
        case 'alternateStudent':
          this.alternateStudent()
          break
      }
    },
    // 转班学员
    reverseStudent() {
      this.isPerson = false
      this.Columns = this.reverseColumns
      let reverse = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.loading = true
      queryChangeStudentsInfo(reverse).then(res => {
        this.reverseList = res
        this.reverseList.forEach((e, i) => {
          // if (e.startDate) {
          //   e.startDate = e.startDate.substring(0, 10)
          // }
          // if (e.endDate) {
          //   e.endDate = e.endDate.substring(0, 10)
          // }
          e.Date = e.startDate + '-' + e.endDate
          e.count = i + 1
        })
      }).catch(err => {
        // this.$Message.error({ content: '获取转班学员失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取转班学员失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    // 报班学员
    attendClass() {
      this.isPerson = false
      this.Columns = this.attendStudentColumns
      let attend = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }
      this.loading = true
      queryRegistStudentsInfo(attend).then(res => {
        // this.reverseList = res
        if (res.length > 0) {
          res.forEach((e, i) => {
            // if (e.createDate) {
            //   e.createDate = e.createDate.substring(0, 10)
            // }
            // if (e.payDate) {
            //   e.payDate = e.payDate.substring(0, 10)
            // }
            e.count = i + 1 + this.reverseList.length
          })
        } else if(res.length === 0 && this.pageNo !== 1){
          this.pageNo--;
          this.$Message.info({ content: '没有更多学员了', duration: 5 })
        }

        this.reverseList.push(...res)
      }).catch(err => {
        // this.$Message.error({ content: '获取报班学员失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取报班学员失败:' + err)
        this.pageNo--
        if (this.pageNo < 1) this.pageNo = 1
      }).finally(() => {
        this.loading = false
      })
    },
    // 退费学员
    applicationClass() {
      this.isPerson = false
      this.Columns = this.applicationClassList
      // queryRefundInfo
      let application = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.loading = true
      queryRefundStudentsInfo(application).then(res => {
        this.reverseList = res
        if (res.length >= 0) {
          this.reverseList.forEach((e, i) => {
            // if (e.createDate) {
            //   e.createDate = e.createDate.substring(0, 10)
            // }
            e.count = i + 1
          })
        } else {
          this.reverseList = []
        }
      }).catch(err => {
        // this.$Message.error({ content: '获取退费学员列表失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取退费学员列表失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    // 调课学员
    classSwitch() {
      this.isPerson = false
      this.Columns = this.classSwitchList
      let switchData = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.loading = true
      queryChangeCourseInfo(switchData).then(res => {
        this.reverseList = res
        if (res.length >= 0) {
          this.reverseList.forEach((e, i) => {
            // if (e.createDate) {
            //   e.createDate = e.createDate.substring(0, 10)
            // }
            e.count = i + 1
          })
        } else {
          this.reverseList = []
        }
      }).catch(err => {
        // this.$Message.error({ content: '获取调课学员列表失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取调课学员列表失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    // 候补学员
    alternateStudent() {
      this.Columns = this.alternate
      let alternateData = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.loading = true
      queryCandidateStudentsInfo(alternateData).then(res => {
        this.reverseList = res.list
        this.isPerson = true
        this.totalCount = res.totalCount
        this.notregCount = res.notregCount
        this.isregCount = res.isregCount
        if (res.list.length > 0) {
          this.reverseList.forEach((e, i) => {
            // if (e.createDate) {
            //   e.createDate = e.createDate.substring(0, 10)
            // }
            e.count = i + 1
          })
        } else {
          this.reverseList = []
        }
      }).catch(err => {
        // this.$Message.error({ content: '获取候补学员列表失败:' + err, duration: 5 })
        this.$emit('errMsg', '获取候补学员列表失败:' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    getIspay(val) {
      return this.ispay[val]
    },
    getChangeStatus(val) {
      return this.changeStatus[val]
    },
    getSwitchType(val) {
      return this.getchangeType[val]
    },
    getWaitStatus(val) {
      return this.getwaitStatus[val]
    },
    // 导出合同
    exportContact() {
      this.registerClassStudentInfoReqs =  this.reverseList.forEach(item=>{
       item.contractNo !== null && item.contractNo.forEach(contractItem=>{
            this.downloadContact({
            stuNo:item.stuNo,
            stuName:item.traineeName,
            stuUid:item.studentUid,
            contractNo: contractItem
          })
        }) 
      })      
    },
    downloadContact(params) {
      this.exportContactLoading = true
      exportContactDownload({
        cityCode: this.classInfo.cityId,
        className: this.classInfo.className,
        ...params
      }).then(res => {
        this.exportContactLoading = false
        downloadFile(res.contractPdfBase64,res.className + params.stuName,'pdf')
      }).catch(err => {
        this.$Message.error({ content: '获取数据失败:' + err.message || err, duration: 5 })
      })
    },
    exportHandle() {
      console.log('exportHandle', this.classInfo)
      this.exportLoading = true
      exportRegistListExportExcel({
        cityCode: this.classInfo.cityId,
        classId: this.classInfo.id
      }).then(res => {
        const reader = new FileReader()
        reader.addEventListener('loadend', (e) => {
          try {
            const result = JSON.parse(e.target.result)
            this.$Message.error((result && result.message) || '导出失败！')
          } catch (errMsg) {
            let objectUrl = URL.createObjectURL(new Blob([res]))
            if (objectUrl) {
              const link = document.createElement('a')
              link.download = this.classInfo.className + '-报班学员表.xlsx'
              link.href = objectUrl
              link.click()
              this.exportDisaled = true
              this.st = setTimeout(() => {
                clearTimeout(this.st)
                this.exportDisaled = false
              }, 10000)
            }
          }
        })
        reader.readAsText(new Blob([res]))
      }).catch(err => {
        this.$Message.error({ content: '获取数据失败:' + err.message || err, duration: 5 })
      }).finally(() => {
        this.exportLoading = false
      })
    }
  }
}
</script>

