<template>
  <div style="position: relative;">
    <Button class="btn-invoice" @click.stop="handleInvoice">一键开票</Button>
    <div class="ivu-table ivu-table-default ivu-table-border relative">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <div class="ivu-table-body ivu-table-overflowX">
        <CheckboxGroup
          v-model="checkAllGroup"
          style="font-size: 12px"
          @on-change="handleCheckGroupChange"
        >
          <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
            <thead>
              <tr class="ivu-table-row">
                <th
                  v-for="(item,index) in tableColumns"
                  :key="index"
                  class="ivu-table-column-center"
                  :width="item.width"
                >
                  <template v-if="item.isCheckBox">
                    <label class="tal-ipt-checkbox" style="margin-left: 0">
                      <input
                        type="checkbox"
                        class="ivu-checkbox-inner"
                        style="margin-left: -9px;"
                        @click="checkAllHandler"
                        :checked="checkAll"
                      />
                      <span></span>
                    </label>
                  </template>
                  <div class="ivu-table-cell" v-else>{{item.title}}</div>
                </th>
              </tr>
            </thead>
            <tbody class="ivu-table-tbody">
              <template v-for="(item) in tableData">
                <template v-if="item.stages && item.stages.length">
                  <tr
                    class="ivu-table-row"
                    v-for="(item2,index2) in item.stages"
                    :key="item2.workOrderId"
                  >
                    <template v-if="index2 === 0">
                      <!-- 报名ID -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">
                          <Button
                            size="small"
                            type="primary"
                            ghost
                            @click="copy(item.registId)"
                            style="margin-right: 5px"
                          >复制</Button>
                          <span>{{item.registId}}</span>
                        </div>
                      </td>
                      <!-- 课程名称 -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">
                          <a @click="showClassDetail(item.classId)">{{ item.className || '--'}}</a>
                        </div>
                      </td>
                      <!-- 业务属性 -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">{{ businessTypeMap[item.businessBelong] }}</div>
                      </td>
                      <!-- 授课类型 -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">{{ getClassType(item.classType) }}</div>
                      </td>
                      <!-- 学科 -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">{{ item.subjectNames }}</div>
                      </td>
                      <!-- 总金额 -->
                      <td class="ivu-table-column-center" :rowspan="item.stages.length">
                        <div class="ivu-table-cell">{{ item.price }}</div>
                      </td>
                    </template>

                    <!-- 分期类型 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{ item2.stagesName || '--' }}</div>
                    </td>
                    <!-- 开课时间 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{ item.classStartDate || '--' }}</div>
                    </td>
                    <!-- 节课时间 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{ item.classEndDate || '--' }}</div>
                    </td>
                    <!-- 分期课次 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{ item2.startNum + '-' + item2.endNum }}</div>
                    </td>
                    <!-- 实缴金额 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">{{ item2.paidAmount }}</div>
                    </td>
                    <!-- 发票状态 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <p v-for="(text, index) in item2.aggInvoiceStatus" :key="index">{{ text }}</p>
                      </div>
                    </td>
                    <!-- 失败原因 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <p v-for="(text, index) in item2.failReasons" :key="index">{{ text }}</p>
                      </div>
                    </td>
                    <!-- 操作 -->
                    <td class="ivu-table-column-center">
                      <a
                        href="javascript:void(0);"
                        v-if="item2.haveDetail"
                        @click="viewInvoiceDetail(item.registId, item.classType, item2.num, item2.isNew)"
                      >查看发票</a>
                    </td>
                    <!-- 选择 -->
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <Checkbox
                          :label="item2.label"
                          v-if="item2.operational && item2.paidAmount > 0"

                          :disabled="disabledCheckBox(item2.label, item.registId)"
                        >{{ }}</Checkbox>
                        <span v-if="item2.paidAmount <= 0" style="color: gray;">实缴金额小于等于0不可开票</span>
                      </div>
                    </td>
                  </tr>
                </template>
              </template>
            </tbody>
          </table>
        </CheckboxGroup>
        <div v-show="!tableData.length">
          <p style="text-align: center;line-height: 50px;width: 100%">
            <span>暂无数据</span>
          </p>
        </div>
      </div>
    </div>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }" v-show="!tableLoading">
      <div style="float: right;">
        <Page
          :total="pageTotal"
          :page-size="currentPageSize"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        ></Page>
      </div>
    </div>
    <!-- 班级详情 -->
    <TalClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></TalClassDetail>

    <TalInvoiceQueryDetail v-bind="invoiceDetail" :cityCode="cityCode"></TalInvoiceQueryDetail>
    <Modal v-model="invoiceModal" footer-hide :width="1200" :transfer="false" title="一键开票">
      <TalFinanceInvoiceTool
        v-if="invoiceModal"
        :student-no="card"
        :student-name="realName"
        :student-id="studentId"
        :city-code="cityCode"
        :uid="uid"
        :mixin-params="checkAllGroup"
      ></TalFinanceInvoiceTool>
    </Modal>
  </div>
</template>

<script>
import { queryInvoiceList } from '../../api/invoice'
import { copyText } from '../../utils/utils'
import TalInvoiceQueryDetail from '../tal-invoice-query-detail'
import TalClassDetail from '../tal-class-details'
import TalFinanceInvoiceTool from '../tal-finance-invoice-tool'
const SEPRATOR = '|'
export default {
  name: 'TalFinanceInvoiceAssistant',
  components: {
    TalInvoiceQueryDetail,
    TalClassDetail,
    TalFinanceInvoiceTool
  },
  props: {
    formData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    uid: {
      type: [Number, String],
      default: ''
    },
    cityCode: {
      type: String,
      default: ''
    },
    studentId: {
      type: String,
      required: true
    },
    realName: {
      type: String,
      required: true
    },
    card: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      tableColumns: Object.freeze([
        {
          title: '报名ID'
          // width: 150
        },
        {
          title: '班级名称',
          width: 100
        },
        {
          title: '业务属性',
          width: 100
        },
        {
          title: '授课类型',
          width: 65
        },
        {
          title: '学科',
          width: 65
        },
        {
          title: '总金额'
          // width: 100
        },
        {
          title: '分期类型',
          width: 65
        },
        {
          title: '开课时间'
          // width: 140
        },
        {
          title: '结课时间'
          // width: 140
        },
        {
          title: '分期课次',
          width: 65
        },
        {
          title: '实缴金额',
          width: 65
        },
        {
          title: '发票状态'
          // width: 150
        },
        {
          title: '失败原因'
          // width: 100
        },
        {
          title: '操作',
          width: 65
        },
        {
          title: 'checkbox',
          isCheckBox: true
          // width: 150
        }
      ]),
      tableData: [],
      tableLoading: false,
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数,
      classDetail: { // 班级详情
        id: '78217601bebb4e7e8e4cf49bf4d0db31',
        show: false
      },
      checkAllGroup: [], // checkboxGroup选中项
      allItem: [], // 所有可选项
      checkAll: false, // 是否全选
      maxStageNum: 20, // 最多可以勾选的分期数量
      maxClassNum: 10, // 最多勾选5个班级
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授',
        8: '线上班',
        16: '自学课'
      },
      businessTypeMap: {
        1: '学而思培优',
        3: '学而思素养中心',
        5: '学而思国际'
      },
      invoiceDetail: {
        detailParams: {
          registerId: '',
          stagesNum: '',
          isNew: null
        },
        ifRedDashed: true
      },
      invoiceModal: false
    }
  },
  computed: {
    checkedClasses () {
      let classes = []
      this.checkAllGroup.forEach(item => {
        classes.push(item.split(SEPRATOR)[0])
      })

      return [...new Set(classes)]
    }
  },
  methods: {
    /***
     * 获取已选班级数量
     */
    getClassSize () {
      let classes = []
      this.checkAllGroup.forEach(item => {
        classes.push(item.split(SEPRATOR)[0])
      })

      return new Set(classes).size
    },
    // 查看发票详情
    viewInvoiceDetail (registerId, classType, stagesNum, isNew) {
      Object.assign(this.invoiceDetail.detailParams, {
        registerId,
        classType,
        stagesNum,
        isNew
      })
    },
    /****
     *
     * 是否提示包含未结课班级
     * @param { String } type 1 当前新增 2 全选
     */
    showNotOverclassTips (type = 1, isAdd = false) {
      let show = false
      const typeBol = (item2) => {
        let bol = false
        if (type === 1) {
          bol = this.checkAllGroup[this.checkAllGroup.length - 1] === item2.label && isAdd
        } else {
          bol = this.checkAllGroup.indexOf(item2.label) > -1
        }
        return bol
      }
      this.tableData.forEach(item => {
        item.stages.forEach(item2 => {
          if (item2.status !== 2 && typeBol(item2)) {
            show = true
          }
        })
      })
      if (show) {
        // 是否选中包含未结课班级
        this.$Message.warning({ content: `未结课的班级提前开票，可能会无法进行调课、转班等操作，建议家长在班级结课后开发票。`, duration: 5 })
      }
    },
    /****
     * 一键开票
     */
    handleInvoice () {
      console.log('一键开票', this.checkAllGroup)
      if (this.checkAllGroup.length === 0) {
        this.$Message.warning({ content: '请选择需要开发票的分期' })
        return
      }
      this.invoiceModal = true
    },
    // 获取授课类型
    getClassType (type) {
      return this.classTypeMap[type]
    },
    // 获取开票状态对应操作
    getOperateByStatus (status) {
      switch (status) {
        // 未开
        case 0:
          return {
            label: '未开发票',
            view: false,
            check: true
          }
        // 部分已开
        case 1:
          return {
            label: '部分已开',
            view: true,
            check: true
          }
        // 全部已开
        case 2:
          return {
            label: '已开发票',
            view: true,
            check: false
          }
      }
    },
    // 禁选 10个分期 5个班级
    disabledCheckBox (label, registId) {
      let overLimit = this.checkAllGroup.length >= this.maxStageNum || (this.checkedClasses.length >= this.maxClassNum && this.checkedClasses.indexOf(registId) === -1)
      if (this.checkAllGroup.indexOf(label) === -1 && overLimit) return true
      return false
    },
    // 全选
    checkAllHandler () {
      this.checkAll = !this.checkAll

      if (this.allItem.length === 0) {
        this.$nextTick(() => {
          this.checkAll = false
        })

        return this.$Message.warning({ content: '没有可开发票的分期' })
      }

      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        // 全选，限制班级数量为5，分期数量为10
        this.allItem.forEach((item) => {
          const classRegistId = item.split(SEPRATOR)[0]
          if (this.checkAllGroup.indexOf(item) === -1 &&
            this.checkAllGroup.length < this.maxStageNum &&
            (this.checkedClasses.length < this.maxClassNum || (this.checkedClasses.length === this.maxClassNum && this.checkedClasses.indexOf(classRegistId) > -1))) {
            this.checkAllGroup.push(item)
          }
        })
        this.showNotOverclassTips(2)
      }
    },
    // 复制班级报名id
    copy (text) {
      copyText(text)
    },
    // 展示班级详情
    showClassDetail (classId) {
      this.classDetail.id = classId
      this.$nextTick(() => {
        this.classDetail.show = true
      })
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    // 查询开票助手列表
    async getTableData () {
      this.tableLoading = true
      this.tableData = []
      this.checkAllGroup = []
      this.checkAll = false
      const params = {
        studentUid: this.uid,
        cityCode: this.cityCode,
        pageNo: this.currentPageNum,
        pageSize: this.currentPageSize,
        type: this.formData.type ? this.formData.type : undefined,
        year: this.formData.yearInvoice ? this.formData.yearInvoice : undefined,
        termId: this.formData.term ? this.formData.term : undefined,
        classType: this.formData.classType ? this.formData.classType : undefined
      }
      try {
        const data = await queryInvoiceList(params)

        const allItem = []
        data.records.forEach(item => {
          item.stages.forEach(item2 => {
            if (item2.operational && item2.paidAmount > 0) {
              const label = `${item.registId}${SEPRATOR}${item2.num}${SEPRATOR}${item.venueId}${SEPRATOR}${item2.payTime}`
              allItem.push(label)
              item2.label = label
            }
          })
        })
        this.tableData = Object.freeze(data.records)
        this.allItem = Object.freeze(allItem)
        this.pageTotal = data.totalCount
        if (this.currentPageNum === 1) {
          this.$Message.warning('仅支持对近两年内缴费的班级开票')
        }
      } catch (error) {
        this.tableData = []
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
      this.tableLoading = false
    },
    // checkGroup on-change
    handleCheckGroupChange () {
      // 是否处于全选状态
      let checkAll = true
      this.allItem.forEach((item) => {
        const classRegistId = item.split(SEPRATOR)[0]
        if (this.checkAllGroup.indexOf(item) === -1 &&
          this.checkAllGroup.length < this.maxStageNum &&
          (this.checkedClasses.length < this.maxClassNum ||
            (this.checkedClasses.length === this.maxClassNum &&
              this.checkedClasses.indexOf(classRegistId) > -1)
          )) {
          checkAll = false
        }
      })

      this.checkAll = checkAll
    },
    _observe () {
      return new Promise(resolve => {
        this._observe.resolve = resolve
      })
    },
    onSuccess () {
      this._observe.resolve()
    }
  },
  watch: {
    checkAllGroup (n, o) {
      if (n.length === this.maxStageNum) {
        this.$Message.warning({ content: `最多支持选择${this.maxStageNum}个分期进行开发票`, duration: 5 })
      }

      if (this.checkedClasses.length >= this.maxClassNum) {
        this.$Message.warning({ content: `最多支持选择${this.maxClassNum}个班级进行开发票`, duration: 5 })
      }

      this.showNotOverclassTips(1, n.length > o.length)
    }
  },
  mounted () {
    // this.getTableData()
  }
}
</script>

<style lang="less" scoped>
.btn-invoice {
  position: absolute;
  right: 50px;
  top: 0;
  transform: translate(0, -50px);
}
.ivu-table:after,
.ivu-table:before {
  height: 0;
  width: 0;
}
.gary {
  color: #ccc;
}
.orange {
  color: #f59a23;
}
</style>
