<script>
import {
  queryClassDetail,
  queryCourseOutlineInfo,
  queryStagesInfo,
  queryClazzDic,
  queryPostClassInfoList,
  queryClassExtraDetail
} from '../../../api/clazz'
import { getRegistTimeByClass } from '../../../api/register'
import SeeStage from './seeStage'
import ClassPresentation from './classpresentation'
import SendPositionMsg from './sendPositionMsg'
import { copyText } from '../../../utils/utils'
import TalSchooltime from '../../tal-schooltime'
import SendWeChat from './sendWeChat.vue'
export default {
  name: 'baseInfo',
  props: {
    classId: {
      type: String,
      required: true
    }
  },
  components: {
    SendPositionMsg,
    SeeStage,
    ClassPresentation,
    TalSchooltime,
    SendWeChat
  },
  data () {
    return {
      loading: false,
      classInfo: {},
      stageArr: [],
      pageStart: 1,
      pageSize: 10,
      pageTotal: 0,
      changeClassType: {
        1: '在线',
        2: '双师',
        4: '面授',
        5: '自习'
      },
      bizType: {
        0: '普通课程',
        1: '第二课堂',
        2: '学而思国际',
        4: '赠送类课程',
        8: '在线小班',
        16: 'VIPX',
        32: '家庭班',
        64: '社区自学',
        128: '微课'
      },
      lockStatus: {
        0: '未锁定',
        1: '已锁定',
        2: '已解锁'
      },
      classWay: {
        0: '通用课堂',
        2: '新通用课',
        1: '小组课',
        3: '新小组课',
        6: '组合小班课',
        20: '同城课',
        100: '小班课'
      },
      seeStageShow: false,
      recommentShow: false,
      // 课程简介默认值
      presentationList: {
        courseOutline: {
          outlineImgDesc: [],
          recruitIntro: '',
          trainTarget: '',
          videoHref: ''
        }
      },
      sendPositionMsgShow: false,
      showSendWeChatModal: false,
      bigClassPageNo: 1,
      bigClassPageSize: 10,
      bigClassList: [],
      bigClassTotalCount: 0,
      bigClassColumns: [
        {
          title: '班级序号',
          align: 'center',
          key: 'classOrder'
        },
        {
          title: '是否上架',
          align: 'center',
          key: 'status',
          render: (h, params) => {
            return h('span', params.row.status === 1 ? '否' : '是')
          }
        },
        {
          title: '辅导编号',
          align: 'center',
          key: 'tutorEmpNo'
        },
        {
          title: '辅导真实名称',
          align: 'center',
          key: 'tutorRealName'
        },
        {
          title: '辅导系统名称',
          align: 'center',
          key: 'tutorSysName'
        },
        {
          title: '限额人数',
          align: 'center',
          key: 'maxPersons'
        },
        {
          title: '报名人数',
          align: 'center',
          key: 'registPersons'
        },
        {
          title: '剩余人数',
          align: 'center',
          key: 'surplusPersons'
        },
        {
          title: '班级id',
          align: 'center',
          key: 'classId'
        },
        {
          title: '班级名称',
          align: 'center',
          key: 'className'
        }
      ],
      isDisplayBigClass: false,
      courseSourceId: '',
      maxPersonBoundaryObj: {},
      maxPersonBoundaryList: [],
      bigClassCityId: ''
    }
  },
  mounted () {},
  methods: {
    // 获取限额标签
    getClassDic (cityId) {
      try {
        let reqParams = {
          platform: 3,
          enName: 'limitLabel',
          cityCode: cityId
        }
        queryClazzDic(reqParams).then(res => {
          if (res !== '') {
            this.maxPersonBoundaryList = JSON.parse(res.value)
            this.maxPersonBoundaryObj = {}
            this.maxPersonBoundaryList.forEach(item => {
              this.maxPersonBoundaryObj[item.id] = item.name
            })
          }
        })
      } catch (error) {
        this.$Message.error({
          content: `获取限额标签失败: ${error.message}`,
          duration: 5
        })
      }
    },
    async initView () {
      this.classInfo = {}
      this.loading = true
      try {
        const classBaseDetail = await queryClassDetail({
          classId: this.classId
        })
        const classExtraDetail = await queryClassExtraDetail({
          classId: this.classId,
          area: classBaseDetail.cityId
        })
        if (classBaseDetail.sourceClassId) {
          let sourceClassId = classBaseDetail.sourceClassId.split(',')[0]
          const sourceClassName = await queryClassDetail({
            classId: sourceClassId
          })
          classBaseDetail.sourceClassName = sourceClassName.className || '-'
        }
        this.getClassDic(classBaseDetail.cityId)
        classBaseDetail.teacherNames = classBaseDetail.teachers !== null && classBaseDetail.teachers[0].teacherName
        classBaseDetail.coursewareSend = (classBaseDetail.otherFunDto && classBaseDetail.otherFunDto.coursewareSend) || ''
        classBaseDetail.coursewareSendAddress = (classBaseDetail.otherFunDto && classBaseDetail.otherFunDto.coursewareSendAddress) || ''
        classBaseDetail.id = classBaseDetail.classId
        const classRegistTime = await getRegistTimeByClass({
          classId: this.classId,
          area: classBaseDetail.cityId
        })
        const classInfo = Object.assign(classBaseDetail, classExtraDetail)
        console.log(classInfo, 'sss')
        this.classInfo = Object.assign(classInfo, classRegistTime)
        this.$emit('classInfo', this.classInfo)
      } catch (err) {
        this.$emit('errMsg', '获取班级信息失败:' + err)
        this.$emit('close')
      } finally {
        this.loading = false
      }
    },
    handleBigClassDetails () {
      this.bigClassCityId = this.classInfo.cityId
      this.courseSourceId = this.classInfo.courseSourceId
      this.fetchPostClassInfoList()
    },
    fetchPostClassInfoList () {
      const reqParams = {
        cityId: this.bigClassCityId,
        courseSourceId: this.courseSourceId,
        pageNo: this.bigClassPageNo,
        pageSize: this.bigClassPageSize
      }
      queryPostClassInfoList(reqParams).then(res => {
        this.isDisplayBigClass = true
        this.bigClassList = res.records || []
        this.bigClassPageNo = res.pageNo
        this.bigClassTotalCount = res.totalCount
      })
    },
    onBigClassChangePage (num) {
      this.bigClassPageNo = num
      this.fetchPostClassInfoList()
    },
    getClassType (val) {
      return this.changeClassType[val]
    },
    getCourseWare (val) {
      console.log(val, 'nn')
      let a = ''
      if (!val || val === '0') {
        return a
      } else if (val === '1') {
        a = '是'
        return a
      } else if (val === '2') {
        a = '否'
        return a
      }
    },
    getCourseWareAddress (val1, val2) {
      console.log(val1, val2, 'nb')
      let a = ''
      if (!val1 || val1 === '0' || val1 === '2' || !val2 || val2 === '0') {
        return a
      } else if (val2 === '1') {
        a = '总校'
        return a
      } else if (val2 === '2') {
        a = '分校'
        return a
      }
    },
    getStartEndTime: function () {
      let time = ''
      if (this.classInfo.startDate && this.classInfo.endDate) {
        time =
          this.classInfo.startDate.substring(0, 10) +
          ' / ' +
          this.classInfo.endDate.substring(0, 10)
      } else if (this.classInfo.startDate) {
        time = this.classInfo.startDate.substring(0, 10) + ' / '
      } else if (this.classInfo.endDate) {
        time = ' / ' + this.classInfo.endDate.substring(0, 10)
      }
      return time
    },
    getbizType (val) {
      return this.bizType[val]
    },
    getClassWay (val) {
      return this.classWay[val]
    },
    // 查看班级介绍
    seeRecoment () {
      let data = {
        courseType: this.classInfo.classType,
        bizType: this.classInfo.bizType,
        videoType: this.classInfo.videoType,
        year: this.classInfo.year,
        termId: this.classInfo.termId,
        gradeId: this.classInfo.gradeId,
        subjectId: this.classInfo.subjectIds,
        levelId: this.classInfo.levelId,
        schoolCode: this.classInfo.cityId
      }
      this.loading = true
      queryCourseOutlineInfo(data)
        .then((res) => {
          this.presentationList = res
          if (this.presentationList.courseOutline.outlineImgDesc) {
            this.presentationList.courseOutline.outlineImgDesc = this.presentationList.courseOutline.outlineImgDesc.split(
              ','
            )
          }
          this.recommentShow = true
        })
        .catch((err) => {
          // this.$Message.error({ content: '获取数据失败:' + err, duration: 5 })
          this.$emit('errMsg', '获取数据失败:' + err)
        })
        .finally(() => {
          this.loading = false
        })
    },
    // 查看分期
    seeStages () {
      this.seeStageShow = true
      let steges = {
        area: this.classInfo.cityId,
        classId: this.classInfo.classId
      }
      this.loading = true
      queryStagesInfo(steges)
        .then((res) => {
          let data = res[0].stagesList
          this.stageArr = data
        })
        .catch((err) => {
          // this.$Message.error({ content: '查看分期失败:' + err, duration: 5 })
          this.$emit('errMsg', '获取数据失败:' + err)
        })
        .finally(() => {
          this.loading = false
        })
    },
    // 查看上课时间
    async seeDetail () {
      try {
        this.loading = true
        const errMsg = await this.$refs.talSchooltime.seeDetail(this.classInfo, true)
        if (errMsg) {
          this.$emit('errMsg', '获取上课时间失败:' + errMsg)
        }
      } catch (error) {
        this.$emit('errMsg', '获取上课时间失败:' + error.message)
      }
      this.loading = false
    },
    closeStage () {
      this.seeStageShow = false
    },
    // 发送位置
    handleShowSendPosition () {
      this.sendPositionMsgShow = true
    },
    copy (info) {
      copyText(
        `${info.className || '无'}-${this.getClassType(info.categoryType) || '无'}-${
          info.subjectCustomName || '无'
        }-${info.teacherNames || '无'}-${info.tutorRealName || '无'}-${
          info.classTimeDisplay || '无'
        }-${info.classroomVenueName || '无'}-${info.classroomName || '无'}`
      )
    },
    handleShowSendWeChat () {
      this.showSendWeChatModal = true
    }
  }
}
</script>

