<!--学员考勤-->
<template>
  <div>
    <Table border no-data-text="暂无考勤信息" :columns="columns" :data="attendanceList" :loading="loading"></Table>
  </div>
</template>

<script>
import { getAttendanceByClassId } from '../../api/attendance'
// import ClassMixin from '../../mixin/classDetail'
export default {
  name: 'TalStuAttendance',
  // mixins: [ ClassMixin ],
  props: {
    classId: {
      type: String,
      require: true
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      show: true,
      loading: true,
      columns: [{
        title: '课次',
        key: 'curriculumNum',
        align: 'center'
      }, {
        title: '班级',
        key: 'className',
        width: 120,
        align: 'center'
      }, {
        title: '上课时间',
        key: 'classTimeName',
        width: 120,
        align: 'center'
      }, {
        title: '主讲教师',
        key: 'teacherName',
        align: 'center'
      }, {
        title: '辅导老师',
        key: 'tutorName',
        align: 'center'
      }, {
        title: '教学点',
        key: 'venueName',
        align: 'center'
      }, {
        title: '电子考勤记录',
        align: 'center',
        render: (h, params) => {
          return h('span', params.row.isAbsent === 1 ? '缺勤' : '正常')
        }
      }, {
        title: 'ITS考勤记录',
        align: 'center',
        render: (h, params) => {
          let aa = ''
          if (params.row.itsIsPunctual === 0) {
            aa = '迟到'
          } else if (params.row.itsIsAbsent === 1) {
            aa = '正常'
          } else {
            aa = '--'
          }
          return h('span', aa)
        }
      }, {
        title: '调课转班',
        align: 'center',
        render: (h, params) => {
          return h('span', params.row.isChangeClass === 0 ? '否' : '是')
        }
      }
      ],
      attendanceList: []
    }
  },
  mounted () {
    this.getAttendanceByClassIdHandler()
  },
  methods: {
    async getAttendanceByClassIdHandler () {
      this.loading = true
      try {
        const rlt = await getAttendanceByClassId({
          cityCode: this.studentDetail.cityCode,
          classId: this.classId,
          // classId: 'ff8080816286e5620162873fb0dd7447' || this.classId,
          studentId: this.studentDetail.studentId,
          // studentId: 'faf3dc26d6944f869dbbb04cc2c4cf59' || this.studentDetail.studentId,
          classDate: '2018-01-28' || time
        })
        this.attendanceList = rlt
      } catch (err) {
        this.$Message.error({ content: '获取考勤失败:' + err.message, duration: 5 })
      } finally {
        this.loading = false
      }
    },
    closeHandler (type) {
      if (!type) this.modalValue = type
    }
  }
}
</script>

