<template>
  <div class="decode">
    <span style="margin-right: 10px">{{text || '--'}}</span>
    <Icon class="icon" v-if="show && encodeText" @click="changeText" type="md-eye-off" />
    <Icon class="icon" v-if="!show && encodeText" @click="changeText" type="md-eye" />
  </div>
</template>

<script>
import {decodePhone, decodeAccessMail, decodeBirthday} from '../../api/student'
export default {
  name: 'TalDecodeText',
  data () {
    return {
      decodeText: '',
      show: false
    }
  },
  props: {
    uid: [String, Number] ,
    encodeText: String,
    type: String
  },
  methods: {
    changeText () {
      if (this.encodeText) {
        if (!this.decodeText) {
          if (this.type === 'phone') {
            decodePhone({
              uid: this.uid,
              encodePhone: this.encodeText
            }).then(data => {
              this.show = !this.show
              this.decodeText = data
            }).catch(err => {
              this.$Message.error({
                content: '解密手机号码失败：' + err.message
              })
            })
          } else if (this.type === 'email') {
            decodeAccessMail({
              uid: this.uid
            }).then(data => {
              this.show = !this.show
              this.decodeText = data
            }).catch(err => {
              this.$Message.error({
                content: '解密邮箱失败：' + err.message
              })
            })
          } else if (this.type === 'birthday') {
            decodeBirthday({
              uid: this.uid
            }).then(data => {
              this.show = !this.show
              this.decodeText = data
            }).catch(err => {
              this.$Message.error({
                content: '解密生日失败：' + err.message
              })
            })
          }
        } else {
          this.show = !this.show
        }
      }
    }
  },
  watch: {
    encodeText () {
      this.decodeText = ''
      this.show = false
    }
  },
  computed: {
    text () {
      return this.show ? this.decodeText : this.encodeText
    }
  }
}
</script>

<style scoped lang="less">
  .decode {
    display: inline-block;
    .icon {
      cursor: pointer;
    }
  }
</style>
