            v-show="!searchFormHidden"
          >
            <Icon type="md-arrow-dropdown" size="22" />展开
          </span>
          <Button type="primary" style="margin-right: 10px;" @click="searchClass">查询</Button>
          <Button style="margin-right: 10px;" @click="resetFields">重置</Button>
        </div>
      </div>
    </Form>
    <Table
      border
      :loading="tableLoading"
      ref="selection"
      :columns="columns"
      :data="tableData"
