<script>
import { depClone } from '../../../utils/utils'
import TabsMixins from './mixins'

export default {
  mixins: [TabsMixins],
  data () {
    return {
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '转班原因类型',
          key: 'firstReason',
          align: 'center'
        },
        {
          title: '转班备注',
          key: 'description',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'studentName',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.showStudentDetail(params.row.studentUid)
                }
              }
            }, params.row.studentName)
          }
        },
        {
          title: '学员编号',
          key: 'cardNo',
          align: 'center'
        },
        {
          title: '年份',
          key: 'sourceYear',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceYear),
              h('span', '>>'),
              h('span', params.row.targetYear)
            ])
          }
        },
        {
          title: '学期',
          key: 'sourceTermName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceTermName),
              h('span', '>>'),
              h('span', params.row.targetTermName)
            ])
          }
        },
        {
          title: '年级',
          key: 'sourceGradeName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceGradeName),
              h('span', '>>'),
              h('span', params.row.targetGradeName)
            ])
          }
        },
        {
          title: '学科',
          key: 'sourceSubjectName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceSubjectName),
              h('span', '>>'),
              h('span', params.row.targetSubjectName)
            ])
          }
        },
        {
          title: '班次',
          key: 'sourceLevelName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceLevelName),
              h('span', '>>'),
              h('span', params.row.targetLevelName)
            ])
          }
        },
        {
          title: '主讲老师',
          key: 'sourceTeacherName',
          align: 'center',
          render: (h, params) => {
            const teacherNames = params.row.sourceTeacherName.split(',')
            const teacherIds = params.row.sourceTeacherId.split(',')
            const targetTeacherNames = params.row.targetTeacherName.split(',')
            const targetTeacherIds = params.row.targetTeacherId.split(',')
            let arrRender = []
            let length = teacherNames.length
            let targetLength = targetTeacherNames.length
            teacherNames.forEach((name, index) => {
              arrRender.push(h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.formData.cityCode, teacherIds[index], '0')
                  }
                }
              }, name + (index === length - 1 ? '' : ',')))
            })
            arrRender.push(h('span', '>>'))
            targetTeacherNames.forEach((name, index) => {
              arrRender.push(h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.formData.cityCode, targetTeacherIds[index], '0')
                  }
                }
              }, targetTeacherNames[index] + (index === targetLength - 1 ? '' : ',')))
            })
            return h('div', arrRender)
          }
        },
        {
          title: '辅导老师',
          key: 'sourceTutorRealName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceTutorRealName),
              h('span', '>>'),
              h('span', params.row.targetTutorRealName)
            ])
          }
        },
        {
          title: '教室',
          key: 'sourceClassroomName',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceClassroomName),
              h('span', '>>'),
              h('span', params.row.targetClassroomName)
            ])
          }
        },
        {
          title: '转班课次',
          key: 'number',
          align: 'center'
        },
        {
          title: '授课类型',
          key: 'sourceClassType',
          align: 'center',
          render: (h, params) => {
            return h('p', [
              h('span', params.row.sourceClassTypeName),
              h('span', '>>'),
              h('span', params.row.targetClassTypeName)
            ])
          }
        },
        {
          title: '操作日期',
          key: 'operateTime',
          align: 'center'
        }
      ],
      formFields: new Set(['changeClassType']),
      formData: {
        type: 1,
        typeName: '转班原因',
        classFlag: 0
      },
      formDataSource: {
        type: 1,
        typeName: '转班原因',
        classFlag: 0
      }
    }
  },
  methods: {
    // 搜索
    searchClass () {
      if (!this.formData.classFlag && this.formData.classFlag !== 0) {
        this.$Message.warning('请填写查询班级')
        return
      }

      if (this.searchFormValidate()) {
        return
      }

      this.formData.pageNo = 1
      this.formData.pageSize = 10
      this.getTableData()
    },
    // 表单重置
    resetFields () {
      depClone(this.formData, this.formDataSource)
      this.termList = []
      this.subjectList = []
      this.gradeTypeList = [] // 年部
      this.gradeList = [] // 年级
      this.levelList = [] // 班次
      this.areaList = [] // 区域
      this.serviceList = [] // 服务中心
      this.teacherDotList = []
      this.changeClassType = [] // 转班类型
      this.operationPlatform = [] // 操作平台
    },
    initView () {
      this.getYears()
      this.getCitys()
      this.getClassTypeList()
    }
  }

}
</script>

