<template>
  <div>
    <div style="display: inline-block;cursor:pointer;" @click="showConfig">
      <img style="display: inline-block;vertical-align: sub;width: 18px;" :src="setImage" alt="设置" class="dis-show">
      <span style="display: inline-block;font-size: 14px;color: #2D8CF0;line-height: 32px;margin-left: 4px;">自定义表头</span>
    </div>

    <TalSelfTransfer
      :value="showConfigFlag"
      title="自定义表头"
      width="540"
      :data="totalColumns"
      :target-keys="targetKeys"
      :cancel-check-status="false"
      @close="showConfigFlag=false"
      @setList="confirmList"
    />
  </div>
</template>

<script>
import setImage from '../../assets/images/setting.png'
import {
  addUserFormData,
  getUserFormData
} from '../../api/auth'
import courseBase from '../../mixin/courseBase'

export default {
  name: 'TalTableSelfColumnsFix',
  props: {
    menu: { // 菜单名
      type: String,
      required: true
    },
    fixColumns: { // 固定、左侧固定
      type: Array,
      default () {
        return []
      }
    },
    resColumns: { // 剩余部分
      type: Array,
      default () {
        return []
      }
    },
    fixRightColumns: { // 右侧固定
      type: Array,
      default () {
        return []
      }
    }
  },
  mixins: [courseBase],
  data () {
    return {
      setImage: setImage,

      /* 用户自定义存储表头数据 */
      checkboxColumns: {},
      checkColumnsKeyList: [],
      titleKeyMap: {},
      buildTableStatus: false,

      totalColumns: [],
      showConfigFlag: false,
      targetKeys: [],
      transListStyle: {
        height: '300px'
      }
    }
  },
  methods: {
    getUserFormData () {
      return new Promise((resolve, reject) => {
        getUserFormData({
          menu: this.menu
        }).then(res => {
          const data = res[0] || {}
          const formItemList = data['formItemList'] || []
          // resolve([])
          resolve(formItemList)
        }).catch(() => {
          reject()
        })
      })
    },
    addUserFormData (list) {
      return new Promise((resolve, reject) => {
        addUserFormData({
          menu: this.menu,
          formItemList: list
        }).then(() => {
          resolve()
        }).catch(() => {
          reject()
        })
      })
    },
    rendColumns (res) {
      return new Promise(resolve => {
        // console.log(res)
        this.checkboxColumns = {}
        let list = []
        /* 准备title key map */
        let titleKeyMap = {}
        let newItem = {}
        this.resColumns.forEach(item => {
          if (res.indexOf(item.key) === -1 && item.isNew) newItem[item.key] = item
          this.checkboxColumns[item.key] = item.title
          titleKeyMap[item.key] = item
        })
        this.titleKeyMap = {
          ...titleKeyMap,
          ...newItem
        }

        let isEmpty = res.length === 0
        res.forEach(item => {
          if (item) {
            const pushItem = titleKeyMap[item]
            if (pushItem) {
              this.checkColumnsKeyList.push(item)
              list.push(pushItem)
            }
          } else {
            if (!isEmpty) isEmpty = true
          }
        })

        for (const newItemKey in newItem) {
          const pushItem = newItem[newItemKey]
          list.push(pushItem)
        }

        if (isEmpty) {
          this.checkColumnsKeyList = Object.keys(titleKeyMap)
          list = this.resColumns
        }

        this.targetKeys = list.map(item => {
          return item.key
        })

        this.updateTable(list)
        resolve()
      })
    },
    updateTable (list = []) {
      const table = [
        ...this.fixColumns,
        ...list,
        ...this.fixRightColumns
      ]
      setTimeout(() => {
        this.$emit('rendColumns', table)
      }, 200)
    },
    showConfig () {
      this.showConfigFlag = true
    },
    confirmList (list, successCallback, errorCallback) {
      this.addUserFormData(list).then(() => {
        this.$Message.success('自定义表头保存成功')
        let resList = []
        list.forEach(z => {
          resList.push(this.titleKeyMap[z])
        })
        if (resList.length === 0) {
          resList = this.resColumns
        }
        this.updateTable(resList)
        this.targetKeys = list
        successCallback && successCallback()
      }).catch(() => {
        this.$Message.error('添加自定义表头失败')
        errorCallback && errorCallback()
      })
    }
  },
  watch: {
  },
  mounted () {
    const fn = (list) => {
      return new Promise(resolve => {
        this.rendColumns(list).then(() => {
          resolve()
        })
      })
    }

    this.getUserFormData().then(res => {
      /*
          * formItemList 数据形式
          *   [key, key, key]
          * */
      res = res || []
      fn(res)

      let list = []
      this.resColumns.forEach(z => {
        list.push(z)
      })
      this.totalColumns = list
    }).catch(() => {
      fn([])
    })
  }
}
</script>

