<style scoped lang="less">
.search-box {
  margin-top: 15px;
  padding: 10px;
  .ivu-form-item {
    margin-bottom: 0;
  }
}
</style>
<!-- 候补记录 -->
<template>
  <div>
    <div class="class_table" style="clear: both;margin-top: 20px;background: #ffffff">
      <Table
        stripe
        :columns="computeTableColumns"
        :data="tableData"
        :loading="tableLoading"
        :row-class-name="rowClassName"
        style="margin-top: 20px"
      >
        <template slot-scope="{ row }" slot="teacherNames">
          <p v-for="(item, index) in row.teacherNames.split(',')" :key="item">
            <a
              @click="showTeacherDetail(row, row.teacherIds.split(',')[index])"
            >{{ item }}</a>
            <template v-if="index != row.teacherNames.split(',').length - 1">
              ,
              <br />
            </template>
          </p>
        </template>
      </Table>
    </div>
    <ClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></ClassDetail>
    <TeacherDetail
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
      :stuId="studentDetail.studentId"
      :stuSchoolCode="studentDetail.cityCode"
      :creatorEmpNo="teacherDetail.empNo"
    ></TeacherDetail>
  </div>
</template>

<script>
import {
  queryWaitClassList
} from '../../api/register'
import { getUserInfoByCookies, getStrLength } from '../../utils/utils'
import ClassMixin from '../../mixin/classDetail'
import TeacherMixin from '../../mixin/teacherDetail'
import TeacherDetail from '../tal-teacher-details'
import ClassDetail from '../tal-class-details'
export default {
  name: 'TalBackupRecordsModal',
  mixins: [ClassMixin, TeacherMixin],
  components: {
    TeacherDetail,
    ClassDetail
  },
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    // 资源管理-任务管理 查看
    isTaskMode: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    // 计算表格列
    computeTableColumns () {
      let columns = this.tableColumns
      if (!this.isTaskMode) {
        columns = columns.filter(item => item.key !== 'sameTermSubject')
      }
      return columns
    }
  },
  data () {
    return {
      creatorEmpNo: '',
      tableLoading: false,
      joinShow: false, // 报名
      reverseClassShow: false, // 转班
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数
      tableColumns: Object.freeze([
        {
          title: ' ',
          key: 'sameTermSubject',
          align: 'center',
          render: (h, params) => {
            return h('span',
              {
                style: {
                  color: 'red'
                }
              }, params.row.isSameClassReg === 1 ? '已报同学期同学科' : '')
          }
        },
        {
          title: '班级状态',
          key: 'classStatus',
          align: 'center',
          render: (h, params) => {
            let statusName = ''
            switch (params.row.classStatus) {
              case 0:
                statusName = '正常'
                break
              case 1:
                statusName = '删除'
                break
              case 2:
                statusName = '关闭'
                break
              case 3:
                statusName = '结课'
                break
              case 4:
                statusName = '隐藏'
                break
              case 5:
                statusName = '异常原因'
                break
            }
            return h('div', statusName)
          }
        }, {
          title: '班级名称',
          key: 'className',
          // minWidth: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.classId)
                  }
                }
              }, params.row.className),
              h('Tag', {
                style: {
                  'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                },
                props: {
                  color: 'green'
                }
              }, '全国班')
            ])
          }
        },
        {
          title: '授课类型',
          key: 'classType',
          // minWidth: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', this.getClassTypeName(params.row.classType))
          }
        },
        { title: '学科', key: 'subjectNames', align: 'center' },
        {
          title: '主讲教师',
          slot: 'teacherNames',
          // width: 100,
          align: 'center',
          render: (h, params) => {
            const tNames = params.row.teacherNames.split(',')
            const tIds = params.row.teacherIds.split(',')
            return h('div', tNames.map((n, index) => {
              return h('a', {
                on: {
                  click: () => {
                    this.showTeacherDetail(this.studentDetail.bindingCode, tIds[index], '0')
                  }
                }
              }, n)
            }))
          }
        },
        {
          title: '辅导教师',
          key: 'tutorRealName',
          // width: 100,
          align: 'center'
        },
        {
          title: '开课日期',
          key: 'classStartDate',
          // width: 150,
          align: 'center'
        },
        {
          title: '结课日期',
          key: 'classEndDate',
          // width: 150,
          align: 'center'
        },
        {
          title: '上课时间',
          key: 'classTimeNames',
          render: (h, params) => {
            if (params.row.classTimeNames && params.row.classTimeNames.length > 15) {
              return h('Poptip', {
                style: {
                  display: 'inline-block',
                  marginRight: '5px'
                },
                props: {
                  trigger: 'hover',
                  width: '300',
                  wordWrap: true,
                  transfer: true,
                  content: params.row.classTimeNames
                }
              }, getStrLength(params.row.classTimeNames, 15, true))
            } else {
              return h('span', params.row.classTimeNames)
            }
          },
          // width: 200,
          align: 'center'
        },
        {
          title: '服务中心',
          key: 'serviceCenterName',
          // width: 150,
          align: 'center'
        },
        {
          title: '教室',
          key: 'classroomName',
          // width: 150,
          align: 'center'
        },
        {
          title: '候补总人数',
          key: 'waitCount',
          // minWidth: 80,
          align: 'center'
        }, {
          title: '候补时间',
          key: 'createDate',
          // minWidth: 150,
          align: 'center'
        }, {
          title: '操作人',
          key: 'creatorName',
          // minWidth: 100,
          align: 'center'
        }, {
          title: '备注',
          key: 'description',
          render: (h, params) => {
            if (params.row.description && params.row.description.length > 15) {
              return h('Poptip', {
                style: {
                  display: 'inline-block',
                  marginRight: '5px'
                },
                props: {
                  trigger: 'hover',
                  width: '300',
                  wordWrap: true,
                  transfer: true,
                  content: params.row.description
                }
              }, getStrLength(params.row.description, 15, true))
            } else {
              return h('span', params.row.description)
            }
          },
          align: 'center'
        }
      ]),
      tableData: [],
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      rowClassName: (row, index) => {
        return !this.isTaskMode ? '' : row.isTaskRelate ? 'task-highlight' : 'task-lowlight'
      }
    }
  },
  mounted () {
    this.initView()
  },
  methods: {
    initView () {
      this.creatorEmpNo = getUserInfoByCookies('columbusUserInfo').id
      this.tableData = []
      this.getList()
    },
    getList () {
      this.tableLoading = true
      let data = {
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId
      }
      queryWaitClassList(data).then(res => {
        if (res && res.length) {
          res.forEach((e, i) => {
            if (e.classStartDate) {
              e.classStartDate = e.classStartDate.substring(0, 10)
            }
            if (e.classEndDate) {
              e.classEndDate = e.classEndDate.substring(0, 10)
            }
          })
          this.tableData = Object.freeze(res)
        } else {
          this.tableData = []
        }
      }).catch(err => {
        this.$Message.error('获取数据失败' + err.message)
      }).finally(() => {
        this.tableLoading = false
      })
    },
    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    getyiyang (arr) {
      let brr = []
      for (let index = 0; index < arr.length; index++) {
        brr.push(arr[index].split(''))
      }
      for (let index = 0; brr < brr.length; index++) {
        for (let index1 = 0; index1 < brr[index].length; index1++) {

        }
      }
    }
  }
}
</script>

<style lang="less">
  .task-highlight{
    >td {
      background-color: #fcf5e8 !important;
    }
  }

  .task-lowlight{
    >td {
      background-color: #fff !important;
    }
  }
</style>
