  <div class="wrapper">
    <div class="title">
      <div>明细</div>
      <div>
        <span style="margin-right: 20px">操作人：{{ scInfo.empName || '--' }} </span>
        <span>所属服务中心：{{ scInfo.serviceCenterName || '--' }} </span>
      </div>
