              <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item.ledgerSubjectName || '教学点' + item.venueName + '未找到对应主体'}}</div></td>
              <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item.rateText}}</div></td>
              <td class="ivu-table-column-center"><div class="ivu-table-cell">{{item.hasInvoiceText}}</div></td>
            </tr>
          </template>
          <tr v-if="!tableData || !tableData.length">
            <td class="ivu-table-column-center" colspan="10"><div class="ivu-table-cell">暂无数据</div></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="summary-title">小结：</div>
    <div class="summary-text" v-if="groupData.length">待开发票（共 {{ groupData.length }} 张，合计金额 {{ total }} 元）：</div>
    <div class="summary-text" v-else>暂无待开发票</div>
    <div v-for="(item, index) in groupData" :key="item.indexs" class="summary-item">
      <p>发票{{ toCN(index + 1) }}（对应序号：{{ item.indexs }}）</p>
      <p>开票主体（销售方）：{{ item.key.ledgerSubjectName }}</p>
      <p><span style="margin-right: 50px">开票金额（人民币）：{{ item.totalPrice }}</span><span>税率：{{ item.key.rateText }}</span></p>
      <div style="text-align: right"><Button type="primary" @click="toInvoice(index, item)">一键开票</Button></div>
    </div>
    <div v-for="(item, index) in groupInvoice" :key="index" class="invoice-group">
      <div class="invoice-type">{{ item.key.invoiceStatus === 2 ? '开票中' : '已开发票'}}（共{{ item.value.length }}张，合计金额{{ item.totalPrice }}元）：</div>
      <div class="invoice-item" v-for="(inv, idx) in item.value" :key="idx">
        <Row>发票{{ item.key.invoiceStatus === 2 ? toCN(idx + 1) : '' }}</Row>
        <Row>开票主体（销售方）：{{ inv.sellerName || '--' }}</Row>
        <Row :gutter="16" class="wrap-text">
