        <Row :gutter="16">
          <Col span="12" style="display: flex">
            <span style="width: 90px">地址、电话：</span>
            <span class="wrap-text">{{ inv.buyerAddressPhone || '--' }}</span>
          </Col>
          <Col span="12" style="display: flex">
            <span style="width: 110px">开户行及账号：</span>
            <span class="wrap-text">{{ inv.buyerOpeningBank || '--' }}</span>
          </Col>
        </Row>
        <Row>开票时间：{{ inv.invoiceTime || '--' }}</Row>
        <Row :gutter="16">
          <Col span="12">领票人邮箱：{{ inv.receiverEmail || '--' }}</Col>
          <Col span="12">领票人手机：{{ inv.receiverPhone || '--' }}</Col>
        </Row>
      </div>
    </div>
    <Modal v-model="fixPriceModal"
           :closable="false"
           footer-hide
           :transfer="false"
           title="金额修改（修改后将重新汇总待开发票）">
      <template v-if="fixPriceModal">
        <span>将{{tableData[fixIndex].classStageName}}的第{{tableData[fixIndex].curriculumNum}}次课【实缴金额】从</span>
        <span style="font-size:16px;font-weight: bold"> {{tableData[fixIndex].actualPrice}}元 </span>
        <span> 修改为：<InputNumber v-model="fixedPrice" :min="0" :max="tableData[fixIndex].tempActualPrice"></InputNumber> 元</span>
        <div style="text-align: right;margin-top: 10px">
          <Button type="primary" @click="fix(0)" style="margin-right: 10px">确定</Button>
          <Button @click="cancelFix">取消</Button>
        </div>
      </template>
    </Modal>
    <Modal v-model="fixRefundModal"
           :closable="false"
           footer-hide
           :transfer="false"
           title="退费状态修改（修改后将重新汇总待开发票）">
      <template v-if="fixRefundModal">
        <span>将{{tableData[fixIndex].classStageName}}的第{{tableData[fixIndex].curriculumNum}}次课【是否退费】从</span>
        <span style="font-size:16px;font-weight: bold"> {{tableData[fixIndex].isRefund === 0 ? '否' : '是'}} </span>
        <span> 修改为 </span>
        <span style="font-size:16px;font-weight: bold"> {{tableData[fixIndex].isRefund === 0 ? '是' : '否'}} </span>
        <div style="text-align: right;margin-top: 10px">
          <Button type="primary" @click="fix(1)" style="margin-right: 10px">确定</Button>
          <Button @click="cancelFix">取消</Button>
        </div>
      </template>
    </Modal>
    <Modal v-model="invoiceFormModal"
           :closable="false"
           :width="600"
           footer-hide
           :transfer="false"
           title="一键开票 - 填写">
      <template v-if="invoiceFormModal">
        <div class="invoice-info">
          <p>发票{{ toCN(invoiceIndex + 1) }}（对应序号：{{ groupData[invoiceIndex].indexs }}）</p>
          <p>开票主体（销售方）：{{ groupData[invoiceIndex].key.ledgerSubjectName }}</p>
          <p><span style="margin-right: 50px">开票金额（人民币）：{{ groupData[invoiceIndex].totalPrice }}</span><span>税率：{{ groupData[invoiceIndex].key.rateText }}</span></p>
        </div>
        <Form ref='form' :model="formData" :rules='ruleData' :label-width='150' inline>
          <FormItem label="收票主体（购买方）：" prop="invoiceSubjectType">
            <RadioGroup v-model="formData.invoiceSubjectType">
              <Radio label="0">个人/非企业单位</Radio>
              <Radio label="1">企业</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="收票主体名称：" prop="buyerName" style="width: 80%">
            <Input v-model.trim="formData.buyerName" :maxlength="50" type="textarea" placeholder="请输入主体名称，最多50个字符"></Input>
          </FormItem>
          <FormItem label="纳税人识别号：" prop="buyerIdentifyNum" style="width: 80%">
            <Input v-model.trim="formData.buyerIdentifyNum" :maxlength="20" placeholder="请输入纳税人识别号，最多20个字符" :disabled="formData.invoiceSubjectType === '0'"></Input>
          </FormItem>
          <FormItem label="地址、电话：" prop="buyerAddressPhone" style="width: 80%">
            <Input v-model.trim="formData.buyerAddressPhone" :maxlength="50" placeholder="请输入地址、电话，最多50个字符" type="textarea" :disabled="formData.invoiceSubjectType === '0'"></Input>
          </FormItem>
          <FormItem label="开户行及账号：" prop="buyerOpeningBank" style="width: 80%">
            <Input v-model.trim="formData.buyerOpeningBank" :maxlength="50" placeholder="请输入开户行及账号，最多50个字符" type="textarea" :disabled="formData.invoiceSubjectType === '0'"></Input>
          </FormItem>
          <FormItem label="发票备注：" prop="invoiceRemark" style="width: 80%">
            <Input v-model.trim="formData.invoiceRemark" :maxlength="50" placeholder="请输入备注，最多50个字符"></Input>
          </FormItem>
          <FormItem label="领票人姓名：" prop="receiverName" style="width: 80%">
            <Input v-model.trim="formData.receiverName" :maxlength="5" placeholder="请输入领票人姓名，最多5个字符"></Input>
          </FormItem>
          <FormItem label="领票人邮箱：" prop="receiverEmail" style="width: 80%">
            <Input v-model.trim="formData.receiverEmail" :maxlength="30" placeholder="请输入领票人邮箱，最多30个字符"></Input>
          </FormItem>
          <FormItem label="领票人手机：" prop="receiverPhone" style="width: 80%">
            <Input v-model.trim="formData.receiverPhone" :maxlength="11" placeholder="请输入11位手机号码"></Input>
          </FormItem>
        </Form>
        <div style="text-align: right;margin-top: 10px">
          <Button type="primary" @click="confirmInvoice" style="margin-right: 10px">保存</Button>
          <Button @click="invoiceFormModal = false">取消</Button>
        </div>
      </template>
    </Modal>
    <Modal v-model="confirmInvoiceModal"
           :closable="false"
           :width="600"
           footer-hide
           :transfer="false"
           title="一键开票 - 确认">
      <template v-if="confirmInvoiceModal">
        <div class="invoice-warning">
          <Icon type="md-alert" size="15"/>  请仔细核对以下信息，确认无误后再点击确认按钮：
        </div>
        <div class="invoice-info">
          <p>发票{{ toCN(invoiceIndex + 1) }}（对应序号：{{ groupData[invoiceIndex].indexs }}）</p>
          <p>开票主体（销售方）：{{ groupData[invoiceIndex].key.ledgerSubjectName }}</p>
          <p><span style="margin-right: 50px">开票金额（人民币）：{{ groupData[invoiceIndex].totalPrice }}</span><span>税率：{{ groupData[invoiceIndex].key.rateText }}</span></p>
        </div>
        <Form :label-width='150'>
          <FormItem label="收票主体（购买方）：" prop="invoiceSubjectType">
            <span>{{ formData.invoiceSubjectType === '0' ? '个人/非企业单位' : '企业' }}</span>
          </FormItem>
          <FormItem label="收票主体名称：" prop="buyerName">
            <span class="confirm-text">{{formData.buyerName || '--'}}</span>
          </FormItem>
          <FormItem label="纳税人识别号：" prop="buyerIdentifyNum">
            <span class="confirm-text">{{formData.buyerIdentifyNum || '--'}}</span>
          </FormItem>
          <FormItem label="地址、电话：" prop="buyerAddressPhone">
            <span class="confirm-text">{{formData.buyerAddressPhone || '--'}}</span>
          </FormItem>
          <FormItem label="开户行及账号：" prop="buyerOpeningBank">
            <span class="confirm-text">{{formData.buyerOpeningBank || '--'}}</span>
          </FormItem>
          <FormItem label="发票备注：" prop="invoiceRemark">
            <span class="confirm-text">{{formData.invoiceRemark || '--'}}</span>
          </FormItem>
          <FormItem label="领票人姓名：" prop="receiverName">
            <span class="confirm-text">{{formData.receiverName || '--'}}</span>
          </FormItem>
          <FormItem label="领票人邮箱：" prop="receiverEmail">
            <span class="confirm-text">{{formData.receiverEmail || '--'}}</span>
          </FormItem>
          <FormItem label="领票人手机：" prop="receiverPhone">
            <span class="confirm-text">{{formData.receiverPhone || '--'}}</span>
          </FormItem>
        </Form>
        <div style="text-align: right;margin-top: 10px">
          <Button type="primary" @click="createInvoice" style="margin-right: 10px">确认</Button>
          <Button @click="confirmCancel">取消</Button>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { getUserExtInfoByType } from '../../api/bd'
import { queryInvoiceInfo, createInvoice } from '../../api/finance'
import { toChinesNum, getUserInfoByCookies, hasCnPt, hasCnPtwithoutBrkt } from '../../utils/utils'
import { queryServiceCenterByEmpNo } from '../../api/clazz'
import GroupMultiple from '../../utils/group'
import { querySubjectLimit } from '../../api/invoice'

export default {
  name: 'TalFinanceInvoiceTool',
  props: {
    mixinParams: { //  选择的开票信息
      type: Array,
      required: true,
      default: () => {
        return []
      }
    },
    uid: {
      type: [String, Number],
      required: true
    },
    cityCode: {
      type: [String, Number],
      required: true
    },
    studentId: {
      type: String,
      required: true
    },
    studentNo: {
      type: String,
      required: true
    },
    studentName: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      invoiceData: {},
      tableData: [],
      loading: false,
      spanArr: [],
      groupData: [],
      total: 0, // 开票总金额
      fixIndex: 0,
      fixedPrice: null,
      fixPriceModal: false,
      fixRefundModal: false,
      invoiceIndex: 0,
      invoiceFormModal: false,
      formData: {
        invoiceSubjectType: '0',
        buyerName: '',
        buyerIdentifyNum: '',
        buyerAddressPhone: '',
        buyerOpeningBank: '',
        receiverName: '',
        receiverEmail: '',
        receiverPhone: '',
        invoiceRemark: ''
      },
      ruleData: {
        buyerName: [
          { required: true, message: '收票主体名称不能为空, 最多50字符', trigger: 'blur', type: 'string' },
          {
            message: '请使用英文半角符号或中文括号，如()',
            trigger: 'blur',
            validator (...params) {
              return !hasCnPtwithoutBrkt(params[1])
            }
          }
        ],
        buyerIdentifyNum: [
          { pattern: /^[a-zA-Z0-9]{7,20}$/, type: 'string', trigger: 'change', message: '纳税人识别号为7至20位数字或字母' },
          {
            message: '请使用英文半角符号，如()、{}等',
            trigger: 'blur',
            validator (...params) {
              return !hasCnPt(params[1])
            }
          }
        ],
        receiverName: { required: true, message: '领票人姓名不能为空', trigger: 'blur', type: 'string' },
        receiverEmail: [
          { required: true, message: '领票人邮箱不能为空', trigger: 'blur', type: 'string' },
          { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
        ],
        receiverPhone: [
          { pattern: /^\d{11}$/, message: '请填写正确的手机号码', trigger: 'blur' }
        ],
        buyerAddressPhone: {
          message: '请使用英文半角符号，如()、{}等',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        },
        buyerOpeningBank: {
          message: '请使用英文半角符号，如()、{}等',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        },
        invoiceRemark: {
          message: '请使用英文半角符号，如()、{}等',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        }
      },
      confirmInvoiceModal: false,
      empInfo: null,
      scInfo: {},
      invoiceList: [],
      groupInvoice: [],
      queryInfo: [],
      tableLoading: []
    }
  },
  watch: {
    'formData.invoiceSubjectType' (val) {
      if (val === '0') {
        this.formData.buyerIdentifyNum = ''
        this.formData.buyerAddressPhone = ''
        this.formData.buyerOpeningBank = ''
      }
    }
  },
  mounted () {
    this.getEmail()
    this.computeParams()
    this.getData()
    this.empInfo = getUserInfoByCookies('columbusUserInfo')
    queryServiceCenterByEmpNo({
      cityCode: this.cityCode,
      empNo: this.empInfo.id
    }).then(data => {
      this.scInfo = data
    }).catch(err => {
      this.$Message.error('获取当前所在服务中心失败:' + err.message)
    })
  },
  methods: {
    getEmail () {
      getUserExtInfoByType({
        cityCode: this.cityCode,
        uid: this.uid,
        type: 'accessEmail'
      }).then(data => {
        this.formData.receiverEmail = data
      }).catch(err => {
        this.$Message.error({
          content: '查询学员邮箱失败：' + err.message,
          duration: 5
        })
      })
    },
    openFixModal (index, type = 0) {
      this.fixIndex = index
      if (type === 0) {
        this.fixPriceModal = true
      } else {
        this.fixRefundModal = true
      }
    },
    fix (type = 0) {
      if (type === 0) {
        if (this.fixedPrice !== null && /^\d+(\.\d{0,2})?$/.test(this.fixedPrice) && this.fixedPrice >= 0 && this.fixedPrice <= this.tableData[this.fixIndex].tempActualPrice) {
          this.tableData[this.fixIndex].actualPrice = this.fixedPrice
          this.fixPriceModal = false
          this.fixedPrice = null
        } else {
          this.$Message.warning({
            content: `请输入大于等于0，小于等于${this.tableData[this.fixIndex].tempActualPrice}的价格，最多保留两位小数`,
            duration: 5
          })
          return
        }
      } else {
        this.tableData[this.fixIndex].isRefund = this.tableData[this.fixIndex].isRefund ? 0 : 1
        this.fixRefundModal = false
      }
      console.log(this.tableData)
      this.computeGroupData()
    },
    cancelFix () {
      this.fixPriceModal = false
      this.fixedPrice = null
      this.fixRefundModal = false
    },
    toInvoice (index, item) {
      const parmas = {
        ledgerSubjectName: item.key.ledgerSubjectName,
        ledgerSubjectId: item.key.ledgerSubjectId,
        cityCode: this.cityCode
      }
      querySubjectLimit(parmas).then(limitPrice => {
        if (item.totalPrice > limitPrice) {
          this.$Modal.info({
            title: '超额提醒',
            content: `该主体（${item.key.ledgerSubjectName}）开票限额为${limitPrice}元，请修改开票金额。`
          })
        } else {
          this.invoiceIndex = index
          this.invoiceFormModal = true
        }
      }).catch(err => {
        this.$Message.error({ content: '获取主体限额失败: ' + err.message, duration: 5 })
      })
    },
    confirmInvoice () {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.formData.invoiceSubjectType === '1' && !this.formData.buyerIdentifyNum) {
            this.$Message.warning('纳税人识别号不能为空')
            return
          }

          this.invoiceFormModal = false
          this.confirmInvoiceModal = true
        }
      })
    },
    createInvoice () {
      const createInvoiceDetail = []
      this.groupData[this.invoiceIndex].value.forEach(g => {
        console.log(g)
        createInvoiceDetail.push({
          subOrderId: g.subOrderId,
          stagesNum: g.stagesNum,
          classNum: g.curriculumNum,
          classType: g.classType,
          haveClass: g.haveClass,
          amount: g.actualPrice,
          courseTime: g.startTime,
          belongClassId: g.classId,
          belongClassName: g.className,
          // classId: 'e394c6186546487380ba0c82ef8f0b6b',
          // className: '班级B',
          uid: this.uid,
          studentNo: this.studentNo,
          studentId: this.studentId,
          studentName: this.studentName,
          stagesId: g.stagesId,
          stagesName: g.stagesName,
          rate: g.rate,
          areaCode: this.cityCode,
          ledgerSubjectName: g.ledgerSubjectName,
          ledgerSubjectId: g.ledgerSubjectId
        })
      })
      console.log(createInvoiceDetail)
      const params = Object.assign({}, {
        cityCode: this.cityCode,
        sellerName: this.groupData[this.invoiceIndex].key.ledgerSubjectName,
        operatorName: this.empInfo.name,
        operatorId: this.empInfo.id,
        serviceCenterName: this.scInfo.serviceCenterName,
        invoiceAmount: this.groupData[this.invoiceIndex].totalPrice,
        invoiceRate: this.groupData[this.invoiceIndex].value[0].rate,
        createInvoiceDetail
      }, this.formData)
      console.log(params)
      createInvoice(params).then(() => {
        this.$Message.success({
          content: '开票申请成功！蓝字电票将发送到领票人邮箱，请提醒对方查收。',
          duration: 5
        })
        this.confirmInvoiceModal = false
        this.getData()
        console.log('开票成功')
      }).catch(err => {
        this.$Message.error({
          content: '开票错误：' + err.message,
          duration: 5
        })
      })
    },
    confirmCancel () {
      this.confirmInvoiceModal = false
      this.invoiceFormModal = true
    },
    getData () {
      this.tableLoading = true
      queryInvoiceInfo({
        cityCode: this.cityCode,
        uid: this.uid,
        queryInfo: this.queryInfo
      }).then(data => {
        // data = { 'invoiceInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stageInfo': [{ 'stagesNum': 1, 'stagesName': '春季班', 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'classInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '春季班', 'curriculumNum': 3, 'shouldPrice': 167.0, 'actualPrice': 154.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-07-11 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b43' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '春季班', 'curriculumNum': 1, 'shouldPrice': 166.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-05-16 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校12', 'rate': 0.02, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b41' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '春季班', 'curriculumNum': 2, 'shouldPrice': 167.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-06-13 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校123', 'rate': 0.02, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b42' }] }], 'subOrderId': '48c22c112e18456caca8195a4c4261b4' }, { 'registId': '48c22c112e18456caca8195a4c4261b3', 'stageInfo': [{ 'stagesNum': 1, 'stagesName': '秋季班', 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'classInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '秋季班', 'curriculumNum': 3, 'shouldPrice': 167.0, 'actualPrice': 154.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-07-11 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b43' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '春季班', 'curriculumNum': 1, 'shouldPrice': 166.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-05-16 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b41' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '春季班', 'curriculumNum': 2, 'shouldPrice': 167.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': '春季班小学三年级数学LYN校内班次', 'startTime': '2020-06-13 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '北京市昌平区学而思培训学校123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b42' }] }], 'subOrderId': '48c22c112e18456caca8195a4c4261b4' }], 'invoiceDetail': [], 'serviceTime': '2020-06-16 17:23:25' }
        this.invoiceData = data
        this.invoiceList = data.invoiceDetail
        this.computeTableData()
        this.computeGroupData()
        this.computeInvoice()
      }).catch(err => {
        console.error(err)
        this.$Message.error({
          content: '查询发票信息错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    computeParams () {
      const data = this.mixinParams.map(rs => {
        const arr = rs.split('|')
        return {
          registId: arr[0],
          stagesNum: arr[1],
          venueId: arr[2],
          payTime: arr[3]
        }
      })
      const groupData = new GroupMultiple(data, ['registId', 'venueId']).group()
      const query = []
      groupData.forEach(g => {
        const stageInfo = g.value.map(v => {
          return {
            stagesNum: v.stagesNum,
            payTime: v.payTime
          }
        })
        query.push({
          registId: g.key.registId,
          venueId: g.key.venueId,
          stageInfo
        })
      })
      console.log(query)
      this.queryInfo = query
    },
    // 计算表格数据
    computeTableData () {
      this.tableData = this.invoiceData.invoiceInfo.map(info => {
        return info.stageInfo.map(s => s.classInfo.sort((s1, s2) => s1.curriculumNum - s2.curriculumNum).map(c => {
          c.classStageName = s.className + ' - ' + s.stagesName
          c.subOrderId = info.subOrderId
          return c
        })).reduce((l, r) => l.concat(r))
      }).reduce((l, r) => l.concat(r))
      // 计算合并单元格合并
      let row = {}
      this.spanArr = []
      for (let i = 0; i < this.tableData.length; i++) {
        row = this.tableData[i]
        row.index = i + 1
        row.tempActualPrice = row.actualPrice // 价格备份，修改价格时使用
        row.rateText = row.rate === null ? '未配置税率，请联系分校财务进行配置' : row.rate === 0 ? '免税' : row.rate * 100 + '%'
        row.hasInvoiceText = row.hasInvoice === 2
          ? '否' : row.hasInvoice === 0
            ? '开票中' : `发票代码：${row.invoiceCode}，发票号码：${row.invoiceNum}，发票时间：${row.invoiceTime}`
        row.classTime = row.startTime.split(' ')[0]
        if (i === 0) {
          this.spanArr.push(1)
          this.pos = 0
        } else {
          // 判断当前元素与上一个元素是否相同
          if (row.classStageName === this.tableData[i - 1].classStageName) {
            this.spanArr[this.pos] += 1
            this.spanArr.push(0)
          } else {
            this.spanArr.push(1)
            this.pos = i
          }
        }
      }
      console.log(this.tableData)
    },
    /**
     * 计算可开发票
     */
    computeGroupData () {
      const data = this.tableData.filter(d => !d.isRefund && d.hasInvoice === 2 && d.ledgerSubjectName && d.rate !== null) // 过滤出未退费和未开票的数据
      console.log(data)
      this.groupData = new GroupMultiple(data, ['ledgerSubjectName', 'ledgerSubjectId', 'rateText', 'productName']).group()
      this.groupData.forEach(g => {
        g.totalPrice = g.value.reduce((l, r) => l + r.actualPrice, 0)
        g.indexs = g.value.reduce((l, r) => l + '、' + r.index, '').slice(1)
      })
      this.groupData = this.groupData.filter(g => g.totalPrice > 0)
      this.total = this.groupData.reduce((l, r) => l + r.totalPrice, 0)
      console.log(this.groupData)
    },
    /**
     * 计算已开发票
     */
    computeInvoice () {
      this.groupInvoice = new GroupMultiple(this.invoiceList, ['invoiceStatus']).group().sort((a, b) => a.key.invoiceStatus - b.key.invoiceStatus)
      this.groupInvoice.forEach(g => {
        g.totalPrice = g.value.reduce((l, r) => l + r.invoiceAmount, 0)
      })
    },
    /**
     * 转换中文数字
     * @param num
     * @returns {string|string}
     */
    toCN (num) {
      return toChinesNum(num)
    }
  }
}
</script>

