import $ from '../utils/api.request'

/**
 * 退费类型
 */

export const getRefundType = params => {
  return $.splatAxios.request({
    url: '/reason/refundType',
    params,
    method: 'get'
  })
}

/**
 * 转班，退费，调课原因查询
 */

export const queryByType = params => {
  return $.splatAxios.request({
    url: '/reason/queryByType',
    data: params,
    method: 'post'
  })
}

/**
 * 操作平台类型 退费
 */

export const operationPlatform = params => {
  return $.splatAxios.request({
    url: '/reason/operationPlatform',
    params,
    method: 'get'
  })
}

/**
 * 操作平台类型 调课、转班
 */

export const changeClassCourseOperationPlatform = params => {
  return $.splatAxios.request({
    url: '/reason/changeClassCourseOperationPlatform',
    params,
    method: 'get'
  })
}

/**
 * 转班，退费，调课原因查询结果导出
 */

export const resultExport = params => {
  return $.splatAxios.request({
    url: 'reason/resultExport',
    data: params,
    method: 'post',
    responseType: 'blob'
  })
}
