<template>
  <div class="file-container">
    <Card dis-hover>
      <Form ref="fileSearchForm" :model="fileSearchForm" :rules="fileSearchRules">
        <Row type="flex">
          <Col>
            <FormItem style="width: 200px;" :label-width="80" label="分校" prop="cityId">
              <Select
                v-model="fileSearchForm.cityId"
                :disabled="cityList.length === 1"
                clearable
                filterable
                transfer
                placeholder="分校"
              >
                <Option v-for="item in cityList" :value="item.value" :key="item.value" :label="item.label"></Option>
              </Select>
            </FormItem>
          </Col>
          <Col class="ml10">
            <FormItem style="width: 120px;" prop="levelDegree">
              <Select
                v-model="fileSearchForm.status"
                clearable
                filterable
                transfer
                placeholder="状态"
              >
                <Option v-for="item in statusList" :value="item.id" :key="item.id" :label="item.name"></Option>
              </Select>
            </FormItem>
          </Col>
          <Col class="ml10">
            <Button
              type="primary"
              :loading="searchLoading"
              :disabled="cityList.length === 0"
              @click="fileSearch"
            >查找
            </Button>
          </Col>

        </Row>
        <Row class="file-notice">温馨提示：导出文件只保留3天</Row>
      </Form>
    </Card>
    <Table
      style="margin-top: 10px;"
      disabled-hover
      :columns="fileColumns"
      :data="fileTable"
      :loading="searchLoading"
      :max-height="800">
      <template slot="index" slot-scope="{ row, index }">
        {{index+1}}
      </template>
      <template slot="fs" slot-scope="{ row }">
        <Button type="primary" v-if="row.status === 0" size="small">生成中</Button>
        <Button type="success" v-if="row.status === 1" size="small">生成成功</Button>
        <Button type="error" v-if="row.status === 2" size="small">生成失败</Button>
      </template>
    </Table>
  </div>
</template>

<script>
/*
  * 班课 导出文件下载
  * TalQueryClassList
  * */

import {
  getExportList
} from '../../api/courseBase'
import courseBase from '../../mixin/courseBase'

export default {
  name: 'TalFileDownload',
  mixins: [courseBase],
  props: {
    watchReload: { // 外部控制是否刷新数据 watch router 前提已选择分校
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      fileSearchForm: {
        cityId: null, // 城市
        status: null
      },
      fileSearchRules: {
        cityId: [
          { required: true, message: '请选择分校', trigger: 'change' }
        ]
      },
      statusList: [
        {
          id: 0,
          name: '生成中'
        },
        {
          id: 1,
          name: '生成成功'
        },
        {
          id: 2,
          name: '生成失败'
        }
      ],
      searchLoading: false,
      fileColumns: [
        { title: '序号', slot: 'index', align: 'center', minWidth: 60 },
        { title: '操作名称', key: 'functionName', align: 'center', minWidth: 100 },
        { title: '文件名称', key: 'fileName', align: 'center', minWidth: 100 },
        { title: '导出条数', key: 'count', align: 'center', minWidth: 100 },
        { title: '导出时间', key: 'updateTime', align: 'center', minWidth: 100 },
        { title: '操作人', key: 'creatorName', align: 'center', minWidth: 100 },
        { title: '状态', slot: 'fs', align: 'center', minWidth: 100 },
        {
          title: '操作',
          key: 'filePath',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            let url = params.row.url || ''
            url = url.replace(/http[s]?:/, '')
            // console.log(url)
            return h('Row', {
              style: {
                position: 'relative',
                display: params.row.url ? 'block' : 'none'
              }
            }, [
              h('a', {
                class: 'ivu-btn ivu-btn-primary ivu-btn-ghost',
                style: {
                  textDecoration: 'none'
                },
                domProps: {
                  href: url
                },
                on: {
                  click: () => {
                    this.downloadFile(params.row.fileName)
                  }
                }
              }, '下载')
            ])
          }
        }
      ],
      fileTable: []
    }
  },
  methods: {
    fileSearch () {
      this.$refs['fileSearchForm'].validate((valid) => {
        if (valid) {
          this.fileSearchFn()
        }
      })
    },
    fileSearchFn () {
      return new Promise(resolve => {
        if (!this.fileSearchForm.cityId) {
          resolve()
          return
        }

        this.searchLoading = true
        getExportList(this.fileSearchForm).then(res => {
          if (res) {
            this.fileTable = res || []
            if (this.fileTable.length === 0) this.$Message.info('查询结果为空')
          } else {
            this.$Message.error('列表获取失败！')
          }
        }).catch((err) => {
          this.$Message.error(err.message || '列表获取失败')
        }).finally(() => {
          this.searchLoading = false
          resolve()
        })
      })
    },
    setDefaultCityCode () {
      // 设置默认城市
      if (this.cityList.length === 1) {
        this.fileSearchForm.cityId = this.cityList[0].value
        this.fileSearchFn()
      }
    },
    downloadFile (fileName) {
      // console.log(fileName)
      this.$emit('downloadClick', fileName)
    }
  },
  mounted () {
    this.getCityList().then(() => {
      this.setDefaultCityCode()
    })
  },
  watch: {
    watchReload () {
      if (this.watchReload) {
        this.fileSearchFn().then(() => {
          this.$emit('reloadDone')
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .file-container {
    min-width: 930px;
    /*background-color: #fff;*/
    font-size: 14px;
    margin-bottom: 10px;

    .ivu-form-item {
      margin-bottom: 0 !important;
    }

    .file-notice {
      color: red;
      font-size: 14px;
      padding: 20px 0 0 30px;
    }

    .ml10 {
      margin-left: 10px;
    }
  }
</style>
