<template>
  <div>
    <!--报名页面-->
    <Modal
      :mask-closable="false"
      title="报名信息"
      v-model="modalShow"
      :z-index="10000"
      @on-visible-change="onVisibleChange"
      width="960px">
      <div class="article">
        <Row>
          <Col span="10">班级名称： <span class="ivu-info-color">{{classInfo.className}}</span></Col>
          <Col span="4">学科： <span class="ivu-info-color">{{classInfo.subjectNames}}</span></Col>
          <Col span="4">总课次： <span class="ivu-info-color">{{classInfo.classCount}}</span></Col>
          <Col span="4">已上课次：<span class="ivu-info-color">{{classInfo.passedCount}}</span></Col>
        </Row>

        <Form
          style="margin-top: 20px"
          :label-width="100"
          label-position="left">
          <FormItem label="缴费截止日期" required>
            <Date-picker
              :options="disableRegistTime"
              type="datetime" format="yyyy-MM-dd HH:mm:ss"
              :value="registerInfo2.payEndDate"
              @on-change="changeDate"
              placeholder="请选择日期..."
            ></Date-picker>
          </FormItem>
          <FormItem label="是否续报">
            <RadioGroup v-model="registerInfo2.isContinue">
              <Radio :label=1>是</Radio>
              <Radio :label=0>否</Radio>
            </RadioGroup>
            <Select v-show="registerInfo2.isContinue===1" v-model="chooseBeforeClass" style="width: 250px;margin-left: 20px">
              <Option v-for="(item,index) in searchBeforeList" :value="item.classId" :key="index">{{ item.className }}</Option>
            </Select>
          </FormItem>
          <FormItem label="报名说明">
            <Input v-model="registerInfo2.remark" style="width: 360px;" type="textarea"/>
          </FormItem>
        </Form>
        <Row style="margin-top: 20px;">
          <Col>缴费前此操作为预留报名，需要在缴费截止日期前缴费，否则将取消此名额！</Col>
        </Row>
      </div>
      <div slot="footer">
        <Button type="primary" @click="save()" :loading="isLoading">保存</Button>
        <Button @click="modalShow = false">关闭</Button>
      </div>
      <Modal
        :mask-closable="false"
        v-model="resultShow"
        title="报名结果"
        :z-index="10001"
        cancel-text=""
        ok-text="确定"
        @on-visible-change="resultVisibleChange"
        width="300">
        <p>{{resultMessage}}</p>
      </Modal>
    </Modal>
  </div>
</template>

<script>
/* eslint-disable */
import { dateFormat } from '../../utils/utils'
import { getCurrentTime } from '../../api/bd'
import {
  querySourceClass,
  regist,
  registResult,
  editRegistInfo,
  validateRegister
} from '../../api/register'

import { queryClassDetail } from '../../api/clazz'

export default {
  name: 'TalBaoClass',
  props: {
    // NoPaymentBatch 未缴费批处理
    // BackupRecords 候补记录
    // ClassQuery 班级查询
    // NoPayment 未缴费班级
    from: {
      type: String,
      default: ''
    },
    show: { // 是否显示
      type: Boolean,
      default: false
    },
    isEdit: { // 是否编辑
      type: Boolean,
      default: false
    },
    classId: { // 班级id
      type: String,
      required: true
    },
    stuId: { // 学生id
      type: String,
      required: true
    },
    stuCityCode: { // 学生分校编码
      type: String,
      required: true
    },
    creatorEmpNo: { // 当前登录的用户钉钉id
      type: String,
      required: true
    },
    // 编辑时回填的信息
    registerInfo: {
      type: Object,
      default: function () {
        return {
          isContinue: 0, // 是否续报
          remark: '', // 报名说明
          payEndDate: '' // 截止缴费日期
        }
      }
    }
  },
  data () {
    return {
      classInfo: {},
      isLoading: false,
      searchBeforeList: [],
      chooseBeforeClass: '',
      // 不可选日期
      disableRegistTime: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now().valueOf()
        }
      },
      resultShow: false,
      resultMessage: '',
      success: false
    }
  },
  watch: {

  },
  methods: {
    onVisibleChange (status) {
      if (status) {
        this.isLoading = false
        queryClassDetail({ classId: this.classId }).then(data => {
          this.classInfo = data
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
        validateRegister({
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          classId: this.classId,
          studentId: this.stuId,
          type: 1 // 操作类型(1:报名;2:预选;3:缴费;4:转班)
        }).then(data => {
          const rlt = data[0]
          if (rlt.code !== '000000') {
            let msg = ''
            switch (rlt.code) {
              case '555555':
                msg = '班级不存在'
                break
              case '52212':
                msg = '没有设置报名时间'
                break
              case '51907':
                msg = '该学员未达到此班入学测试标准，会预留此次名额。请及时提醒家长完成入学测试与缴费'
                break
              case '51908':
                msg = '报名时间未到，报名时间：' + rlt.message
                break
              case '52210':
                msg = '预选时间未到，预选时间：' + rlt.message
                break
              default:
                msg = '未知错误'
            }
            this.$Message.warning({
              content: msg,
              duration: 5
            })
          }
        }).catch(err => {
          this.$Message.error({
            content: `报名校验失败：${err.message}`,
            duration: 5
          })
        })
      }
    },
    resultVisibleChange (status) {
      if(!status) {
        this.resultMessage = ''
        if (this.success) {
          this.modalShow = false
        }
      }
    },
    changeDate (data) {
      this.registerInfo2.payEndDate = data
    },
    save () {
      if (!this.registerInfo2.payEndDate) {
        this.$Message.warning('请选择日期')
        return
      }
      // if (!this.registerInfo2.remark) {
      //   this.$Message.warning('说明不能为空')
      //   return
      // }
      if (this.isEdit) {
        let req = {
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          uid: this.registerInfo.uid,
          payEndDate: this.registerInfo2.payEndDate, // 缴费截止时间
          remark: this.registerInfo2.remark, // 备注
          registId: this.registerInfo2.registId,
          isContinue: this.registerInfo2.isContinue, // 类型 0:正常报名;1:续报;2:跨班报;3:候补报名;4:预报名;5:试听报名,默认为0
          sourceClassId: this.registerInfo2.isContinue === 1 ? this.chooseBeforeClass : undefined // 原班id
        }
        if (req.isContinue === 1) {
          if (!req.sourceClassId) {
            this.$Message.warning({ content: '请选择原班', duration: 5 })
            return
          }
        }
        this.isLoading = true
        editRegistInfo(req).then(res => {
          this.$Message.success('保存成功')
          this.$emit('success')
          this.isLoading = false
          this.modalShow = false
        }).catch(err => {
          this.$Message.warning({ content: '保存失败: ' + err.message, duration: 5 })
          this.isLoading = false
        })
      } else {
        let req = {
          studentId: this.stuId,
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          classId: this.classId,
          creatorEmpNo: this.creatorEmpNo,
          remark: this.registerInfo2.remark, // 备注
          type: this.registerInfo2.isContinue, // 类型 0:正常报名;1:续报;2:跨班报;3:候补报名;4:预报名;5:试听报名,默认为0
          sourceClassId: this.registerInfo2.isContinue === 1 ? this.chooseBeforeClass : '', // 原班id
          payEndDate: this.registerInfo2.payEndDate // 缴费截止时间
        }
        if (req.type === 1) {
          if (!req.sourceClassId) {
            this.$Message.warning({ content: '请选择原班', duration: 5 })
            return
          }
        }
        this.isLoading = true
        regist(req).then(data => {
          if (data.execResult !== '2') {
            const int = setInterval(() => {
              registResult({
                cityCode: this.stuCityCode,
                'X-Ca-referer': this.from,
                studentId: this.stuId,
                classId: this.classId,
                registId: data.registId
              }).then(rr => {
                if (rr.execResult !== '0') {
                  clearInterval(int)
                  if (rr.execResult === '1') {
                    this.success = true
                    this.resultMessage = '报名成功'
                    this.$emit('success')
                  } else if (rr.execResult === '2') {
                    this.success = false
                    this.resultMessage = `报名失败，原因：${rr.message}`
                    this.$emit('fail')
                  }
                  this.resultShow = true
                  this.isLoading = false
                }
              }).catch(err => {
                clearInterval(int)
                this.isLoading = false
                console.error(err)
              })
            }, 1000)
          } else {
            this.success = false
            this.resultMessage = `报名失败，原因：${data.message}`
            this.resultShow = true
            this.$emit('fail')
            this.isLoading = false
          }
        }).catch(err => {
          this.success = false
          this.resultMessage = `报名失败，原因：${err.message}`
          this.resultShow = true
          this.isLoading = false
        })
      }
    },
    isContinueChange () {
      let req = {
        cityCode: this.stuCityCode,
        'X-Ca-referer': this.from,
        studentId: this.stuId,
        type: 0,
        year: this.classInfo.year,
        termId: this.classInfo.termId
      }
      querySourceClass(req).then(res => {
        if (res.length <= 0) {
          this.$Message.warning('暂无数据')
        } else {
          this.searchBeforeList = res
          // 如果是编辑
          if (this.registerInfo2.sourceClassId) {
            this.chooseBeforeClass = this.registerInfo2.sourceClassId
          } else {
            this.chooseBeforeClass = this.searchBeforeList[0].classId
          }
        }
      }).catch(err => {
        this.$Message.error('续报查找原班失败:' + err.message)
      })
    }
  },
  computed: {
    modalShow: {
      get: function () {
        if (this.show === true) {
          if (!this.isEdit) {
            this.registerInfo.isContinue = 0
            this.registerInfo.sourceClassId = ''
            this.registerInfo.remark = ''
            getCurrentTime().then(data => {
              this.registerInfo.payEndDate = dateFormat(data + 24 * 60 * 60 * 1000, 'yyyy-MM-dd HH:mm:ss')
            }).catch(err => {
              this.$Message.error({
                content:'获取服务器当前时间失败：' + err.message,
                duration: 5
              })
            })
          }
        }
        return this.show
      },
      set: function (val) {
        if (val === false) {
          this.searchBeforeList = []
          this.chooseBeforeClass = ''
          this.$emit('close')
        }
      }
    },
    registerInfo2: {
      get: function () {
        if (this.registerInfo.isContinue === 1) {
          this.isContinueChange()
        }
        return this.registerInfo
      },
      set: function () {

      }
    }
  }
}
</script>

<style scoped>
.changeColor {
  color: orange;
}
</style>
