<template>
  <div class="score-recording">
    <Form
      :model="formData"
      inline
      :label-width="180"
    >
      <!-- 客户管理-报班管理-成绩查询-成绩录入页面
           客户管理-成绩录入页面 -->
      <template v-if="!diagnosisRecordId && !hiddenDiagnosisType">
        <FormItem>
          <span slot="label">
            <span class="required">*</span>
            <span class="label">诊断类型: </span>
          </span>
          <RadioGroup v-model="formData.diagnosisType" @on-change="handleRadioChange">
            <Radio
              v-for="(item, index) in conditions.diagnosisType"
              :value="item.id"
              :label="item.id"
              :key="index"
            >{{ item.name }}</Radio>
          </RadioGroup>
        </FormItem>
        <br>
        <!-- 前台散诊时选择 -->
        <template v-if="formData.diagnosisType === 1">
          <FormItem>
            <span slot="label">
              <span class="required">*</span>
              <span class="label">用户意向: </span>
            </span>
            <Select
              v-model="formData.stuIntention"
              placeholder="请选择"
              class="score-select"
            >
              <Option
                v-for="(item,index) in conditions.stuIntention"
                :value="item"
                :key="index"
              >{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <span slot="label">
              <span class="required">*</span>
              <span class="label">渠道标签: </span>
            </span>
            <Select
              style="width: 150px;"
              v-model="formData.channelId"
              label-in-value
              placeholder="请选择"
              class="score-select"
              @on-change="handleChannelIdChange"
            >
              <Option
                v-for="(item,index) in diagnosisChannels"
                :value="item.id"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
          <br>
        </template>
      </template>
      <FormItem>
        <span slot="label">
          <span class="required">*</span>
          <span class="label">学员是否可见</span>
          <Tooltip
            max-width="300"
            content="勾选「否」，则录入的成绩不在APP/小程序的「我的诊断」中显示；勾选「是」则该成绩在APP/小程序的「我的诊断」中显示"
            placement="right"
          >
            <Icon
              size="14"
              type="ios-help-circle-outline"
            />
          </Tooltip>
          <span>:</span>
        </span>
        <RadioGroup v-model="formData.isVisible">
          <Radio label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <br>
      <FormItem>
        <span slot="label">
          <span class="required">*</span>
          <span class="label">成绩标签</span>
          <Tooltip
            max-width="300"
            content="请选择学员想报班的年份/学期/年级/学科"
            placement="right"
          >
            <Icon
              size="14"
              type="ios-help-circle-outline"
            />
          </Tooltip>
          <span>:</span>
        </span>
        <Select
          v-model="formData.cityCode"
          placeholder="请选择城市"
          class="score-select"
          @on-change="changeCity"
        >
          <Option
            v-for="(option,index) in cityList"
            :value="option.id"
            :key="index"
          >{{option.name}}</Option>
        </Select>
        <Select
          v-model="formData.year"
          placeholder="请选择年份"
          class="score-select"
        >
          <Option
            v-for="(item,index) in yearList"
            :value="item"
            :key="index"
          >{{ item }}</Option>
        </Select>
        <Select
          v-model="formData.termId"
          placeholder="请选择学期"
          class="score-select"
          @on-change="changeToLevelDegree"
          filterable
        >
          <Option
            v-for="(item,index) in termList"
            :value="item.schoolId"
            :key="index"
          >{{ item.customName }}</Option>
        </Select>
        <Select
          v-model="formData.gradeId"
          placeholder="请选择年级"
          class="score-select"
          @on-change="changeToLevelDegree"
          filterable
        >
          <Option
            v-for="(item,index) in gradeList"
            :value="item.id"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
        <Select
          v-model="formData.subjectId"
          placeholder="请选择学科"
          class="score-select"
          @on-change="changeToLevelDegree"
          filterable
        >
          <Option
            v-for="(item,index) in subjectList"
            :value="item.id"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <br>
      <FormItem>
        <span slot="label">
          <span class="label">分数</span>
          <Tooltip
            max-width="300"
            content="录入成绩仅作为标记，不作为录入班次的校验条件"
            placement="right"
          >
            <Icon
              size="14"
              type="ios-help-circle-outline"
            />
          </Tooltip>
          <span>:</span>
        </span>
        <Input
          v-model="formData.score"
          class="score-select"
          @on-blur="validateScore"
        ></Input>
      </FormItem>
      <br>
      <FormItem>
        <span slot="label">
          <span class="required">*</span>
          <span class="label">可报班次</span>
          <Tooltip
            max-width="300"
            content="成绩录入只能单选，优秀体系中，高级别班次向下兼容低级别班次。创新体系中，单次设置只对指定班次名称生效，但同时向下兼容优秀体系。独立体系中，单次设置只对指定班次级别生效"
            placement="right"
          >
            <Icon
              size="14"
              type="ios-help-circle-outline"
            />
          </Tooltip>
          <span>:</span>
        </span>
        <Select
          v-model="formData.levelDegree"
          placeholder="请选择级别"
          class="score-select"
          filterable
          @on-change="getLevel"
        >
          <Option
            v-for="(item,index) in levelDegreeList"
            :value="item.id"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
        <Select
          :disabled="Number(formData.levelDegree) < 4"
          v-model="formData.levelId"
          placeholder="请选择名称"
          filterable
          class="score-select"
        >
          <Option
            v-for="(item,index) in levelList"
            :value="item.id"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
    </Form>
    <div style="text-align: center">
      <Button
        type="primary"
        @click="confirm"
        :loading="isLoading"
      >保存</Button>
    </div>
    <Modal
      :mask-closable="false"
      title="录入确认"
      width="480px"
      v-model="confirmModal"
    >
      <div>
        <template v-if="formData.diagnosisType === 1 && !diagnosisRecordId && !hiddenDiagnosisType">
          <h5 style="padding-left: 50px;margin-top: 10px">诊断类型：{{formData.diagnosisType === 1 ? '前台散诊' : '其他'}}</h5>
          <h5 style="padding-left: 50px;margin-top: 10px">用户意向：{{formData.stuIntention}} &nbsp;&nbsp;&nbsp;&nbsp; 渠道标签：{{formData.channelName}}</h5>
        </template>
        <h5 style="padding-left: 50px;margin-top: 10px">用户是否可见：{{formData.isVisible === '0' ? '否' : '是'}}</h5>
        <h5 style="padding-left: 50px;margin-top: 10px">成绩标签：{{formData.cityName}}-{{formData.year}}-{{formData.term}}-{{formData.gradeName}}-{{formData.subjectName}}</h5>
        <h5 style="padding-left: 50px;margin-top: 10px">分数：{{formData.score || '未填写'}}</h5>
        <h5 style="padding-left: 50px;margin-top: 10px">可报班次：{{formData.levelDegreeName}}{{formData.levelName ? ('-' + formData.levelName): ''}}</h5>
      </div>
      <div
        slot="footer"
        style="text-align: center"
      >
        <Button
          @click="save"
          type="primary"
        >确认</Button>
        <Button @click="confirmModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getCityList, getYearList, getLinkage, getTermList } from '../../api/bd'
import { addScoreInfo } from '../../api/student'
import { getUserInfoByCookies } from '../../utils/utils'
import { addDiagnosisScore, queryDiagnosisChannels, condition } from '../../api/diagnosis'
export default {
  name: 'TalScoreRecord',
  data () {
    return {
      cityList: [],
      yearList: [],
      termList: [],
      gradeList: [],
      subjectList: [],
      levelDegreeList: [],
      levelList: [],
      formData: {
        isVisible: '1',
        cityCode: '',
        cityName: '',
        year: '',
        termId: '',
        term: '',
        gradeId: '',
        gradeName: '',
        subjectId: '',
        subjectName: '',
        score: '',
        levelDegree: '',
        levelDegreeName: '',
        levelId: '',
        levelName: '',
        diagnosisType: 4, // 诊断类型 1 前台散诊 4其他
        channelId: '', // 渠道标签ID
        channelName: '', // 渠道标签名称
        stuIntention: '' // 用户意向
      },
      confirmModal: false,
      isLoading: false,
      diagnosisChannels: [], // 渠道标签
      conditions: {
        stuIntention: []
      } // 用户意向
    }
  },
  props: {
    cityCode: String,
    stuCard: String,
    stuId: String,
    stuName: String,
    stuUid: [String, Number],
    diagnosisRecordId: String,
    hiddenDiagnosisType: { // 隐藏诊断类型: 呼叫平台不展示诊断类型、用户意向和渠道标签
      type: Boolean,
      default: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.formData.cityCode = this.cityCode
      this.getCity()
      this.getYear()
      this.changeCity()
      if (!this.hiddenDiagnosisType) {
        this.queryDiagnosisChannels()
        this.getCondition()
      }

      if (this.diagnosisRecordId) this.formData.diagnosisType = 1
    })
  },
  methods: {
    // 监听诊断类型变更
    handleRadioChange () {
      this.changeToLevelDegree()
    },
    // 渠道标签ID变更
    handleChannelIdChange (data) {
      if (data) {
        this.formData.channelName = data.label
      }
    },
    // 渠道标签列表
    queryDiagnosisChannels () {
      queryDiagnosisChannels({}).then(res => {
        this.diagnosisChannels = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 用户意向
    getCondition () {
      condition({}).then(res => {
        this.conditions = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    validateScore () {
      if (this.formData.score && !/^\d{1,3}$/.test(this.formData.score)) {
        this.$Message.error({ content: '请输入正确的分数：1-3位数字', duration: 5 })
        this.formData.score = ''
      }
    },
    changeCity () {
      this.termList = []
      this.gradeList = []
      this.subjectList = []
      this.formData.termId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.getTerm()
      this.getGrade()
      this.getSubject()
    },
    changeToLevelDegree () {
      this.formData.levelDegree = ''
      this.levelDegreeList = []
      this.getLevelDegree()
    },
    getCity () {
      getCityList().then(data => {
        this.cityList = Object.freeze(data)
      }).catch(err => {
        this.$Message.error({
          content: '获取城市列表失败：' + err.message
        })
      })
    },
    getYear () {
      getYearList().then((data) => {
        this.yearList = Object.freeze(data.sort((d1, d2) => Number(d2) - Number(d1)))
      }).catch((err) => {
        this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
      })
    },
    getTerm () {
      let params = {
        cityCode: this.formData.cityCode
      }
      getTermList(params).then(res => {
        this.termList = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: '获取学期失败:' + err.message, duration: 5 })
      })
    },
    getGrade () {
      let params = {
        cityCode: this.formData.cityCode,
        queryType: 'g'
      }
      getLinkage(params).then(res => {
        this.gradeList = Object.freeze(res)
        // if (this.studentDetail.gradeId) {
        //   this.formMessage.gradeId = String(this.studentDetail.gradeId)
        // }
      }).catch(err => {
        this.$Message.error({ content: '获取年级失败:' + err.message, duration: 5 })
      })
    },
    getLevelDegree () {
      if (this.formData.cityCode && this.formData.year && this.formData.termId && this.formData.gradeId && this.formData.subjectId) {
        const isDiagnosisEnter = (this.formData.diagnosisRecordId || this.formData.diagnosisType === 1) ? 1 : null
        let params = {
          cityCode: this.formData.cityCode,
          years: this.formData.year,
          termIds: this.formData.termId,
          gradeIds: this.formData.gradeId,
          subjectIds: this.formData.subjectId,
          queryType: 'cd',
          isDiagnosisEnter: isDiagnosisEnter // 是否为散诊情况
        }
        getLinkage(params).then(res => {
          this.levelDegreeList = Object.freeze(res)
        }).catch(err => {
          this.$Message.error({ content: '获取班次级别失败:' + err.message, duration: 5 })
        })
      }
    },
    getLevel () {
      this.formData.levelId = ''
      this.levelList = []
      if (Number(this.formData.levelDegree) > 3.5) {
        let params = {
          cityCode: this.formData.cityCode,
          years: this.formData.year,
          termIds: this.formData.termId,
          gradeIds: this.formData.gradeId,
          subjectIds: this.formData.subjectId,
          levelDegrees: this.formData.levelDegree,
          queryType: 'c'
        }
        getLinkage(params).then(res => {
          this.levelList = Object.freeze(res)
        }).catch(err => {
          this.$Message.error({ content: '获取班次名称失败:' + err.message, duration: 5 })
        })
      }
    },
    getSubject () {
      let baseSubject = {
        cityCode: this.formData.cityCode,
        queryType: 's'
      }
      getLinkage(baseSubject).then(res => {
        this.subjectList = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: '获取学科失败:' + err.message, duration: 5 })
      })
    },
    confirm () {
      // 非散诊页面成绩录入
      // 前台散诊
      if (!this.diagnosisRecordId && this.formData.diagnosisType === 1 && !this.hiddenDiagnosisType) {
        if (!this.formData.stuIntention) {
          this.$Message.warning({ content: '用户意向不能为空', duration: 5 })
          return
        }

        if (!this.formData.channelId) {
          this.$Message.warning({ content: '渠道标签不能为空', duration: 5 })
          return
        }
      }

      if (!this.formData.cityCode) {
        this.$Message.warning({ content: '城市不能为空', duration: 5 })
        return
      }
      if (!this.formData.year) {
        this.$Message.warning({ content: '年份不能为空', duration: 5 })
        return
      }
      if (!this.formData.termId) {
        this.$Message.warning({ content: '学期不能为空', duration: 5 })
        return
      }
      if (!this.formData.gradeId) {
        this.$Message.warning({ content: '年级不能为空', duration: 5 })
        return
      }
      if (!this.formData.subjectId) {
        this.$Message.warning({ content: '学科不能为空', duration: 5 })
        return
      }
      if (!this.formData.levelDegree) {
        this.$Message.warning({ content: '班次级别不能为空', duration: 5 })
        return
      }
      if (Number(this.formData.levelDegree) > 3.5 && !this.formData.levelId) {
        this.$Message.error({ content: '班次名称不能为空', duration: 5 })
        return
      }
      this.formData.cityName = this.cityList.filter(c => c.id === this.formData.cityCode)[0].name
      this.formData.term = this.termList.filter(c => c.schoolId === this.formData.termId)[0].customName
      this.formData.gradeName = this.gradeList.filter(c => c.id === this.formData.gradeId)[0].name
      this.formData.subjectName = this.subjectList.filter(c => c.id === this.formData.subjectId)[0].name
      this.formData.levelDegreeName = this.levelDegreeList.filter(c => c.id === this.formData.levelDegree)[0].name
      this.formData.levelName = Number(this.formData.levelDegree) > 3.5 ? this.levelList.filter(c => c.id === this.formData.levelId)[0].name : ''
      this.confirmModal = true
    },
    save () {
      const userInfo = getUserInfoByCookies('columbusUserInfo')

      // 录入成绩或者录入散诊成绩
      let addScoreApi = null
      if (this.diagnosisRecordId) {
        addScoreApi = addDiagnosisScore
      } else {
        addScoreApi = addScoreInfo
      }

      addScoreApi(Object.assign({}, this.formData, {
        empName: userInfo ? userInfo.name : '',
        stuInfo: [
          {
            cityCode: this.cityCode,
            stuCard: this.stuCard,
            stuId: this.stuId,
            stuName: this.stuName,
            stuUid: this.stuUid
          }
        ],
        id: this.diagnosisRecordId // 散诊id
      })).then(data => {
        this.$Message.success({ content: '成绩录入成功', duration: 5 })
        this.confirmModal = false
        this.$emit('success')
        this.formData.stuIntention = ''
        this.formData.channelName = ''
        this.formData.channelId = ''
        this.formData.year = ''
        this.formData.termId = ''
        this.formData.term = ''
        this.formData.gradeId = ''
        this.formData.gradeName = ''
        this.formData.subjectId = ''
        this.formData.subjectName = ''
        this.formData.levelDegree = ''
        this.formData.levelDegreeName = ''
        this.formData.levelId = ''
        this.formData.levelName = ''
        this.formData.score = ''
      }).catch(err => {
        this.$Message.error({ content: '成绩录入失败：' + err.message, duration: 5 })
        this.confirmModal = false
        this.$emit('fail')
      })
    }
  }
}
</script>

<style scoped lang="less">
.score-recording {
  .required {
    color: red;
    font-size: 16px;
  }
  .label {
    font-size: 14px;
  }
  .score-select {
    width: 100px;
    margin-right: 10px;
  }
}
</style>
