<style lang="less" scoped>
  .class-presentation{
    .header{
      height: 45px;
      position: absolute;
      top: 70px;
      z-index: 100;
      width: 100%;
      a{
        display: inline-block;
        width: 190px;
        height: 45px;
        line-height: 45px;
        border: 1px #dcdcdc solid;
        text-align: center;
        color: #515a6e;
        background-color: rgba(242, 242, 242, 1);
      }
      a.on{
        background: white;
      }
    }
    .content{
      margin-top: 80px;
      height: 535px;
      overflow-y: scroll;
      .title{
        padding:  10px 0;
        font-weight: bold;
        font-size: 14px;
      }
    }
    .content::-webkit-scrollbar {
      display: none;
    }
  }
</style>
<template>
  <div>
    <Modal
      :mask-closable="false"
      title="课程简介"
      v-model="modalValue"
      class="modal-height1"
      :z-index="9999"
      width="960px">
      <div class="class-presentation">
        <div class="header">
          <a href="#card_1" style="border-right: 0" :class="isFocusing('card_1')" @click="setAnchor('card_1')">视频简介</a>
          <a href="#card_2" style="border-right: 0" :class="isFocusing('card_2')" @click="setAnchor('card_2')">适合对象</a>
          <a href="#card_3" style="border-right: 0" :class="isFocusing('card_3')" @click="setAnchor('card_3')">培养目标</a>
          <a href="#card_4" :class="isFocusing('card_4')" @click="setAnchor('card_4')">图文简介</a>
        </div>
        <div class="content">
          <div id="card_1">
            <p class="title">视频简介</p>
            <video  style="width: 100%;" :src="persentationInfo.courseOutline.videoHref" controls="controls"></video>
          </div>
          <div id="card_2">
            <p class="title">适合对象</p>
            <p style="text-indent:20px;word-wrap:break-word">{{persentationInfo.courseOutline.recruitIntro}}</p>
          </div>
          <div id="card_3">
            <p class="title">培养目标</p>
            <p style="text-indent:20px;word-wrap:break-word">{{persentationInfo.courseOutline.trainTarget}}</p>
          </div>
          <div id="card_4">
            <p class="title">图文简介</p>
            <img style="width: 100%; height: auto;margin-top: 15px" v-for="(item,index) in persentationInfo.courseOutline.outlineImgDesc" :key="index" :src="item">
          </div>
        </div>
      </div>
      <div slot="footer">
        <Button  @click="modalValue = false">关闭</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'classPresentation',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    persentationInfo: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      anchor: 'card_1'
    }
  },
  methods: {
    setAnchor (val) {
      this.anchor = val
    },
    isFocusing (val) {
      return val === this.anchor ? 'on' : ''
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  }
}
</script>
