<template>
  <Table
    class="no-padding"
    :columns="columns"
    :data="tableData"
    :loading="loading">
    <template slot-scope="{ row }" slot="payType" >
        {{row.type === 'pay' ? '缴费' : '转班'}}
    </template>
    <template slot-scope="{ row }" slot="payTypeName" >
      {{row.type === 'pay' ? row.payTypeName : clacPayType(row.stagesList) ? row.payTypeName : '--'}}
    </template>
    <template slot-scope="{ row }" slot="subjectName" >
      <template v-if="row.stagesList.length === 0">
        {{row.stagesList[0].stagesName}}
      </template>
      <template v-else>
        <p v-for="item in row.stagesList" :key="item.stagesName">
          <span>{{item.stagesName}}</span>
        </p>
      </template>
    </template>
    <template slot-scope="{ row }" slot="classInstallment" >
      <template v-if="row.stagesList.length === 0">
        {{row.stagesList[0].shouldPrice}}
      </template>
      <template v-else>
        <p v-for="item in row.stagesList" :key="item.stagesName">
          <span>{{item.shouldPrice}}</span>
        </p>
      </template>
    </template>
    <template slot-scope="{ row }" slot="discountAmount" >
      <template v-if="row.stagesList.length === 0">
        {{row.stagesList[0].stagesName}}
      </template>
      <template v-else>
        <p v-for="item in row.stagesList" :key="item.stagesName">
          <template v-if="item.promotionArray!==null&&item.promotionArray.length>0">
            <p v-for="items in item.promotionArray" :key="items.promotionId">
              <span>{{items.promotionName}}:{{items.promotionActualPrice}}</span>
            </p>
          </template>
          <template v-if="item.couponArray!==null&&item.couponArray.length>0">
            <p v-for="(itemC,index) in item.couponArray" :key="index">
              <span>优惠劵：{{itemC.couponActualValue}}</span>
            </p>
          </template>
          <template v-if="(item.promotionArray===null||item.promotionArray.length===0)&& (item.couponArray===null&&item.couponArray.length===0)">
            --
          </template>
        </p>
      </template>
    </template>
    <template slot-scope="{ row }" slot="curriculumNum" >
      <template v-if="row.type === 'pay'">
        <template v-if="row.stagesList.length === 1">
          {{row.stagesList[0].startPosition + '-' + row.stagesList[0].endPosition}}
        </template>
        <template v-else>
          <p v-for="(item, index) in row.stagesList" :key="index">
            <span>{{item.startPosition + '-' + item.endPosition}}</span>
          </p>
        </template>
      </template>
      <template v-else>
        --
      </template>
    </template>
    <template slot-scope="{ row }" slot="price" >
      <template v-if="row.type === 'pay'">
        <template v-if="row.stagesList.length === 1">
          {{row.stagesList[0].price}}
        </template>
        <template v-else>
          <p v-for="(item, index) in row.stagesList" :key="index">
            <span>{{item.price}}</span>
          </p>
        </template>
      </template>
      <template v-else>
        --
      </template>
    </template>
    <template slot-scope="{ row }" slot="availableClass" >
      <template v-if="row.type !== 'pay'">
        <template v-if="row.stagesList.length === 1">
          {{row.stagesList[0].startPosition + '-' + row.stagesList[0].endPosition}}
        </template>
        <template v-else>
          <p v-for="(item, index) in row.stagesList" :key="index">
            <span>{{item.startPosition + '-' + item.endPosition}}</span>
          </p>
        </template>
      </template>
      <template v-else>
        --
      </template>
    </template>
    <template slot-scope="{ row }" slot="dtsbDescription" >
      <template  v-if="row.type !== 'pay'">
        <template v-if="row.stagesList.length === 1">
          <template v-if="Number(row.stagesList[0].price) === 0">
            --
          </template>
          <template v-else>
            {{(row.stagesList[0].price > 0 ? `少补`: `多退`) + Math.abs(row.stagesList[0].price)}}
          </template>
        </template>
        <template v-else>
          <p v-for="(item, index) in row.stagesList" :key="index">
            <template v-if="Number(item.price) === 0">
              --
            </template>
            <template v-else>
              <span>{{(item.price > 0 ? `少补`: `多退`) + Math.abs(item.price)}}</span>
            </template>
          </p>
        </template>
      </template>
      <template v-else>
        --
      </template>
    </template>
    <!--<template v-for="item in columns">-->
      <!--<template v-if="item.key">-->
        <!--<template slot-scope="{ row }" v-slot="item.key">-->
          <!--<p>{{row[item.key]}}</p>-->
          <!--<p>123</p>-->
        <!--</template>-->
      <!--</template>-->
    <!--</template>-->
  </Table>
</template>

<script>
export default {
  name: 'talTable',
  props: {
    columns: {
      type: Array,
      required: true
    },
    tableData: {
      type: Array,
      required: true
    },
    loading: {
      type: Boolean,
      required: true
    }
  },
  computed: {
  },
  data () {
    return {
    }
  },
  methods: {
    clacPayType (stages) {
      let hasLost = 0
      stages.forEach(obj => {
        if (obj.price > 0) {
          hasLost += 1
        }
      })
      return hasLost > 0
    }
  }
}
</script>

<style scoped lang="less">
  .no-padding {
    /deep/ .ivu-table-cell {
      padding-left: 2px;
      padding-right: 2px;
      p {
        line-height: 48px;
        span {
          display: inline-block;
          line-height: 1.5em;
        }
        &:not(:last-of-type):not(:only-child) {
          border-bottom: 1px #e8eaec solid;
        }
      }
      .ivu-checkbox {
        & + span {
          display: none;
        }
      }
    }
  }
</style>
