<!--课程简介-->
<template>
  <div class="class-presentation">
    <template  v-if="!hasData">
      <p class="no-data">
        <span>该课程未配置简介</span>
      </p>
    </template>
    <template v-else>
      <div class="header">
        <a href="#courseInfoCard1" style="border-right: 0" :class="isFocusing('courseInfoCard1')" @click="setAnchor('courseInfoCard1')">课程简介</a>
        <a href="#courseInfoCard2" style="border-right: 0" :class="isFocusing('courseInfoCard2')" @click="setAnchor('courseInfoCard2')">课程大纲</a>
        <a href="#courseInfoCard3" style="border-right: 0" :class="isFocusing('courseInfoCard3')" @click="setAnchor('courseInfoCard3')">图文简介</a>
        <a href="#courseInfoCard4" :class="isFocusing('courseInfoCard4')" @click="setAnchor('courseInfoCard4')">视频介绍</a>
      </div>

      <div class="content-box" :style="{height:contentHeight}">
        <div class="clearfix content-item" id="courseInfoCard1">
          <p class="title">课程简介</p>
          <div class="content">
            <p class="title2">课程亮点：</p>
            <p style="font-size: 14px;padding: 10px 0;">{{introductionDetail.detailBright}}</p>
            <p class="title2">适合对象：</p>
            <p style="font-size: 14px;padding: 10px 0;">{{introductionDetail.detailSuit}}</p>
            <p class="title2">培养目标：</p>
            <p style="font-size: 14px;padding: 10px 0;">{{introductionDetail.detailTarget}}</p>
          </div>
        </div>
        <div class="clearfix content-item" id="courseInfoCard2">
          <p class="title">课程大纲</p>
          <div class="content">
            <p v-for="(item,index) in introductionDetail.detailOutline" :key="index">{{item}}</p>
          </div>
        </div>
        <div class="clearfix content-item" id="courseInfoCard3">
          <p class="title">图文简介</p>
          <div class="content">
            <img :src="introductionDetail.detailDescImg" style="max-width: 300px;max-height: 500px;object-fit: contain;cursor: pointer;" @click="showImage()" alt=""/>
          </div>
        </div>
        <div class="clearfix content-item" id="courseInfoCard4">
          <p class="title">视频介绍</p>
          <div class="content">
            <div>
              <a :href="introductionDetail.detailTopVideoUrl" target="_blank" style="margin-left:15px;margin-right: 10px;font-size: 14px">{{introductionDetail.detailTopVideoUrl}}</a>
              <Button type="primary" size="small" @click="copy(introductionDetail.detailTopVideoUrl)">复制</Button>
            </div>
            <div style="text-align: center">
              <video style="margin: 10px 0;max-height: 500px" :src="introductionDetail.detailTopVideoUrl" controls="controls">您的浏览器不支持 video 标签。</video>
            </div>
          </div>
        </div>
      </div>
      <Modal
        width="960"
        :z-index="10000"
        v-model="showImageModel"
        title="图文简介">
        <div style="text-align: center;">
          <img style="max-width: 100%" :src="introductionDetail.detailDescImg" alt="">
        </div>
        <div slot="footer">
          <Button @click="showImageModel = false">关闭</Button>
        </div>
      </Modal>
    </template>
  </div>
</template>

<script>
import {
  queryIntroductionDetail
} from '../../../api/clazz'
import { copyText } from '../../../utils/utils'
export default {
  name: 'classInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      anchor: 'courseInfoCard1',
      showImageModel: false,
      hasData: false,
      contentHeight: 'auto',
      introductionDetail: {
        detailBright: '', // 课程亮点
        detailSuit: '', // 适用对象
        detailTarget: '', // 培养目标
        detailOutline: [], // 课程大纲
        detailDescImg: '', // 课程详情的图片
        detailTopVideoUrl: '', // 教师视频
        detailTopVideoImage: '' // 教师视频封面
      }
    }
  },
  methods: {
    initView () {
      let clientHeight = document.documentElement.clientHeight
      this.contentHeight = (clientHeight - 350 > 600 ? 600 : clientHeight - 350) + 'px'
      this.hasData = false
      this.anchor = 'courseInfoCard1'
      this.introductionDetail = {
        detailBright: '', // 课程亮点
        detailSuit: '', // 适用对象
        detailTarget: '', // 培养目标
        detailOutline: [], // 课程大纲
        detailDescImg: '', // 课程详情的图片
        detailTopVideoUrl: '', // 教师视频
        detailTopVideoImage: '' // 教师视频封面
      }
      this.getData()
    },
    getData () {
      queryIntroductionDetail({ classId: this.classInfo.id }).then((data) => {
        if (data) {
          this.introductionDetail = data
          this.hasData = true
        } else {
          this.hasData = false
        }
      }).catch((err) => {
        this.$Message.warning(`获取课程简介信息失败:${err.message}`)
      })
    },
    setAnchor (val) {
      this.anchor = val
    },
    isFocusing (val) {
      return val === this.anchor ? 'on' : ''
    },
    showImage () {
      this.showImageModel = true
    },
    copy (text) {
      copyText(text)
    }
  }
}
</script>
