        color: #515a6e;
        background-color: rgba(242, 242, 242, 1);
      }
      a.on{
        background: white;
        color: #2d8cf0;
      }
    }
    .content-box{
      margin-top: 60px;
      /*height: 535px;*/
      overflow-y: scroll;
      .content-item{
        margin-bottom: 20px;
      }
      .title{
        width: 100px;
        font-weight: bold;
        font-size: 16px;
        float: left;
        overflow: hidden;
      }
      .title2{
        padding:  5px 0 0 0;
        font-weight: bold;
