<!-- 联报金发票查看详情 -->
<template>
  <detail
    :value="detailFlag"
    :inDetailData="inDetailData"
    :ifRedDashed="ifRedDashed"
    @close="closeModal"
    @setOffset="setOffset"
    @sendEmail="sendEmail"
  ></detail>
</template>

<script>
import detail from './detail'
import { retrySendInvoice } from '../../api/invoice'
import { queryInvoiceDetail, offsetInvoice } from '../../api/voucher'
export default {
  name: 'TalInvoiceQueryDetailScholarship',
  components: {
    detail
  },
  data () {
    return {
      detailFlag: false,
      inDetailData: []
    }
  },
  props: {
    cityCode: { // 城市编码
      type: String,
      default: ''
    },
    ifRedDashed: { // 是否有红冲操作，并且发票为蓝字发票时才可红冲
      type: Boolean,
      default: true
    },
    detailParams: { // 详情请求参数
      type: Object,
      default: function () {
        return {
          voucherId: null
        }
      }
    }
  },
  watch: {
    'detailParams.voucherId' () {
      if (this.detailParams.voucherId) {
        this.getDetailData()
      }
    }
  },
  methods: {
    // 重新发送
    sendEmail (params) {
      params.cityCode = this.cityCode
      retrySendInvoice(params).then(() => {
        this.$Message.success({ content: '发送成功', duration: 5 })
      }).catch(err => {
        this.$Message.error({ content: '发送失败: ' + err.message, duration: 5 })
      })
    },
    closeModal (val) {
      this.detailFlag = val
      if (!val) {
        this.detailParams.voucherId = null
      }
    },
    getDetailData () {
      // 获取详情信息
      this.detailParams.cityCode = this.cityCode
      queryInvoiceDetail(this.detailParams).then(res => {
        this.inDetailData = res || []
        if (this.inDetailData.length === 0) {
          this.inDetailData = false
          this.$Message.warning({ content: '无可查看发票', duration: 5 })
        } else {
          this.detailFlag = true
        }
      }).catch((err) => {
        this.$Message.error({ content: '获取数据失败:' + err.message, duration: 5 })
        this.detailParams.registerId = null
      })
    },
    setOffset (params) {
      // 红冲
      params.cityCode = this.cityCode
      offsetInvoice(params).then(res => {
        this.$Modal.success({
          title: '一键红冲',
          content: `红冲成功，红字发票已发送到领票人邮箱，请提醒对方查收`,
          onOk: () => {
            this.getDetailData()
          }
        })
      }).catch((err) => {
        this.$Message.error({ content: '红冲失败:' + err.message, duration: 5 })
      })
    }
  }
}
</script>

