/**
 * 添加邮寄地址
 *       address 地址 body false string
 *       cityId 城市Id body false string
 *       cityName 城市名称 body false string
 *       createDate 创建时间 body false string
 *       created body false string
 *       creater body false string
 *       createrName body false string
 *       creatorId 创建人ID body false st1ring
 *       creatorName 创建人名称 body false string
 *       deleted 是否删除 body false integer(int32)
 *       districtId 区县Id body false string
 *       districtName 区名称 body false string
 *       homePhone 家庭电话 body false string
 *       id 邮寄地址Id，此条邮寄地址信息为修改时需传入 body false string
 *       isDefault 是否是默认地址，0否，1是默认 body false integer(int32)
 *       modified body false string(date-time)
 *       modifier body false string
 *       modifierId 修改人ID body false string
 *       modifierName 修改人名称 body false string
 *       modifyDate 修改时间 body false string(date-time)
 *       name 收货人 body false string
 *       postcode 邮编 body false string
 *       provinceId 省份Id body false string
 *       provinceName 省名称 body false string
 *       tel 收货人联系方式 body false string
 *       uid uid body false integer(int32)
 */

export function addRecipient (params) {
  return $.splatAxios.request({
    url: 'student/addRecipient',
    method: 'post',
    data: params
  })
}

