    getNameLength (name, len = 15) {
      if (name && name.length > len) {
        return name.substring(0, len) + '...'
      } else {
        return name
      }
    },
    showStuDetail (row) {
      this.stuDetail.cityCode = row.cityCode
      this.stuDetail.uid = row.uid
      this.stuDetail.show = true
    },
    queryNotPayStageListHandler () {
      queryNotPayStageList().then(res => {
        this.classStageList = res
      })
    },
    registerPlatformList () {
      queryRegisterPlatformList().then(res => {
        this.registWay = res
      })
    },
    // 授课类型
    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    closeParDetail () {
      this.payDetailModal = false
      this.parDetailList = []
    },
    search () {
      this.currentPageNum = 1
      this.getTableData()
    },
    getTableData () {
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择所在城市')
        return
      }
      if (!this.formData.year) {
        this.$Message.warning('请选择年份')
        return
      }
      if (!this.formData.termId) {
        this.$Message.warning('请选择学期')
        return
      }
      if (this.payDateStart && this.payDateEnd && this.payDateStart > this.payDateEnd) {
        this.$Message.warning('缴费截止日期错误：开始时间不能晚于结束时间')
        return
      }
      this.formData.pageSize = this.currentPageSize
      this.formData.pageNo = this.currentPageNum
      this.tableLoading = true
      let areaName = ''
      if (this.formData.areaId) {
        areaName = this.areaList.filter(a => a.schoolId === this.formData.areaId)[0].name
      }

      this.checkAll = false
      this.allItem = []
      this.tableLoading = true
      this.checkAllGroup = []
      getNotPayList(Object.assign({}, this.formData, { areaName })).then(res => {
        if (res.records.length === 0) {
          this.$Message.warning('暂无数据')
        }
        this.pageTotal = res.totalCount
        this.cancelOrderErrorMap = {}
        this.cancelRegisterIds = '' // 清空选中项
        this.checkAllGroup = [] // TODO xxx
        let cityName = ''
        this.allCityList.forEach(res => {
          if (res.id === this.formData.cityCode) {
            cityName = res.name
          }
        })
        // this.allCityList = this.allCityList.filter(obj => obj.id !== '1111')
        // debugger
        this.studentDetail = {
          cityCode: this.formData.cityCode,
          cityName: cityName
        }
        res.records.forEach((e) => {
          if (this.allItem.indexOf(e.registId) < 0) {
            this.allItem.push(e.registId)
          }
          this.cancelOrderErrorMap[e.registId] = e.className
          // e.cityName = this.allCityMap[e.cityCode]
          e.cityName = cityName
          if (e.startDate) {
            e.startDate = e.startDate.substring(0, 10)
          }
          if (e.endDate) {
            e.endDate = e.endDate.substring(0, 10)
          }
          e.Application = (e.maxPersons - e.remainNum) + '/' + e.maxPersons
        })
        this.tableData = Object.freeze(res.records)
        this.tableLoading = false
      }).catch(err => {
        this.tableData = []
        this.tableLoading = false
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      })
    },
    // 缴费详情页
    platformDetail (data) {
      let req = {
        cityCode: data.cityCode,
        registId: data.registId
      }
      queryStudentRegisterStage(req).then(res => {
        if (res) {
          this.parDetailList.push(res)
          this.parDetailList.forEach((e) => {
            if (e.stagesInfos && e.stagesInfos.length) {
              e.rowspan = e.stagesInfos.length
            }
          })
        }
      }).catch(err => {
        this.$Message.error({ content: '获取缴费详情失败' + err.message, duration: 5 })
      })
      this.payDetailModal = true
    },
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // cityCode
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        if (this.needInitArea) {
          queryServiceCenterByEmpNo({
            cityCode: this.formData.cityCode,
            empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
          }).then(data => {
            this.formData.areaId = data.areaId
            this.changeArea(data.areaId, data.serviceCenterId)
          }).catch(err => {
            this.$Message.error('获取当前所在服务中心失败:' + err.message)
          })
        }
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
      // this.getTimeTypeListHandler()
      // this.getTimeListHandler()
    },
    cityYearChange (val) { // 改变城市
      this.formData.termId = ''
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.venueId = ''
      this.formData.classtimeTimePeriod = ''
      this.formData.classtimeTypeClassify = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      this.areaList = []
      this.serviceCenterList = []
      this.venueList = []
      // this.classtimeList = []
      if (!val) {
        return false
      }

      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }

      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectIds: this.formData.subjectId,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = Object.freeze(res.records)
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    // 获取辅导老师
    getTutor (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.tutorNameList = []
      let req = {
        sysName: query,
        teacherType: 1,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        // this.tutorNameList = res.records  TODO @@@
        this.tutorNameList = Object.freeze(res.records)
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取辅导老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    payDateStartChange (val) {
      this.formData.payEndDateStart = val
    },
    payDateEndChange (val) {
      this.formData.payEndDateEnd = val
    },
    // 报班日期日期
    registDateChange (val) {
      this.formData.registDateStart = val[0]
      this.formData.registDateEnd = val[1]
    },
    // 撤单
    cancelOrder (isAll, item) {
      let req = {}
      if (isAll) {
        if (!this.checkAllGroup.length) {
          this.$Message.warning({ content: '未选择班级', duration: 5 })
          return
        }
        req.registIds = this.checkAllGroup.join(',')
        // req.registIds = this.cancelRegisterIds
      } else {
        if (item && item.registId) {
          req.registIds = item.registId
        } else {
          this.$Message.warning({ content: '未选择班级', duration: 5 })
          return
        }
      }
      req.cityCode = this.tableData[0].cityCode
      this.$Modal.confirm({
        title: '撤单',
        content: '是否确认撤单？',
        onOk: () => {
          cancelRegisters(req).then((res) => {
            if (Object.keys(res.errorMap).length > 0) {
              for (let i in res.errorMap) {
                this.$Message.error({ content: '[ ' + this.cancelOrderErrorMap[i] + ' ]' + '撤单失败 :' + res.errorMap[i], duration: 10, closable: true })
              }
            } else {
              this.$Message.success({ content: '撤单成功', duration: 5 })
            }
            this.getTableData()
          }).catch((err) => {
            this.$Message.warning({ content: '撤单失败 :' + err.message, duration: 5 })
          })
        }
      })
    },
    // 转班
    changeClass (item) {
      this.changeClassModal = true
      this.studentDetail.studentId = item.studentId
      this.oldClassInfo = item
    },
    // 编辑
    editClass (item) {
      let req = {
        registId: item.registId,
        cityCode: item.cityCode
      }
      getRegistInfo(req).then((res) => {
        this.studentInfo.cityCode = item.cityCode
        this.studentInfo.studentId = item.studentId
        this.oldClassInfo = item
        res.uid = item.uid
        this.registerInfo = res
        this.registModal = true
      }).catch((err) => {
        this.$Message.warning({ content: '获取数据失败 :' + err.message, duration: 5 })
      })
    },
    // 表单重置
    resetFields () {
      depClone(this.formData, this.formData2)
      this.payDateStart = ''
      this.payDateEnd = ''
      this.registDateStartEnd = ''
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    }
  },
  mounted() {
    this.currentPageSize = this.pages[0] || 10
    this.getYearListHandler()
    this.getCityListHandler()
    this.getClassTypeHandler()
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
    this.isSchoolTestHandler()
    this.registerPlatformList()
    this.queryNotPayStageListHandler()
    setFrom({
      apiName: 'noPayBatchPage'
    })
  }
}
</script>

