import $ from '../utils/api.request'

/**
 *
 查询学员考勤信息
 @param classId 班级id query false string
 @param  studentId 学员id query false string
  */
export function getAttendanceByClassId (params) {
  return $.splatAxios.request({
    url: 'attendance/getAttendanceByClassId',
    method: 'get',
    params
  })
}

/**
 * 查询学员考勤信息
 * @param classDate 日期 query true string
 * @param studentId 学员id query true string
 */
export function getAttendanceByDate (params) {
  return $.request({
    url: 'attendance/getAttendanceByDate',
    method: 'get',
    params
  })
}
