<template>
  <div class="invoice-query">
    <Form
    ref="form"
    :label-width="75"
    label-position="left"
    :model="formItem"
    :rules="ruleItem"
    inline
    style="text-align: left; padding:0 20px;"
    >
      <FormItem label="分校：" prop="areaCode">
        <Select v-model="formItem.areaCode" :filterable="true" :clearable="true" placeholder="请选择分校" class="w120">
          <Option v-for="item in allAreaCode" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="业务线：" prop="businessCode">
        <Select v-model="formItem.businessCode" :clearable="true" placeholder="请选择业务线" class="w120">
          <Option v-for="item in allBusiness" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="费用类型：" prop="queryType" :label-width="85">
        <Select v-model="formItem.queryType" placeholder="请选择费用类型" clearable class="w140">
          <Option v-for="(item) in feeTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="开票类型：" prop="invoiceType">
        <Select v-model="formItem.invoiceType" placeholder="请选择开票类型" clearable class="w140" filterable>
          <Option v-for="(item, key) in invoiceTypeList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="学员姓名：">
        <Input v-model="formItem.studentName" placeholder="请输入" :maxlength="400" clearable class="w120"/>
      </FormItem>
      <FormItem label="学员编号：">
        <Input v-model="formItem.studentNo" placeholder="请输入" :maxlength="400" clearable class="w120"/>
      </FormItem>
      <FormItem label="开票人姓名：" :label-width="85">
        <Input v-model="formItem.operatorName" placeholder="请输入" :maxlength="400" clearable class="w120"/>
      </FormItem>
      <FormItem label="原发票号码：" :label-width="85">
        <Input v-model="formItem.originalInvoiceNum" placeholder="请输入" :maxlength="400" clearable class="w120"/>
      </FormItem>
      <FormItem label="原发票代码：" :label-width="85">
        <Input v-model="formItem.originalInvoiceCode" placeholder="请输入" :maxlength="400" clearable class="w120"/>
      </FormItem>
      <FormItem label="开票日期：">
        <Date-picker
        type="date"
        :value="formItem.invoiceTime"
        placeholder="请选择日期"
        placement="bottom-end"
        format="yyyy-MM-dd"
        @on-change="changeDate"
        class="w140"
        ></Date-picker>
      </FormItem>
      <FormItem label="开票主体：">
        <Input v-model="formItem.subjectName" placeholder="请输入" :maxlength="400" clearable class="w120 ml10"/>
      </FormItem>
      <FormItem label="发票订单号：" :label-width="85">
        <Input v-model="formItem.invoiceId" placeholder="请输入" :maxlength="500" clearable class="w120"/>
      </FormItem>
      <Row type="flex" align="middle" justify="space-between">
      <Col>数据合计：蓝字发票共{{tableList.countBlue || 0}}张，合计金额{{tableList.sumBlueAmount || 0}}元；红字发票共{{tableList.countRed || 0}}张，合计金额{{tableList.sumRedAmount || 0}}元</Col>
      <Col class="invoice-all-button">
          <Button type="primary" @click="getTableData(true)" :loading="tableLoading">查询</Button>
          <Button type="primary" style="margin-left: 15px;"  @click="resubmitAll">批量重新提交</Button>
      </Col>
      </Row>
    </Form>
    <Row class="table">
      <Table style="width: 100%;margin-top: 10px" :columns="columns" :data="tableList.data" @on-selection-change="selectionChange" :loading="tableLoading">
        <template slot-scope="{ row }"  slot="sp">
          <a @click="getDetailData(row)">查看详情</a>
        </template>
        <template slot-scope="{ row, index }"  slot="index">
          {{(formItem.pageNum - 1) * formItem.pageSize + index + 1}}
        </template>
        <template slot-scope="{ row }"  slot="invoiceId">
          {{row.invoiceId || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="invoiceRate">
          {{row.invoiceRate|filterRate}}
        </template>
        <template slot-scope="{ row }"  slot="invoiceSubjectType">
          {{row.invoiceSubjectType|filterInvoiceTypeName}}
        </template>
        <template slot-scope="{ row }"  slot="buyer">
          {{row.buyer || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="buyerIdentifyNum">
          {{row.buyerIdentifyNum || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="buyerAddressPhone">
          {{row.buyerAddressPhone || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="buyerOpeningBank">
          {{row.buyerOpeningBank || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="createTime">
          {{row.createTime|formatTimeFilter}}
        </template>
        <template slot-scope="{ row }"  slot="invoiceType">
          {{row.invoiceType|filterInvoiceName}}
        </template>
        <template slot-scope="{ row }"  slot="source">
          {{{source: row.source, sourceList: sourceList}|filterSourceName}}
        </template>
        <template slot-scope="{ row }"  slot="resultMsg">
          {{row.resultMsg || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="originalInvoiceCode">
          {{row.originalInvoiceCode || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="originalInvoiceNum">
          {{row.originalInvoiceNum || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="receiver">
          {{row.receiver || '--'}}
        </template>
        <template slot-scope="{ row }"  slot="sub">
          <a @click="resubmit(row)">重新提交</a>
        </template>
      </Table>
    </Row>
    <Row style="text-align: right;margin-top: 10px;">
      <Page
        :total="tableList.count"
        :page-size="formItem.pageSize"
        :current.sync="formItem.pageNum"
        show-elevator
        show-sizer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"></Page>
    </Row>
    <Row v-if="this.formItem.queryType !== 3">
      <detail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></detail>
    </Row>
    <Row v-if="this.formItem.queryType === 3">
      <scholarshipDetail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></scholarshipDetail>
    </Row>
  </div>
</template>

<script>
import detail from '../tal-invoice-query/detailModal/detail'
import scholarshipDetail from '../tal-invoice-query-detail-scholarship/detail'
import filters from '../../filters/index'
export default {
  name: 'talInvoiceProcessing',
  data () {
    return {
      formItem: {
        areaCode: '', // 分校
        businessCode: '', // 业务线
        queryType: null, // 费用类型
        invoiceType: '', // 发票类型
        studentName: '', // 学员姓名
        studentNo: '', // 学员编号
        operatorName: '', // 开票人姓名
        originalInvoiceNum: '', // 原发票号码
        originalInvoiceCode: '', // 原发票代码
        invoiceTime: '', // 开票日期
        subjectName: '', // 开票主体
        invoiceId: '', // 发票订单号
        pageSize: 10,
        pageNum: 1
      },
      ruleItem: {
        areaCode: [
          { required: true, message: '请选择分校', trigger: 'change' }
        ],
        businessCode: [
          { required: true, message: '请选择业务线', trigger: 'change' }
        ],
        queryType: [
          { required: true, message: '请选择费用类型', trigger: 'change', type: 'number' }
        ]
      },
      columns: [
        { type: 'selection', width: 60, align: 'center' },
        { title: '操作', align: 'center', width: 85, slot: 'sp' },
        { title: '序号', align: 'center', width: 60, slot: 'index' },
        { title: '分校', align: 'center', width: 100, key: 'areaCode' },
        { title: '业务线', align: 'center', width: 100, key: 'businessCode' },
        { title: '发票订单号', align: 'center', width: 170, slot: 'invoiceId' },
        { title: '开票主体（销售方）', align: 'center', width: 150, key: 'sellerName' },
        { title: '开票金额', align: 'center', width: 90, key: 'invoiceAmount' },
        { title: '税率', align: 'center', width: 60, slot: 'invoiceRate' },
        { title: '收票主体（购买方）', align: 'center', width: 150, slot: 'invoiceSubjectType' },
        { title: '收票主体名称', align: 'center', width: 120, slot: 'buyer' },
        { title: '纳税人识别号', align: 'center', width: 170, slot: 'buyerIdentifyNum' },
        { title: '地址电话', align: 'center', width: 150, slot: 'buyerAddressPhone' },
        { title: '开户行及账户', align: 'center', width: 110, slot: 'buyerOpeningBank' },
        { title: '开票时间', align: 'center', width: 165, slot: 'createTime' },
        { title: '开票类型', align: 'center', width: 90, slot: 'invoiceType' },
        { title: '发票状态', align: 'center', width: 90, key: 'invoiceStateDesc' },
        { title: '来源', align: 'center', width: 110, slot: 'source' },
        { title: '失败原因', align: 'center', width: 280, slot: 'resultMsg' },
        { title: '原发票代码', align: 'center', width: 100, slot: 'originalInvoiceCode' },
        { title: '原发票号码', align: 'center', width: 100, slot: 'originalInvoiceNum' },
        { title: '开票人姓名', align: 'center', width: 100, key: 'operatorName' },
        { title: '领票人姓名', align: 'center', width: 100, slot: 'receiver' },
        { title: '领票人邮箱', align: 'center', width: 180, key: 'receiverEmail' },
        { title: '领票人手机', align: 'center', width: 120, key: 'receiverPhone' },
        { title: '学员姓名', align: 'center', width: 90, key: 'studentName' },
        { title: '学员编号', align: 'center', width: 120, key: 'studentNo' },
        { title: '操作', align: 'center', width: 85, slot: 'sub' }
      ],
      tableLoading: false,
      detailFlag: false,
      invoiceTypeList: [
        { id: 0, name: '蓝字发票' },
        { id: 1, name: '红字发票' }
      ],
      selectionArr: []
    }
  },
  components: {
    detail,
    scholarshipDetail
  },
  filters: filters,
  props: {
    ifRedDashed: { // 是否有红冲和发送操作
      type: Boolean,
      default: false
    },
    allAreaCode: { // 分校
      type: Array,
      default () {
        return []
      }
    },
    allBusiness: { // 业务线
      type: Array,
      default () {
        return []
      }
    },
    feeTypeList: {
      type: Array,
      default () {
        return []
      }
    },
    sourceList: {
      type: Array,
      default () {
        return []
      }
    },
    tableList: {
      type: Object,
      default: function () {
        return {
          count: 0,
          data: [],
          countBlue: 0,
          sumBlueAmount: 0,
          countRed: 0,
          sumRedAmount: 0
        }
      }
    },
    inDetailData: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    pageSizeChange (val) {
      this.formItem.pageNum = 1
      this.formItem.pageSize = val
      this.getTableData()
    },
    changePage (val) {
      this.formItem.pageNum = val
      this.getTableData()
    },
    getTableData (mark) {
      // 获取列表数据
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (mark) {
            this.formItem.pageNum = 1
            this.tableLoading = true
          }
          this.$emit('getInvoiceList', this.formItem, () => {
            this.tableLoading = false
          })
        }
      })
    },
    getDetailData (row) {
      const params = { invoiceId: row.invoiceId }
      this.$emit('getInvoiceDetail', params, this.formItem.queryType, () => {
        this.detailFlag = true
      })
    },
    selectionChange (selection) {
      this.selectionArr = selection
    },
    resubmitAll () { // 批量导出
      if (this.selectionArr.length === 0) {
        this.$Message.warning('选中的数据列表为空')
        return
      }
      const invoiceIdList = this.selectionArr.map((item) => {
        return item.invoiceId
      })
      this.$emit('resubmit', { invoiceIdList }, this.formItem)
    },
    resubmit (row) {
      const params = { invoiceIdList: [row.invoiceId] }
      this.$emit('resubmit', params, this.formItem)
    },
    closeModal (val) {
      this.detailFlag = val
    },
    changeDate (date) {
      this.formItem.invoiceTime = date
    }
  }
}
</script>

