import $ from '../utils/api.request'

/**
 *
 根据员工编号获取身份列表
 @param 员工编号 empNo
*/
export function getEmpDeptRolesByEmpNo (params) {
  return $.authAxios.request({
    url: 'auth/getEmpDeptRolesByEmpNo',
    method: 'get',
    params
  })
}

export function getResourcesByEmpNo (params) {
  return $.authAxios.request({
    url: '/resource/getResourcesByEmpNo',
    method: 'get',
    params
  })
}

// 阿里oss 鉴权
export const uploadGetAuth = params => {
  return $.authAxios.request({
    url: '/upload/getAuth',
    method: 'get',
    params: params
  })
}
// 获取登录二维码 无参数 返回uuid
export const yachGenQrcode = () => {
  return $.authAxios.request({
    url: 'auth/genQRCode',
    method: 'get'
  })
}

/*
* 用户自定义数据添加
* header
*   empNo、platform
* data
*   menu 自定义菜单名称
*   formItem 自定义数据
* */
export function addUserFormData (data) {
  return $.authAxios.request({
    url: '/userForm/add',
    method: 'post',
    data
  })
}

/*
* 获取用户自定义数据
* header
*   empNo、platform
* data
*   menu 自定义菜单名称 String 'xx,yy'
* @return Array 对应 'xx,yy'
*
* */
export function getUserFormData (params) {
  return $.authAxios.request({
    url: '/userForm/list',
    method: 'get',
    params
  })
}
