<template>
  <Form
    ref="formValidate"
    :model="formValidate"
    :rules="ruleValidate"
    :label-width="120"
    label-position="left"
  >

    <FormItem label="收款人名称" prop="payName">
      <Input clearable v-model="formValidate.payName" placeholder="请输入收款人名称" />
    </FormItem>
    <FormItem label="收款人银行账户" prop="payBankAccount">
      <Input
        clearable
        type="number"
        v-model="formValidate.payBankAccount"
        placeholder="请输入收款人银行账户"
      />
    </FormItem>
    <FormItem label="收款银行类型" prop="payBankTypeName">
      <Select
        v-model="formValidate.payBankTypeName"
        filterable
        remote
        clearable
        :remote-method="q => selectRemoteLoad(q, 'bank')"
        @on-change="handleSelectChange($event, 'bank')"
        @on-clear="selectRemoteLoad('', 'bank')"
        :loading="bank.isLoading"
        placeholder="请选收款银行类型"
      >
        <Option
          :value="item.bankName"
          v-for="(item) in bank.options"
          :key="item.bankCode"
        >{{ item.bankName }}</Option>
      </Select>
    </FormItem>
    <Row>
      <Col span="12">
        <FormItem label="收款银行所在省份" prop="payBankProvinceName">
          <Select
            class="wd260"
            v-model="formValidate.payBankProvinceName"
            filterable
            remote
            clearable
            :remote-method="q => selectRemoteLoad(q, 'bankProvince')"
            @on-change="handleSelectChange($event, 'bankProvince')"
            @on-clear="selectRemoteLoad('', 'bankProvince')"
            :loading="bankProvince.isLoading"
            placeholder="请选择收款银行所在省份"
          >
            <Option
              :value="item.bankProvinceName"
              v-for="(item) in bankProvince.options"
              :key="item.bankProvinceCode"
            >{{ item.bankProvinceName }}</Option>
          </Select>
        </FormItem>
      </Col>
      <Col span="12">
        <FormItem label="收款银行所在市" prop="payBankCityName">
          <Select
            ref="payBankCity"
            class="wd260"
            v-model="formValidate.payBankCityName"
            filterable
            remote
            clearable
            :remote-method="q => selectRemoteLoad(q, 'bankCity')"
            @on-change="handleSelectChange($event, 'bankCity')"
            @on-clear="selectRemoteLoad('', 'bankCity')"
            :loading="bankCity.isLoading"
            placeholder="请选择收款银行所在市"
            :disabled="!formValidate.payBankProvince"
          >
            <Option
              :value="item.bankCityName"
              v-for="(item) in bankCity.options"
              :key="item.bankCityCode"
            >{{ item.bankCityName }}</Option>
          </Select>
        </FormItem>
      </Col>
    </Row>
    <FormItem label="收款人开户行全称" prop="payBankName">
      <Input clearable v-model="formValidate.payBankName" placeholder="请输入开户行全称" />
    </FormItem>
  </Form>
</template>

<script>
import { getBank, getBankCity, getBankProvince } from '../../../api/specialRefund'
export default {
  name: 'BankinfoCollect',
  props: {
    value: {
      type: Object,
      default: () => {
        return {
          payName: '',
          payBankAccount: '',
          payBankName: '',
          payBankType: '',
          payBankTypeName: '',
          payBankProvince: '',
          payBankProvinceName: '',
          payBankCity: '',
          payBankCityName: ''
        }
      }
    }
  },
  computed: {
    formValidate: {
      get () {
        return this.value
      },
      set () {
        this.$emit('input', this.value)
      }
    }
  },
  data () {
    return {
      ruleValidate: {
        'payName': [
          { required: true, message: '请填写收款人名称', trigger: 'blur', type: 'string' },
          { max: 64, message: '您输入的内容太多了，缩减一些吧，最多64个字符', trigger: 'blur', type: 'string' }
        ],
        'payBankAccount': [
          { required: true, message: '请填写收款人银行账户', max: 64, trigger: 'blur', type: 'string' },
          { max: 64, message: '您输入的内容太多了，缩减一些吧，最多64个字符', trigger: 'blur', type: 'string' }
        ],
        'payBankTypeName': [
          { required: true, message: '请选择银行类型', trigger: 'change', type: 'string' }
        ],
        'payBankProvinceName': [{ required: true, message: '请选择收款银行所在省份', trigger: 'change', type: 'string' }],
        'payBankCityName': [{ required: true, message: '请选择收款银行所在城市', trigger: 'change', type: 'string' }],
        'payBankName': [
          { required: true, message: '请输入开户行全称，格式为总行+支行', max: 64, trigger: 'blur', type: 'string' },
          { max: 64, message: '您输入的内容太多了，缩减一些吧，最多64个字符', trigger: 'blur', type: 'string' }
        ]
      },
      bank: {
        options: [],
        isLoading: ''
      },
      bankProvince: {
        options: [],
        isLoading: ''
      },
      bankCity: {
        options: [],
        isLoading: ''
      }

    }
  },
  methods: {
    //   表单校验
    validate () {
      return new Promise((resolve) => {
        this.$refs.formValidate.validate(valid => {
          resolve(valid)
        })
      })
    },
    resetFields () {
      this.$refs.formValidate.resetFields()
    },
    /****
     *
     * 关键模糊搜索开户行类型、省份、城市
     */
    async selectRemoteLoad (query, type) {
      this[type].isLoading = true
      try {
        let data = null
        if (type === 'bank') {
          data = await getBank({ name: query })
        } else if (type === 'bankProvince') {
          data = await getBankProvince({ name: query })
        } else if (type === 'bankCity') {
          data = await getBankCity({ name: query, provinceCode: this.formValidate.payBankProvince })
        }
        this[type].isLoading = false
        this[type].options = [...data]
      } catch (error) {
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
    },
    handleSelectChange (data, type) {
      if (!data) return
      let code = ''
      // 城市
      if (type === 'bankCity') {
        this[type].options.forEach(item => {
          if (item.bankCityName === data) code = item.bankCityCode
        })
        this.formValidate.payBankCity = code
        // 省份
      } else if (type === 'bankProvince') {
        this[type].options.forEach(item => {
          if (item.bankProvinceName === data) code = item.bankProvinceCode
        })
        this.formValidate.payBankProvince = code
        this.$refs.payBankCity.clearSingleSelect() // 清空城市
        this.selectRemoteLoad('', 'bankCity')
        // 银行类型
      } else if (type === 'bank') {
        this[type].options.forEach(item => {
          if (item.bankName === data) code = item.bankCode
        })
        this.formValidate.payBankType = code
      }
    }
  },
  created () {
    this.selectRemoteLoad('', 'bankProvince')
    this.selectRemoteLoad('', 'bank')
  }

}
</script>

<style lang="less" scoped>
.wd260 {
  width: 260px;
}
</style>
