  <div class="work_container">
    <div class="work_top">
      <Form :model="formInline" :label-width="80" inline style="font-size: 16px; text-align: left;">
        <FormItem label="学员姓名" prop="stuName" v-show="!onlyCode">
          <Input
            id="search-history-stu-name"
