          />
          <SearchHistory ref="stuName" name="stuRealName" trigger-element-id="search-history-stu-name" @select-history="formInline.stuName = $event"/>
        </FormItem>
        <FormItem label="电话号码" prop="phone" v-show="!onlyCode">
          <Input
            id="search-history-phone"
