      <Row type="flex" justify="space-between" class="code-row-bg">
        <Col span="5">
          <span class="spaLeft">学员姓名：</span>
          <span class="spaMid">{{ studentDetail.realName || '--'}}</span>
          <Tag checked="false" v-if="studentDetail.businessBelong === 3" class="spaMid">素养中心</Tag>
          <Icon v-if="studentDetail.uid && callShow" custom="iconfont icon-phone-fill" style="cursor: pointer;margin-left: 5px" @click="$store.state.app.callInstance.call(studentDetail.cityCode, studentDetail.uid)"></Icon>
        </Col>
        <Col span="5">
          <span class="spaLeft">联系电话：</span>
          <span class="spaRight">
            <decode-text
              :encode-text="studentDetail.attentionReqList && studentDetail.attentionReqList.length ? studentDetail.attentionReqList[0].tel : ''"
              type="phone"
              :uid="studentDetail.uid"
            ></decode-text>
          </span>
        </Col>
        <Col span="5">
          <span class="spaLeft">学员编号：</span>
          <span class="spaRight">{{studentDetail.card || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">绑定分校：</span>
          <span class="spaRight">{{studentDetail.bindingName || '--'}}</span>
        </Col>
        <Col span="4">
          <span class="spaLeft">性别：</span>
          <span class="spaRight">{{studentDetail.sexName || '--'}}</span>
        </Col>
      </Row>
      <Row type="flex" justify="space-between" class="code-row-bg" style="margin-top: 5px">
        <Col span="5">
          <span class="spaLeft">新老学员：</span>
          <span class="spaRight">{{studentDetail.isRegfeePaName || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">年级：</span>
          <span class="spaRight">{{studentDetail.gradeName || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">经验值：</span>
          <span class="spaRight">{{studentDetail.points || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">等级：</span>
          <span class="spaRight">{{studentDetail.level || '--'}}</span>
        </Col>
        <Col span="4">
          <span class="spaLeft">经验值折扣率：</span>
          <span class="spaRight">{{studentDetail.agio || '--'}}</span>
        </Col>
      </Row>
      <Row
        type="flex"
        justify="space-between"
        class="code-row-bg"
        style="margin-top: 5px"
        v-if="showCrmInfo"
      >
        <Col span="5">
          <span class="spaLeft">客户状态：</span>
          <span class="spaRight">{{studentCrmDetail.activeStatusName || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">绑定状态：</span>
          <span class="spaRight">{{studentCrmDetail.bindStatusName || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">绑定人：</span>
          <span class="spaRight">{{studentCrmDetail.consultant || '--'}}</span>
        </Col>
        <Col span="5">
          <span class="spaLeft">归属类型：</span>
          <span class="spaRight">{{studentCrmDetail.belongDeptType || '--'}}</span>
        </Col>
        <Col span="4">
          <span class="spaLeft">归属：</span>
          <span class="spaRight">{{studentCrmDetail.belongDeptName || '--'}}</span>
        </Col>
      </Row>
    </div>
    <!--选择学员-->
    <Modal
      :mask-closable="false"
      title="学员查询"
      v-model="chooseStudentModal"
      width="1060px"
      :footer-hide="true"
      @on-visible-change="studentAddVisibleModal"
    >
      <div>
        <Form ref="form" inline :label-width="80">
          <FormItem label="城市" style="margin-bottom: 0">
            <Select v-model="cityCode" style="width: 120px">
              <Option
                v-for="(option,index) in cityAndGrade.cities"
                :value="option.id"
                :key="index"
              >{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="年级" style="margin-bottom: 0">
            <Select v-model="gradeId" style="width: 120px">
              <Option
                v-for="(option,index) in cityAndGrade.grades"
                :value="option.id"
                :key="index"
              >{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem style="float: right">
            <Button
              type="primary"
              style="margin-right: 10px;"
              :loading="isLoading"
              @click="searchStu(true)"
            >筛选</Button>
          </FormItem>
        </Form>
      </div>
      <div>
        <Table
          stripe
          :columns="changeStudentColumns"
          :data="studentList"
          style="margin-top: 20px;width: 100%"
          :loading="isLoading"
        >
          <template slot-scope="{ row }" slot="operation">
            <!--正常和已冻结的学员都可以选择-->
            <Button
              type="primary"
              @click="choose(row)"
              v-if="row.status === '0'"
              :loading="isLoading"
            >{{choiceBtnText || '选择'}}</Button>
            <Button :disabled="true" v-else-if="row.status === '1'">冻结</Button>
            <Button :disabled="true" v-else-if="row.status === '2'">已删除</Button>
            <Button :disabled="true" v-else>无效</Button>
          </template>
        </Table>
        <Button
          class="load-more-btn"
          v-if="studentList.length >= 20 && canLoadMore"
          @click="searchStu"
          :loading="isLoading"
          :disabled="isLoading"
        >加载更多</Button>
      </div>
    </Modal>

    <Modal
      :mask-closable="false"
      :footer-hide="true"
      :title="(isAdd ? '新增学员' : '修改学员信息')"
      width="960px"
      class="modal-height1"
      v-model="addStudentModal"
    >
      <!--新增/修改学员-->
      <student-add
        v-if="addStudentModal"
        :formData="addStudentForm"
        :isAdd="isAdd"
        :requireSchoolCity="requireSchoolCity"
        @success="updateSuccess"
        @close="addStudentModal = false"
      ></student-add>
    </Modal>
    <template v-if="studentDetail.studentId">
      <Modal :mask-closable="false" title="学员详情" v-model="stuDetailModal" width="960px">
        <!--学员详情-->
        <student-details
          :cityCode="studentDetail.cityCode"
          :uid="studentDetail.uid"
          v-if="stuDetailModal"
          @close="stuDetailModal = false"
        ></student-details>
        <div slot="footer">
          <Button type="error" @click="editPhone" ghost>更改绑定手机号码</Button>
          <Button type="warning" @click="openReset" ghost>重置密码</Button>
          <Button type="info" @click="addStudent" ghost>新增</Button>
          <Button type="primary" @click="updateStudent">编辑</Button>
        </div>
      </Modal>
      <Modal
        :mask-closable="false"
        :footer-hide="true"
        title="重置密码"
        v-model="resetPasswordModal"
        width="960px"
        :closable="false"
        ref="form"
      >
        <!--重置密码-->
        <reset-password
          :studentDetail="studentDetail"
          v-if="resetPasswordModal"
          @close="closeReset"
        ></reset-password>
      </Modal>
      <Modal
        :mask-closable="false"
        title="更改绑定手机号"
        v-model="phoneModal"
        width="480px"
        :closable="false"
        @on-visible-change="onVisibleChange"
        ref="form"
      >
        <Row type="flex" align="middle">
          <Col span="6">
            绑定手机号
            <Tooltip transfer max-width="300" content="修改绑定手机号后学员将无法通过原手机号登陆系统，请谨慎修改" placement="right">
              <Icon size="14" type="md-help-circle" />
            </Tooltip>：
          </Col>
          <Col span="8">
            <Input v-model="newPhone" style="width: 150px" />
          </Col>
          <Col push="1" span="6">
            <Button type="default" @click="sendMessageHandler(newPhone, 'bindCode')" v-if="!sendBtn" :disabled="studentDetail.phone === newPhone" class="wd80">发送验证码</Button>
            <Button type="default" :disabled="true" v-else class="wd80">{{msgNum}}秒</Button>
          </Col>
          <Col span="6" style="margin-top: 24px;">
            <p style="display: flex;">
              <span style="color: #ed4014;font-size: 16px;margin-right: 6px;">*</span>请输入验证码:
            </p>
          </Col>
          <Col span="12" style="margin-top: 24px;">
            <Input v-model="smsCode" style="width: 150px"/>
          </Col>
        </Row>
        <div slot="footer">
          <Button type="primary" @click="updatePhone">确定</Button>
          <Button @click="closePhoneModal">取消</Button>
        </div>
      </Modal>
    </template>
  </div>
</template>
<script>
import { depClone, browser } from '../../utils/utils'
import StudentDetails from '../tal-stu-details'
import ResetPassword from '../tal-stu-reset-pwd'
import StudentAdd from '../tal-stu-add'
import DecodeText from '../tal-decode-text'
import {
  getProvinceListByType
} from '../../api/bd'
import {
  queryStudentInfo,
  getStudentDetail,
  fuzzySearchCityAndGrid,
  queryStudentInfoByCardNum,
  updateUserBindingPhone,
  decodeAll,
  decodePhone,
  getUserSchoolList
} from '../../api/student'
import {
  getStudentCrmFeature
} from '../../api/crmStudent'
import { getCurrency } from '../../api/fee'
import Cookie from 'js-cookie'
import verificationCode from '../../mixin/verificationCode.js'
import SearchHistory from './search-history.vue'
export default {
  name: 'TalStuSearch',
  mixins: [verificationCode],
  provide () {
    return {
      workbench: this
    }
  },
  props: {
    stuCard: {
      type: String
    },
    stuCityCode: {
      type: String
    },
    attendanceSearch: {
      type: Boolean,
      default: true
    },
    addStu: {
      type: Boolean,
      default: false
    },
    stuDetail: {
      type: Boolean,
      default: false
    },
    showDetail: {
      type: Boolean,
      default: true
    },
    showCrmInfo: {
      type: Boolean,
      default: true
    },
    stuInfoText: {
      type: String
    },
    needCity: {
      type: Boolean,
      default: true
    },
    ifStudentsFile: {
      type: Boolean,
      default: false
    },
    cityBlacklist: {
      type: Array,
      default: () => {
        return []
      }
    },
    onlyCode: {
      type: Boolean,
      default: false
    },
    choiceBtnText: {
      type: String
    },
    showSendMsg: {
      type: Boolean,
      default: false
    },
    showDetailAfterUpdate: {
      type: Boolean,
      default: true
    }
  },
  components: {
    StudentDetails,
    ResetPassword,
    StudentAdd,
    DecodeText,
    SearchHistory
  },
  data () {
    return {
      requireSchoolCity: [ '010' ], // 需要校验是否填写就读学校的城市
      newPhone: '',
      tableLoading: false,
      hackReset: true,
      isFirstLoad: true,
      isLoading: false,
      tabsName: 'orderCenter',
      formInline: {
        phone: '',
        stuName: '',
        stuNo: ''
      },
      changeStudentColumns: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: '操作',
          slot: 'operation',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'card',
          align: 'center',
          className: 'xueyuan',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#4DA2FF'
              }
            }, params.row.card)
          }
        },
        {
          title: '所在城市',
          key: 'areaName',
          width: 100,
          align: 'center'
        },
        {
          title: '姓名',
          key: 'realName',
          align: 'center',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#4DA2FF'
              }
            }, params.row.realName)
          }
        },
        {
          title: '性别',
          key: 'sex',
          align: 'center',
          width: 60,
          render: (h, params) => {
            let sex = ''
            if (params.row.sex === 1) {
              sex = '男'
            } else if (params.row.sex === 2 || params.row.sex === 0) {
              sex = '女'
            }
            return h('span', {
            }, sex)
          }
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '注册时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '报班数量',
          key: 'registerClassCount',
          align: 'center'
        }
      ],
      information: [], // 学员信息
      chooseStudentModal: false,
      studentList: [], // 选择学员列表
      addStudentModal: false, // 新增学员信息的模态框
      isAdd: true, // 判断是新增还是编辑

      // tab切换
      classQuery: '班级查询',
      classRecord: '报班记录',
      backupRecords: '候补记录',
      reserved: '预留转班名额记录',
      performance: '成绩查询',
      attendance: '学员考勤',
      discount: '优惠券',

      yearList: [], // 年份
      allCityList: [], // 学员所在城市列表
      addStudentForm: {}, // 新增学员表单
      provinceList: [], // 省
      stuDetailModal: false, // 学员详情
      resetPasswordModal: false, // 重置密码
      studentDetail: {
        attentionReqList: []
      }, // 學員詳情
      studentCrmDetail: {},
      tabPaneName: '',
      formData: {
        year: '',
        term: '',
        classType: ''
      },
      pageNo: 1,
      canLoadMore: true,
      cityCode: '',
      gradeId: '',
      cityAndGrade: {},
      phoneModal: false,
      wsCount: 0,
      wsIsOpen: 0, // WebSocket服务是否已打开,
      ioOrder: 0,
      sendBtn: false, // 验证码已发送
      msgNum: '', // 发送验证码后的倒计时
      smsCode: '', // 验证码
      localHistoryList: localStorage['localHistoryList']
        ? JSON.parse(localStorage['localHistoryList'])
        : {},
      historyList: [],
      showHistory: {
        phone: false,
        stuName: true,
        stuNo: false
      }
    }
  },
  computed: {
    callShow () {
      const callGrayCity = this.$store.state.app.callGrayCity
      if ((!callGrayCity || !callGrayCity.length || callGrayCity.includes(this.studentDetail.cityCode)) && this.$store.state.roleRelativeData.roleIds.includes(this.$store.state.roleRelativeData.userInfo.crmRoleId)) {
        return true
      }
      return false
    }
  },
  methods: {
    updateSuccess () {
      this.$emit('updateSuccess')
      if (this.showDetailAfterUpdate) {
        this.enterDetail()
      }
    },
    sendMsg () {
      this.$emit('sendMsg')
    },
    async onVisibleChange (status) {
      if (status) {
        try {
          const res = await decodePhone({
            uid: this.studentDetail.uid,
            encodePhone: this.studentDetail.phone
          })
          this.newPhone = res
        } catch (error) {
          this.$Message.error({ content: '手机号解码失败:' + error.message, duration: 5 })
        }
      } else {
        this.newPhone = ''
      }
    },
    studentAddVisibleModal (show) {
      if (!show) {
        this.pageNo = 1
        this.cityCode = ''
        this.gradeId = ''
        this.canLoadMore = true
      }
    },
    // web控件回调函数
    // searchByCardNo (msg) {
    //   try {
    //     let json = eval('(' + msg + ')')
    //     if (json.jumpType) {
    //       let cardNo = json.returnMessage
    //       queryStudentInfoByCardNum({
    //         cardNum: cardNo
    //       }).then(data => {
    //         this.choose({
    //           cityCode: data.cityCode,
    //           studentId: data.studentId,
    //           uid: data.uid
    //         })
    //       }).then(err => {
    //         this.$Message.error({
    //           content: '查询学员失败：' + err.message,
    //           duration: 5
    //         })
    //       })
    //     } else {
    //       this.$Message.error({
    //         content: '读卡失败',
    //         duration: 5
    //       })
    //     }
    //   } catch (e) {
    //     this.$Message.error({
    //       content: '读卡失败：' + e.message,
    //       duration: 5
    //     })
    //   }
    // },
    readCard () {
      // if (browser().ie) {
      //   document.Card.ReadCard()
      // } else {
      this.sendMsgToCard('', 1)
      // }
    },
    searchStuByUid (data) {
      this.isLoading = true
      queryStudentInfo(data).then(res => {
        this.isLoading = false
        if (res.length === 0) {
          this.$Message.warning({
            content: '查无此学员',
            duration: 5
          })
        } else if (res[0].status !== '0' || res[0].status !== '1') {
          this.choose(res[0])
        } else {
          this.canLoadMore = false
          this.studentList = res
          this.chooseStudentModal = true
        }
      }).catch(err => {
        this.$Message.warning({ content: err.message, duration: 5 })
      }).finally(() => {
        this.isLoading = false
      })
    },
    // 搜索学员
    searchStu (filter) {
      // 保存搜索历史
      this.$refs.stuName.setHistory(this.formInline.stuName)
      this.$refs.phone.setHistory(this.formInline.phone)
      this.$refs.stuNo.setHistory(this.formInline.stuNo)

      if (this.formInline.stuName) {
        this.formInline.stuName = this.formInline.stuName.trim()
      } else {
        this.formInline.stuName = ''
      }
      if (this.isLoading) return
      if (!this.formInline.phone && !this.formInline.stuName && !this.formInline.stuNo) {
        this.$Message.warning({ content: '请填写学员信息', duration: 5 })
      } else if (this.formInline.stuName && this.formInline.stuName.length < 2) {
        this.$Message.warning({ content: '请至少输入2个字', duration: 5 })
      } else {
        if (filter === true) {
          this.pageNo = 1
          this.studentList = []
          this.canLoadMore = true
        }
        let user = {
          card: this.formInline.stuNo,
          phone: this.formInline.phone,
          realName: this.formInline.stuName,
          current: this.pageNo,
          cityCode: this.cityCode,
          gradeId: this.gradeId
        }
        this.isLoading = true
        if (this.pageNo === 1) {
          this.studentList = []
        }
        fuzzySearchCityAndGrid(user).then(data => {
          this.cityAndGrade = data
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
        queryStudentInfo(user).then(res => {
          if (res.length === 1 && this.pageNo === 1) {
            this.isLoading = false
            if (res[0].status === '0' || res[0].status === '1') { // 正常和已冻结的都可以选择
              this.choose(res[0])
            } else {
              this.canLoadMore = false
              this.studentList = res
              this.chooseStudentModal = true
            }
          } else {
            if (res.length < 20) {
              this.canLoadMore = false
            }
            this.chooseStudentModal = true
            this.studentList = this.studentList.concat(res)
            this.isLoading = false
            this.pageNo += 1
          }
        }).catch(err => {
          this.isLoading = false
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      }
    },
    // 选择学员
    choose (data) {
      if (data) {
        Cookie.set('currentStudent', data)
      }
      if (this.isLoading) return
      let choose = {
        cityCode: data.cityCode,
        uid: data.uid,
        studentUid: data.uid
        // studentId: data.stuId
      }
      this.isLoading = true
      getStudentDetail(choose).then(async (res) => {
        try {
          // 获取币种信息，保存到studentDetail中输出
          const currency = await getCurrency({ cityCode: choose.cityCode })
          res.currency = currency
          if (!res.gradeName) {
            this.$Message.warning('请补充学员年级信息')
            this.updateStudent()
          }
        } catch (err) {
          this.isLoading = false
          this.$Message.warning({ content: err.message, duration: 5 })
        }

        if (!this.isFirstLoad) {
          this.hackReset = false
          this.$nextTick(() => {
            this.hackReset = true
            this.$nextTick(() => {
              this.isLoading = false
              this.tabPaneName = ''
              this.isFirstLoad = false
              if (!this.choiceBtnText) {
                this.chooseStudentModal = false
              }
              this.setStudentInfo(res)
              // this.$store.commit('updateStudentInfo', res)
              this.tabsName = 'classQuery'
            })
          })
        } else {
          this.isLoading = false
          this.tabPaneName = ''
          this.isFirstLoad = false
          if (!this.choiceBtnText) {
            this.chooseStudentModal = false
          }
          this.setStudentInfo(res)
          // this.$store.commit('updateStudentInfo', res)
          this.tabsName = 'classQuery'
        }
      }).catch((err) => {
        this.isLoading = false
        this.$Message.warning({ content: err.message, duration: 5 })
      })

      if (this.showDetail && this.showCrmInfo) {
        getStudentCrmFeature(choose).then(res => {
          this.studentCrmDetail = Object.freeze(res) || {}
          this.$emit('crmInfo', this.studentCrmDetail)
        }).catch((err) => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      }
    },
    // 获取学员详细信息
    enterDetail () {
      this.stuDetailModal = false
      this.addStudentModal = false
      if (this.studentDetail.cityCode && this.studentDetail.uid) {
        let studentDetail = {
          cityCode: this.studentDetail.cityCode,
          uid: this.studentDetail.uid,
          studentId: this.studentDetail.studentId
        }
        getStudentDetail(studentDetail).then((res) => {
          this.stuDetailModal = true
          if (!res.recipientReqList) res.recipientReqList = []
          this.setStudentInfo(res, true)
          this.newPhone = res.phone
        }).catch((err) => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
      } else {
        this.$Message.warning('请选择学员')
      }
    },
    setStudentInfo (res, emit) {
      if (this.ifStudentsFile) { // 如果是学员档案，对黑名单进行限制
        let cityCode = res.cityCode
        let result = this.cityBlacklist.filter(res => cityCode === res.cityCode)[0]
        if (result) { // 如果学员所在城市在黑名单内，限制之后的接口调用
          this.$Message.error({
            content: '当前城市暂不支持使用学员档案',
            duration: 5
          })
          return
        }
      }
      this.$Message.success('查询成功')
      if (this.requireSchoolCity.includes(res.cityCode)) {
        getUserSchoolList({
          cityCode: res.cityCode,
          uid: res.uid,
          gradeType: res.gradeTypeId
        }).then(data => {
          if (!data || !data.length) {
            this.$Message.warning({
              content: '该学员当前年部就读学校信息未填写，请及时补充',
              duration: 5
            })
          }
        }).catch(err => {
          this.$Message.error({
            content: '获取学员就读学校失败：' + err.message,
            duration: 5
          })
        })
      }
      if (res.isPaidRegfee === 0) {
        res.isRegfeePaName = '新学员'
      } else if (res.isPaidRegfee === 1) {
        res.isRegfeePaName = '老学员'
      } else {
        res.isRegfeePaName = ''
      }
      if (res.sex === 2 || res.sex === 0) {
        res.sexName = '女'
      } else if (res.sex === 1) {
        res.sexName = '男'
      } else {
        res.sexName = ''
      }
      this.studentDetail = res
      if (this.needCity && (this.addStu || this.stuDetail)) {
        this.getProvinceListByType()
      }
      // 向外抛出查询出的学生信息
      if (!emit) {
        this.$emit('stuInfo', res)
      }
    },
    // 新增/修改学员
    addStudent () {
      this.isAdd = true
      this.addStudentForm = this.getStudentForm()
      this.addStudentModal = true
    },
    updateStudent () {
      this.isAdd = false
      decodeAll(this.studentDetail).then(data => {
        let obj = {}
        depClone(obj, data)
        if (obj.attentionReqList) {
          obj.attentionReqList.forEach((e) => {
            if (e.typeId === 8) {
              e.otherName = e.typeName
            }
          })
        }
        if (obj.recipientReqList) {
          obj.recipientReqList.forEach((e) => {
            e.delete = 0
          })
        } else {
          obj.recipientReqList = []
        }
        obj.callCenterEmpNo = '' // 操作人员工编号(必填)
        obj.name = '' // 操作人姓名(必填)
        // 历史数据中女的为2，现在的数据女的为0。此处做兼容统一处理为0
        if (obj.sex !== 1) {
          obj.sex = 0
        }
        this.addStudentForm = obj
        this.addStudentModal = true
      }).catch(err => {
        this.$Message.error({ content: '查询学员详情失败：' + err.message, duration: 5 })
      })
    },
    // 重置密码modal
    openReset () {
      this.resetPasswordModal = true
    },
    closeReset () {
      this.resetPasswordModal = false
    },
    editPhone () {
      this.phoneModal = true
      this.newPhone = ''
      this.smsCode = ''
      this.resetSendBtn()
    },
    updatePhone () {
      if (!this.newPhone) {
        this.$Message.warning({
          content: '绑定手机号必填',
          duration: 5
        })
        return
      }
      // 香港分校不校验手机号码格式，由接口校验
      if (this.studentDetail.cityCode !== '00852' && !/(^\d{11}$)|(^\d{3}([a-zA-Z]{4})|(\*{4})\d{4}$)/.test(this.newPhone)) {
        this.$Message.warning({
          content: '手机号码' + this.newPhone + '有误，请输入正确的手机号码',
          duration: 5
        })
        return
      }
      updateUserBindingPhone({
        cityCode: this.studentDetail.cityCode,
        uid: this.studentDetail.uid,
        oldPhone: this.studentDetail.phone,
        newPhone: this.newPhone,
        code: this.smsCode
      }).then(() => {
        this.$Message.success({
          content: '修改成功',
          duration: 5
        })
        this.phoneModal = false
        this.newPhone = ''
        this.enterDetail()
      }).catch(e => {
        this.$Message.error({
          content: e.message,
          duration: 5
        })
      })
    },
    closePhoneModal () {
      this.phoneModal = false
    },
    // 获取新增学员初始表单值
    getStudentForm () {
      let object = {
        realName: '', // 姓名(必填)
        callCenterEmpNo: '', // 操作人员工编号(必填)
        name: '', // 操作人姓名(必填)
        sex: 1, // 性别(必填)
        gradeId: '', // 年级(必填)
        gradeTypeId: '', // 年部(必填)
        provinceId: '', // 所在城市省份ID(必填)
        areaCode: '', // 所在城市ID(必填)
        birthday: '', // 生日
        // 联系人
        attentionReqList: [{
          'tel': '', // 联系人手机号
          'typeId': '', // 联系人类型Id
          'typeName': '', // 联系人类型名称
          'otherName': ''
        }],
        // 邮寄地址列表
        recipientReqList: [
          //   {
          //   'address': '', // 详细地址
          //   'name': '', // 收件人
          //   'tel': '', // 联系电话
          //   'isDefault': 1, // 是否默认地址
          //   'provinceId': '', // 省Id
          //   'cityId': '', // 城市Id
          //   'districtId': '' // 区/县Id
          // }
        ] // 邮寄地址
      }
      return object
    },
    // 获取省份
    getProvinceListByType () {
      getProvinceListByType({ type: 'c' }).then((data) => {
        this.provinceList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取省份失败:' + err.message, duration: 5 })
      })
    },
    initWS () {
      let host = browser().ie ? '127.0.0.1' : 'localhost'
      this.ws = new WebSocket(`wss://${host}:11311/Reader`)
      this.ws.onopen = e => {
        this.wsIsOpen = 1
        console.log('Openened connection to websocket')
        console.log(e)
      }
      const _this = this
      this.ws.onclose = () => {
        console.log('Close connection to websocket')
        this.wsIsOpen = 0
        this.wsCount += 1
        // 断线重连
        if (this.wsCount < 10) {
          _this.initWS()
        }
      }
      this.ws.onmessage = e => {
        const data = JSON.parse(e.data)
        switch (data.command) {
          case 1:
            if (Number(data.result) === 1) {
              queryStudentInfoByCardNum({
                cardNum: data.content.substring(0, 16)
              }).then(data => {
                this.choose({
                  cityCode: data.cityCode,
                  studentId: data.studentId,
                  uid: data.uid
                })
              }).catch(err => {
                this.$Message.error({
                  content: '查询学员失败：' + err.message,
                  duration: 5
                })
              })
            } else {
              this.$Message.error({
                content: '读取考勤卡失败：' + data.content,
                duration: 5
              })
            }
            break
          case 2:
            if (Number(data.result) === 1) {
              this.$emit('writeSuccess', data.content)
            } else {
              this.$Message.error({
                content: '写入考勤卡失败：' + data.content,
                duration: 5
              })
            }
            break
          default:
            this.$Message.error({
              content: '未知错误：' + data.content,
              duration: 5
            })
            break
        }
      }
    },
    /**
       * 读写考勤卡
       * @param msg 要写入传卡号，读取传 ""
       * @param type 1：读取，2：写入
       */
    sendMsgToCard (msg, type) {
      this.ioOrder++
      this.ws.send(JSON.stringify({ content: msg, command: type, order: this.ioOrder }))
    },
    printQR () {
      this.$emit('printQR')
    }
  },
  mounted () {
    if (this.attendanceSearch) {
      this.initWS()
    }
  }
}
</script>

