<template>
  <div class="tal-school-select">
    <div class="select-all">
      <Checkbox size="large" class="checkbox" v-model="selectAllSchools" :indeterminate="indeterminate" @on-change="handleSelectAll">全选</Checkbox>
    </div>
    <template v-for="(item,index) in schoolList">
      <div :key="index">
        <div class="select-level">
          <span class="level-label">{{item.level}}分校</span>
          <Checkbox size="large" class="checkbox" v-model="item.all" :indeterminate="item.indeterminate" @on-change="handleSelectLevel(index)">全选</Checkbox>
        </div>
        <div class="select-wrapper">
          <CheckboxGroup v-model="item.checkboxValue" @on-change="handleSelectSchool(index, $event)">
            <label :for="item.cityCode" v-for="(checkbox,i) in item.checkboxes" :key="i" class="checkbox-wrapper">
              <Checkbox size="large" class="select-checkbox" :value="checkbox.cityCode" :label="checkbox.name"></Checkbox>
            </label>
          </CheckboxGroup>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { queryAllSchoolList } from '../../api/masterBD'
export default {
  name: 'TalSchoolSelect',
  props: {
    defaultSchoolCodes: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      schoolArr: [],
      schoolList: [],
      selectSchools: [],
      indeterminate: false,
      selectAllSchools: false
    }
  },
  methods: {
    async getSchoolList () {
      try {
        const response = await queryAllSchoolList()
        this.schoolArr = response || []
        this._normalizeSchoolList()
      } catch (error) {
        console.log(error)
        this.$Message.error('获取分校失败,请稍后再试')
      }
    },
    _normalizeSchoolList () {
      this.schoolList = []
      const tmp = {}
      this.schoolArr.forEach((value) => {
        if (!tmp[value.level]) {
          tmp[value.level] = {
            level: value.level,
            all: false,
            checkboxes: [],
            checkboxValue: [],
            indeterminate: false
          }
        }
        tmp[value.level].checkboxes.push(value)
      })
      const schoolLevelList = Object.keys(tmp)
      const schoolLevelDict = {}
      schoolLevelList.forEach(item => {
        if (!schoolLevelDict[item[0]]) {
          schoolLevelDict[item[0]] = []
        }
        schoolLevelDict[item[0]].push(item)
      })
      for (const key in schoolLevelDict) {
        schoolLevelDict[key] = schoolLevelDict[key].sort()
      }
      const sortedKeys = Object.keys(schoolLevelDict).sort().reverse()
      let sortedLevels = []
      sortedKeys.forEach(key => {
        sortedLevels = sortedLevels.concat(schoolLevelDict[key])
      })
      sortedLevels.forEach(level => {
        this.schoolList = this.schoolList.concat(tmp[level])
      })
    },
    handleSelectSchool (index, val) {
      this.schoolList[index].checkboxValue = val
      this.schoolList[index].all = this.schoolList[index].checkboxValue.length === this.schoolList[index].checkboxes.length
      if (this.schoolList[index].checkboxValue.length === 0) {
        this.schoolList[index].indeterminate = false
      } else if (this.schoolList[index].checkboxValue.length < this.schoolList[index].checkboxes.length) {
        this.schoolList[index].indeterminate = true
      } else {
        this.schoolList[index].indeterminate = false
      }
      let isAll = true
      for (const index in this.schoolList) {
        if (this.schoolList.hasOwnProperty(index)) {
          if (this.schoolList[index].all === false) {
            isAll = false
          }
        }
      }
      this.selectAllSchools = isAll
      this._emitSchools()
    },
    handleSelectLevel (index) {
      if (this.schoolList[index].all === true) {
        const tmp = []
        this.schoolList[index].checkboxes.forEach(item => {
          tmp.push(item.name)
        })
        this.schoolList[index].checkboxValue = tmp
        this.schoolList[index].indeterminate = false
      } else {
        this.schoolList[index].checkboxValue = []
        this.schoolList[index].indeterminate = false
      }
      // 判断是否选择全部
      let isAll = true
      for (let i = 0; i < this.schoolList.length; i++) {
        if (this.schoolList[i].all === false) {
          isAll = false
          break
        }
      }
      this.selectAllSchools = isAll
      this._emitSchools()
    },
    handleSelectAll () {
      this.schoolList.forEach(group => {
        group.indeterminate = false
        group.all = this.selectAllSchools
        if (group.all) {
          const tmp = []
          group.checkboxes.forEach(item => {
            tmp.push(item.name)
          })
          group.checkboxValue = tmp
        } else {
          group.checkboxValue = []
        }
      })
      this._emitSchools()
    },
    _emitSchools () {
      const selectSchoolList = []
      for (const index in this.schoolList) {
        for (const index1 in this.schoolList[index].checkboxValue) {
          for (let index2 = 0; index2 < this.schoolList[index].checkboxes.length; index2++) {
            if (this.schoolList[index].checkboxes[index2].name === this.schoolList[index].checkboxValue[index1]) {
              selectSchoolList.push({
                name: this.schoolList[index].checkboxes[index2].name,
                cityCode: this.schoolList[index].checkboxes[index2].cityCode,
                level: this.schoolList[index].checkboxes[index2].level
              })
              break
            }
          }
        }
      }
      this.indeterminate = !(selectSchoolList.length === 0 || this.schoolArr.length === selectSchoolList.length)
      this.$emit('change', selectSchoolList)
    },
    _initDetail () {
      this.selectSchools = []
      this.selectAllSchools = false
      this.indeterminate = false
      this.handleSelectAll()
      if (this.defaultSchoolCodes.length === this.schoolArr.length) {
        this.selectAllSchools = true
        this.indeterminate = false
        this.handleSelectAll()
      } else {
        if (this.defaultSchoolCodes.length === 0) {
          this.selectAllSchools = false
          this.indeterminate = false
          this.handleSelectAll()
          return
        }
        this.$nextTick(() => {
          this.selectAllSchools = false
          this.indeterminate = true
          this.defaultSchoolCodes.forEach(schoolCode => {
            for (let i = 0; i < this.schoolArr.length; i++) {
              if (this.schoolArr[i].cityCode === schoolCode) {
                let level = this.schoolArr[i].level
                for (let j = 0; j < this.schoolList.length; j++) {
                  if (this.schoolList[j].level === level) {
                    this.schoolList[j].checkboxValue.push(this.schoolArr[i].name)
                    if (this.schoolList[j].checkboxValue.length === this.schoolList[j].checkboxes.length) {
                      this.schoolList[j].all = true
                      this.schoolList[j].indeterminate = false
                    } else {
                      this.schoolList[j].indeterminate = this.schoolList[j].checkboxValue.length !== 0
                    }
                    break
                  }
                }
                break
              }
            }
          })
          this._emitSchools()
        })
      }
    }
  },
  mounted () {
    this.getSchoolList().then(() => {
      this._initDetail()
    })
  },
  watch: {
    defaultSchoolCodes () {
      this._initDetail()
    }
  }
}
</script>

<style scoped lang="less">
.tal-school-select {
  .select-all {
    margin-bottom: 12px;
  }
  .select-level {
    .level-label {
      line-height: 22px;
      margin-right: 16px;
      font-weight: 500;
      font-size: 14px;
    }
  }
  /deep/.ivu-checkbox-group {
    width: 100%;
  }
  .select-wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    .checkbox-wrapper {
      display: inline-block;
      flex: 0 0 20%;
      width: 20%;
      padding: 12px;
      box-sizing: border-box;
      &:hover {
        background-color: rgba(24, 144, 255, 0.1);
      }
      .checkbox {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>
