<template>
  <div class="tal-notification-list">
    <div v-if="tabList.length > 0">
      <Tabs v-model="currentTab" :animated="false" size="small" @on-click="handleClickTab">
        <TabPane v-for="item in tabList" :key="item.menu" :name="item.menu" :label="renderLabel(item)"></TabPane>
      </Tabs>
    </div>
    <div>
      <Row type="flex" align="middle">
        <Col :span="12">
          <Checkbox v-model="unReadMessage" @on-change="handleCheckboxChange">只看未读消息</Checkbox>
        </Col>
        <Col :span="12">
          <Select v-model="category" size="small" placeholder="请选择" filterable clearable @on-change="handleCategoryChange">
            <Option v-for="item in categoryList" :key="item.menu" :value="item.menu">{{item.name}}</Option>
          </Select>
        </Col>
      </Row>
    </div>
    <div class="content">
      <div class="notification-list" v-if="notificationList.length > 0">
        <div v-for="item in notificationList" :key="item.id" @click="handleClickItem(item)">
          <div class="notification-list-item">
            <div class="top-wrapper">
              <div class="title">
                <h3 v-if="item.isRead === 0" :style="{color: primaryColor}" class="no-read">{{item.title}}</h3>
                <h3 v-else>{{item.title}}</h3>
              </div>
              <div class="extra">
                <p class="text">{{item.createDate}}</p>
              </div>
            </div>
            <div class="content-wrapper">
              <p class="text">{{item.body.msg}}</p>
            </div>
          </div>
          <Divider />
        </div>
        <a v-if="pageNo < maxPage" href="javascript:void(0);" class="load-more-btn" @click="handleLoadMore">加载更多</a>
        <span v-else class="load-more-btn">暂无更多通知</span>
      </div>
      <div class="empty" v-else>
        <img src="../../assets/images/notification.png"/>
        <p class="text">暂无通知消息</p>
      </div>
    </div>
    <Row class="footer">
      <Col :span="12">
        <a href="javascript:void(0);" @click="handleGetAllMessages">查看全部消息记录</a>
      </Col>
      <Col :span="12">
        <Poptip confirm title="确定将全部未读消息标记为已读？" @on-ok="handleSetRead([])">
          <a style="display: inline-block;width: 100%;text-align:right;" href="javascript:void(0)">全部标记已读</a>
        </Poptip>
      </Col>
    </Row>
  </div>
</template>

<script>
import {
  ERROR_TOKEN_CODE,
  getNoReadCount,
  getPlatFormMenuTree,
  getPopupMessage,
  queryHotNoticeMessage,
  setRead
} from '../../api/notice'
import { dateFormat } from '../../utils/utils'

export default {
  name: 'TalNotificationList',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    primaryColor: {
      type: String,
      default: '#2d8cf0'
    },
    frequency: {
      type: Number,
      default: 1000
    }
  },
  data () {
    return {
      showModal: false,
      currentTab: '',
      category: '',
      categoryList: [],
      tabList: [],
      notificationList: [],
      unReadMessage: false,
      timer: null,
      versionNumber: 0,
      number: 0,
      pageNo: 1,
      maxPage: 0,
      renderLabel (data) {
        return (h) => {
          return h('div', {}, [
            h('span', {}, data.name),
            h('Badge', {
              props: {
                dot: this.handleIsShowDot(data)
              },
              style: {
                bottom: '10px'
              }
            }, '')
          ])
        }
      },
      menuTree: []
    }
  },
  methods: {
    async getMenuTree () {
      try {
        const response = await getPlatFormMenuTree()
        this.tabList = response || []
        this.menuTree = response || []
        if (this.tabList.length > 0) {
          this.tabList.unshift({
            name: '全部通知',
            menu: 'all'
          })
        }
        this.currentTab = this.tabList.length > 0 ? this.tabList[0].menu : 'all'
        this.getCategoryList()
      } catch (error) {
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      }
    },
    handleSetRead (variable) {
      if (Array.isArray(variable)) {
        const params = {
          messageReadDtos: variable
        }
        setRead(params).then(() => {
          this.notificationList = []
          this.getNotificationList().then(response => {
            this.notificationList = response
          }).catch(error => {
            this.$Message.warning('获取通知失败:' + error.message)
            if (error.code === ERROR_TOKEN_CODE) {
              this.$emit('on-token-error')
            }
          })
        }).catch(error => {
          if (error.code === ERROR_TOKEN_CODE) {
            this.$emit('on-token-error')
          }
        })
      } else {
        const params = {
          messageReadDtos: [
            {
              menuId: variable.menu,
              messageId: variable.id
            }
          ]
        }
        setRead(params).then(() => {
          variable.isRead = 1
        }).catch(error => {
          if (error.code === ERROR_TOKEN_CODE) {
            this.$emit('on-token-error')
          }
        })
      }
    },
    getNotificationList () {
      return new Promise((resolve, reject) => {
        const params = {
          pageNo: this.pageNo,
          pageSize: 10
        }
        if (this.category) {
          params.menu = this.category
        } else if (this.currentTab !== 'all') {
          params.menu = this.currentTab
        } else {
          params.menu = null
        }
        if (this.unReadMessage) {
          params.isRead = 0
        }
        queryHotNoticeMessage(params).then(response => {
          const list = response.records || []
          list.forEach(item => {
            const date = item.createDate
            // 日期由yyyy-MM-dd HH:mm:ss 改成 MM-dd HH:mm
            item.createDate = dateFormat(new Date(date), 'MM-dd HH:mm')
            // eslint-disable-next-line
            item.body = eval('(' + item.body + ')')
          })
          this.maxPage = Math.ceil(response.totalCount / 10)
          resolve(list)
        }).catch(error => {
          reject(error)
        })
      })
    },
    getCategoryList () {
      let categoryList = []
      if (this.currentTab === 'all') {
        this.menuTree.forEach(item => {
          const arr = item.children || []
          categoryList = categoryList.concat(arr)
        })
      } else {
        const filterResult = this.menuTree.filter(item => item.menu === this.currentTab)
        const arr = filterResult[0].children || []
        categoryList = categoryList.concat(arr)
      }
      this.categoryList = categoryList
    },
    handleIsShowDot (item) {
      if (item.menu === 'all') {
        return this.number > 0
      } else {
        return item.noReadCount > 0
      }
    },
    handleClickTab () {
      this.pageNo = 1
      this.notificationList = []
      this.category = ''
      this.categoryList = []
      this.getCategoryList()
      this.getNotificationList().then(response => {
        this.notificationList = response
      }).catch(error => {
        this.$Message.warning('获取通知失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    },
    handleCategoryChange () {
      this.pageNo = 1
      this.notificationList = []
      this.getNotificationList().then(response => {
        this.notificationList = response
      }).catch(error => {
        this.$Message.warning('获取通知失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    },
    handleLoadMore () {
      this.pageNo += 1
      this.getNotificationList().then(response => {
        this.notificationList = this.notificationList.concat(response)
      }).catch(error => {
        this.$Message.warning('获取通知失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    },
    handleClickItem (item) {
      if (item.isRead === 0) {
        this.handleSetRead(item)
      }
      if (item.type === 3) {
        this.$emit('on-click-item', item.body.data)
      }
    },
    handleCheckboxChange () {
      this.pageNo = 1
      this.notificationList = []
      this.getNotificationList().then(response => {
        this.notificationList = response
      }).catch(error => {
        this.$Message.warning('获取通知失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    },
    handleGetAllMessages () {
      this.$emit('on-get-all-messages')
    },
    handleGetPopUpMessages () {
      getPopupMessage().then(response => {
        const list = response || []
        list.forEach(item => {
          const date = item.createDate
          // 日期由yyyy-MM-dd HH:mm:ss 改成 MM-dd HH:mm
          item.createDate = dateFormat(new Date(date), 'MM-dd HH:mm')
          // eslint-disable-next-line
          item.body = eval('(' + item.body + ')')
          if (item.type === 10) {
            this.handleSetRead(item)
            this.$emit('on-click-item', item.body.data)
          } else {
            this.$Notice.info({
              title: item.title,
              render: h => {
                return h('div', {}, [
                  h('p', {
                    style: {
                      cursor: item.type === 3 ? 'pointer' : 'normal',
                      fontSize: '14px',
                      lineHeight: '20px'
                    },
                    on: {
                      click: () => {
                        this.handleSetRead(item)
                        if (item.type === 3) {
                          this.$emit('on-click-item', item.body.data)
                        }
                      }
                    }
                  }, item.body.msg),
                  h('p', {
                    style: {
                      marginTop: '24px',
                      textAlign: 'right'
                    }
                  }, item.createDate)
                ])
              },
              duration: 0,
              onClose: () => {
                this.handleSetRead(item)
              }
            })
          }
        })
      }).catch(error => {
        this.$Message.warning('获取通知失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    }
  },
  mounted () {
    setTimeout(async () => {
      this.timer = setInterval(() => {
        getNoReadCount().then(response => {
          this.versionNumber = response.versionNumber
          this.number = response.number
          if (response.poPupNumber > 0) {
            this.handleGetPopUpMessages()
          }
        }).catch(error => {
          if (error.code === ERROR_TOKEN_CODE) {
            this.$emit('on-token-error')
          }
        })
      }, this.frequency)
      this.number = parseInt(sessionStorage.getItem('message-number')) || 0
    }, 1000)
    this.$Notice.config({
      top: 68,
      duration: 0
    })
  },
  beforeDestroy () {
    clearInterval(this.timer)
    this.timer = null
  },
  watch: {
    async show (val) {
      if (val) {
        if (this.tabList.length === 0) {
          await this.getMenuTree()
        }
        this.getNotificationList().then(response => {
          this.notificationList = response
        }).catch(error => {
          this.$Message.warning('获取通知失败:' + error.message)
          if (error.code === ERROR_TOKEN_CODE) {
            this.$emit('on-token-error')
          }
        })
      } else {
        this.pageNo = 1
        this.maxPage = 1
      }
    },
    versionNumber () {
      this.getMenuTree()
      this.$emit('on-new-message', this.number)
    },
    number (val) {
      this.getMenuTree()
      this.$emit('on-change-number', val)
      sessionStorage.setItem('message-number', val)
    }
  }
}
</script>

<style scoped lang="less">
  .tal-notification-list {
    width: 100%;
    white-space: normal;
    word-break: break-all;
    .ivu-poptip {
      width: 100%;
      /deep/.ivu-poptip-rel {
        width: 100%;
      }
    }
    .content {
      margin: 10px 0;
      padding: 10px 0;
      max-height: 500px;
      overflow-y: auto;
      border-top: 1px solid #aaaaaa;
      border-bottom: 1px solid #aaaaaa;
      .notification-list {
        list-style: none;
        .notification-list-item {
          cursor: pointer;
          .top-wrapper {
            display: flex;
            justify-content: space-between;
            .title {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              min-width: 50px;
              margin-right: 5px;
              .no-read {
                &:before {
                  content: ' ';
                  float: left;
                  margin-top: 8px;
                  margin-right: 5px;
                  width: 6px;
                  height: 6px;
                  background-color: #ff0000;
                  border-radius: 50%;
                  z-index: 1000;
                }
              }
            }
            .extra {
              .text {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
          }
          .content-wrapper {
            margin-top: 5px;
          }
        }
        .ivu-divider-horizontal {
          margin: 5px 0;
        }
        .load-more-btn {
          display: inline-block;
          width: 100%;
          margin: 5px 0;
          text-align: center;
        }
      }
      .empty {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 300px;
        img {
          width: 100px;
          height: 100px;
        }
        .text {
          margin-top: 10px;
          font-size: 13px;
          color: #aaaaaa;
        }
      }
    }
    .footer {
      margin-top: 10px;
    }
  }
</style>
