<template>
  <div style="position: relative;">
    <Spin v-if="isLoading" fix></Spin>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      label-position="left"
    >
      <Row type="flex" align="middle">
        <Col span="6">类型：电子钱包提现</Col>
        <template v-if="payType === 1">
          <Col span="18">
            <FormItem
              prop="withDrawTypeName"
              style="margin-bottom: 0;"
              label="提现方式: "
              :label-width="100"
            >
              <RadioGroup v-model="formValidate.withDrawTypeName" @on-change="handleRadioChange">
                <Radio label="现金提现" value="0"></Radio>
                <Radio label="转网银提现" value="1"></Radio>
              </RadioGroup>
            </FormItem>
          </Col>
        </template>
        <Col v-else span="12">提现方式：转网银提现</Col>
      </Row>
      <Row style="margin-top: 12px;">
        <Col span="6">提现总笔数：{{ formValidate.details.length }}</Col>
        <Col span="12">提现总金额：{{formValidate.returnableBalance}}元</Col>
      </Row>
      <template v-if="formValidate.withDrawType === 1 && isShareSchool">
        <section class="ivu-form-item-required">
          <span class="ivu-form-item-label" style="display: inline-block;width: 120px;">上传附件</span>
          <div style="padding-left: 120px;margin: 30px 0;">
            <Tooltip max-width="500" placement="right">
              <span style="color: red;font-size: 18px;">
                提供材料说明
                <Icon type="ios-help-circle" />
              </span>
              <div slot="content">
                <div>
                  <p>1.家长身份证复印件/照片；</p>
                  <p>2.家长储蓄卡复印件/照片；</p>
                </div>
              </div>
            </Tooltip>
            <Fileupload
              v-model="formValidate.billCommonFileVos"
              multiSelection
              @on-change="handleFileChange"
            />
            <span
              v-if="!formValidate.billCommonFileVos.length && isShowFileWarn"
              style="color: red;"
            >请上传退费材料</span>
          </div>
        </section>
        <Row>
          <Col span="12">
            <FormItem label="选择服务中心主管" prop="serviceCenterDirector">
              <Select
                class="wd260"
                v-model="formValidate.serviceCenterDirector"
                label-in-value
                filterable
                remote
                clearable
                :remote-method="q => selectRemoteLoad(q, 'servcieCenter')"
                :loading="servcieCenter.isLoading"
                placeholder="请选择服务中心主管"
                @on-change="handleSelectChange($event, 'servcieCenter')"
              >
                <Option
                  :value="item.empNo"
                  v-for="(item, index) in servcieCenter.options"
                  :key="index"
                >{{ item.name }} - {{item.empNo}}</Option>
              </Select>
              <!-- <Input
              class="wd260"
              v-else
              disabled
              :value="formValidate.serviceCenterDirectorName + '-' + formValidate.serviceCenterDirector"
              />-->
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="选择大区主任" prop="regionalDirector">
              <Select
                class="wd260"
                v-model="formValidate.regionalDirector"
                label-in-value
                filterable
                remote
                clearable
                :remote-method="q => selectRemoteLoad(q, 'district')"
                :loading="district.isLoading"
                placeholder="请选择大区主任"
                @on-change="handleSelectChange($event, 'district')"
              >
                <Option
                  :value="item.empNo"
                  v-for="(item, index) in district.options"
                  :key="index"
                >{{ item.name }} - {{item.empNo}}</Option>
              </Select>
              <!-- <Input
              class="wd260"
              v-else
              disabled
              :value="formValidate.regionalDirectorName + '-' + formValidate.regionalDirector"
              />-->
            </FormItem>
          </Col>
        </Row>
        <!-- 网银信息 -->
        <section>
          <p>网银账户信息</p>
          <div style="width: 90%;margin: 20px auto;">
            <BankinfoCollect ref="bankinfoCollect" v-model="formValidate.onlineBank" />
          </div>
        </section>
      </template>
      <!-- 现金退费提醒 -->
      <p v-else-if="formValidate.withDrawType === 0" style="color: red;padding: 24px 0; font-size: 14px;">注意：点击“退费”按钮后，直接将现金退还给家长即可。</p>
      <!-- 网银退，非共享分校提醒 -->
      <p v-else style="color: red;padding: 24px 0; font-size: 14px;">注意：由于非共享分校未对接集团报账平台，请前往知音楼逐笔发起转网银审核流程。</p>

      <!-- 提交和关闭 -->
      <Row type="flex" justify="center">
        <Col>
          <Button :loading="submitDisable" type="primary" @click="handleSubmit('formValidate')">退费</Button>
        </Col>
        <Col>
          <Button style="margin-left: 30px" @click="close">关闭</Button>
        </Col>
      </Row>
    </Form>
  </div>
</template>

<script>
import { getEmployeeList } from '../../api/specialRefund'
import BankinfoCollect from '../tal-finance-refund-special-apply/bankinfo-collect'
import Fileupload from '../tal-finance-refund-special-apply/fileupload'
import { cashOut, getWorkOrder, isCity } from '../../api/finance'
export default {
  name: 'TalFinanceWalletWithdraw',
  components: {
    BankinfoCollect,
    Fileupload
  },
  props: {
    studentDetail: {
      type: Object,
      default: () => {
        return {
          studentName: '',
          studentId: '',
          studentCard: '',
          studentUid: '',
          cityCode: ''
        }
      }
    },
    details: {
      type: Array,
      default: () => {
        return []
      }
    },
    payType: { // 1现金 2刷卡 3支票
      type: Number,
      default: 1
    },
    operate: { // 操作 1新增 2修改
      type: Number,
      default: 1
    },
    id: { // 查询工单id
      type: String,
      default: ''
    }
  },
  data () {
    return {
      formValidate: {
        billCommonFileVos: [], // 附件
        serviceCenterDirector: '', // 服务中心主管
        regionalDirector: '', // 大区主任
        onlineBank: {
          payName: '', // 收款人
          payBankAccount: '', // 收款人账户
          payBankType: '', // 收款银行类型
          payBankTypeName: '', // 收款银行类型
          payBankProvince: '', // 银行所在省份
          payBankProvinceName: '', // 银行所在省份
          payBankCity: '', // 银行所在市
          payBankCityName: '', // 银行所在市
          payBankName: '' // 开户行全称
        },
        withDrawTypeName: '转网银提现', // 支付类型
        withDrawType: 1, // 0现金 1网银
        returnableBalance: 0,
        details: []
      },
      ruleValidate: {
        serviceCenterDirector: [{ required: true, message: '请选择服务中心主管', trigger: 'change', type: 'string' }],
        regionalDirector: [{ required: true, message: '请选择大区主任', trigger: 'change', type: 'string' }],
        withDrawTypeName: [{ required: true, message: '请选择提现方式', trigger: 'change', type: 'string' }]
      },
      // 服务中心可选项
      servcieCenter: {
        isLoading: false,
        options: []
      },
      // 大区主任可选项
      district: {
        isLoading: false,
        options: []
      },
      isLoading: true, // 请求中...
      submitDisable: false, // 是否可以提交
      isShowFileWarn: false, // 是否显示上传退费文件提示
      isShareSchool: false // 是否为共享分校
    }
  },
  async mounted () {
    this.isLoading = true
    this.formValidate.returnableBalance = this.details.reduce((total, cur) => total + cur.returnableBalance, 0).toFixed(2)
    this.formValidate.payPrice = this.details.reduce((total, cur) => total + cur.payPrice, 0).toFixed(2)
    this.formValidate.details = [...this.details]
    if (this.payType !== 1) {
      this.formValidate.withDrawType = 1
      this.formValidate.withDrawTypeName = this.getWithDrawTypeName(1)
    } else {
      this.formValidate.withDrawType = 0
      this.formValidate.withDrawTypeName = this.getWithDrawTypeName(0)
    }
    await this.isCity()
    if (this.id) {
      await this.getWorkOrder()
    }
    this.isLoading = false
  },
  methods: {
    async isCity () {
      try {
        const bol = await isCity({ cityCode: this.studentDetail.cityCode })
        this.isShareSchool = !bol
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    getWithDrawTypeName (type) {
      switch (type) {
        case 0: return '现金提现'
        case 1: return '转网银提现'
        default: return ''
      }
    },
    // 查询工单
    async getWorkOrder () {
      const params = {
        id: this.id,
        cityCode: this.studentDetail.cityCode
      }
      try {
        const data = await getWorkOrder(params)
        const billCommonFileVos = data.billCommonFileVos.map(item => Object.assign(item, { percent: 100, status: 'finished' }))
        this.formValidate = Object.assign({}, this.formValidate, data, {
          billCommonFileVos,
          withDrawType: data.refundType,
          withDrawTypeName: this.getWithDrawTypeName(data.refundType),
          cityCode: this.studentDetail.cityCode,
          returnableBalance: data.details.reduce((total, cur) => total + cur.returnableBalance, 0).toFixed(2),
          payPrice: data.details.reduce((total, cur) => total + cur.payPrice, 0).toFixed(2)
        })
      } catch (error) {
        this.$Message.error({ content: '获取工单失败：' + error.message, duration: 5 })
      }
    },
    // 提现方式切换监听
    handleRadioChange (label) {
      switch (label) {
        case '现金提现':
          this.formValidate.withDrawType = 0; break
        case '转网银提现':
          this.formValidate.withDrawType = 1; break
        default:
          this.formValidate.withDrawType = 99
      }
    },
    close () {
      this.$emit('close')
    },
    // 远程加载数据
    async selectRemoteLoad (query, type) {
      this[type].isLoading = true
      try {
        const params = {
          cityCode: this.cityCode,
          name: isNaN(Number(query)) ? query : '',
          empNo: isNaN(Number(query)) ? '' : query
        }
        const data = await getEmployeeList(params)

        this[type].isLoading = false
        this[type].options = Object.freeze(data)
      } catch (error) {
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
    },
    handleSelectChange (data, type) {
      if (!data) return
      const name = data.label.split(' - ')[0]
      if (type === 'district') {
        this.formValidate.regionalDirectorName = name
      } else {
        this.formValidate.serviceCenterDirectorName = name
      }
    },
    // 文件上传完成
    handleFileChange () {
      this.isShowFileWarn = true
    },
    // 退费提交
    async handleSubmit (name) {
      let params = Object.assign({},
        this.formValidate,
        {
          ...this.formValidate.onlineBank,
          operate: this.operate,
          refundType: this.formValidate.withDrawType,
          billCommonFileVos: this.formValidate.billCommonFileVos.filter(item => item.status === 'finished')
        }
      )

      if (params.billCommonFileVos.length < this.formValidate.billCommonFileVos.length) {
        return this.$Message.warning({ content: '附件上传中，请稍后再点击提交', duration: 5 })
      }

      if (this.operate === 1) {
        Object.assign(params, {
          ...this.studentDetail,
          details: this.details
        })
      }
      console.log('---- submit params -----', params)
      try {
        let valid1 = true; let valid2 = true; let valid3 = true
        if (this.formValidate.withDrawType === 1 && this.isShareSchool) {
          valid1 = await this.$refs[name].validate()
          valid2 = await this.$refs.bankinfoCollect.validate()
          valid3 = this.formValidate.billCommonFileVos.length
        }

        this.isShowFileWarn = true
        if (valid1 && valid2 && valid3) {
          this.submitDisable = true
          await cashOut(params)
          this.close()
          this.$Message.success('退费成功')
          this.$emit('success')
          this.submitDisable = false
        }
      } catch (error) {
        this.submitDisable = false
        this.$Message.error({ content: '提交失败:' + error.message, duration: 5 })
      }
    }
  }
}
</script>

