import $ from '../utils/api.request'

/**
 * API 查询缴费详情-历史转班详情
 * 数据项        参数方式 属性名          是否必填 描述
 * 报名ID        body    registId       Y       报名ID
 * uid          body    uid             Y       退费单号
 * @param params
 */

export const queryClassPayments = params => {
  return $.splatAxios.request({
    url: 'order/queryClassPayments',
    params,
    method: 'get'
  })
}

