import $ from '../utils/api.request.course'
const course = $.courseAxios

/* 剩余名额 导出选中、全部 */
export const exportClazzRemain = params => {
  return course.request({
    url: '/export/clazzRemain',
    method: 'POST',
    data: params
  })
}

