import { queryByType, resultExport } from '../../../api/reason'
import TalTeacherDetails from '../../tal-teacher-details'
import TeacherMixin from '../../../mixin/teacherDetail'
import TalStuDetail from '../../tal-stu-details'
import CommonFormSearch from '../../../mixin/commonFormSearch'
import { dateFormat } from '../../../utils/utils'
export default {
  components: {
    TalTeacherDetails,
    TalStuDetail
  },
  mixins: [TeacherMixin, CommonFormSearch],
  data () {
    return {
      stuDetail: {
        show: false
      },
      tableData: [],
      tableLoading: false,
      checkedData: [], // 已选项
      searchFormHidden: true,
      exportCheckedLoading: false, // 导出中
      exportAllLoading: false, // 导出中
      formData: {
        minRemainNumber: null, // 剩余名额开始值
        maxRemainNumber: null, // 剩余名额结束值
        number: null, // 退班课次、转班课次、调班课次
        maxNumber: null // 调课课次结束值
      },
      formDataSource: {
        minRemainNumber: null, // 剩余名额开始值
        maxRemainNumber: null, // 剩余名额结束值
        number: null, // 退班课次、转班课次、调班课次
        maxNumber: null // 调课课次结束值
      }
    }
  },
  methods: {
    // 搜索表单条件限制
    searchFormValidate () {
      if (!this.formData.cityCode) {
        this.$Message.warning('请填写所在城市')
        return true
      }
      if (!this.formData.year) {
        this.$Message.warning('请选择年份')
        return true
      }

      if (!this.formData.termId) {
        this.$Message.warning('请选择学期')
        return true
      }

      if ((this.formData.minRemainNumber && this.formData.minRemainNumber < 0) || (this.formData.maxRemainNumber && this.formData.maxRemainNumber < 0)) {
        this.$Message.warning({ content: '剩余名额必须大于等于0', duration: 5 })
        return true
      }

      // Number(null) = 0
      if (this.formData.minRemainNumber && this.formData.minRemainNumber >= 0 && this.formData.maxRemainNumber === null) {
        this.$Message.warning({ content: '请输入剩余名额结束值', duration: 5 })
        return true
      }

      if (this.formData.minRemainNumber === null && this.formData.maxRemainNumber && this.formData.maxRemainNumber >= 0) {
        this.$Message.warning({ content: '请输入剩余名额开始值', duration: 5 })
        return true
      }

      if (!this.formData.operateStartDate || !this.formData.operateEndDate) {
        this.$Message.warning({ content: '请选择操作日期', duration: 5 })
        return true
      }

      if (this.calcDays(this.formData.operateStartDate, this.formData.operateEndDate) >= 180) {
        this.$Message.warning({ content: '只能选择查询半年之内的数据', duration: 5 })
        return true
      }
    },
    calcDays (start, end) {
      return (new Date(end).getTime() - new Date(start).getTime()) / 86400000
    },
    // 展示学员详情
    showStudentDetail (uid) {
      this.stuDetail.uid = uid
      this.stuDetail.show = true
    },
    // 选中事件
    handleSelectionChange (selection) {
      this.checkedData = [...selection.map(item => item.id)]
    },
    // 分页
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
      this.checkedData = []
      this.exportAllLoading = false
      this.exportCheckedLoading = false
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
      this.checkedData = []
      this.exportAllLoading = false
      this.exportCheckedLoading = false
    },
    // 查询奖学金列表
    getTableData () {
      this.tableLoading = true
      queryByType(this.formData)
        .then(data => {
          this.formData.totalCount = data.totalCount
          this.tableData = data.rows
        })
        .catch(err => {
          this.tableData = []
          this.formData.totalCount = 0
          this.$Message.error({
            content: '获取退费原因查询列表失败：' + err.message
          })
        })
        .finally(() => {
          this.tableLoading = false
        })
    },
    // 导出
    resultExportHandler (loadingProps = 'exportCheckedLoading') {
      let ids = this.checkedData || []
      if (loadingProps === 'exportAllLoading') {
        ids = []
      }
      const params = {
        ...this.formData,
        ids
      }
      this[loadingProps] = true
      resultExport(params).then(res => {
        const reader = new FileReader()
        reader.addEventListener('loadend', (e) => {
          try {
            const result = JSON.parse(e.target.result)
            this.$Message.error((result && result.message) || '导出失败！')
            this[loadingProps] = false
          } catch (errMsg) {
            let objectUrl = URL.createObjectURL(new Blob([res]))
            if (objectUrl) {
              const link = document.createElement('a')
              link.download = `${this.formData.typeName}-${dateFormat(new Date(), 'yyyy年MM月dd日')}.xlsx`
              link.href = objectUrl
              link.click()

              if (this.tableData.length > 10000) {
                this.$Message.warning({ content: '仅可导出前1万条数据', duration: 5 })
              }

              let st = setTimeout(() => {
                clearTimeout(st)
                this[loadingProps] = false
                URL.revokeObjectURL(objectUrl)
              }, 5000)
            }
          }
        })
        reader.readAsText(new Blob([res]))
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
        this[loadingProps] = false
      })
    }
  }
}
