<template>
  <!--学员详情-->
  <div>
    <div class="reset_top" style="padding: 15px;">
      <p>学员信息</p>
      <Row>
        <Col span="4">
          <span>姓名:</span>
          <span class="stuRight">{{studentDetail.realName}}</span>
        </Col>
        <Col span="6">
          <span>学员编号:</span>
          <span class="stuRight">{{studentDetail.card}}</span>
        </Col>
        <Col span="2">
          <span>性别:</span>
          <span class="stuRight">{{studentDetail.sex === 1 ? '男': '女'}}</span>
        </Col>
        <Col span="5">
          <span>年级:</span>
          <span class="stuRight">{{studentDetail.gradeName}}</span>
        </Col>
        <Col span="7">
          <span>学员证件号码:</span>
          <span class="stuRight">{{studentDetail.identifyCardNo || '--'}}</span>
        </Col>
      </Row>
      <Row v-if="studentDetail.attentionReqList && studentDetail.attentionReqList[0]">
        <Col span="4">
          <span>第一联系人:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[0].typeName}}</span>
        </Col>
        <Col span="6">
          <span>第一联系人手机:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[0].tel}}</span>
        </Col>
      </Row>
      <Row v-if="studentDetail.attentionReqList && studentDetail.attentionReqList[1]">
        <Col span="4">
          <span>第二联系人:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[1].typeName}}</span>
        </Col>
        <Col span="6">
          <span>第二联系人手机:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[1].tel}}</span>
        </Col>
      </Row>
      <Row style="border-bottom: 1px solid gainsboro;"></Row>
      <Form :label-width="100"
            :rules="ruleInline" label-position="left" style="margin-top: 20px;padding: 10px 10px 10px 10px">
        <FormItem label="新密码">
          <Input v-model="newPassword" :type="'password'" style="width: 200px;margin-right: 10px"/>
          <Tooltip max-width="300" content="此处可修改培优所有产品线的学员登录密码，如在线，app，IPS，大语文，分校业务等" placement="right">
            <Icon type="ios-alert-outline" size="18"/>
          </Tooltip>
        </FormItem>
        <FormItem label="确认新密码">
          <Input v-model="confirmPassword" :type="'password'" style="width: 200px;"/>
        </FormItem>
        <FormItem label="手机号">
          <Select v-model="newPhone" placeholder="请选择手机号" style="width: 200px;">
            <Option v-for="item in phoneList" :value="item.num" :key="item.num" >
              <template v-if="item.num === '0'">注册手机号</template>
              <template v-else>第{{ item.num }}联系人</template>
                {{ item.phone }}
            </Option>
          </Select>
          <Button type="default" @click="sendMessageHandler(newPhone, 'bindCode')" v-if="!sendBtn"
            class="wd80" style="margin-left: 20px;">
            发送验证码
          </Button>
          <Button type="default" :disabled="true" v-else class="wd80" style="margin-left: 20px;">{{msgNum}}秒</Button>
        </FormItem>
        <FormItem label="请输入验证码:">
          <Input v-model="smsCode" style="width: 150px"/>
        </FormItem>
      </Form>
    </div>
    <div class='ivu-modal-footer'>
      <Button type="primary" @click="updatePass()" :loading="isLoading">修改密码</Button>
      <Button @click="modalValue = false">关闭</Button>
    </div>
  </div>
</template>
<script>
import { resetPassword } from '../../api/student'
import { queryPhones, sendMessage } from '../../api/sms'

export default {
  name: 'TalStuResetPwd',
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      newPassword: '', // 新密码
      confirmPassword: '', // 确认新密码
      ruleInline: {
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '请重新输入新密码', trigger: 'blur' }
        ]
      },
      isLoading: false,
      sendBtn: false, // 验证码已发送
      msgNum: '', // 发送验证码后的倒计时
      smsCode: '', // 验证码
      newPhone: '',
      phoneList: []
    }
  },
  mounted () {
    let req = {
      cityCode: this.studentDetail.cityCode,
      uid: this.studentDetail.uid
    }
    queryPhones(req).then(res => {
      if (res) {
        this.phoneList = res.map(r => {
          r.num = String(r.num)
          return r
        })
      }
    }).catch(err => {
      this.$Message.warning({ content: '获取手机号失败:' + err.message, duration: 3 })
    })
  },
  methods: {
    updatePass () {
      if (this.isLoading) {
        return
      }
      if (!this.newPassword) {
        this.$Message.warning('请输入新密码')
        return
      }
      if (!this.confirmPassword) {
        this.$Message.warning('请重新输入新密码')
        return
      }
      if (this.newPassword !== this.confirmPassword) {
        this.$Message.warning('两次输入密码不一致')
        return
      }
      let req = {
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId,
        uid: this.studentDetail.uid,
        newPassword: this.newPassword,
        newConfirmedPassword: this.confirmPassword,
        smsCode: this.smsCode
      }
      this.isLoading = true
      resetPassword(req).then(res => {
        this.$Message.success('重置成功')
        this.isLoading = false
        this.modalValue = false
      }).catch((err) => {
        this.$Message.warning({ content: '修改失败:' + err.message, duration: 5 })
        this.isLoading = false
        // this.modalValue = false
      })
    },
    // ⬇️调用sms发送验证码接口
    sendMessageHandler () {
      if (!this.sendBtn) {
        if (!this.newPhone) {
          this.$Message.warning({
            content: '请选择手机号码',
            duration: 3
          })
          return
        }
        this.sendBtn = true
        this.msgNum = 600
        let set
        let req = {
          num: this.newPhone,
          uid: this.studentDetail.uid,
          cityCode: this.studentDetail.cityCode,
          validType: 'resetPassword'
        }
        sendMessage(req).then(res => {
          this.$Message.success({ content: '发送成功', duration: 3 })
          set = setInterval(() => {
            if (this.msgNum <= 0) {
              clearInterval(set)
              this.sendBtn = false
            } else {
              this.msgNum -= 1
            }
          }, 1000)
        }).catch(err => {
          clearInterval(set)
          this.sendBtn = false
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.newPassword = ''
        this.confirmPassword = ''
        this.$emit('close', val)
      }
    }
  }
}
</script>
