import $ from '../utils/api.request'
import { apiGateWayHeader } from '../utils/utils'

const { outboundAxios } = $

const apiAuthSalt = '40288b1566c021e90166c2e896ed0322'

const getHeaders = () => {
  const timestamp = Date.now()
  return {
    'X-Ca-Timestamp': timestamp,
    'X-Ca-Signature': apiGateWayHeader(timestamp + '', apiAuthSalt)
  }
}
/**
 * 根据uid获取第一联系人手机号
 * @returns {*}
 */
export const getPhoneByUid = params => {
  return outboundAxios.request({
    method: 'get',
    url: '/access/getPhoneByUid',
    params,
    headers: getHeaders()
  })
}

/**
 * 通话记录插入
 * @param params
 * @returns {*}
 */
export const callRecordInsert = params => {
  return outboundAxios.request({
    method: 'post',
    url: '/callrecord/insert',
    data: params,
    headers: getHeaders()
  })
}

/**
 * 通话记录更新
 * @param params
 * @returns {*}
 */
export const callRecordUpdate = params => {
  return outboundAxios.request({
    method: 'post',
    url: '/callrecord/update',
    data: params,
    headers: getHeaders()
  })
}
