<template>
  <div class="score-recording">
    <Form ref='form' :model="formData" :rules='ruleData' :label-width='90' inline style="text-align: left">
      <FormItem label="城市" prop="cityCode">
        <Select
          v-model="formData.cityCode"
          filterable
          style="width: 120px;"
          @on-change="cityChangeHandle">
          <Option v-for="(option,index) in cityWithoutAllCity" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="区域" prop="areaId">
        <Select
          v-model="formData.areaId"
          @on-change="changeAreaHandle"
          clearable
          style="width:120px">
          <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="服务中心" prop="serviceCenterId">
        <Select
          v-model="formData.serviceCenterId"
          clearable
          style="width:120px">
          <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="学员编号" prop="card">
        <Input v-model.trim="formData.card" @on-enter="searchTableData" clearable placeholder="请输入" style="width: 120px" />
      </FormItem>
      <FormItem style="float: right">
        <Button type="primary" style="margin-right: 10px;" @click="searchTableData" :loading="tableLoading">查询</Button>
        <Button @click="resetFieldsHandle">重置</Button>
      </FormItem>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableData" :loading="tableLoading">
      <template slot-scope="{ row }"  slot="stuName">
        <a @click='stuNameClickHandle(row)'>{{row.stuName}}</a>
      </template>
    </Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="text-align: right;">
        <Page
          :total="formData.totalCount"
          :page-size="formData.pageSize"
          :current.sync="formData.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import BaseDataMixin from '../../mixin/baseData'
import { queryContinueConflictPage } from '../../api/clazz'
export default {
  name: 'TalContinueConflictQuery',
  mixins: [BaseDataMixin],
  components: {
  },
  data () {
    return {
      cityList: [],
      cityWithoutAllCity: [],
      areaList: [], // 区域
      serviceCenterList: [], // 服务中心
      tableData: [],
      cols: [
        { title: '学员编号', align: 'center', key: 'card', width: 130 },
        { title: '学员姓名', align: 'center', slot: 'stuName', width: 100 },
        { title: '冲突一班级名称', align: 'center', key: 'claName1' },
        { title: '冲突一班级ID', align: 'center', key: 'claId1' },
        { title: '冲突二班级名称', align: 'center', key: 'claName2' },
        { title: '冲突二班级ID', align: 'center', key: 'claId2' },
        { title: '冲突二是否缴费', align: 'center', key: 'isPay2', width: 90 },
        { title: '冲突时间', align: 'center', key: 'claClasstimeName', width: 150 }
      ],
      ruleData: {
        cityCode: [
          { required: true, message: '请选择城市', trigger: 'change' }
        ],
        areaId: [
          { required: true, message: '请选择区域', trigger: 'change' }
        ],
        serviceCenterId: [
          { required: true, message: '请选择服务中心', trigger: 'change' }
        ]
      },
      formData: {
        cityCode: '', // 城市code
        areaId: '', // 区域id
        serviceCenterId: '', // 服务中心id
        card: '',
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      tableLoading: false
    }
  },
  props: {
    cityCode: String
  },
  watch: {
    allCityList (val) {
      // if (val && val.length) {
      //   this.formData.cityCode = val[0].id
      //   this.cityYearChange(this.formData.cityCode)
      // }
      this.cityWithoutAllCity = val.filter(obj => obj.id !== '1111')
    }
  },
  mounted () {
    this.getCityListHandler()
  },
  methods: {
    cityChangeHandle () {
      if (this.formData.cityCode) {
        this.getAreaListHandler({
          cityCode: this.formData.cityCode
        }).then(res => {
          this.areaList = res
        }).catch(err => {
          this.$Message.error({
            content: '获取区域列表失败：' + err.message
          })
        })
      }
    },
    changeAreaHandle () {
      if (this.formData.areaId) {
        this.getServiceCenterListHandler({
          cityCode: this.formData.cityCode,
          areaId: this.formData.areaId
        }).then(res => {
          this.serviceCenterList = res
        }).catch(err => {
          this.$Message.error({
            content: '获取服务中心列表失败：' + err.message
          })
        })
      }
    },
    stuNameClickHandle (row) {
      this.$emit('stuNameClick', row)
    },
    /**
     * "查询"
     */
    searchTableData () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.formData.pageNo = 1
          this.getTableData()
        }
      })
    },
    resetFieldsHandle () {
      this.$refs['form'].resetFields()
    },
    getTableData () {
      this.tableLoading = true
      console.log(this.formData)
      queryContinueConflictPage(this.formData).then(data => {
        console.log(1111111)
        this.formData.totalCount = data.totalCount
        this.tableData = data.records
      }).catch(err => {
        console.log(2222222)
        this.tableData = []
        this.formData.totalCount = 0
        this.$Message.error({
          content: '获取续排班冲突列表失败：' + err.message
        })
      }).finally(() => {
        console.log(222233333222)
        this.tableLoading = false
      })
    },
    /**
     * 明细翻页
     * @param index
     */
    changePage (index) {
      this.formData.pageNo = index
      this.getTableData()
    },
    /**
     * 变更pageSize
     * @param size
     */
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    }
  }
}
</script>

