<template>
  <div class="self-for-course">
    <Card dis-hover class="card-wrap-middle">
      <Form
        class="self-form"
        @submit.native.prevent
        ref="continueClassForm"
        :model="continueClassForm"
        inline
      >
        <div>
          <div style="vertical-align: top" class="dib form-item-wrap flow-form-item-dib ivu-form-item-required">
            <label class="ivu-form-item-label"></label>
            <FormItem prop="cityId" :error="cityIdErr">
              <Select
                v-model="continueClassForm.cityId"
                @on-change="cityChange"
                :disabled="cityList.length===1"
                clearable
                filterable
                transfer
                placeholder="分校(必填)"
              >
                <Option
                  v-for="(item, index) in cityList"
                  :value="item.value"
                  :key="index"
                  :label="item.label"
                ></Option>
              </Select>
            </FormItem>
          </div>

          <div style="vertical-align: top" class="dib form-item-wrap flow-form-item-dib ivu-form-item-required">
            <label class="ivu-form-item-label"></label>
            <FormItem prop="year" :error="yearErr">
              <Select
                v-model="continueClassForm.year"
                clearable
                filterable
                placeholder="年份"
                transfer
                @on-change="yearChange"
              >
                <Option
                  v-for="item in baseList.years"
                  :value="String(item)"
                  :key="String(item)"
                  :label="String(item)">
                </Option>
              </Select>
            </FormItem>
          </div>

          <FormItem prop="productType">
            <Select
              v-model="continueClassForm.productType"
              clearable
              filterable
              transfer
              placeholder="产品类型"
            >
              <Option
                v-for="item in baseList.productType"
                :value="item.id"
                :key="item.id"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="seasonId">
            <Select
              v-model="continueClassForm.seasonId"
              clearable
              filterable
              transfer
              placeholder="学季"
            >
              <Option
                v-for="item in baseList.season"
                :value="item.id"
                :key="item.id"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="gradeId" style="max-width: 200px">
            <Select
              v-model="continueClassForm.gradeId"
              multiple
              clearable
              filterable
              transfer
              placeholder="年级"
            >
              <Option
                v-for="item in baseList.grades"
                :value="item.standardId"
                :key="item.standardId"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="subjectId">
            <Select
              v-model="continueClassForm.subjectId"
              clearable
              filterable
              transfer
              placeholder="学科"
            >
              <Option
                v-for="item in baseList.subjects"
                :value="item.standardId"
                :key="item.standardId"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="levelDegree">
            <Select
              v-model="continueClassForm.levelDegree"
              v-bind:class="{'global-select-loading':queryLoading.levelDegree}"
              clearable
              filterable
              transfer
              @on-change="levelDegreeListChange"
              label-in-value
              placeholder="班次级别"
            >
              <Option
                v-for="item in baseList.levelDegrees"
                :value="item.standardId"
                :key="item.standardId"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="bookVersion">
            <Select
              v-model="continueClassForm.bookVersion"
              v-bind:class="{'global-select-loading':queryLoading.bookVersion}"
              placeholder="输入检索版本"
              filterable
              remote
              clearable
              transfer
              label-in-value
              ref="bookVersion"
              @on-query-change="getClassLevelListLinkName"
              :loading="queryLoading.bookVersion"
              @on-change="classLevelListChange"
            >
              <Option
                v-for="item in baseList.bookVersion"
                :value="item.schoolId"
                :key="item.schoolId"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="categoryType">
            <Select
              v-model="continueClassForm.categoryType"
              clearable
              filterable
              transfer
              placeholder="授课类型"
            >
              <Option
                v-for="(item, index) in baseList.categoryType"
                :key="index"
                :value="item.id"
                :label="item.name"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="isContinueClass">
            <RadioGroup class="ml10 mr10" v-model="continueClassForm.isContinueClass">
              <Radio label="0">原班</Radio>
              <Radio label="1">续排班</Radio>
            </RadioGroup>
          </FormItem>
        </div>

        <div>
          <FormItem
            class="form-item-wrap"
            prop="areaId"
          >
            <Select
              transfer
              filterable
              clearable
              label-in-value
              placeholder="地区"
              v-model="continueClassForm.areaId"
              :class="queryLoading.area?'global-select-loading':''"
              @on-change="areaChange"
            >
              <Option
                v-for="option in areaList"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></Option>
            </Select>
          </FormItem>

          <FormItem
            class="form-item-wrap"
            prop="serviceCenterId"
          >
            <Select
              transfer
              filterable
              clearable
              label-in-value
              placeholder="服务中心"
              v-model="continueClassForm.serviceCenterId"
              :class="queryLoading.serviceCenter?'global-select-loading':''"
            >
              <Option
                v-for="option in serviceCenterList"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></Option>
            </Select>
          </FormItem>

          <FormItem prop="teacherEmpNo">
            <Select
              v-model="continueClassForm.teacherEmpNo"
              clearable
              filterable
              transfer
              placeholder="主讲老师"
              v-bind:class="{'global-select-loading':queryLoading.teacher}"
              @on-query-change="handleTeacherName"
              :loading="queryLoading.teacher"
            >
              <Option
                v-for="item in teacherList"
                :key="item.empNo"
                :value="item.empNo"
                :label="item.sysName"
              >
                <div slot>{{`${item.sysName} (${item.empNo})`}}</div>
              </Option>
            </Select>
          </FormItem>

          <FormItem prop="classId">
            <Input
              style="width: 160px;"
              v-model="continueClassForm.classId"
              clearable
              placeholder="班级ID"
            />
          </FormItem>

          <FormItem prop="tutorId">
            <Select
              v-model="continueClassForm.tutorId"
              clearable
              filterable
              transfer
              placeholder="辅导老师"
              label-in-value
              v-bind:class="{'global-select-loading':queryLoading.tutor}"
              @on-query-change="handleTutorName"
              @on-change="tutorChange"
              :loading="queryLoading.tutor"
            >
              <Option
                v-for="item in tutorList"
                :key="item.empNo"
                :value="item.empNo"
                :label="item.sysName"
              >
                <div slot>{{`${item.sysName} (${item.empNo})`}}</div>
              </Option>
            </Select>
          </FormItem>

          <Button size="large" type="primary" class="mr15" @click="searchListData">查找</Button>
          <Button size="large" @click="resetSearchList">重置</Button>
        </div>
      </Form>
    </Card>

    <div class="mt10">
      <Table
        @on-select="selectHandler"
        @on-select-cancel="selectCancelHandler"
        @on-select-all-cancel="selectCancelAllHandler"
        @on-select-all="selectAllHandler"
        :loading="tableLoading"
        :columns="tableColumns"
        :data="tableData"
      >
      </Table>
    </div>

    <div class="mt10" v-if="tableData.length">

      <ButtonGroup class="dib">
        <Button type="primary" ghost class="mr10" @click="exportClass('choose')" :loading="exportChooseLoading">导出选中班级
        </Button>
        <Button type="primary" ghost @click="exportClass('all')" :loading="exportAllLoading">导出全部班级</Button>
      </ButtonGroup>

      <Page
        style="float:right;padding-right: 20px;"
        show-elevator
        :total="pageTotal"
        :current="pageCurrent"
        @on-change="pageChangeHandler"
        @on-page-size-change="pageSizeChangeHandler"
        :page-size="pageSize"
        :page-size-opts="pageSizeOpts"
        show-total
        show-sizer
        transfer
      />
    </div>

  </div>
</template>

<script>

/*
  * emit 手动调用自己平台的班级 教师组件
  *   openClassDetail 班级 data
  *   openTeacherDetail 教师 data
  *
  *   exportSuccess 导出成功 跳转
  * */

import {
  queryAreaList,
  queryServiceCenterList,
  queryClassLevelList
} from '../../api/masterBD'

import {
  combineList,
  queryTeacher,
  getClassResubmitList,
  exportClassResubmitList
} from '../../api/courseBase'

import courseBase from '../../mixin/courseBase'

export default {
  name: 'TalContinueClass',
  props: {
    platform: {
      type: String, // 平台码
      required: true
    }
  },
  mixins: [courseBase],
  data () {
    return {
      queryLevelListTimer: '',
      cityIdErr: '',
      yearErr: '',
      baseList: {
        years: [],
        productType: [],
        season: [],
        grades: [],
        subjects: [],
        levelDegrees: [],
        bookVersion: []
      },
      queryLoading: {
        bookVersion: false,
        area: false,
        serviceCenter: false,
        teacher: false,
        tutor: false
      },
      queryTeacherTimer: 0,
      queryTutorTimer: 0,
      continueClassForm: {
        cityId: '',
        year: '',
        productType: '',
        categoryType: '',
        seasonId: '',
        gradeId: [],
        subjectId: '',
        levelDegree: '',
        bookVersion: '',
        bookVersionName: '',
        isContinueClass: '0',
        areaId: '',
        serviceCenterId: '',
        teacherEmpNo: '',
        classId: '',
        tutorId: '',
        tutorName: ''
      },
      queryData: {},
      tableLoading: false,
      tableColumns: [
        {
          align: 'center',
          width: 60,
          type: 'selection',
          fixed: 'left'
        },
        {
          title: '原授课类型',
          align: 'center',
          minWidth: 100,
          key: 'sourceCategoryTypeName'
        },
        {
          title: '原班级名称',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            const name = params.row.sourceClassName || ''
            const classId = params.row.sourceClassId || ''
            return h('a', {
              class: 'hover-a',
              on: {
                click: () => {
                  this.showClassDetail(classId)
                }
              }
            }, name)
          }
        },
        {
          title: '原年份',
          align: 'center',
          minWidth: 100,
          key: 'sourceYear'
        },
        {
          title: '原产品类型',
          align: 'center',
          minWidth: 100,
          key: 'sourceProductTypeName'
        },
        {
          title: '原学季',
          align: 'center',
          minWidth: 100,
          key: 'sourceSeasonName'
        },
        {
          title: '原年级',
          align: 'center',
          minWidth: 100,
          key: 'sourceGradeName'
        },
        {
          title: '原主讲教师',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            const teacherList = params.row.sourceTeacherList || []
            const list = teacherList.map(item => {
              return h('a', {
                class: 'inline-a hover-a',
                on: {
                  click: () => {
                    this.showTeacherItemDetail(params.row.cityId, item.teacherEmpNo, '0')
                  }
                }
              }, item.teacherName)
            })

            return h('div', list)
          }
        },
        {
          title: '原辅导教师',
          align: 'center',
          minWidth: 100,
          key: 'sourceTutorName'
        },
        {
          title: '原学科',
          align: 'center',
          minWidth: 100,
          key: 'sourceSubjectName'
        },
        {
          title: '原版本',
          align: 'center',
          minWidth: 100,
          key: 'sourceBookVersionName'
        },
        {
          title: '原上课时间预览',
          align: 'center',
          minWidth: 120,
          key: 'sourceClassTimeDisplay'
        },
        {
          title: '原教室',
          align: 'center',
          minWidth: 100,
          key: 'sourceClassroomName'
        },
        {
          title: '原限额人数',
          align: 'center',
          minWidth: 100,
          key: 'sourceMaxPersons'
        },
        {
          title: '续授课类型',
          align: 'center',
          minWidth: 100,
          key: 'categoryTypeName'
        },

        {
          title: '续班级名称',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            const name = params.row.className || ''
            const classId = params.row.classId || ''
            return h('a', {
              class: 'hover-a',
              on: {
                click: () => {
                  this.showClassDetail(classId)
                }
              }
            }, name)
          }
        },
        {
          title: '续年份',
          align: 'center',
          minWidth: 100,
          key: 'year'
        },
        {
          title: '续产品类型',
          align: 'center',
          minWidth: 100,
          key: 'productTypeName'
        },
        {
          title: '续学季',
          align: 'center',
          minWidth: 100,
          key: 'seasonName'
        },
        {
          title: '续年级',
          align: 'center',
          minWidth: 100,
          key: 'gradeName'
        },
        {
          title: '续主讲教师',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            const teacherList = params.row.teacherList || []
            const list = teacherList.map(item => {
              return h('a', {
                class: 'inline-a hover-a',
                on: {
                  click: () => {
                    this.showTeacherItemDetail(params.row.cityId, item.teacherEmpNo, '0')
                  }
                }
              }, item.teacherName)
            })

            return h('div', list)
          }
        },
        {
          title: '续辅导教师',
          align: 'center',
          minWidth: 100,
          key: 'tutorName'
        },
        {
          title: '续学科',
          align: 'center',
          minWidth: 100,
          key: 'subjectName'
        },
        {
          title: '续版本',
          align: 'center',
          minWidth: 100,
          key: 'bookVersionName'
        },
        {
          title: '续上课时间预览',
          align: 'center',
          minWidth: 120,
          key: 'classTimeDisplay'
        },
        {
          title: '续教室',
          align: 'center',
          minWidth: 100,
          key: 'classroomName'
        },
        {
          title: '续限额人数',
          align: 'center',
          minWidth: 100,
          key: 'maxPersons'
        }
      ],
      tableData: [],
      selectIdList: [],
      pageTotal: 0,
      pageSize: 10,
      pageSizeOpts: [10, 20, 40, 100],
      pageCurrent: 1,

      exportChooseLoading: false,
      exportAllLoading: false,

      areaList: [],
      serviceCenterList: [],
      teacherList: [],
      tutorList: []
    }
  },
  methods: {
    cityChange (val) {
      console.log(val)
      if (val) {
        this.cityIdErr = ''
        this.getBaseData()
        this.queryAreaAjax()
      }
      this.tableData = []
      this.queryData = {}
    },
    setDefaultCityCode () {
      if (this.cityList.length === 1) {
        this.continueClassForm.cityId = this.cityList[0].value
        console.log(this.continueClassForm.cityId)
        this.cityChange(this.continueClassForm.cityId)
        this.setDefaultYear()
        this.searchListDataFn()
      }
    },
    setDefaultYear () {
      this.continueClassForm.year = new Date().getFullYear() + ''
    },
    levelDegreeListChange (val) {
      if (this.isEmptyObject(val)) {
        this.continueClassForm.levelDegree = ''
        this.continueClassForm.levelDegreeName = ''
      } else {
        this.continueClassForm.levelDegree = val.value
        this.continueClassForm.levelDegreeName = val.label
      }
      this.getClassLevelList('')
      this.continueClassForm.bookVersion = ''
      this.continueClassForm.bookVersionName = ''
      this.baseList.bookVersion = []
    },
    classLevelListChange (val) {
      console.log(val)
      if (this.isEmptyObject(val)) {
        this.continueClassForm.bookVersion = ''
        this.continueClassForm.bookVersionName = ''
      } else {
        this.continueClassForm.bookVersion = val.value
        this.continueClassForm.bookVersionName = val.label
      }
    },
    getClassLevelListLinkName (name) {
      console.log(name)
      clearTimeout(this.queryLevelListTimer)
      this.queryLevelListTimer = setTimeout(() => {
        this.getClassLevelList(name)
      }, 400)
    },
    getClassLevelList (name) {
      console.log(name)

      name = name || this.continueClassForm.bookVersionName

      if (this.continueClassForm.cityId) {
        return new Promise((resolve, reject) => {
          this.queryLoading.bookVersion = true
          queryClassLevelList({
            schoolCode: this.continueClassForm.cityId,
            status: 0,
            degree: this.continueClassForm.levelDegree,
            current: 1,
            disable: 0,
            size: 100,
            name: name || ''
          }).then(res => {
            if (res) {
              this.baseList.bookVersion = res.records
              console.log(this.baseList.bookVersion)
              resolve()
            }
          }).catch((err) => {
            this.$Message.error(err.message || '获取版本列表失败')
            reject()
          }).finally(() => {
            this.queryLoading.bookVersion = false
          })
        })
      } else {
        console.log('cityId 暂未加载')
        console.log(this.continueClassForm)
        console.log(this.baseList.bookVersion)
      }
    },
    searchListData () {
      let flag = true
      if (!this.continueClassForm.cityId) {
        this.cityIdErr = '请选择分校'
        flag = false
      }

      if (!this.continueClassForm.year) {
        this.yearErr = '请选择年份'
        flag = false
      }

      if (!flag) {
        return
      }

      this.pageCurrent = 1
      // this.pageSize = 10
      this.selectIdList = []

      this.searchListDataFn()
    },
    yearChange (val) {
      if (val) {
        this.yearErr = ''
      }
    },
    resetSearchList () {
      this.$refs['continueClassForm'].resetFields()
      this.setDefaultCityCode()
      this.setDefaultYear()
      this.queryData = {}
    },
    searchListDataFn () {
      if (!this.continueClassForm.cityId) {
        return
      }

      if (!this.continueClassForm.year) {
        return
      }

      this.queryData = {
        ...this.continueClassForm,
        current: this.pageCurrent,
        size: this.pageSize
      }

      this.tableLoading = true
      getClassResubmitList(this.queryData).then(res => {
        if (res) {
          const data = res || {}
          this.pageTotal = data.total || 0
          const list = data.records || []
          this.tableData = list.map(item => {
            item._checked = this.selectIdList.indexOf(item.id) !== -1
            return item
          })
        }
      }).catch((err) => {
        this.tableData = []
        this.pageTotal = 0
        this.$Message.error(err.message || '获取续排班列表失败')
      }).finally(() => {
        this.tableLoading = false
      })
    },
    initYear () {
      combineList({
        yearsReq: {}
      }).then(res => {
        this.baseList.years = res.years || []
        this.setDefaultYear()
      }).catch((err) => {
        this.$Message.error(err.message || '获取年份失败')
      })
    },
    getBaseData () {
      combineList({
        schoolCode: this.continueClassForm.cityId,
        propertiesReq: {
          enNames: [
            'categoryType',
            'productType',
            'season'
          ]
        },
        gradesReq: {},
        subjectsReq: {},
        levelDegreesReq: {}
      }).then(res => {
        if (res) {
          const results = res
          if (!results) return
          let arr = results.properties
          arr.forEach((val) => {
            results[val.enName] = val.value ? JSON.parse(val.value) : []
          })

          this.baseList = {
            ...this.baseList,
            ...results
          }
        }
      }).catch((err) => {
        this.$Message.error(err.message || '获取字典失败')
      })
    },
    selectHandler (data, row) {
      this.selectIdList.push(row.id)
    },
    selectCancelHandler (data, row) {
      const i = this.selectIdList.indexOf(row.id)
      this.selectIdList.splice(i, 1)
    },
    selectAllHandler () {
      const list = this.tableData.map(item => {
        return item.id
      })

      const joinList = new Set(
        [
          ...this.selectIdList,
          ...list
        ]
      )
      this.selectIdList = Array.from(joinList)
    },
    selectCancelAllHandler () {
      this.tableData.forEach(item => {
        const i = this.selectIdList.indexOf(item.id)
        this.selectIdList.splice(i, 1)
      })
    },
    pageChangeHandler (page) {
      this.pageCurrent = page
      this.searchListDataFn()
    },
    pageSizeChangeHandler (pageSize) {
      this.pageSize = pageSize
      this.searchListDataFn()
    },
    exportClass (type) {
      if (this.isEmptyObject(this.queryData)) {
        this.$Message.error('请先进行查询操作')
        return
      }

      if (type === 'choose' && this.selectIdList.length === 0) {
        this.$Message.error('请选择要导出的班级')
        return
      }
      const params = type === 'choose' ? {
        ...this.queryData,
        ids: this.selectIdList
      } : {
        ...this.queryData
      }

      if (type === 'choose') {
        this.exportChooseLoading = true
      } else {
        this.exportAllLoading = true
      }

      console.log(params)

      exportClassResubmitList(params).then(res => {
        if (res) {
          this.fileDownLoadAlert(this.platform)
        } else {
          this.$Message.error(res.data.message || '导出失败')
        }
      }).catch(() => {
        this.$Message.error('调用续排班导出接口失败')
      }).finally(() => {
        this.$nextTick(() => {
          if (type === 'choose') {
            this.exportChooseLoading = false
          } else {
            this.exportAllLoading = false
          }
        })
      })
    },
    showClassDetail (classId) {
      if (classId) {
        this.$emit('openClassDetail', {
          classId: classId,
          flag: true
        })
      }
    },
    showTeacherItemDetail (schoolCode, empNo, type) {
      console.log(schoolCode, empNo, type)

      this.$emit('openTeacherDetail', {
        schoolCode,
        empNo,
        type,
        flag: true
      })
    },
    areaChange (val) {
      if (val) {
        this.continueClassForm.areaId = val.value
        this.continueClassForm.areaName = val.label
        this.queryServiceCenterAjax(val.value)
      } else {
        this.continueClassForm.areaId = ''
        this.continueClassForm.areaName = ''
      }
    },
    queryAreaAjax () {
      if (!this.continueClassForm.cityId) {
        return
      }

      this.queryLoading.area = true

      queryAreaList({
        schoolCode: this.continueClassForm.cityId,
        status: 0
      }).then(res => {
        if (res) {
          const data = res || []

          this.areaList = data.map(item => {
            return {
              value: item.schoolId,
              label: item.name
            }
          })
        }
      }).finally(() => {
        this.queryLoading.area = false
      })
    },
    queryServiceCenterAjax (val) {
      if (!this.continueClassForm.cityId) {
        return
      }

      return new Promise((resolve, reject) => {
        this.queryLoading.serviceCenter = true

        queryServiceCenterList({
          areaid: val,
          schoolCode: this.continueClassForm.cityId,
          status: 0
        }).then(res => {
          if (res) {
            const data = res || []

            this.serviceCenterList = data.map(item => {
              return {
                value: item.schoolId,
                label: item.name
              }
            })
            resolve()
          } else {
            this.$Message.error(res.data.message || '获取服务中心失败')
            reject()
          }
        }).catch(() => {
          this.$Message.error('调用服务中心接口失败')
          reject()
        }).finally(() => {
          this.queryLoading.serviceCenter = false
        })
      })
    },
    handleTeacherName (value) {
      if (!value.trim()) {
        return
      }

      if (!this.continueClassForm.cityId) {
        this.$Message.error('请选择分校')
        this.cityIdErr = '请选择分校'
        return
      }
      let payloads = {
        schoolCode: this.continueClassForm.cityId,
        teacherType: 0,
        pageNo: 1,
        pageSize: 100,
        sysName: value
      }
      clearTimeout(this.queryTeacherTimer)
      this.queryTeacherTimer = setTimeout(() => {
        this.queryLoading.teacher = true
        queryTeacher(payloads).then(res => {
          if (res) {
            this.teacherList = res.rows
          }
          this.queryLoading.teacher = false
        }).catch(err => {
          this.$Message.error(err.message || '获取教师失败')
        }).finally(() => {
          this.queryLoading.teacher = false
        })
      }, 400)
    },
    handleTutorName (value) {
      if (!value.trim()) {
        return
      }

      if (!this.continueClassForm.cityId) {
        this.$Message.error('请选择分校')
        this.cityIdErr = '请选择分校'
        return
      }
      let payloads = {
        schoolCode: this.continueClassForm.cityId,
        teacherType: 1,
        pageNo: 1,
        pageSize: 100,
        sysName: value
      }
      clearTimeout(this.queryTutorTimer)
      this.queryTutorTimer = setTimeout(() => {
        this.queryLoading.tutor = true
        queryTeacher(payloads).then(res => {
          if (res) {
            this.tutorList = res.rows
          }
          this.queryLoading.tutor = false
        }).catch(err => {
          this.$Message.error(err.message || '获取辅导失败')
        }).finally(() => {
          this.queryLoading.tutor = false
        })
      }, 400)
    },
    tutorChange (val) {
      console.log(val)
      this.continueClassForm.tutorId = val ? val.value : ''
      this.continueClassForm.tutorName = val ? val.label : ''
    }
  },
  mounted () {
    this.getCityList().then(() => {
      this.initYear()
      this.setDefaultCityCode()
    })
  },
  watch: {
    '$route' (to) {
      if (to.name === 'applyContinue' || to.name === 'ContinueClassManagement') {
        setTimeout(() => {
          if (window.XesAnalytics && window.XesAnalytics.track) {
            XesAnalytics.view(
              '15-3540',
              {
                platform: this.platform
              }
            )
          }

          if (window.xesAnalyLog && window.xesAnalyLog.track) {
            xesAnalyLog.view(
              '15-3540',
              {
                platform: this.platform
              }
            )
          }
        }, 200)
      }
    }
  }
}
</script>

