import { verificationCodeSend } from '../api/student'

export default {
  data () {
    return {
      sendBtn: false, // 验证码已发送
      msgNum: '', // 发送验证码后的倒计时
      smsCode: '' // 验证码
    }
  },
  methods: {
    // 发送验证码
    sendMessageHandler (phone, mode) {
      if (!this.sendBtn) {
        if (!phone) {
          this.$Message.warning({
            content: '请输入手机号',
            duration: 3
          })
          return
        }
        this.sendBtn = true
        this.msgNum = 30
        let set
        let req = {
          phone,
          mode // 模式，regCode【注册】bindCode【绑定手机】
        }
        verificationCodeSend(req)
          .then(res => {
            this.$Message.success({ content: '发送成功', duration: 3 })
            set = setInterval(() => {
              if (this.msgNum <= 0) {
                clearInterval(set)
                this.sendBtn = false
              } else {
                this.msgNum -= 1
              }
            }, 1000)
          })
          .catch(err => {
            clearInterval(set)
            this.sendBtn = false
            this.$Message.error({
              content: err.message,
              duration: 5
            })
          })
      }
    },
    resetSendBtn () {
      this.sendBtn = false
    }
  }
}
