import $ from '../utils/api.request'

/**
 * 发票助手-查询列表
 * @param { String } studentUid 学员编号
 * @param { String } cityCode 城市编号
 * @param { String } termId 学期
 * @param { Number } classType 授课类型
 * @param { String } year 学员编号
 * @param { Number } type 班级状态 1 在读 2退费 3结课
 */
export function queryInvoiceList (params) {
  return $.splatAxios.request({
    url: 'invoice/queryInvoiceList',
    method: 'post',
    data: params
  })
}
// 发票详情
export const queryInvoiceDetail = params => {
  return $.splatAxios.request({
    url: '/invoice/queryInvoiceDetail',
    data: params,
    method: 'post'
  })
}
