<template>
<div>
    <div style="padding: 0 5px 5px 5px;" v-if="isClassId">
        <Row>
            <Col span="10">
            <span>班级信息:</span>
            <span class="stuRight">{{
            classInfoData.year +
              "+" +
              classInfoData.termCustomName +
              "+" +
              classInfoData.gradeCustomName +
              "+" +
              classInfoData.subjectCustomName +
              "+" +
              classInfoData.bookVersionName +
              "+" +
              classTypeMap[classInfoData.categoryType]
          }}</span>
            </Col>
            <!-- <Col span="7">
            <span>已上 / 总课次:</span>
            <span class="stuRight ivu-info-color">{{ oldClassInfo.passedCount }} /
                {{ oldClassInfo.classCount }}</span>
            </Col> -->
            <!-- <Col span="7">
            <span>总金额:</span>
            <span class="stuRight">{{ oldClassInfo.price }}</span>
            </Col> -->
        </Row>
        <Row style="margin-top: 10px">
            <Col span="10">
            <span>教师:</span>
            <span class="stuRight ivu-info-color">{{
            classInfoData.tutorSysName
          }}</span>
            </Col>
            <Col span="7">
            <span>辅导老师:</span>
            <span class="stuRight">{{ classInfoData.tutorRealName }}</span>
            </Col>
            <Col span="7">
            <span>教室:</span>
            <span class="stuRight ivu-info-color">{{
            classInfoData.classroomName
          }}</span>
            </Col>
        </Row>
        <Row style="margin-top: 10px">
            <!-- <Col span="10">
            <span>上课时间:</span>
            <span class="stuRight">{{ classInfoData.classtimeNames }}</span>
            </Col> -->
            <Col span="7">
            <span>开课日期:</span>
            <span class="stuRight">{{ classInfoData.startDate }}</span>
            </Col>
            <Col span="7">
            <span>结课日期:</span>
            <span class="stuRight">{{ classInfoData.endDate }}</span>
            </Col>
        </Row>
    </div>
    <div style="position: relative" @keydown.enter="search">
        <Form ref="form" :model="formData" label-position="right" inline>
            <div class="class_top">
                <FormItem>
                    <Select v-model="formData.cityCode" placeholder="城市" filterable style="width: 120px;" @on-change="cityYearChange">
                        <Option v-for="(option,index) in cityWithoutAllCity" :value="option.id" :key="index">{{option.name}}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.year" placeholder="年份" style="width: 120px;" @on-change="cityYearChange">
                        <Option v-for="(item, index) in yearList" :value="item" :key="index">{{ item }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.termId" placeholder="学期" style="width: 120px;" @on-change="termChange" clearable>
                        <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.gradeTypeId" placeholder="年部" style="width: 120px;" @on-change="gradeTypeChange" clearable>
                        <Option v-for="(item,index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.gradeId" placeholder="年级" style="width: 120px;" @on-change="gradeChange" clearable>
                        <Option v-for="(item,index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.subjectId" placeholder="学科" style="width: 120px;" @on-change="subjectChange" clearable>
                        <Option v-for="(item,index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.levelId" placeholder="班次" clearable style="width: 120px;">
                        <Option v-for="(option,index) in levelList" :value="option.id" :key="index">{{option.name}}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.areaId" placeholder="区域" @on-change="changeArea" clearable style="width:120px">
                        <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.serviceCenterId" placeholder="服务中心" @on-change="changeService" clearable style="width:120px">
                        <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.venueId" placeholder="教学点" clearable style="width:120px">
                        <Option v-for="(item,index) in venueList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.classType" placeholder="在线" style="width: 120px;" clearable disabled>
                        <Option v-for="(item,index) in classTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.teacherId" placeholder="老师姓名" filterable clearable remote @on-change="optionChange" :remote-method="getTeacher" label-in-value style="width: 120px;" :loading="loading1">
                        <Option v-for="option in teacherNameList" :value="option.empNo" :key="option.teaId">{{option.sysName}}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.tutorId" placeholder="辅导老师" filterable clearable remote @on-change="optionChange" :remote-method="getTutor" label-in-value style="width: 120px;" :loading="loading1">
                        <Option v-for="option in tutorNameList" :value="option.empNo" :key="option.teaId">{{option.sysName}}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.classtimeTypeClassify" placeholder="上课时间类型" clearable style="width:120px">
                        <Option v-for="(item,index) in classtimeTypeList" :value="item.name" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Select v-model="formData.classtimeTimePeriod" placeholder="上课时间" clearable style="width:120px">
                        <Option v-for="(item,index) in classtimeList" :value="item.name" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem>
                    <Button type="primary" style="margin-right: 10px;" @click="search" :loading="tableLoading">查询班级</Button>
                </FormItem>
            </div>
        </Form>
    </div>
    <div class="class_table">
        <Table :columns="tableColumns" :data="tableData">
            <template slot-scope="{ row }" slot="action" v-if="row.categoryType == '1' && row.passedCount > 0">
                <a @click="sendVideo(row)">发送回放</a>
            </template>
        </Table>
        <div style="text-align: right">
            <Page :total="pageTotal" :page-size="currentPageSize" :current="currentPageNum" :page-size-opts='pages' show-elevator show-sizer show-total @on-page-size-change="pageSizeChange" @on-change="changePage"></Page>
        </div>
    </div>
    <TalClassDetail :class-id="showClassId" :show="showClassStatus" @close="showClassStatus = false" />
    <TalTeacherDetail ref="teacherRef" :schoolCode="formData.cityCode || ''" :teacherId="teacherDetail.id" :teacherEmpNo="teacherDetail.empNo" :type="'0'" />
    <Modal :mask-closable="false" title="发放回放课链接-选课次" v-model="modalValue" width="960">
        <TalSendPlaybackBysessions v-if="modalValue" @success="sendSuccess" :classInfo="classInfo" :creatorEmpNo="creatorEmpNo" :studentUid="studentUid" :cityCode="cityCode" />
    </Modal>
</div>
</template>

<script>
import {
  queryClassInfo,
  queryClassInfoList
} from '../../api/clazz'
import BaseDataMixin from '../../mixin/baseData'
import TalSendPlaybackBysessions from '../tal-send-playback-bysessions'
import TalClassDetail from '../tal-class-details'
import TalTeacherDetail from '../tal-teacher-details'
export default {
  name: 'TalSendPlaybackByclass',
  mixins: [BaseDataMixin],
  components: {
    TalSendPlaybackBysessions,
    TalClassDetail,
    TalTeacherDetail
  },
  props: {
    // 原班信息
    oldClassInfo: {
      type: Object,
      default: function () {
        return {}
      }
    },
    pages: {
      type: Array,
      default: function () {
        return [10, 20, 30, 40]
      }
    },
    cityCode: {
      type: String,
      default: '010'
    },
    studentUid: {
      type: String,
      default: ''
    },
    classId: {
      type: String,
      // default: '5136ba2fddaa4f1a90b51f4fb1f5c64f'
      default: ''
    },
    creatorEmpNo: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      cityWithoutAllCity: [],
      isClassId: false,
      showClassStatus: false,
      showClassId: '',
      teacherDetail: {
        cityCode: '',
        id: '',
        type: '',
        studentId: '',
        empNo: ''
      },
      showTeacherStatus: false,
      formData: {
        year: new Date().getFullYear() + '', // 必填
        cityCode: '', // 必填
        termId: Math.ceil((new Date().getMonth() + 1) / 3),
        gradeTypeId: '',
        gradeId: '',
        subjectId: '',
        levelId: '', // 班次
        areaId: '',
        serviceCenterId: '',
        venueId: '', // 教学点
        classType: '1', // 授课类型
        teacherId: '', // 主讲老师
        tutorId: '', // 辅导老师
        classtimeTypeClassify: '', // 上课时间类型id
        classtimeTimePeriod: '', // 上课时间id
        studentCard: '', // 学员编号
        studentRealName: '', // 学员姓名
        registDateStart: '', // 报名起始时间
        registDateEnd: '', // 报名截止时间
        payEndDateStart: '', // 最晚缴费时间起始
        payEndDateEnd: '', // 最晚缴费时间截止
        isSchoolTest: '', // 是否进行入学测试0否，1是
        newStudent: '', // 是否新学员0否，1是
        registWay: '', // 注册平台
        stagesNum: '' // 未交费分期
      },
      loading1: false,
      tableLoading: false,
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      pageTotal: 0, // 总条数
      pageNo: 1,
      pageSize: 20,
      pageNum: '',
      tableData: [],
      allCityList: [], // 分校
      allCityMap: {}, // 分校
      yearList: [], // 年份
      termList: [], // 学期
      gradeTypeList: [], // 年部
      subjectList: [], // 学科
      gradeList: [], // 年级
      levelList: [], // 班次
      areaList: [], // 区域
      serviceCenterList: [], // 服务中心
      venueList: [], // 教学点
      classTypeList: [], // 授课类型
      classtimeTypeList: [], // 上课时间类型
      teacherNameList: [], // 老师列表
      tutorNameList: [], // 辅导老师列表
      classStageList: [], // 未缴费分期
      registWay: [], // 报名平台
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授',
        16: '自学课'
      },
      checkAll: false,
      allItem: [],
      checkAllGroup: [],
      classInfoData: '',
      tableColumns: [{
        title: '班级名称',
        key: 'className',
        minWidth: 100,
        align: 'center',
        render: (h, params) => {
          return h(
            'a', {
              props: {
                href: 'javascript:void(0)'
              },
              on: {
                click: () => {
                  this.showClassDetail(params)
                }
              }
            },
            params.row.className
          )
        }
      },
      {
        title: '授课类型',
        key: 'classType',
        align: 'center',
        minWidth: 50,
        render: (h, params) => {
          return h(
            'span', {
              style: {}
            },
            this.getClassTypeName(params.row.categoryType)
          )
        }
      },
      {
        title: '学科',
        key: 'subjectName',
        align: 'center',
        minWidth: 50
      },
      {
        title: '主讲教师',
        key: 'teacherName',
        align: 'center',
        minWidth: 50,
        render: (h, params) => {
          return h(
            'a', {
              props: {
                href: 'javascript:void(0)'
              },
              on: {
                click: () => {
                  this.$nextTick(() => {
                    this.teacherDetail.empNo = params.row.teachers[0]['teacherEmpNo']
                    this.$refs.teacherRef.show()
                  })
                }
              }
            },
            params.row.teachers[0]['teacherName']
          )
        }
      },
      {
        title: '辅导教师',
        key: 'tutorRealName',
        align: 'center',
        minWidth: 50
      },
      {
        title: '开课日期',
        key: 'startDate',
        align: 'center',
        minWidth: 70
      },
      {
        title: '结课日期',
        key: 'endDate',
        align: 'center',
        minWidth: 70
      },
      {
        title: '上课时间',
        align: 'center',
        minWidth: 100,
        key: 'classTimeDisplay'
      },
      {
        title: '服务中心',
        key: 'classroomServiceCenterName',
        align: 'center',
        minWidth: 100
      },
      {
        title: '上课进度',
        align: 'center',
        minWidth: 50,
        render: (h, params) => {
          return h(
            'span',
            params.row.passedCount + '/' + params.row.classCount
          )
        }
      },
      {
        title: '操作',
        minWidth: 80,
        align: 'center',
        slot: 'action'
      }
      ],
      modalValue: false,
      classInfo: {}
    }
  },
  watch: {
    allCityList (val) {
      // if (val && val.length) {
      //   this.formData.cityCode = this.cityCode
      //   this.cityYearChange(this.formData.cityCode)
      //   if (!this.classId) {
      //     this.formData.termId = (Math.ceil((new Date().getMonth() + 1) / 3)).toString()
      //     this.termChange(this.formData.termId)
      //     this.getTableData()
      //   }
      // }
      this.cityWithoutAllCity = val.filter(obj => obj.id !== '1111')
    }
  },
  mounted () {
    this.formData.cityCode = this.cityCode
    this.$nextTick()
    this.cityYearChange(this.formData.cityCode)
    if (this.classId) {
      this.getQueryClassInfo()
    } else {
      this.formData.termId = (Math.ceil((new Date().getMonth() + 1) / 3)).toString()
      this.termChange(this.formData.termId)
      this.getTableData()
    }
    this.getYearListHandler()
    this.getCityListHandler()
    // this.getClassTypeHandler()
    this.getTimeTypeListHandler()
    this.getTimeListHandler()
  },
  methods: {
    // 获取班级信息
    getQueryClassInfo () {
      queryClassInfo({
        'classId': this.classId
      }).then(res => {
        if (res && this.classId) {
          this.isClassId = true
        }
        this.classInfoData = res
        this.formData.year = res.year
        this.formData.termId = res.termCustomId.toString()
        this.termChange(this.formData.termId)
        this.getTableData()
      }).catch(err => {
        this.$Message.warning({
          content: err,
          duration: 5
        })
      })
    },
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        this.checkAllGroup = this.allItem
      }
    },
    checkCheckboxStatus () {
      this.checkAll = (this.checkAllGroup.length === this.allItem.length)
    },
    // 变动记录
    getNameLength (name, len = 15) {
      if (name && name.length > len) {
        return name.substring(0, len) + '...'
      } else {
        return name
      }
    },
    showStuDetail (row) {
      this.stuDetail.cityCode = row.cityCode
      this.stuDetail.uid = row.uid
      this.stuDetail.show = true
    },
    queryNotPayStageListHandler () {
      queryNotPayStageList().then(res => {
        this.classStageList = res
      })
    },
    registerPlatformList () {
      queryRegisterPlatformList().then(res => {
        this.registWay = res
      })
    },
    // 授课类型
    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    closeParDetail () {
      this.payDetailModal = false
      this.parDetailList = []
    },
    search () {
      this.currentPageNum = 1
      this.getTableData()
    },
    getTableData () {
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择所在城市')
        return
      }
      if (!this.formData.year) {
        this.$Message.warning('请选择年份')
        return
      }
      if (!this.formData.termId) {
        this.$Message.warning('请选择学期')
        return
      }
      if (this.payDateStart && this.payDateEnd && this.payDateStart > this.payDateEnd) {
        this.$Message.warning('缴费截止日期错误：开始时间不能晚于结束时间')
        return
      }
      this.formData.pageSize = this.currentPageSize
      this.formData.pageNo = this.currentPageNum
      this.tableLoading = true
      let areaName = ''
      if (this.formData.areaId) {
        areaName = this.areaList.filter(a => a.schoolId === this.formData.areaId)[0].name
      }

      this.checkAll = false
      this.allItem = []
      this.tableLoading = true
      this.checkAllGroup = []
      let req = Object.assign({}, this.formData, {
        cityId: this.formData.cityCode,
        categoryType: this.formData.classType,
        gradeCustomId: this.formData.gradeId,
        period: this.formData.classtimeTimePeriod,
        startDateFrom: this.formData.startDate,
        startDateTo: this.formData.startDateTo,
        classroomVenueId: this.formData.venueId,
        classroomAreaId: this.formData.areaId,
        classroomServiceCenterId: this.formData.servicecenterId,
        classTimeTypeId: this.formData.classtimeTimeClassify,
        subjectCustomIdList: this.formData.subjectId === '' ? [] : [this.formData.subjectId],
        className: this.formData.name,
        bookVersion: this.formData.levelId,
        teacherEmpNo: this.formData.teacherId,
        tutorEmpNo: this.formData.tutorId,
        termCustomId: this.formData.termId

      })
      req.subjectIds = this.formData.subjectId === '' ? [] : [this.formData.subjectId]
      req.pageNo = this.currentPageNum
      req.pageSize = this.currentPageSize
      req.highlightPreTag = '<span style="color: red">'
      req.highlightPostTag = '</span>'

      queryClassInfoList(Object.assign({}, req, {
        areaName
      })).then(res => {
        if (res.records.length === 0) {
          this.$Message.warning('暂无数据')
        }
        this.pageTotal = res.totalCount
        this.cancelOrderErrorMap = {}
        this.cancelRegisterIds = '' // 清空选中项
        this.checkAllGroup = [] // TODO xxx
        let cityName = ''
        this.allCityList.forEach(res => {
          if (res.id === this.formData.cityCode) {
            cityName = res.name
          }
        })
        this.studentDetail = {
          cityCode: this.formData.cityCode,
          cityName: cityName
        }
        res.records.forEach((e) => {
          if (this.allItem.indexOf(e.registId) < 0) {
            this.allItem.push(e.registId)
          }
          this.cancelOrderErrorMap[e.registId] = e.className
          e.cityName = cityName
          if (e.startDate) {
            e.startDate = e.startDate.substring(0, 10)
          }
          if (e.endDate) {
            e.endDate = e.endDate.substring(0, 10)
          }
          e.Application = (e.maxPersons - e.remainNum) + '/' + e.maxPersons
        })
        this.tableData = Object.freeze(res.records)
        this.tableLoading = false
      }).catch(err => {
        this.tableData = []
        this.tableLoading = false
        this.$Message.warning({
          content: err,
          duration: 5
        })
      })
    },
    // 缴费详情页
    platformDetail (data) {
      let req = {
        cityCode: data.cityCode,
        registId: data.registId
      }
      queryStudentRegisterStage(req).then(res => {
        if (res) {
          this.parDetailList.push(res)
          this.parDetailList.forEach((e) => {
            if (e.stagesInfos && e.stagesInfos.length) {
              e.rowspan = e.stagesInfos.length
            }
          })
        }
      }).catch(err => {
        this.$Message.error({
          content: '获取缴费详情失败' + err.message,
          duration: 5
        })
      })
      this.payDetailModal = true
    },
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // cityCode
      let toNum = {
        cityCode: this.formData.cityCode
      }
      this.getAreaListHandler(toNum).then(res => {
        this.areaList = res
        if (this.needInitArea) {
          queryServiceCenterByEmpNo({
            cityCode: this.formData.cityCode,
            empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
          }).then(data => {
            this.formData.areaId = data.areaId
            this.changeArea(data.areaId, data.serviceCenterId)
          }).catch(err => {
            this.$Message.error('获取当前所在服务中心失败:' + err.message)
          })
        }
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
    },
    cityYearChange (val) { // 改变城市
      this.formData.termId = ''
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.venueId = ''
      this.formData.classtimeTimePeriod = ''
      this.formData.classtimeTypeClassify = ''
      this.termList = []
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      this.areaList = []
      this.serviceCenterList = []
      this.venueList = []
      if (!val) {
        return false
      }

      if (!this.formData.cityCode) {
        this.$Message.warning('请选择城市')
        return false
      }
      this.getBaseData()
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      this.getLinkageHandler(req).then(res => {
        this.termList = res
      }).catch(err => {
        this.$Message.error('获取学期失败:' + err.message)
      })
    },
    termChange (val) { // 改变学期
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeTypeList = res
      }).catch(err => {
        this.$Message.error('获取年部失败:' + err.message)
      })
    },
    gradeTypeChange (val) { // 改变年部
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      this.getLinkageHandler(req).then(res => {
        this.gradeList = res
      }).catch(err => {
        this.$Message.error('获取年级失败:' + err.message)
      })
    },
    gradeChange (val) { // 改变年级
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      this.getLinkageHandler(baseSubject).then(res => {
        this.subjectList = res
      }).catch(err => {
        this.$Message.error('获取学科失败:' + err.message)
      })
    },
    subjectChange (val) { // 改变学科
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeId,
        subjectIds: this.formData.subjectId,
        queryType: 'c'
      }
      this.getLinkageHandler(req).then(res => {
        this.levelList = res
      }).catch(err => {
        this.$Message.error('获取班次失败:' + err.message)
      })
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
        }
      }).catch(err => {
        this.$Message.warning('获取服务中心失败:' + err.message)
      })
    },
    changeService (val) { // 改变服务中心
      this.venueList = []
      this.formData.venueId = ''
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        this.teacherNameList = Object.freeze(res.records)
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    // 获取辅导老师
    getTutor (query) {
      if (!this.formData.cityCode) {
        this.$Message.warning('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.tutorNameList = []
      let req = {
        sysName: query,
        teacherType: 1,
        cityCode: this.formData.cityCode,
        businessType: this.formData.teacherType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        // this.tutorNameList = res.records  TODO @@@
        this.tutorNameList = Object.freeze(res.records)
        this.loading1 = false
      }).catch(err => {
        this.loading1 = false
        this.$Message.error('获取辅导老师姓名失败:' + err.message)
      })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },
    payDateStartChange (val) {
      this.formData.payEndDateStart = val
    },
    payDateEndChange (val) {
      this.formData.payEndDateEnd = val
    },
    // 报班日期日期
    registDateChange (val) {
      this.formData.registDateStart = val[0]
      this.formData.registDateEnd = val[1]
    },
    // 撤单
    cancelOrder (isAll, item) {
      let req = {}
      if (isAll) {
        if (!this.checkAllGroup.length) {
          this.$Message.warning({
            content: '未选择班级',
            duration: 5
          })
          return
        }
        req.registIds = this.checkAllGroup.join(',')
        // req.registIds = this.cancelRegisterIds
      } else {
        if (item && item.registId) {
          req.registIds = item.registId
        } else {
          this.$Message.warning({
            content: '未选择班级',
            duration: 5
          })
          return
        }
      }
      req.cityCode = this.tableData[0].cityCode
      this.$Modal.confirm({
        title: '撤单',
        content: '是否确认撤单？',
        onOk: () => {
          cancelRegisters(req).then((res) => {
            if (Object.keys(res.errorMap).length > 0) {
              for (let i in res.errorMap) {
                this.$Message.error({
                  content: '[ ' + this.cancelOrderErrorMap[i] + ' ]' + '撤单失败 :' + res.errorMap[i],
                  duration: 10,
                  closable: true
                })
              }
            } else {
              this.$Message.success({
                content: '撤单成功',
                duration: 5
              })
            }
            this.getTableData()
          }).catch((err) => {
            this.$Message.warning({
              content: '撤单失败 :' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    showClassDetail (row) {
      this.showClassStatus = true
      this.showClassId = row.row.classId
    },
    showTeacher (row) {
      this.showTeacherStatus = true
      console.log(row)
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    sendVideo (row) {
      console.log(row)
      this.classInfo = row
      this.modalValue = true
    },
    sendSuccess () {
      this.modalValue = false
    }
  }
}
</script>

<style lang="less" scoped>
.stuRight {
    margin-left: 10px;
}

.colorRed {
    color: red;
}
</style>
