 * 报名信息查询 包含剩余名额 学员名单 查询、导出
 *
 * 必传入参：
 * platform 平台字段
 *   aladdin	阿拉丁系统
 *   auth	权限系统
 *   call-center 呼叫平台
 *   course	班课管理
 *   marketing	营销平台
 *   mplattraderegist	乐高报名
 *   register	报名系统
 *   room	教室平台
 *   splat	盘古平台
 *   teacher	教学平台
 *   workflow	工作流系统
 *
 * emit
 *   exportSuccess 导出成功进度回调 处理跳转逻辑
 * */

import {
  linkQueryByBD,
  queryTeacher,
