  },
  mixins: [courseBase],
  methods: {

    initYear () {
      return new Promise(resolve => {
        combineList({
          yearsReq: {}
        }).then(res => {
          if (res) {
            let results = res || {}
            this.baseList.years = results.years || []
            this.setFormYear()
          } else {
            this.$Message.error('获取年份失败')
          }

          this.loadingMap.queryList = false
          resolve()
        }).catch((err) => {
          this.loadingMap.queryList = false
          this.$Message.error(err.message || '获取年份失败')
        })
      })
    },
    getBaseList () {
      combineList({
        propertiesReq: {
          enNames: [
            'categoryType',
            'contentType',
            'courseType',
            'status',
            'sellChannel',
            'period',
            'week',
            'classTimeType',
            'limitLabel'
          ]
        }
      }).then(res => {
        if (res) {
          let results = res
          let arr = results.properties
          arr.forEach((val) => {
            results[val.enName] = val.value ? JSON.parse(val.value) : []
          })
          this.baseList = {
            ...this.baseList,
            ...results
          }
        } else {
          this.$Message.error('字典列表获取失败')
        }

        this.loadingMap.queryList = false
      }).catch((err) => {
        this.loadingMap.queryList = false
        this.$Message.error(err.message || '字典列表获取失败')
      })
    },
    setFormYear () {
      // 设置默认的年份
      var date = new Date()
      var month = date.getMonth() + 1
      // 年份：每年3-12月显示当年年份；每年1-2月显示去年年份；
      if (month === 1 || month === 2) {
        this.siFromData.year = (date.getFullYear() - 1) + ''
      } else {
        this.siFromData.year = date.getFullYear() + ''
      }
    },
    cityChange (val) {
      if (val) {
        this.getBaseList()
      }
    },
    productTypeChange (val) {
      if (val) {
        this.formItemErr.productType = ''
        this.reQueryFlag = true
      }
    },
    seasonChange (val) {
      if (val) {
        this.formItemErr.seasonId = ''
        this.reQueryFlag = true
      }
    },
    gradeChange (val) {
      if (val) {
        this.formItemErr.gradeId = ''
        this.reQueryFlag = true
      }
      this.siFromData.gradeName = (val && val.label) || ''
    },
    subjectChange (val) {
      if (val) {
        this.formItemErr.subjectId = ''
        this.reQueryFlag = true
      }
      this.siFromData.subjectName = (val && val.label) || ''
    },
    linkageFn (type) {
      let queryType = ''
      switch (type) {
        case 'productType':
          queryType = 'p'
          break
        case 'seasonId':
          queryType = 'se'
          break
        case 'gradeIds':
          queryType = 'g'
          break
        case 'subjectId':
          queryType = 's'
          break
        case 'levelDegree':
          queryType = 'l'
          break
        case 'bookVersion':
          queryType = 'b'
          break
        default:
          queryType = ''
      }

      if (!queryType || !this.siFromData.cityId) {
        return
      }

      const fn = (bool) => {
        switch (type) {
          case 'productType':
            this.loadingMap.productType = bool
            break
          case 'seasonId':
            this.loadingMap.seasonId = bool
            break
          case 'gradeIds':
            this.loadingMap.gradeId = bool
            break
          case 'subjectId':
            this.loadingMap.subjectId = bool
            break
          case 'levelDegree':
            this.loadingMap.levelDegree = bool
            break
          case 'bookVersion':
            this.loadingMap.bookVersion = bool
            break
          default:
            console.log(bool)
        }
      }

      fn(true)

      linkQueryByBD({
        queryType,
        schoolCode: this.siFromData.cityId,
        years: [this.siFromData.year],
        productTypeIds: queryType === 'p' ? undefined : (!this.siFromData.productType ? undefined : [this.siFromData.productType]),
        seasonIds: queryType === 'se' ? undefined : (!this.siFromData.seasonId ? undefined : [this.siFromData.seasonId]),
        gradeIds: queryType === 'g' ? undefined : (!this.siFromData.gradeIds ? undefined : [this.siFromData.gradeIds]),
        subjectIds: queryType === 's' ? undefined : (!this.siFromData.subjectId ? undefined : [this.siFromData.subjectId]),
        levelDegrees: queryType === 'l' ? undefined : (!this.siFromData.levelDegree ? undefined : [this.siFromData.levelDegree]),
        bookVersions: queryType === 'b' ? undefined : (!this.siFromData.bookVersion ? undefined : [this.siFromData.bookVersion])
      }).then(res => {
        console.log(res)
        if (res) {
          this.baseList[type] = res || []
        } else {
          this.$Message.error('获取联动查询项数据失败')
        }
      }).catch((err) => {
        this.$Message.error(err.message || '获取联动查询项数据失败')
      }).finally(() => {
        fn(false)
      })
    },
    linkQueryAll (status, type) {
      console.log(status)
      if (status) {
        this.baseList.productType = [] // 产品类型
        this.baseList.seasonId = [] // 学季
        this.baseList.gradeIds = [] // 年级
        this.baseList.subjectId = [] // 学科
        this.baseList.levelDegree = [] // 班次级别
        this.baseList.bookVersion = [] // 版本

        this.linkageFn(type)
      }
    },
    limitClassCount (val) {
      val = val.trim().replace(/\D/g, '')
      this.$nextTick(() => {
        this.siFromData.classCount = val > 100 ? 100 : val
      })
    },
    filterClassroomChange () {
      this.linkClassroom.resetFormStatus = true
    },
    getClassroomData (val) {
      if (this.linkClassroom.filterClassroom === 'classroom') {
        this.siFromData.areaId = val.areaId || ''
        this.siFromData.serviceCenterId = val.serviceCenterId || ''
        this.siFromData.venueId = val.venueId || ''
        this.siFromData.classroomId = val.roomId || ''

        this.siFromData.liveAreaId = ''
        this.siFromData.liveServiceCenterId = ''
        this.siFromData.liveVenueId = ''
        this.siFromData.liveRoomId = ''
      } else {
        this.siFromData.areaId = ''
        this.siFromData.serviceCenterId = ''
        this.siFromData.venueId = ''
        this.siFromData.classroomId = ''

        this.siFromData.liveAreaId = val.areaId || ''
        this.siFromData.liveServiceCenterId = val.serviceCenterId || ''
        this.siFromData.liveVenueId = val.venueId || ''
        this.siFromData.liveRoomId = val.roomId || ''
      }
    },
    datePickerChange (val, type) {
      let vs = val[0] || ''
      vs = vs ? vs + ' 00:00:00' : ''
      let ve = val[1] || ''
      ve = ve ? ve + ' 23:59:59' : ''

      this.siFromData[type + 'Start'] = vs
      this.siFromData[type + 'End'] = ve
    },
    queryList () {
      this.$refs['siFromData'].validate(valid => {
        this.formItemErr.productType = ''

        if (valid) {
          /* 校验成对范围值 */
          let flag = true
          if (!this.siFromData.remainNumStart && !this.siFromData.remainNumEnd) {
            this.formItemErr.remainNumStart = ''
            this.formItemErr.remainNumEnd = ''
          } else if (!this.siFromData.remainNumStart || !this.siFromData.remainNumEnd) {
            this.$Message.error('请完善剩余名额查询范围值')
            flag = false
            if (!this.siFromData.remainNumStart) {
              this.formItemErr.remainNumStart = '剩余名额开始值'
            } else {
              this.formItemErr.remainNumStart = ''
            }
            if (!this.siFromData.remainNumEnd) {
              this.formItemErr.remainNumEnd = '剩余名额结束值'
            } else {
              this.formItemErr.remainNumEnd = ''
            }
          }

          if (!this.siFromData.maxPersonsStart && !this.siFromData.maxPersonsEnd) {
            this.formItemErr.maxPersonsStart = ''
            this.formItemErr.maxPersonsEnd = ''
          } else if (!this.siFromData.maxPersonsStart || !this.siFromData.maxPersonsEnd) {
            this.$Message.error('请完善限额查询范围值')
            flag = false
            if (!this.siFromData.maxPersonsStart) {
              this.formItemErr.maxPersonsStart = '限额开始值'
            } else {
              this.formItemErr.maxPersonsStart = ''
            }
            if (!this.siFromData.maxPersonsEnd) {
              this.formItemErr.maxPersonsEnd = '限额结束值'
            } else {
              this.formItemErr.maxPersonsEnd = ''
            }
          }

          if (!flag) {
            return
          }

          this.selectIdMap = {}
          this.pageCurrent = 1
          this.queryListFn()
        }
      })
    },
    queryListFn () {
      if (!this.siFromData.cityId) {
        return
      }

      this.siFromData.courseTypeList = filterCourseTypeBySelfCourse(this.baseList.courseType, this.siFromData.isShowSelfCourse)

      /* 年级此处为单选 */
      let params = {
        ...this.siFromData,
        gradeIds: !this.siFromData.gradeIds ? undefined : [this.siFromData.gradeIds],

        current: this.pageCurrent,
        size: this.pageSize
      }

      this.loadingMap.queryList = true
      this.loadingMap.tableLoading = true

      queryRemainPageList(params).then(res => {
        if (res) {
          this.setAnalytics('search', params)

          this.queryData = params
          this.reQueryFlag = false
          const data = res || {}

          let tableData = data.records || []
          this.tableData = tableData.map(item => {
            let selfObj = []
            if (item.teacherEmpNo) {
              const empList = item.teacherEmpNo.split(',')
              const nameList = item.teacherName.split(',')
              empList.forEach((oItem, o) => {
                selfObj.push({
                  emp: oItem,
                  name: nameList[o]
                })
              })
            }
            item.teacherEmpNoSelf = selfObj

            item._checked = !!this.selectIdMap[item.classId]
            return item
          })
          this.pageTotal = data.total || 0

          this.$nextTick(() => {
            // 让表格吸顶
            document.getElementById('tableWrap').scrollIntoView()
          })
        } else {
          this.queryData = {}
          this.$Message.error('获取报名信息列表失败')
        }
      }).catch((err) => {
        this.$Message.error(err.message || '获取报名信息列表失败')
      }).finally(() => {
        this.loadingMap.queryList = false
        this.loadingMap.tableLoading = false
      })
    },
    resetForm () {
      this.$refs['siFromData'].resetFields()

      this.startDatePicker = ''
      this.siFromData.startDateStart = ''
      this.siFromData.startDateEnd = ''

      this.endDatePicker = ''
      this.siFromData.endDateStart = ''
      this.siFromData.endDateEnd = ''

      this.scheduleDatePicker = ''
      this.siFromData.scheduleDateStart = ''
      this.siFromData.scheduleDateEnd = ''

      this.createTimePicker = ''
      this.siFromData.createTimeStart = ''
      this.siFromData.createTimeEnd = ''

      this.payEndDatePicker = ''
      this.siFromData.payEndDateStart = ''
      this.siFromData.payEndDateEnd = ''

      this.linkClassroom.filterClassroom = 'classroom'
      this.linkClassroom.resetFormStatus = true
      this.queryData = {}

      this.setDefaultCity()
      this.setFormYear()
    },
    setDefaultCity () {
      if (this.cityList.length === 1) {
        this.siFromData.cityId = this.cityList[0].value
        this.getBaseList()
        this.queryListFn()
      }
    },
    limitNumInput (val, source, type) {
      if (val) this.formItemErr[source + type] = ''

      this.$nextTick(() => {
        val = val.replace(/\D/g, '')
        if (val - 2000 > 0) {
          val = '2000'
        }
        this.siFromData[source + type] = val
      })
    },
    limitNumBlur (source, type) {
      this.$nextTick(() => {
        let val = this.siFromData[source + type].trim()
        const sibFlag = type === 'Start' ? 'End' : 'Start'
        let sibVal = this.siFromData[source + sibFlag]
        sibVal = sibVal === undefined ? '' : sibVal
        console.log(val, sibVal)
        if (sibVal !== '') {
          if (type === 'Start') {
            if (val - sibVal > 0) {
              this.$Message.error('起始值大于结束值，请重新输入')
              val = ''
            }
          } else if (type === 'End') {
            if (sibVal - val > 0) {
              this.$Message.error('结束值小于起始值，请重新输入')
              val = ''
            }
          }
        }

        this.siFromData[source + type] = val
      })
    },
    queryTeacher (val, type) {
      if (!val.trim()) {
        return
      }

      if (!this.siFromData.cityId) {
        this.$Message.error('请选择分校')
        this.formItemErr.cityId = '请选择分校'
        return
      }

      let teacherType = type === 'teacher' ? 0 : (type === 'tutor' ? 1 : 2)
      // 0：主讲老师 1：辅导老师 2：班主任

      let payloads = {
        schoolCode: this.siFromData.cityId,
        teacherType,
        pageNo: 1,
        pageSize: 20,
        sysName: val
      }
      clearTimeout(this.queryTeacherTimer)
      this.queryTeacherTimer = setTimeout(() => {
        queryTeacher(payloads).then(res => {
          if (res) {
            this[type + 'List'] = res.rows || []
          }
          this.loadingMap[type] = false
        }).catch((err) => {
          this.$Message.error(err.message || '查询教师失败')
        }).finally(() => {
          this.loadingMap[type] = false
        })
      }, 300)
    },

    showClassDetail (item) {
      if (this.hasOwn) {
        this.opItem = {
          ...item,
          modalShow: true
        }
      } else {
        this.$emit('openClassDetail', {
          classId: item.classId,
          flag: true
        })
      }

      this.setAnalytics('classDetail', item)
    },
    closeClassDetail () {
      this.opItem = {
        modalShow: false,
        classId: ''
      }
    },
    showTeacherItemDetail (schoolCode, empNo, type) {
      this.teacherQueryInfo = {
        schoolCode,
        empNo,
        type,
        flag: true
      }

      this.setAnalytics('teacherDetail', this.teacherQueryInfo)

      this.$emit('openTeacherDetail', this.teacherQueryInfo)
    },

    selectHandler (data, row) {
      this.$set(this.selectIdMap, row.classId, row)
    },
    selectCancelHandler (data, row) {
      this.$delete(this.selectIdMap, row.classId)
    },
    selectAllHandler () {
      this.tableData.forEach(item => {
        this.$set(this.selectIdMap, item.classId, item)
      })
    },
    selectCancelAllHandler () {
      this.tableData.filter(item => {
        this.$delete(this.selectIdMap, item.classId)
      })
    },

    pageChangeHandler (page) {
      this.pageCurrent = page
      this.queryListFn()
    },
    pageSizeChangeHandler (pageSize) {
      this.pageSize = pageSize
      this.pageCurrent = 1
      this.queryListFn()
    },

    exportClass (type) {
      if (type === 'choose' && this.selectIdList.length === 0) {
        this.$Message.error('请选择要导出的班级')
        return
      }

      const params = type === 'choose' ? {
        ...this.queryData,
        classIds: this.selectIdList.join(',')
      } : {
        ...this.queryData
      }

      if (type === 'choose') {
        this.loadingMap.exportChooseLoading = true
      } else {
        this.loadingMap.exportAllLoading = true
      }

      exportClazzRemain(params).then(() => {
        this.fileDownLoadAlert(this.platform)
        if (type === 'choose') {
          this.setAnalytics('exportChoose', params)
        } else {
          this.setAnalytics('exportAll', params)
        }
      }).catch((err) => {
        this.$Message.error(err.message || '导出续排班失败')
      }).finally(() => {
        this.$nextTick(() => {
          if (type === 'choose') {
            this.loadingMap.exportChooseLoading = false
          } else {
            this.loadingMap.exportAllLoading = false
          }
        })
      })
    },
    exportTeacher () {
      let flag = true
      if (this.siFromData.productType - 1 !== 0) {
        this.formItemErr.productType = '请选择长期班'
        this.$Message.error(`请查询<span style="color: red;vertical-align: top">长期班</span>下的班级数据进行导出`)
        flag = false
      }

      if (!this.siFromData.seasonId) {
        this.formItemErr.seasonId = '请选择学季'
        flag = false
      }

      if (this.reQueryFlag) {
        this.$Message.warning('查询中，请稍后操作')
        this.selectIdMap = {}
        this.queryListFn()
        return
      }

      if (!flag) {
        return
      }

      this.loadingMap.exportTeacherLoading = true
      exportClazzRemainSZ({
        ...this.queryData
      }).then(() => {
        this.setAnalytics('exportTeacher', this.queryData)
        this.fileDownLoadAlert(this.platform)
      }).catch((err) => {
        this.$Message.error(err.message || '导出全部班级师资数据接口')
      }).finally(() => {
        this.loadingMap.exportTeacherLoading = false
      })
    },
    dataToModalTable () {
      let flag = true
      if (!this.siFromData.gradeIds) {
        this.formItemErr.gradeId = '请选择年级'
        flag = false
      }

      if (!this.siFromData.subjectId) {
        this.formItemErr.subjectId = '请选择学科'
        flag = false
      }

      if (this.reQueryFlag) {
        this.$Message.warning('查询中，请稍后操作')
        this.selectIdMap = {}
        this.queryListFn()
        return
      }

      if (!flag) {
        this.$Message.error('请选择年级和学科后再勾选相关班级进行剩余名额网页生成操作！')
        return
      }

      if (!this.selectIdList.length) {
        this.$Message.error('请选择要生成数据的班级')
        return
      }

      const list = Object.values(this.selectIdMap)
      if (list.length === 0) {
        this.$Message.error('数据为空')
        return
      }

      this.loadingMap.dataToModalLoading = true

      this.setAnalytics('page', list)

      setTimeout(() => {
        this.simpleDataModal.tableData = list
        this.loadingMap.dataToModalLoading = false
        this.simpleDataModal.flag = true
      }, 300)
    },
    exportChooseJoinData () {
      if (!this.selectIdList.length) {
        this.$Message.error('请选择要导出的班级')
        return
      } else if (this.selectIdList.length > 200) {
        this.$Message.error('学员名单导出最多支持200个班级')
        return
      }

      this.loadingMap.exportJoinLoading = true
      const params = {
        ...this.queryData,
        classIds: this.selectIdList.join(',')
      }
      exportClazzStudent(params).then(() => {
        this.setAnalytics('exportStudent', params)
        this.fileDownLoadAlert(this.platform)
      }).catch((err) => {
        this.$Message.error(err.message || '导出选中班级学员信息失败')
      }).finally(() => {
        this.loadingMap.exportJoinLoading = false
      })
    },
    closeSimpleDataModal () {
      this.simpleDataModal.flag = false
      setTimeout(() => {
        this.simpleDataModal.tableData = []
      }, 100)
    },
    setAnalytics (idKey, query) {
      const anMap = {
        search: '15-3169', // 查询
        classDetail: '15-3170', // 班级详情查询
        teacherDetail: '15-3171', // 教师详情查询
        exportChoose: '15-3172', // 导出选中班级
        exportAll: '15-3173', // 导出全部班级
        exportTeacher: '15-3174', // 导出全部班级师资数据
        page: '15-3175', // 剩余名额生成网页
        exportStudent: '15-3176' // 导出选中班级学员信息
      }
      if (window.XesAnalytics && window.XesAnalytics.track) {
        const key = anMap[idKey] || ''
        if (key) {
          XesAnalytics.track(
            key, {
              platform: this.platform,
              query
            }
          )
        }
      } else if (window.xesAnalyLog && window.xesAnalyLog.track) {
        const key = anMap[idKey] || ''
        if (key) {
          xesAnalyLog.track(
            key, {
              platform: this.platform,
              query
            }
          )
        }
      } else {
        console.log('埋点失败')
      }
    },
    rendColumns (list) {
      this.tableColumns = list
    },
    extShow () {
      this.showItem = !this.showItem
    },
    dropClick (name) {
      switch (name) {
        case 'choose':
          this.exportClass('choose')
          break
        case 'teacher':
          this.exportTeacher()
          break
        case 'table':
          this.dataToModalTable()
          break
        case 'join':
          this.exportChooseJoinData()
          break
      }
    },
    goToTop () { // 滚动到顶部
      document.getElementById('roll-search-wrap').scrollIntoView({
        behavior: 'smooth'
      })
    },
    filterCourseType (item) {
      // 是否显示自建课 选择为否时 课程类型不展示自建课518
      return !(this.siFromData.isShowSelfCourse === '0' && item.id - 518 === 0)
    },
    isShowSelfCourseChange (val) {
      if (val === '0' && this.siFromData.courseType - 518 === 0) {
        this.siFromData.courseType = ''
      }
    }
  },
  watch: {
    '$route' (to) {
      if (to.name === 'applyInfoSearch' || to.name === 'SignInfoManagement') {
        setTimeout(() => {
          if (window.XesAnalytics && window.XesAnalytics.track) {
            XesAnalytics.view(
              '15-3541', {
                platform: this.platform
              }
            )
          }

          if (window.xesAnalyLog && window.xesAnalyLog.track) {
            xesAnalyLog.view(
              '15-3541', {
                platform: this.platform
              }
            )
          }
        }, 200)
      }
    }
  },
  computed: {
    selectIdList () {
      return Object.keys(this.selectIdMap)
    }
  },
  created () {
    this.getCityList().then(() => {
      this.initYear().then(() => {
        this.setDefaultCity()
      })
    })
  },
  mounted () {
  }
}
</script>

