<template>
  <div class="tal-notification-page">
    <div class="form-wrapper">
      <Form
        ref="form"
        inline
        class="ivu-common-shadow"
        style="padding: 18px 0;"
        :model="formMessage"
        :rules="formRules"
        label-position="right"
        :label-width="90">
        <FormItem label="通知时间" prop="startDate">
          <DatePicker v-model="formMessage.startDate" :clearable="false" format="yyyy-MM-dd HH:mm:ss" type="datetime" @on-open-change="startTimeChange" style="width: 160px">
          </DatePicker>
          <span style="margin-left: 10px;">至</span>
        </FormItem>
        <FormItem label="" :label-width="0" prop="endDate">
          <DatePicker v-model="formMessage.endDate" :clearable="false" format="yyyy-MM-dd HH:mm:ss" type="datetime" @on-open-change="endTimeChange" style="width: 160px">
          </DatePicker>
        </FormItem>
        <FormItem label="通知已读状态">
          <Select v-model="formMessage.isRead" style="width: 160px;" clearable>
            <Option label="未读" :value="0"></Option>
            <Option label="已读" :value="1"></Option>
          </Select>
        </FormItem>
        <FormItem label="通知类型">
          <Select v-model="formMessage.menuId" style="width: 160px;" clearable>
            <Option v-for="item in menuList" :key="item.menu" :value="item.menu" :label="item.name"></Option>
          </Select>
        </FormItem>
        <Button type="primary" :loading="tableLoading" @click="searchFn">搜索</Button>
        <a href="javascript:void(0)" style="margin-left: 10px" @click="handleAllRead">全部标记已读</a>
      </Form>
    </div>
    <div class="table-wrapper">
      <Table
        size="small"
        :columns="tableColumns"
        :data="tableData"
        :loading="tableLoading"
        @on-selection-change="handleSelectChange">
        <template slot="isRead" slot-scope="scope">
          <span v-if="scope.row.isRead === 1">已读</span>
          <span v-else style="color: #ff0000">未读</span>
        </template>
        <template slot="operation" slot-scope="scope">
          <Button type="primary" size="small" v-if="scope.row.isRead === 0" @click="handleSetRead(scope.row)">标记为已读</Button>
        </template>
      </Table>
    </div>
    <div class="page-wrapper">
      <Button type="primary" @click="handleSelectRead">将勾选项标记为已读</Button>
      <div style="float: right;">
        <Page
          :total="dataTotal"
          :page-size="currentPageSize"
          :page-size-opts="pageSizeOpt"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="handleCurrentPageSizeChange"
          @on-change="handleCurrentPageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import table from '../../mixin/table'
import { getPlatFormMenuTree, queryMessage, setRead, ERROR_TOKEN_CODE } from '../../api/notice'
import { dateFormat, formatSearchTime } from '../../utils/utils'
export default {
  name: 'TalNotificationPage',
  mixins: [table],
  data () {
    return {
      pageSizeOpt: [10, 20, 40, 100],
      currentPageSize: 20,
      formMessage: {
        startDate: formatSearchTime(-7),
        endDate: formatSearchTime(1),
        isRead: '',
        menuId: ''
      },
      menuList: [],
      formRules: {
        startDate: [{ type: 'date', required: true, message: '开始时间必填', trigger: 'blur' }],
        endDate: [{ type: 'date', required: true, message: '结束时间必填', trigger: 'blur' }]
      },
      tableColumns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          key: 'menuName',
          title: '通知类型',
          width: 100,
          align: 'center'
        },
        {
          key: 'createDate',
          title: '通知时间',
          width: 200,
          align: 'center'
        },
        {
          key: 'msg',
          title: '通知内容',
          minWidth: 200,
          align: 'center'
        },
        {
          slot: 'isRead',
          title: '是否已读',
          width: 100,
          align: 'center'
        },
        {
          slot: 'operation',
          title: '操作',
          width: 200,
          align: 'center'
        }
      ],
      selectedData: []
    }
  },
  methods: {
    startTimeChange () {
      const { startDate, endDate } = this.formMessage
      if (startDate && endDate) {
        if (new Date(startDate).getTime() > new Date(endDate).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formMessage.endDate = new Date(new Date(this.formMessage.startDate).getTime() + 24 * 60 * 60 * 1000)
          return
        }
        if (new Date(endDate).getTime() - new Date(startDate).getTime() > 90 * 24 * 60 * 60 * 1000) {
          this.$Message.warning({ content: '最长可查询3个月的数据', duration: 5 })
          this.formMessage.endDate = new Date(new Date(this.formMessage.startDate).getTime() + 90 * 24 * 60 * 60 * 1000)
        }
      }
    },
    endTimeChange () {
      const { startDate, endDate } = this.formMessage
      if (startDate && endDate) {
        if (new Date(startDate).getTime() > new Date(endDate).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formMessage.startDate = new Date(new Date(this.formMessage.endDate).getTime() - 24 * 60 * 60 * 1000)
          return
        }
        if (new Date(endDate).getTime() - new Date(startDate).getTime() > 90 * 24 * 60 * 60 * 1000) {
          this.$Message.warning({ content: '最长可查询3个月的数据', duration: 5 })
          this.formMessage.startDate = new Date(new Date(this.formMessage.endDate).getTime() - 90 * 24 * 60 * 60 * 1000)
        }
      }
    },
    getTableData () {
      let params = {
        startDate: dateFormat(this.formMessage.startDate, 'yyyy-MM-dd HH:mm:ss'),
        endDate: dateFormat(this.formMessage.endDate, 'yyyy-MM-dd HH:mm:ss'),
        menuId: this.formMessage.menuId,
        isRead: this.formMessage.isRead,
        pageNo: this.currentPageNum,
        pageSize: this.currentPageSize
      }
      this.tableLoading = true
      queryMessage(params).then(response => {
        const list = response.records || []
        list.forEach((item) => {
          const { body } = item
          // eslint-disable-next-line
          item.msg = eval('(' + body + ')').msg
          item._disabled = item.isRead === 1
        })
        this.tableData = list
        this.dataTotal = response.totalCount
        this.tableLoading = false
      }).catch(error => {
        this.$Message.error('获取历史消息失败:' + error.message)
        this.tableLoading = false
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
      })
    },
    handleSelectChange (val) {
      this.selectedData = val
    },
    handleSetRead (variable) {
      const params = {
        messageReadDtos: []
      }
      if (Array.isArray(variable)) {
        params.messageReadDtos = variable
      } else {
        params.messageReadDtos = [
          {
            menuId: variable.menu,
            messageId: variable.id
          }
        ]
      }
      setRead(params).then(() => {
        this.searchFn()
        this.$Modal.remove()
      }).catch(error => {
        this.$Message.warning('操作失败:' + error.message)
        if (error.code === ERROR_TOKEN_CODE) {
          this.$emit('on-token-error')
        }
        this.$Modal.remove()
      })
    },
    handleAllRead () {
      this.$Modal.confirm({
        title: '提示',
        content: '确定将全部未读消息标记为已读？',
        loading: true,
        onOk: () => {
          this.handleSetRead([])
        }
      })
    },
    handleSelectRead () {
      if (this.selectedData.length === 0) {
        this.$Message.error('请勾选数据')
        return
      }
      this.$Modal.confirm({
        title: '提示',
        content: '确定将勾选消息标记为已读',
        loading: true,
        onOk: () => {
          const messageReadDtos = this.selectedData.map(item => {
            return {
              menuId: item.menu,
              messageId: item.id
            }
          })
          this.handleSetRead(messageReadDtos)
        }
      })
    },
    _normalizeMenu (menuTree) {
      menuTree.forEach(item => {
        if (item.isLeaf === 1) {
          this.menuList.push(item)
        } else {
          if (item.children && item.children.length > 0) {
            this._normalizeMenu(item.children)
          }
        }
      })
    }
  },
  mounted () {
    getPlatFormMenuTree().then(response => {
      this._normalizeMenu(response)
      this.searchFn()
    }).catch(error => {
      this.$Message.warning('获取通知菜单失败:' + error.message)
      if (error.code === ERROR_TOKEN_CODE) {
        this.$emit('on-token-error')
      }
    })
  }
}
</script>

<style scoped lang="less">
  .tal-notification-page {
    padding: 10px;
    overflow: auto;
    .table-wrapper {
      margin-top: 10px;
    }
    .page-wrapper {
      margin-top: 10px;
      height: 40px;
    }
  }
</style>
