import $ from '../utils/api.request'

const auth = $.authAxios

/* 阿里oss 鉴权 */
export const uploadGetAuth = params => {
  return auth.request({
    url: '/upload/getAuth',
    method: 'get',
    params: params
  })
}

/* 阿里oss 获取回调信息 */
export const uploadGetUrl = params => {
  return auth.request({
    url: 'upload/getUrl',
    method: 'get',
    params: params
  })
}

/* 阿里oss 获取删除信息 */
export const uploadCancel = params => {
  return auth.request({
    url: 'upload/cancel',
    method: 'get',
    params: params
  })
}
