<template>
  <div>
    <Table :columns="cols" :data="dataList" no-data-text="暂无退费详情" :loading="tableLoading"></Table>
    <div style="text-align: right;margin-top: 10px">
      <span>实缴金额：</span>
      <count-to :startValue="0" :endVal="row.actualPayedPrice - row.actualRefundedPrice" :duration="1" suffix="元" :decimals="2" style="margin-right: 20px"></count-to>
      <span>调价后总额：</span>
      <count-to :startValue="0" :endVal="afterPrice" :duration="1" suffix="元" :decimals="2"></count-to>
    </div>
  </div>
</template>
<script>
  import { queryRefundDiffDetail } from '../../api/finance'
  import CountTo from 'vue-count-to'
  export default {
    props: {
      cityCode: String,
      row: Object
    },
    components: {
      CountTo
    },
    data () {
      return {
        cols: [
          { title: '班级ID', align: 'center', key: 'id' },
          { title: '班级名称', align: 'center', key: 'name' },
          { title: '主讲老师', align: 'center', key: 'teacherNames' },
          {
            title: '班级原价',
            align: 'center',
            key: 'originalPriceBeforeCorrect',
            render: (h, params) => {
              // :start-val="0" :endVal="item.classPrice" :duration="1" :prefix="'￥'" :decimals="2"
              return h(CountTo, {
                props: {
                  startValue: 0,
                  endVal: params.row.originalPriceBeforeCorrect,
                  duration: 1,
                  suffix: '元',
                  decimals: 2
                }
              })
            }
          },
          {
            title: '调价后价格',
            align: 'center',
            key: 'originalPrice',
            render: (h, params) => {
              return h(CountTo, {
                props: {
                  startValue: 0,
                  endVal: params.row.originalPrice,
                  duration: 1,
                  suffix: '元',
                  decimals: 2
                }
              })
            }
          },
          {
            title: '包含报名费',
            align: 'center',
            key: 'feetype',
            render: (h, params) => {
              return h('span', {}, params.row.feetype === 1 ? '否' : '是')
            }
          },
          { title: '总课次', align: 'center', key: 'classCount' },
          { title: '课程分期', align: 'center', key: 'stageNum' },
          {
            title: '分期课次',
            align: 'center',
            key: 'actualPrice',
            render: (h, params) => {
              return h('span', {}, params.row.startCurriculumNum + '-' + params.row.endCurriculumNum)
            }
          },
          {
            title: '优惠券',
            align: 'center',
            render: (h, params) => {
              if (params.row.couponPrice === null) {
                return h('span', {}, '无')
              } else {
                return h(CountTo, {
                  props: {
                    startValue: 0,
                    endVal: params.row.couponPrice,
                    duration: 1,
                    suffix: '元',
                    decimals: 2
                  }
                })
              }
            }
          },
          {
            title: '限时购',
            align: 'center',
            render: (h, params) => {
              if (params.row.promotionPrice === null) {
                return h('span', {}, '无')
              } else {
                return h(CountTo, {
                  props: {
                    startValue: 0,
                    endVal: params.row.promotionPrice,
                    duration: 1,
                    suffix: '元',
                    decimals: 2
                  }
                })
              }
            }
          },
          {
            title: '上课课次数',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, params.row.endPosition - params.row.startPosition + 1)
            }
          },
          { title: '退费课次数', align: 'center', key: 'refundCourseCount' },
          {
            title: '应收金额',
            align: 'center',
            key: 'actualPrice',
            render: (h, params) => {
              return h(CountTo, {
                props: {
                  startValue: 0,
                  endVal: params.row.actualPrice - params.row.actualShouldRefundPrice,
                  duration: 1,
                  suffix: '元',
                  decimals: 2
                }
              })
            }
          }
        ],
        dataList: [],
        tableLoading: false,
        afterPrice: 0
      }
    },
    mounted () {
      this.tableLoading = true
      queryRefundDiffDetail({
        cityCode: this.cityCode,
        registId: this.row.registryId
      }).then(data => {
        this.dataList = data
        this.dataList.forEach(d => {
          this.afterPrice += d.actualPrice - d.actualShouldRefundPrice
        })
      }).catch(err => {
        this.$Message.error({ content: '查询差价退费详情失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    }
  }
</script>
