        '4': '面授'
      },
      gradeTypeList: [], // 年部列表
      gradeList: [], // 年级列表
      levelList: [], // 班次列表
      areaList: [], // 城市列表
      serviceCenterList: [], // 服务中心列表
      venueList: [], // 区域列表
      classTimeTypeList: ['周一', '周二', '周三', '周四', '周五', '周六', '周日', '零期', '一期', '二期', '三期', '四期', '五期', '六期'], // 上课时间类型
      teacherList: [], // 老师列表
      tutorTeacherList: [], // 辅导老师列表
      changeClassTypeList: [], // 转班类型列表
      startDate: '',
      endDate: '',
      // 转班弹框表单
      formData1: {
        registId: '', // 报名id
        studentId: '', // 学员id
        targetClassId: '', // 目标班级id
        sourceClassId: '', // 原班id
        beginNum: null, // 转班起始课次
        cityCode: this.studentDetail.cityCode, // 分校编码
        description: '', // 转班描述
        changeClassType: this.changeClassType, // 转班类型 0-普通转班 1-候补转班
        reasonTypeId: '', // 转班原因id
        isSpecial: 0 // 是否为特殊转班 0-否 1-是
      },
      // 班级查找弹框表单
      formData2: {
        cityCode: '', // 城市	是
        year: new Date().getFullYear() + '', // 年份	是
        termId: '', // 学期id	是
        gradeTypeId: '', // 年部id	是
        gradeId: '',	// String	年级id
        levelId: '',		// String	班次id
        classType: '', // 授课类型
        teacherIds: '', // 老师id
        tutorId: '', // 辅导老师id
        subjectIds: '', // 学科id
        areaId: '', // 区域
        serviceCenterId: '', // 服务中心id
        venueId: '', // 教学点id
        classtimeTypeId: '', // 请选择上课时间类型id
        classtimeTimePeriod: '', // 上课时间
        startDate: '', // 开课日期start
        endDate: '', // 开课日期end
        isSchoolTest: undefined, // 是否进行入学测试
        claStartCount: null, // 剩余课次区间start
        claEndCount: null, // 剩余课次区间end
        minRemainNum: null, // 剩余名额区间start
        maxRemainNum: null // 剩余名额区间end
      },
      tableData: [],
      tableColumns: [
        {
          title: '班级名称',
          key: 'name',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '全国班')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.id)
                  }
                }
              }, params.row.name)
            ])
          }
        },
        { title: '授课类型',
          key: 'classType',
          align: 'center',
          render: (h, params) => {
            return h('span', {
              style: {}

            }, this.getClassType(params.row.classType))
          } },
        { title: '学科', key: 'subjectNames', align: 'center' },
        { title: '主讲教师', key: 'teacherNames', align: 'center' },
        { title: '辅导教师', key: 'tutorRealName', align: 'center' },
        { title: '开课日期', key: 'startDate', align: 'center' },
        { title: '结课日期', key: 'endDate', align: 'center' },
        { title: '上课时间', key: 'classtimeNames', align: 'center' },
        { title: '服务中心', key: 'serviceCenterName', align: 'center' },
        { title: '教室', key: 'classroomName', align: 'center' },
        { title: '上课进度',
          key: 'classjin',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.passedCount + '/' + params.row.classCount)
          } },
        { title: '报名进度', key: 'baojin', align: 'center' },
        { title: '剩余名额', key: 'remainNum', align: 'center' },
        { title: '候补人数', key: 'waitCount', align: 'center' },
        { title: '总金额', key: 'price', align: 'center' },
        { title: '变动记录',
          key: 'classChangeName',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('Poptip', {
              style: {
                display: 'inline-block',
                marginRight: '5px'
              },
              props: {
                trigger: 'hover',
                width: '300',
                wordWrap: true,
                content: params.row.classChangeName
              }
            }, this.getClassChangeName(params.row.classChangeName))
          }
        },
        { title: '是否进行入学测试',
          key: 'isSchooltest',
          width: 130,
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isSchooltest === 0 ? '否' : '是')
          } },
        {
          title: '操作',
          key: 'caozuo',
          width: 80,
          align: 'center',
          render: (h, params) => {
            if (params.row.id !== this.oldClassInfo.id) {
              return h('div', [
                h('a', {
                  style: {
                    display: 'inline-block',
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.changeConfirm(params.row)
                    }
                  }
                }, '转入')
              ])
            }
          }
        }
      ]
    }
  },
  methods: {
    getClassTypeHandler () {
      let req = {}
      getClassType(req).then(res => {
        this.classTypeList = res
        this.formData2.classType = this.oldClassInfo.classType
        this.getLevelList()
      }).catch(err => {
        this.$Message.warning({ content: '获取授课类型失败:' + err.message, duration: 5 })
      })
    },
    // 变动记录
    getClassChangeName (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    },
    save () {
      if (this.isLoading) {
        return
      }
      if (!this.formData1.beginNum) {
        this.$Message.warning({ content: '缺少转班起始课次', duration: 5 })
        return
      }
      if (this.formData1.beginNum > this.oldClassInfo.classCount) {
        this.$Message.warning({ content: '原班级转班课次应小于或等于总课次！', duration: 5 })
        return
      }
      if (!this.formData1.targetClassId) {
        this.$Message.warning({ content: '请选择转入班级', duration: 5 })
        return
      }
      this.formData1.studentId = this.studentDetail.studentId
      this.formData1.sourceClassId = this.oldClassInfo.classId
      this.formData1.cityCode = this.studentDetail.cityCode // 分校编码
      this.formData1.uid = this.oldClassInfo.uid
      this.formData1.registId = this.oldClassInfo.registId
      this.formData1['X-Ca-referer'] = this.from
      this.isLoading = true
      changeClass(this.formData1).then(data => {
        const int = setInterval(() => {
          changeClassResult({
            cityCode: data.cityCode,
            studentId: this.studentDetail.studentId,
            classId: this.formData1.targetClassId, // 目标班级id
            sourceClassId: this.oldClassInfo.classId, // 原班id
            targetNum: this.formData1.beginNum, // 起始转班课次
            registId: data.registId,
            tradeResultId: data.queryKey,
            'X-Ca-referer': this.from
          }).then(rr => {
            if (rr.execResult !== '0') {
              clearInterval(int)
              if (rr.execResult === '1') {
                this.modalValue = false
                this.$Message.success({ content: '转班成功', duration: 5 })
                this.$emit('success')
              } else if (rr.execResult === '2') {
                this.$Message.error({ content: `转班失败，原因：${rr.message}`, duration: 5 })
                this.$emit('fail')
              }
              this.isLoading = false
            }
          }).catch(err => {
            this.isLoading = false
            clearInterval(int)
            this.$Message.error({ content: `转班失败，原因：${err.message}`, duration: 5 })
          })
        }, 1000)
      }).catch((err) => {
        this.$Message.error({ content: '保存失败:' + err.message, duration: 5 })
        this.isLoading = false
      })
    },

    // 开课日期、结课日期改变
    startDateChange (val) {
      if (this.formData2.endDate) {
        if (new Date(val).getTime() > new Date(this.formData2.endDate).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData2.startDate = ''
          this.startDate = ''
          return
        }
      }
      this.formData2.startDate = val
    },
    endDateChange (val) {
      if (this.formData2.startDate) {
        if (new Date(this.formData2.startDate).getTime() > new Date(val).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData2.endDate = ''
          this.endDate = ''
          return
        }
      }
      this.formData2.endDate = val
    },

    // 区域改变
    areaChange (val) {
      this.serviceCenterList = []
      this.formData2.serviceCenterId = ''
      if (!val) {
        return false
      }
      this.getServiceCenter()
    },
    serviceChange (val) {
      this.venueList = []
      this.formData2.venueId = ''
      if (!val) {
        return false
      }
      this.getVenue()
    },
    // 获取服务中心
    getServiceCenter () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        areaId: this.formData2.areaId
      }
      getServiceCenter(req).then((data) => {
        this.serviceCenterList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心失败:' + err.message, duration: 5 })
      })
    },
    // 获取教学点
    getVenue () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        serviceId: this.formData2.serviceCenterId
      }
      getVenue(req).then((data) => {
        this.venueList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取教学点失败:' + err.message, duration: 5 })
      })
    },

    // 获取主讲教师列表  教师类型（0：主讲老师 1：辅导老师 2：班主任）
    getTeacherList (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          cityCode: this.studentDetail.cityCode,
          businessType: this.formData2.classType,
          sysName: query,
          teacherType: 0,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req).then((data) => {
          this.teacherList = data.records
          this.searchLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: '获取老师失败:' + err.message, duration: 5 })
          this.searchLoading = false
        })
      }
    },
    getTeacherList2 (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          cityCode: this.studentDetail.cityCode,
          businessType: this.formData2.classType,
          sysName: query,
          teacherType: 1,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req).then((data) => {
          this.tutorTeacherList = data.records
          this.searchLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: '获取老师失败:' + err.message, duration: 5 })
          this.searchLoading = false
        })
      }
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    // 年部改变
    gradeTypeChange (val) {
      this.formData2.gradeId = ''
      this.formData2.levelId = ''
      this.gradeList = []
      this.levelList = []
      if (!val) {
        return
      }
      this.getGradeList()
    },
    // 年级改变
    gradeChange (val) {
      this.formData2.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      this.getLevelList()
    },
    // 获取年级
    getGradeList (val) {
      let req = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'g',
        year: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId
      }
      getLinkage(req).then((data) => {
        this.gradeList = data
        if (val) {
          this.formData2.gradeId = val
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取年级失败:' + err.message, duration: 5 })
      })
    },
    // 获取班次
    getLevelList () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'c',
        year: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId,
        gradeIds: this.formData2.gradeId,
        classType: this.formData2.classType
      }
      getLinkage(req).then((data) => {
        this.levelList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取班次失败:' + err.message, duration: 5 })
      })
    },

    // 获取基础数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // 获取年部
      let req1 = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'gt',
        years: this.oldClassInfo.year,
        termIds: this.oldClassInfo.termId
      }
      getLinkage(req1).then((data) => {
        this.gradeTypeList = data
        this.formData2.gradeTypeId = this.oldClassInfo.gradeTypeId
        this.getGradeList(this.oldClassInfo.gradeId)
        this.getLevelList()
      }).catch((err) => {
        this.$Message.warning({ content: '获取年部失败:' + err.message, duration: 5 })
      })
      // 获取区域
      let req4 = {
        cityCode: this.studentDetail.cityCode
      }
      getArea(req4).then((data) => {
        this.areaList = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取区域失败:' + err.message, duration: 5 })
      })
      // 获取上课时间类型
      // getClassTimeTypeList(req4).then((data) => {
      //   this.classTimeTypeList = data
      // }).catch((err) => {
      //   this.$Message.warning({content:'获取上课时间类型失败:' + err.message,duration:5})
      // })
    },

    // 转班类型查询
    getChangeClassType () {
      getChangeClassType({
        cityCode: this.studentDetail.cityCode
      }).then((res) => {
        this.changeClassTypeList = res
        if (this.changeClassTypeList) {
          this.formData1.reasonTypeId = res[0].chanageClassTypeId
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取转班类型失败:' + err.message, duration: 5 })
      })
    },
    beginNumChange (val) {
      if (val && val > this.oldClassInfo.classCount) {
        this.$Message.warning({ content: '原班级转班课次应小于或等于总课次！', duration: 5 })
        this.$nextTick(function () {
          this.formData1.beginNum = this.oldClassInfo.classCount
        })
      } else {
        this.formData1.beginNum = val
      }
    },
    // 查找目标班级
    queryTargetClass () {
      if (this.formData2.claStartCount != null && this.formData2.claEndCount != null) {
        if (this.formData2.claStartCount > this.formData2.claEndCount) {
          this.$Message.warning('剩余课次填写错误')
          return
        }
      }
      if (this.formData2.minRemainNum != null && this.formData2.maxRemainNum != null) {
        if (this.formData2.minRemainNum > this.formData2.maxRemainNum) {
          this.$Message.warning('剩余名额填写错误')
          return
        }
      }
      this.formData2.year = this.oldClassInfo.year
      this.formData2.termId = this.oldClassInfo.termId
      this.formData2.subjectIds = this.oldClassInfo.subjectIds
      this.formData2.cityCode = this.studentDetail.cityCode
      this.formData2.pageNo = this.pageNum
      this.formData2.pageSize = this.pageSize
      if (!this.formData2.gradeTypeId) {
        this.$Message.warning({ content: '请选择年部', duration: 5 })
        return
      }
      getClassInfo(this.formData2).then((res) => {
        this.tableData = res.records
        this.pageTotal = res.totalCount
        this.tableData.forEach((e) => {
          if (e.startDate) {
            e.startDate = e.startDate.substring(0, 10)
          }
          if (e.endDate) {
            e.endDate = e.endDate.substring(0, 10)
          }
        })
        this.$Message.success('查询成功')
      }).catch((err) => {
        this.tableData = []
        this.$Message.warning({ content: '查询错误:' + err.message, duration: 5 })
      })
    },
    changeConfirm (row) {
      this.formData1.targetClassId = row.id
      this.targetClassName = row.name
      this.modalValue2 = false
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    pageChange (val) {
      this.pageNum = val
      this.queryTargetClass()
    },
    pageSizeChange (size) {
      this.pageSize = size
      this.pageNum = 1
      this.queryTargetClass()
    },
    search () {
      this.pageNum = 1
      this.queryTargetClass()
    },
    setEnterEvent (e) {
      let key = window.event.keyCode
      if (key === 13) {
        this.search()
      }
    }
  },
  // 监听第二个弹框
  watch: {
    modalValue2: function (val) {
      if (val) {
        this.getBaseData()
        // 监听enter事件
        document.addEventListener('keydown', this.setEnterEvent)
      } else {
        document.removeEventListener('keydown', this.setEnterEvent)
        this.tableData = []
        this.pageTotal = 0
        this.pageNum = 1
        this.pageSize = 10
        // this.gradeList = []
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        if (this.value === true) {
          this.formData1.beginNum = this.oldClassInfo.passedCount + 1
          this.getChangeClassType()
        }
        return this.value
      },
      set: function (val) {
        if (val === false) {
          // 关闭modal清空表单信息
          this.formData1.registId = ''
          this.formData1.studentId = ''
          this.formData1.targetClassId = ''
          this.formData1.sourceClassId = ''
          this.formData1.cityCode = ''
          this.formData1.description = ''
          this.formData1.reasonTypeId = ''
          this.formData1.isSpecial = 0
          this.targetClassName = ''
          this.$emit('close')
        }
      }
    }
  },
  mounted () {
    this.getClassTypeHandler()
  }
}
</script>
<style lang="less" scoped>
  .stuRight{
    margin-left: 10px;
  }
  .class_top {
    width: 100%;
    background: #fcfcfc;
  }

  .class_nav {
    position: relative;
    width: 100%;
    background: #fff;
    margin-top: 15px;
    font-size: 14px;
    padding: 10px;
  }
</style>
